import d3 from '@/libs/d3/d3.min';

/**
 * Функция хелпер для преобразования даты в строку.
 *
 * @param {Date} date - Дата для преобразования
 * @param {String} delimiter - Разделитель между датой и временем
 * @param {Boolean} withYear - включать год в дату?
 *
 * @returns {String}
 *
 * @since Version 1.0.1
 */
export const dateToShortString = (date) => {
  const format = '%e %b %y <br> %I:%M %p';
  const dateFormat = d3.timeFormat(format);
  return dateFormat(date);
};

export const dateToShortStringLine = (date) => {
  const format = '%e %b %y, %I:%M %p';
  const dateFormat = d3.timeFormat(format);
  return dateFormat(date);
};

/**
 * Функция хелпер для преобразования даты в строку.
 *
 * @param {Date} date - Дата для преобразования
 * @param {String} delimiter - Разделитель между датой и временем
 * @param {Boolean} withYear - включать год в дату?
 *
 * @returns {String}
 *
 * @since Version 1.0.1
 */
export const dateToString = (date, delimiter = '@ ', withYear = true) => {
  const dateFormatForToday = d3.timeFormat(`Today ${delimiter}%I:%M %p`);
  const format = withYear ? `%d %b %Y ${delimiter}%I:%M %p` : `%d %B ${delimiter}%I:%M %p`;

  const dateFormat = d3.timeFormat(format);
  const d = new Date(date);
  const todaysDate = new Date();
  let result;

  if (d.setHours(0, 0, 0, 0) === todaysDate.setHours(0, 0, 0, 0)) {
    result = dateFormatForToday(date);
  } else {
    result = dateFormat(date);
  }

  return result;
};


/**
 *
 * Возвращает прошедшее количество времени в подходящих единицах измерения (секунды, минуты дни)
 *
 * @param {number} delta - проемжуток времени в милисекундах
 * @returns {string}
 *
 * @since Version 1.0.2
 */
export const dateToPastTime = (delta) => {
  let res = '';

  if (delta < 2001) {
    res = '1 second';
  } else if (delta < 60 * 1000) {
    res = `${Math.floor(delta / 1000)} seconds`;
  } else if (delta < 2 * 60 * 1000) {
    res = '1 minute';
  } else if (delta < (60 * 60 * 1000) + 1) {
    res = `${Math.floor(delta / (60 * 1000))} minutes`;
  } else if (delta < (2 * 60 * 60 * 1000)) {
    res = '1 hour';
  } else if (delta < (24 * 60 * 60 * 1000)) {
    res = `${Math.floor(delta / (60 * 60 * 1000))} hours`;
  } else if (delta < (2 * 24 * 60 * 60 * 1000)) {
    res = '1 day';
  } else {
    res = `${Math.floor(delta / (24 * 60 * 60 * 1000))} days`;
  }

  return `${res} ago`;
};


/**
 * Функция форматирования дат для трейдов
 *
 * @param {Date} date - Дата для преобразования
 * @returns {string}
 *
 * @since Version 1.0.3
 */
export const tradesDate = (date) => {
  const template = '%B %-d @ %H:%M:%S';
  const formatTime = d3.timeFormat(template);
  return formatTime(date);
};

