import { BigNumber } from 'bignumber.js';
import { helpers } from 'vuelidate/lib/validators';

export default decimalPlaces => helpers.withParams(
  { type: 'maxDecimalPlaces', max: decimalPlaces },
  (value) => {
    const isEmpty = !helpers.req(value);
    const num = BigNumber(value);
    return isEmpty || (!num.isNaN() && num.dp() <= decimalPlaces);
  },
);



