<template>
  <div class="the-wallet-balance">
    <div class="the-wallet-balance__head">
      <div class="the-wallet-balance__token">
        <div class="the-wallet-balance__heading the-wallet-balance__token-title">Token</div>
        <div class="the-wallet-balance__btn">
          <div class="the-wallet-balance__search">
            <div
              class="the-wallet-balance__search-placeholder"
              @click="searchActiveted = true"
              v-if="!searchActiveted"
            >
              Search
              <div class="the-wallet-balance__search-placeholder-icon">
                <e-icon :src="iconSearch"/>
              </div>
            </div>
            <div
              v-else
              class="the-wallet-balance__search-input"
            >
              <e-token-search
                v-model="searchString"
                @blur="closeSearch"
              />
            </div>
          </div>
        </div>
      </div>

      <div class="the-wallet-balance__heading the-wallet-balance__heading_number">
        <div class="the-wallet-balance__heading-text">
          {{
            {
              [PRIVATE_KEY_TYPE]: 'Wallet',
              [METAMASK_WALLET_TYPE]: 'MetaMask',
              [LEDGER_WALLET_TYPE]: 'Ledger',
            }[walletType]
          }} balance
        </div>
        <div class="the-wallet-balance__hint">
          ?
          <div class="the-wallet-balance__tooltip">
            <div class="the-wallet-balance__tooltip-heading">private balance</div>
            <div class="the-wallet-balance__tooltip-text">
              These are your private funds, you can't trade them on our platform. Deposit them to your Contract balance to be able to trade.
            </div>
          </div>
        </div>
      </div>

      <div class="the-wallet-balance__heading the-wallet-balance__heading_number">
        <div class="the-wallet-balance__heading-text">
          Contract balance
        </div>
        <div class="the-wallet-balance__hint">
          ?
          <div class="the-wallet-balance__tooltip">
            <div class="the-wallet-balance__tooltip-heading">Contract balance</div>
            <div class="the-wallet-balance__tooltip-text">
              These are your private funds too, but stored on the DMEX smart contract. You can use these funds to trade. Btw, we can't do anything with them without your signature.
            </div>
          </div>
        </div>
      </div>

      <div class="the-wallet-balance__heading the-wallet-balance__heading_number">
        <div class="the-wallet-balance__heading-text">
          ETH Value
        </div>
        <div class="the-wallet-balance__hint">
          ?
          <div class="the-wallet-balance__tooltip">
            <div class="the-wallet-balance__tooltip-heading">Ether value</div>
            <div class="the-wallet-balance__tooltip-text">
              These are sum of your funds converted to ETH. Latest market price is used as conversion price.
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="the-wallet-balance__tokens-table">
      <e-wallet-token
        v-for="{ contract, privateBalance, tradableBalance, name, shortName, decimals, price, operations } of displayedBalance"
        :key="contract"
        v-bind="{ privateBalance, tradableBalance, price, operations }"
        :token="{ contract, name, shortName, decimals }"
        @click="handleRowClick(contract)"
      />
    </div>
    <footer class="the-wallet-balance__footer">
      <div class="the-wallet-balance__history-btn">
        <e-button
          fill="white"
          size="s"
          padding="small"
          adaptiveMod="sm-size"
          width="full"
          @click="showHistoryPopup"
        >
          TRANSACTION HISTORY
        </e-button>
      </div>
    </footer>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import iconSearch from '@/icons/e-icon_search_wide.svg';

import EWalletToken from '@/components/EWalletToken';
import ETokenSearch from '@/components/ETokenSearch';
import ESwitch from '@/components/ESwitch';
import EButton from '@/components/EButton';

import TheTokenSearch from '@/containers/blocks/TheTokenSearch';
import EIcon from '@/components/EIcon';

import { POPUP_HISTORY } from '@/store/constants';
import { routesMixin, searchTokenMixin } from '@/util/mixins';

export default {
  name: 'the-wallet-balance',

  data() {
    return {
      searchString: '',
      searchActiveted: false,
      iconSearch,
    };
  },

  computed: {
    ...mapGetters({
      walletType: 'wallets/activeWalletType',
      balance: 'balance/userEthAndTokens',
      missingTokens: 'balance/missingTokens',
    }),

    displayedBalance() {
      if (this.searchString) {
        return this.$searchTokens(this.searchString, this.balance.concat(this.missingTokens));
      }

      return this.balance;
    },
  },

  methods: {
    showHistoryPopup() {
      this.$router.push(this.$popupRoute(POPUP_HISTORY));
    },

    closeSearch() {
      if (this.searchString === '') {
        this.searchActiveted = false;
      }
    },

    handleRowClick(contract) {
      // this.$router.push(this.$popupRoute({ popupPage: POPUP_MY_WALLET, walletToken: contract }));

      this.$router.push({
        name: 'wallet',
        params: { walletToken: contract },
      });
    },
  },

  components: {
    EWalletToken,
    EIcon,
    TheTokenSearch,
    ETokenSearch,
    EButton,
    ESwitch,
  },
  mixins: [routesMixin, searchTokenMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-balance {
  $this: &;

  &__head {
    display: flex;
    align-items: center;
    margin-bottom: 11px;
    height: 24px;
  }

  &__heading {
    text-transform: uppercase;
    font-size: 8px;
    font-weight: 700;
    letter-spacing: .58px;
    color: $color-dark-blue;
    position: relative;
  }

  &__hint {
    min-width: 20px;
    min-height: 20px;
    max-width: 20px;
    max-height: 20px;
    border-radius: 50%;
    border: 1px solid #EBEDF0;
    background-color: #fff;
    color: $color-blue;
    font-weight: 700;
    font-size: 8px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 3;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
      #{$this}__tooltip {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__tooltip {
    position: absolute;
    right: 10px;
    bottom: 23px;
    background-color: $color-blue;
    padding: 15px 15px;
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
    transition: $transition-time;
    min-width: 309px;
    min-height: 138px;
    width: 100%;
    border-radius: 5px 5px 0 5px;

    &:before {
      content: '';
      position: absolute;
      bottom: -10px;
      right: 0;
      width: 0;
      height: 0;
      border-top: 10px solid $color-blue;
      border-left: 15px solid transparent;
    }
  }

  &__tooltip-heading {
    text-transform: uppercase;
    color: #fff;
    font-size: 13px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__tooltip-text {
    line-height: 1.34;
    font-size: 12px;
    font-weight: 600;
    color: #b3bdb7;
    text-transform: initial;

    a {
      color: #b3bdb7;
    }
  }

  &__token {
    flex: 0 0 auto;
    width: 25%;
    box-sizing: border-box;
    padding-left: 7px;
    display: flex;
    align-items: center;
  }

  &__heading_number {
    flex: 0 0 auto;
    width: 25%;
    box-sizing: border-box;
    padding-right: 15px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  &__search {
    width: 95px;
    height: 24px;
    border-radius: 3px;
    margin-left: 16px;
  }

  &__search-placeholder {
    position: relative;
    border-radius: 3px;
    cursor: pointer;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    padding-top: 5px;
    padding-left: 14px;
    background-color: #fff;
    color: $color-blue;
    font-size: 10px;
    font-weight: 800;
    letter-spacing: .71px;
    text-transform: uppercase;
    box-shadow: 0 1px 3px rgba(black, .08);
  }

  &__heading-text {
    display: inline-block;
  }

  &__search-placeholder-icon {
    position: absolute;
    top: 50%;
    right: 13px;
    margin-top: -6px;
    width: 11px;
    height: 11px;

    .e-icon svg {
      fill: $color-blue;
    }
  }

  &__tokens-table {
    margin-bottom: 26px;
  }

  &__footer {
    padding-left: 14px;
    margin-bottom: 14px;
    display: flex;
    justify-content: space-between;
  }

  &__history-btn {
    display: inline-block;
    max-width: 210px;
    width: 100%;
    margin-left: auto;
    margin-top: -9px;
  }
}
</style>



