import backend from './endpoint';

export const register = async ({ email, encryptedPrivateKey, walletAddress, promoCode, ref, cid }) =>
  backend.httpProd.post('/api/users/register', { email, encrypted_private_key: encryptedPrivateKey, wallet_address: walletAddress, promo_code: promoCode || undefined, ref: ref || undefined, cid });

export const resendVerificationEmail = async ({ email }) =>
  backend.httpProd.post('/api/users/resend-verification-email', { email });

export const verifyEmail = async ({ email, verificationCode }) =>
  backend.httpProd.post('/api/users/verify-email', { email, verification_code: verificationCode });

export const login = async ({ email }) =>
  backend.httpProd.post('/api/users/login', { email });



