const allServices = new Map();

window.allServices = allServices; // test global var

const getServiceMap = (serviceId) => {
  let serviceMap = allServices.get(serviceId);
  if (serviceMap === undefined) {
    serviceMap = new Map();
    allServices.set(serviceId, serviceMap);
  }
  return serviceMap;
};

export default paramsToKey => (serviceId, factory) => (params) => {
  const key = paramsToKey(params);
  const map = getServiceMap(serviceId);
  const prev = map.get(key);
  if (prev === undefined) {
    const newInstance = factory(params);
    map.set(key, newInstance);
    return newInstance;
  }
  return prev;
};

export const clearCachedServices = () => {
  allServices.clear();
};



