<template>
  <div
    class="e-notification"
    :class="'e-notification_' + status"
    :style="{ zIndex, bottom: `${verticalOffset}px` }"
    @mouseenter="clearTimer"
    @mouseleave="startTimer"
  >
    <header class="e-notification__header">
      <div class="e-notification__status e-notification__centering">
        <div class="e-notification__status-icon">
          <e-icon :src="statusIcon"/>
        </div>

        <div class="e-notification__title">{{ title ? title : status }}</div>
      </div>

      <div
        class="e-notification__close-btn e-notification__centering"
        @click="close"
      >
        <span class="e-notification__close-txt">CLOSE</span>

        <div class="e-notification__close-icon">
          <e-icon :src="iconClose"/>
        </div>
      </div>
    </header>

    <div class="e-notification__body">
      <div
        v-if="this.$slots.headline || headline"
        class="e-notification__headline"
      >
        <slot name="headline">{{ headline }}</slot>
      </div>
      <div class="e-notification__message">
        <slot>{{ message }}</slot>
      </div>
    </div>

    <div class="e-notification__btns">
      <button
        v-for="({ label, onClick }, i) of buttons"
        :key="i"
        type="button"
        class="e-notification__btn"
        @click="onClick"
      >
        {{ label }}
      </button>
    </div>

  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconWarning from '@/icons/e-icon_notification_warning.svg';
import iconSuccess from '@/icons/e-icon_notification_success.svg';
import iconClose from '@/icons/e-icon_notification-close.svg';

import { DEFAULT_NOTIFICATION_DURATION } from '@/config';
import nextZIndex from './nextZIndex';

const statusToIcon = {
  success: 'iconSuccess',
  warning: 'iconWarning',
  info: 'iconWarning',
  error: 'iconWarning',
};

/**
 * Duration: false to disable
 */
export default {
  name: 'e-notification',

  props: {
    status: {
      type: String,
      default: 'info',
      validator: v => ['success', 'warning', 'info', 'error'].includes(v),
    },
    title: {
      type: String,
      default: '',
    },
    message: {
      type: String,
      default: '',
    },
    headline: {
      type: String,
      default: '',
    },
    buttons: {
      type: Array,
      default: () => [],
    },
    duration: {
      type: [Boolean, Number],
      default: DEFAULT_NOTIFICATION_DURATION,
    },
    verticalOffset: 0,
  },

  data() {
    return {
      timer: 0,
    };
  },

  computed: {
    statusIcon: vm => vm[statusToIcon[vm.status]],
  },

  created() {
    Object.assign(this, {
      zIndex: nextZIndex(),
      iconSuccess,
      iconWarning,
      iconClose,
    });
  },

  mounted() {
    this.startTimer();
  },

  methods: {
    close() {
      this.$emit('close');
    },

    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          this.$emit('auto-close');
        }, this.duration);
      }
    },

    clearTimer() {
      clearTimeout(this.timer);
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-notification {
  $this: &;

  color: $color-black;
  position: fixed;
  right: 10px;
  width: 319px;
  box-shadow: 0 3px 10px 0 rgba(148, 165, 180, 0.14);
  background-color: white;
  border-radius: 5px;
  transition: opacity .3s, transform .3s, left .3s, right .3s, top .4s, bottom .3s;
  overflow: hidden;

  &_info {
    background-color: #A1BADD;

    #{$this}__header {
      background-color: #98B1D5;
    }

    #{$this}__btn {
      background-color: #98B1D5;

      &:hover {
        background-color: darken(#98B1D5, 5%);
      }
    }
  }

  &_success {
    background-color: #00DB9C;

    #{$this}__header {
      background-color: darken(#00DB9C, 5%);
    }

    #{$this}__btn {
      background-color: darken(#00DB9C, 5%);

      &:hover {
        background-color: darken(#00DB9C, 10%);
      }
    }
  }

  &_error {
    background-color: #C27575;

    #{$this}__header {
      background-color: #CD7C7C;
    }

    #{$this}__btn {
      background-color: #CD7C7C;

      &:hover {
        background-color: darken(#CD7C7C, 5%);
      }
    }
  }

  &_warning {
    background-color: lighten($color-orange, 15%);

    #{$this}__header {
      background-color: $color-orange;
    }

    #{$this}__btn {
      background-color: $color-orange;

      &:hover {
        background-color: darken($color-orange, 5%);
      }
    }
  }

  &__header {
    display: flex;
    flex-direction: space-between;
    padding: 8px 10px;
    text-transform: uppercase;
    justify-content: space-between;

    .e-icon {
      margin-top: -2px;
    }
  }

  &__centering {
    align-items: center;
    display: flex;
  }

  &__status-icon {
    height: 12px;
    width: 12px;

    .e-icon {
      margin-top: -1px;

      svg {
        fill: $color-white;
      }
    }
  }

  &__close-btn {
    cursor: pointer;
    font-size: 11px;
    letter-spacing: 0.83px;

    &:hover {
      #{$this}__close-icon {
        .e-icon svg {
          opacity: 1;
        }
      }
    }
  }

  &__close-txt {
    display: block;
  }

  &__close-icon {
    height: 10px;
    margin-left: 6px;
    width: 11px;

    .e-icon svg {
      fill: $color-white;
      transition: opacity ease $transition-time;
      opacity: 0.5;
    }
  }

  &__title {
    font-weight: 800;
    margin-left: 7px;
  }

  &__body {
    padding: 15px 18px;
  }

  &__headline {
    font-size: 13px;
    font-weight: 800;
    margin-bottom: 10px;
  }

  &__btns {
    display: flex;
  }

  &__btn {
    height: 40px;
    transition: background-color ease $transition-time, color ease $transition-time;
    width: 100%;

    &:not(:last-of-type) {
      margin-right: 1px;
    }

    &:hover {
      color: darken(#ffffff, 5%);
    }
  }
}
</style>



