/**
 * @module store/order-book/actions
 *
 * @version 1.0.2
 */
import * as types from '@/store/mutation-types';
import { getOrderBook, onOrderBookChange, removeOrderBookListener } from '@/api/backend';
import { mapOrderInOrderBook } from './helpers';

/**
 * Action: Загрузка orderbook
 * Should be called only after activeToken is set
 *
 * @since 1.0.0
 */
const loadOrderBook = async ({ commit, rootGetters, dispatch }) => {
  await Promise.all([
    dispatch('contracts/subscribeToContract', undefined, { root: true }), // ensures active contract loaded
    dispatch('tokens/subscribeToToken', undefined, { root: true }),
  ]);
  const activeToken = rootGetters['tokens/activeToken'];
  const contractAddress = rootGetters['contracts/activeMainContractAddress'];
  const tokenAddress = rootGetters['tokens/activeTokenContract'];
  commit(types.GET_ORDER_BOOK_ASYNC.PENDING);
  try {
    // Запрашиваем с бекенда все не заполненные и не отмененные ордера
    const { data } = await getOrderBook({
      contractAddress,
      tokenAddress,
    });

    const decimals = activeToken.decimals;
    const mappingFn = o => mapOrderInOrderBook(o, decimals);
    commit(types.GET_ORDER_BOOK_ASYNC.SUCCESS, {
      asks: data.asks.map(mappingFn),
      bids: data.bids.map(mappingFn),
    });
  } catch (e) {
    if (rootGetters.isConnected) {
      commit(types.GET_ORDER_BOOK_ASYNC.FAILURE, e);
    }
  }
};


/**
 * Action: Synchronous
 * Should be called only after activeToken is set
 * Производит подписку на обновление информации ордербука
 *
 * @since Version 1.0.1
 * @todo Убрать после миграции на частичные обновления ордербука
 */
const listenToOrderBookChanges = ({ rootGetters, commit }) => {
  onOrderBookChange((data) => {
    const decimals = rootGetters['tokens/activeToken'].decimals;
    const mappingFn = o => mapOrderInOrderBook(o, decimals);
    commit(types.GET_ORDER_BOOK_ASYNC.SUCCESS, {
      asks: data.orderbook.asks.map(mappingFn),
      bids: data.orderbook.bids.map(mappingFn),
    });
  });
};


const stopListeningToOrderBookChanges = () => {
  removeOrderBookListener();
};

export default {
  listenToOrderBookChanges,
  loadOrderBook,
  stopListeningToOrderBookChanges,
};



