<template>
  <div class="the-wallet-signup-in-popup" :class="[ `the-wallet-signup-in-popup__${theme}` ]">
    <div class="the-wallet-signup-in-popup__container">
      <form class="the-wallet-signup-in-popup__form" action="#">
        <div class="the-wallet-signup-in-popup__inputbox">
          <e-input-wallet-address
            v-model="address"
            :vuelidate-property="$v.address"
            data-test="address"
          />
        </div>
        <div class="the-wallet-signup-in-popup__inputbox">
          <e-input-secret-key
            v-model="privateKey"
            :vuelidate-property="$v.privateKey"
            data-test="privateKey"
          />
        </div>
        <div class="the-login-flow-popup__footer-sbmt">
          <div class="the-login-flow-popup__footer-sbmt-item">
            <button
              type="button"
              class="e-receipt__btn e-receipt__btn_active the-login-flow-popup__cancel-button"
              @click="cancelOperation"
            >
              Close
            </button>
          </div>
          <div class="the-login-flow-popup__footer-sbmt-item">
            <button
              type="button"
              class="e-receipt__btn e-receipt__btn_active the-login-flow-popup__action-button"
              :disabled="disabled"
              @click="importWallet"
            >
              Import wallet
            </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import { validationMixin } from 'vuelidate';
import { required, minLength, maxLength } from 'vuelidate/lib/validators';
import { normalizePrivateKey } from '@/util/crypto';
import { putGAClientID } from '@/api/backend/ga';

import EInputWalletAddress from '@/components/EInputWalletAddress';
import EInputSecretKey from '@/components/EInputSecretKey';
import EButton from '@/components/EButton';

import iconWalletImport from '@/icons/e-icon_wallet_import.svg';

import { walletMatchesPrivateKey, isEthereumAddress } from '@/util/validators';
import { PRIVATE_KEY_TYPE } from '@/util/constants';
import { routesMixin } from '@/util/mixins';

export default {
  name: 'the-wallet-signup-in-popup',

  data() {
    return {
      /**
       * Адрес кошелька вводимый пользователем
       */
      address: '',

      /**
       * Приватный ключе вводимый пользователем
       */
      privateKey: '',
    };
  },

  computed: {
    disabled() {
      return this.$v.$invalid;
    },

    /**
     * Нормализированная версия приватного ключа
     */
    normalizedPrivateKey() {
      return normalizePrivateKey(this.privateKey);
    },

    ...mapGetters({
      theme: 'ui/getTheme',
      referrerAddress: 'affiliate/referrerAddress',
      firstContractHash: 'futuresActiveContract/firstContractHash',
    }),
  },

  validations: {
    address: {
      required,
      minLength: minLength(42),
      maxLength: maxLength(42),
      isEthereumAddress,
      // notImported(wallet) {
      //   return !this.$store.getters['wallets/hasPrivateKeyWallet'](wallet);
      // },
    },

    privateKey: {
      required,
      privateKeyLength() {
        return this.normalizedPrivateKey.length === 66;
      },
      privateKeyMatchesWallet() {
        return walletMatchesPrivateKey(this.address, this.normalizedPrivateKey);
      },
    },
  },

  created() {
    Object.assign(this, { iconWalletImport });
  },

  methods: {
    async importWallet(e) {
      e.preventDefault();
      if (!this.$store.getters['wallets/hasPrivateKeyWallet'](this.address)) {
        await this.$store.dispatch('wallets/importWallet', {
          address: this.address,
          privateKey: this.normalizedPrivateKey,
          type: PRIVATE_KEY_TYPE,
        });

        // Отправка аналитики по импорту кошелька
        this.$ga.page({
          page: '/virtual/import',
          title: 'Wallet Was Imported',
          location: window.location.href,
        });

        const cid = window.ga && window.ga.getAll() && window.ga.getAll()[0] ? window.ga.getAll()[0].get('clientId') : undefined;

        putGAClientID({ walletAddress: this.address, clientID: cid, ga: this.$ga, ref: this.referrerAddress, login_type: 'privateKey' });
        this.$ga.event({
          eventCategory: 'login',
          eventAction: 'login_successful',
          eventLabel: 'private_key',
        });
      } else {
        this.$store.dispatch('wallets/setActiveWallet', {
          address: this.address,
          type: PRIVATE_KEY_TYPE,
        });
      }

      this.cancelOperation();
    },

    goToTrading() {
      this.$router.push({ name: 'futures' });
    },

    ...mapMutations({ cancelOperation: 'ui/HIDE_LOGIN_FLOW' }),
  },

  components: {
    EInputWalletAddress,
    EInputSecretKey,
    EButton,
  },

  mixins: [validationMixin, routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-signup-in-popup {
  &__container {
    margin-top: 30px;
    max-width: 625px;

    @include r(1200) {
      max-width: 439px;
    }
  }

  &__inputbox {
    position: relative;
    margin-bottom: 30px;

    @include r(1200) {
      margin-bottom: 20px;
    }
  }

  .e-text-input {
    width: 100%;
  }

  .e-text-input__left-outer-icon-wrap {
    display: none;
  }


  .e-text-input_size-xl .e-text-input__box {
    width: 100%;
    border-width: 1px;
  }

  .e-text-input-length-validated.valid .e-text-input__box,
  .e-text-input-validated.valid .e-text-input__box {
    border-color: $color-green-dark;
  }

  .e-text-input-length-validated.valid .e-text-input__input,
  .e-text-input-validated.valid.recolor-text .e-text-input__input {
    color: $color-green-dark;
  }

  .e-text-input-validated.valid {
    fill: $color-green-dark;
  }

  .e-text-input__box {
    height: 54px;
  }

  .e-text-input__label {
    top: 14px;
  }

  .e-text-input-length-validated__counter {
    height: 54px;
  }

  &__dark {
    .e-text-input__box {
      border-color: $color-border-dark;
    }

    .e-text-input__box,
    .e-text-input__box input {
      background-color: $dark-background !important;
      color: #D9D9D9 !important;
      -webkit-text-fill-color: #D9D9D9 !important;
      /*-webkit-box-shadow: 0 0 0 1000px $dark-background-lighter inset;*/
    }

    .e-text-input__box input:-webkit-autofill {
      -webkit-text-fill-color: #D9D9D9 !important;
      -webkit-box-shadow: 0 0 0 1000px $dark-background-lighter inset;
    }

    .e-text-input__icon.e-text-input__icon_right {

    }

    .e-text-input-length-validated.in-progress .e-text-input__box,
    .e-text-input-validated.focused .e-text-input__box {
      border-color: $color-blue-dark-mode;
    }

    .e-text-input-length-validated.in-progress .e-text-input-length-validated__counter {
      background: $dark-background-lighter;
      color: #D9D9D9;
    }
  }

}
</style>




