const isModernEthereumBrowser = !!window.ethereum;

const isLegacyEthereumBrowser = !!(window.web3 && window.web3.currentProvider);

// export const isMetamask = (
//   (isModernEthereumBrowser && window.ethereum.isMetaMask === true) ||
//   (isLegacyEthereumBrowser && window.web3.currentProvider.isMetaMask === true)
// );

/**
 * Specifies if user has Ethereum Compatible Browser.
 * Example: using Chrome with Metamask extension.
 *
 * @type {boolean}
 */
const isEthereumCompatibleBrowser = isModernEthereumBrowser || isLegacyEthereumBrowser;

export default isEthereumCompatibleBrowser;



