const watcherPromise = testFn => function watcherPromiseBound(expOrFn) {
  return new Promise((resolve) => {
    let resolved = false;
    const unwatch = this.$watch(expOrFn, (val) => {
      if (testFn(val)) {
        resolved = true;
        resolve();
        if (typeof unwatch !== 'undefined') unwatch(); // only on non-immediate watcher success
      }
    }, { immediate: true });
    if (resolved) unwatch(); // only on immediate watcher success
  });
};

export default {
  /* eslint-disable no-param-reassign */
  install: (Vue) => {
    Vue.prototype.$whenTruthy = watcherPromise(v => v);
    Vue.prototype.$whenFalsy = watcherPromise(v => !v);
  },
  /* eslint-enable */
};



