/**
 * @module store/futures-orders/mutations
 *
 * @version 1.0.3
 */

import log from '@/util/logger';
import * as types from '@/store/mutation-types';

const mutations = {
  /**
   * Мутация меняющая информацио об мое ордере в списке моих оредров
   *
   * @since 1.0.2
   * @todo Сейчас реализация предполагает, что мы храним в массивах ВСЕ ордера, в будущем нужно будет реализовать алгоритм
   */
  [types.SET_MY_ORDER](state, order) {
    const indexOfOrder = state.myOrders.findIndex(item => item.orderHash === order.orderHash);

    if (indexOfOrder !== -1) {
      state.myOrders.splice(indexOfOrder, 1, order);
    } else {
      state.myOrders.push(order);
    }
  },

  [types.CONFIRMATION_CANCEL_ORDER](state, payload) {
    state.cancelledOrder = payload;
  },

  [types.SUBMIT_CANCEL_ORDER](state) {
    state.cancelledOrder = null;
  },

  [types.CANCEL_CANCEL_ORDER](state) {
    state.cancelledOrder = null;
  },


  /**
   * Начало асинхронной загрузки страницы моих активных оредров для текущего токена
   *
   * @since Version 1.0.3
   */
  [types.GET_MY_ORDERS_PAGE_ASYNC.PENDING](state) {
    state.myOrdersPageLoading = true;
  },

  /**
   * Начало асинхронной загрузки страницы моих активных оредров для текущего токена
   *
   * @since Version 1.0.3
   */
  [types.GET_MY_ORDERS_PAGE_ASYNC.FAILURE](state, error) {
    state.myOrdersPageLoading = false;
    log.error(`Error loading my orders page: ${error}`, {
      module: 'orders',
      layer: 'backend',
    });
  },

  /**
   * Начало асинхронной загрузки страницы моих активных оредров для текущего токена
   *
   * @since Version 1.0.3
   */
  [types.GET_MY_ORDERS_PAGE_ASYNC.SUCCESS](state, { orders, total }) {
    state.myOrdersPageLoading = false;
    state.myOrdersPage = orders;
    state.totalNumberOfOrders = total;
  },

  /**
   * Установка режима отображения
   *
   * @since Version 1.0.3
   */
  [types.SET_MY_ORDERS_MODE](state, mode) {
    state.myOrdersMode = mode;
  },

  /**
   * Установка текущей страницы для отбражения
   *
   * @since Version 1.0.3
   */
  [types.SET_MY_ORDERS_PAGE_NUMBER](state, pageNumber) {
    if (pageNumber < Math.ceil(state.totalNumberOfOrders / state.ordersPerPage)) {
      state.activePageNumber = pageNumber;
    }
  },

  /**
   * Установка общего количества оредров в текущем режиме отображения
   *
   * @since Version 1.0.3
   */
  [types.SET_TOTAL_NUMBER_OF_ORDERS](state, totalNumberOfOrders) {
    state.totalNumberOfOrders = totalNumberOfOrders;
  },
};


export default mutations;



