<template>
  <div
    class="e-wallet-token"
    :class="[ `e-wallet-token__${theme}` ]"
    @click="$emit('click')"
  >
    <div class="e-wallet-token__wrapper">
      <div class="e-wallet-token__head">
        <div class="e-wallet-token__image">
          <e-token-icon :contract="token.contract"/>
        </div>

        <div class="e-wallet-token__body">
          <div class="e-wallet-token__shortname">{{token.shortName}}</div>

          <div class="e-wallet-token__name">{{token.name}}</div>
        </div>
      </div>

      <div class="e-wallet-token__p-balance e-wallet-token__balance">
        <div class="e-wallet-token__value" data-test="balancePrivateAmount">
          <div
            v-if="nonDepositOperations.length"
            class="e-wallet-token__preloader"
          >
            <e-preloader small-preloader/>
          </div>

          <e-big-number
            :value="privateBalance"
            :decimals="token.decimals"
            :min-decimal-places="DEFAULT_MIN_DECIMALS"
            :max-decimal-places="DEFAULT_MAX_DECIMALS"
          />
        </div>

        <e-balance-transfer-operations
          v-if="nonDepositOperations.length"
          :operations="nonDepositOperations"
          :token="token"
          position="right"
          class="e-wallet-token__operations"
        />
      </div>

      <div class="e-wallet-token__e-balance e-wallet-token__balance">
        <div class="e-wallet-token__value" data-test="balanceTradableAmount">
          <div
            v-if="depositOperations.length"
            class="e-wallet-token__preloader"
          >
            <e-preloader :small-preloader="true"/>
          </div>

          <e-big-number
            :value="tradableBalance"
            :decimals="token.decimals"
            :min-decimal-places="DEFAULT_MIN_DECIMALS"
            :max-decimal-places="DEFAULT_MAX_DECIMALS"
          />
        </div>

        <e-balance-transfer-operations
          v-if="depositOperations.length"
          :operations="depositOperations"
          :token="token"
          position="right"
          class="e-wallet-token__operations"
        />
      </div>

      <div class="e-wallet-token__eth-value">
        <div
          v-if="ethValueDisplayed"
          class="e-wallet-token__value"
        >
          <e-big-number
            v-if="ethValueExist"
            :value="ethValue"
            :decimals="ETH_DECIMALS"
            :min-decimal-places="DEFAULT_MIN_DECIMALS"
            :max-decimal-places="DEFAULT_MAX_DECIMALS"
          />
          <template v-else>N/A</template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ZERO } from '@/util/numbers';
import { mapActions, mapGetters } from 'vuex';
import { BigNumber } from 'bignumber.js';
import {
  ETH_DECIMALS,
  ETH_ADDRESS,
  DEPOSIT_OPERATION,
  WITHDRAWAL_OPERATION,
  TRANSFER_OPERATION,
} from '@/util/constants';

import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import ETokenIcon from '@/components/ETokenIcon';
import EBigNumber from '@/components/EBigNumber';
import EBalanceTransferOperations from '@/components/EBalanceTransferOperations';
import EPreloader from '@/components/EPreloader';

export default {
  name: 'e-wallet-token',

  props: {
    token: {
      type: Object,
      required: true,
    },

    privateBalance: {
      type: BigNumber,
      default: () => ZERO,
    },

    tradableBalance: {
      type: BigNumber,
      default: () => ZERO,
    },

    price: {
      type: BigNumber,
      default: () => BigNumber(NaN),
    },

    operations: {
      type: Array,
      default: () => [],
    },
  },

  computed: {
    ethValue() {
      const propPrice = this.price;
      const price = propPrice.isNaN() || propPrice.isZero() ? BigNumber(NaN) : propPrice;
      const balanceSum = this.privateBalance.plus(this.tradableBalance);
      return price.times(balanceSum).shiftedBy(ETH_DECIMALS - this.token.decimals);
    },

    ethValueDisplayed() {
      return this.token.contract !== ETH_ADDRESS;
    },

    ethValueExist() {
      return !this.ethValue.isNaN();
    },

    noPrivateBalance() {
      return this.privateBalance.isZero();
    },

    noTradableBalance() {
      return this.tradableBalance.isZero();
    },

    depositOperations() {
      return this.operations.filter(o => o.type === DEPOSIT_OPERATION);
    },

    nonDepositOperations() {
      return this.operations.filter(o => o.type !== DEPOSIT_OPERATION);
    },

    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  methods: {
    ...mapActions({
      startOperation: 'deposit/startTokenTransfer',
    }),

    showDepositPopup() {
      this.startOperation({
        operation: DEPOSIT_OPERATION,
        tokenContract: this.token.contract,
      });
    },

    showWithdrawalPopup() {
      this.startOperation({
        operation: WITHDRAWAL_OPERATION,
        tokenContract: this.token.contract,
      });
    },

    showTransferPopup() {
      this.startOperation({
        operation: TRANSFER_OPERATION,
        tokenContract: this.token.contract,
      });
    },
  },

  components: {
    EBalanceTransferOperations,
    EBigNumber,
    EButton,
    ETokenIcon,
    EIcon,
    EPreloader,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-wallet-token {
  $this: &;

  cursor: pointer;
  min-height: 60px;
  background-color: #fff;
  box-shadow: 0 0 0px 1px #F3F4F5;

  &:not(:last-child) {
    margin-bottom: 6px;
  }

  &__wrapper {
    display: flex;
    height: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
    box-sizing: border-box;
  }

  &__image {
    max-width: 30px;
    max-height: 30px;
    min-width: 30px;
    min-height: 30px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    overflow: hidden;
  }

  &__head {
    flex: 0 0 auto;
    width: 25%;
    padding-left: 15px;
    display: flex;
    align-items: flex-start;
    box-sizing: border-box;
  }

  &__eth-value {
    flex: 0 0 auto;
    width: 25%;
    padding-right: 15px;
    padding-left: 61px - 15px - 10px;
    box-sizing: border-box;
    display: flex;
    flex-flow: column;
    align-items: flex-end;
  }

  &__balance {
    flex: 0 0 auto;
    width: 25%;
    padding-right: 15px;
    box-sizing: border-box;
    display: flex;
    flex-flow: column;
    align-items: flex-end;
  }

  &__value {
    height: 30px;
    display: flex;
    align-items: center;
    position: relative;
    color: #0D105B;

    .e-big-number {
      font-size: 12px;

      &__int {
        color: $color-dark-blue;
      }

      &__fraction {
        color: #B6B7CD
      }
    }
  }

  &__preloader {
    position: absolute;
    left: -12px;
    top: 7px;
  }

  &__body {
    padding-left: 10px;
    padding-top: 2px;
  }

  &__shortname {
    font-size: 10px;
    color: $color-grey;
    letter-spacing: 0.67px;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 2px;
  }

  &__name {
    font-size: 14px;
    line-height: 1;
    color: $color-dark-blue;
    font-weight: 600;
    padding-right: 10px;
  }

  &__operations {
    position: relative;
    left: 61px;
  }

  &__dark {
    background: #2E2E2E;
    box-shadow: 0 0 0 1px #414141;

    #{$this}__name {
      color: #fff;
    }

    #{$this}__value {
      color: #fff;

      .e-big-number__int {
        color: #fff;
      }
    }
  }
}

</style>



