/* eslint-disable */
import { getStorageValue } from '@/util/storage';
import backend from './endpoint';

// eslint-disable-next-line import/prefer-default-export
export const putGAClientID = async ({ walletAddress, clientID, ga, ref, login_type }) => {
  ga.set('userId', walletAddress);

  const ga_medium = getStorageValue('utm_medium') || undefined;
  const ga_campaign = getStorageValue('utm_campaign') || undefined;
  const ga_source = getStorageValue('utm_source') || undefined;

  return backend.httpProd.put(`/api/users/${walletAddress}/ga-client-id`, {
    ga_client_id: clientID || null,
    ref: ref || undefined,
    ga_medium,
    ga_campaign,
    ga_source,
    login_type: login_type || undefined,
  });
};



