import TransportU2F from '@ledgerhq/hw-transport-u2f';

let cachedSupportStatus;

/**
 * Can't be rejected (i.e. always resolves).
 * @returns {Promise<*>}
 */
export default async () => {
  if (cachedSupportStatus !== undefined) return cachedSupportStatus;
  try {
    cachedSupportStatus = await TransportU2F.isSupported();
  } catch (e) {
    cachedSupportStatus = false;
  }
  return cachedSupportStatus;
};



