import { getTimestamp } from '@/api/backend';

/**
 * @param {Signatures} signatures
 */
export default class SignaturesService {
  constructor({ signatures }) {
    this.signatures = signatures;
  }

  /**
   * @async
   */
  signData(data) {
    return Promise.resolve(this.signatures.signData(data));
  }

  /**
   * @async
   */
  createUpdateMarginHash(data) {
    return Promise.resolve(this.signatures.createUpdateMarginHash(data));
  }

  /**
   * @async
   * @param signParams
   * @returns {Promise<*>}
   */
  async signOrder(signParams) {
    const nonce = await getTimestamp();
    return this.signatures.signOrder({ ...signParams, nonce });
  }

  /**
   * @async
   * @param signParams
   * @returns {Promise<*>}
   */
  async signCancelOrder(signParams) {
    const nonce = await getTimestamp();
    return this.signatures.signCancelOrder({ ...signParams, nonce });
  }

  /**
   * @param signParams
   * @returns {Promise<*>}
   */
  async signWithdrawal(signParams) {
    const nonce = await getTimestamp();
    return this.signatures.signWithdrawal({ ...signParams, nonce });
  }

  /**
   * @param signParams
   * @returns {Promise<*>}
   */
  async signPTokenRedeem(signParams) {
    const nonce = await getTimestamp();
    return this.signatures.signPTokenRedeem({ ...signParams, nonce });
  }

  /**
   * @param {Boolean} isMetamaskLedger
   */
  setIsMetamaskLedger(isMetamaskLedger) {
    this.signatures.isMetamaskLedger = isMetamaskLedger;
  }
}



