import * as constants from '@/util/constants';
import * as configs from '@/config';
import * as storeConstants from '@/store/constants';
import * as backendConstants from '@/api/backend/constants';

export default {
  install: (Vue) => {
    Object.assign(Vue.prototype, constants, configs, storeConstants, backendConstants);
  },
};



