let storage;
try {
  const uid = new Date().toString();
  storage = window.localStorage;
  storage.setItem(uid, uid);
  const fail = storage.getItem(uid) !== uid;
  storage.removeItem(uid);
  if (fail) storage = null;
} catch (exception) {
  storage = null;
}

export const setStorageValue = (key, value) => {
  if (storage) {
    storage.setItem(key, JSON.stringify(value));
  }
};

export const setStorageArray = (key, value) => {
  if (storage) {
    const array = value == null ? [] : value;
    storage.setItem(key, JSON.stringify(array));
  }
};

export const getStorageValue = (key) => {
  if (storage) {
    const stored = storage.getItem(key);
    if (stored !== null) {
      try {
        return JSON.parse(stored);
      } catch (ignored) {
        // ignored
      }
    }
  }
  return null;
};

export const getStorageArray = (key) => {
  if (storage) {
    const stored = storage.getItem(key);
    if (stored !== null) {
      try {
        return JSON.parse(stored);
      } catch (ignored) {
        // ignored
      }
    }
  }
  return [];
};



