import BigNumber from 'bignumber.js';

import backend from '../endpoint';

export const contractTransformer = c => ({
  id: c.id,
  contractAddress: c.contract_address,
  futuresContractHash: c.futures_contract_hash,
  futuresAssetHash: c.futures_asset_hash,
  baseToken: c.base_token,
  asset: c.asset,
  expirationBlock: c.expiration_block,
  margin: c.margin,
  oppositeMargin: c.opposite_margin ? c.opposite_margin : c.margin,
  expiresInSeconds: c.expires_in_seconds,
  side: c.side,
  multiplier: BigNumber(c.multiplier),
  contractState: c.contract_state,
  closingPrice: c.closing_price !== null ? BigNumber(c.closing_price) : null,
  closingReason: c.closing_reason,
  closedAt: c.closed_at !== null ? new Date(c.closed_at) : null,
  createdAt: new Date(c.created_at),
  markPrice: BigNumber(c.mark_price),
  expiresApproxAt: new Date(c.expires_approx_at),
  fundingRate: c.funding_rate ? BigNumber(c.funding_rate) : null,
  isModel: c.is_model,
  perpetual: c.perpetual,
  chartSymbol: c.futures_asset.chart_symbol,
  decimals: c.futures_asset.decimals,
  amountDec: c.futures_asset.amount_dec,
  maintenanceMargin: c.maintenance_margin ? BigNumber(c.maintenance_margin) : null,
  futuresAssetName: c.futures_asset.name,
  notional: +c.futures_asset.notional,
});

export const getOneContract = async (contractHash) => {
  const result = await backend.http.get(`/api/futures/contracts/${contractHash}`);
  return contractTransformer(result.data.data);
};

export const getContracts = async ({ contractAddress, limit = 50, offset = 0, ...otherParams }) => {
  const reqParams = {
    contract_address: contractAddress,
    limit,
    offset,
  };

  if (otherParams.contractState) {
    reqParams.contract_state = otherParams.contractState;
  }

  if (otherParams.isModel !== undefined) {
    reqParams.is_model = otherParams.isModel;
  }

  if (otherParams.isHidden !== undefined) {
    reqParams.is_hidden = otherParams.isHidden;
  }

  if (otherParams.futuresAssetHash) {
    reqParams.futures_asset_hash = otherParams.futuresAssetHash;
  }

  if (otherParams.margin) {
    reqParams.margin = otherParams.margin;
  }

  const result = await backend.http.get('/api/futures/contracts', {
    params: reqParams,
  });
  return result.data.data.map(contractTransformer);
};

export const onFuturesContracts = callback => backend.socket.on('futures_contracts', callback);

export const offFuturesContracts = callback => backend.socket.off('futures_contracts', callback);



