const sizes = ['Bytes', 'Kb', 'Mb', 'Gb', 'Tb'];
const bytesInKb = 1024;

export default (bytes) => {
  const bytesInt = parseInt(bytes, 10);
  if (bytesInt === 0) return '0 Kb';
  if (bytesInt <= bytesInKb) return '1 Kb';
  const i = Math.floor(Math.log(bytesInt) / Math.log(bytesInKb));
  return `${Math.ceil(bytesInt / (bytesInKb ** i))} ${sizes[i]}`;
};



