/**
 * @module store/futures-contracts/getters
 * @version 1.0.1
 */

/**
 * Returns active contract title
 */
const contractTitle = state => (state.activeContract ? state.activeContract.asset : 'Futures Contract');

/**
 * Returns active contract name
 */
const contractName = state => (state.activeContract ? state.activeContract.asset : null);

/**
 * Returns active asset hash
 */
const assetHash = state => (state.activeContract ? state.activeContract.futuresAssetHash : null);

/**
 * Returns first contract hash
 */
const firstContractHash = state => state.firstContractHash;

const contractBaseToken = (state, getters, rootState, rootGetters) => {
  if (state.activeContract === null) {
    return null;
  }

  const token = rootGetters['tokens/tokenByContract'](state.activeContract.baseToken);

  return token || null;
};

export default {
  contractTitle,
  contractName,
  assetHash,
  firstContractHash,
  contractBaseToken,
};



