<template>
  <div class="e-hint" :class="[answer ? 'is-active' : '']">
    <div
      v-if="!mod && !wide"
      class="e-hint__icon e-hint__action"
      :class="[border ? 'e-hint__label_border' : '']"
      @mouseover="handleHover"
      @mouseout="handleHover"
      @click="handleClick"
    >
      ?
    </div>
    <div
      v-if="mod"
      class="e-hint__label e-hint__action"
      @click="handleClick"
      @mouseover="handleHover"
      @mouseout="handleHover"
    >
      ?
    </div>
    <div class="e-hint__label e-hint__label_wide" v-if="wide"
      @click="handleClick"
      @mouseover="handleHover"
      @mouseout="handleHover"
    >
      ?
    </div>
    <div class="e-hint__info"
      v-show="answer"
      :class="[
        mod ? 'e-hint__info_mod' : '',
        wide ? 'e-hint__info_wide' : '',
      ]"
      >
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-hint',

  data() {
    return {
      answer: false,
    };
  },

  props: {
    mod: {
      type: Boolean,
      default: false,
    },

    click: {
      type: Boolean,
      default: false,
    },

    wide: {
      type: Boolean,
      default: false,
    },

    border: {
      type: Boolean,
      default: false,
    },
  },

  methods: {
    showAnswer() {
      this.answer = !this.answer;
    },
    handleClick() {
      if (this.click) {
        this.showAnswer();
      }
    },
    handleHover() {
      if (!this.click) {
        this.showAnswer();
      }
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-hint {
  $this: &;

  position: relative;
  z-index: 10;

  &.is-active,
  &:hover {
    #{$this}__action {
      background-color: $color-dark-blue;
      color: $color-white;
    }

    #{$this}__label_wide {
      background-color: #fff;
      color: $color-blue;
    }
  }

  &__icon {
    display: inline-block;
    width: 16px;
    height: 16px;
    color: #0123e7;
    font-size: 10px;
    font-style: normal;
    text-align: center;
    line-height: 16px;
    background-color: #FFFFFF;
    border-radius: 50%;
    box-shadow: 0 2px 3px 0 rgba(0,0,0,0.08);
    cursor: pointer;
    transition: background-color linear 0.3s;
  }

  &__label {
    height: 28px;
    width: 28px;
    border: 1px solid #f4f5f7;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(#0D105B, .2);
    cursor: pointer;

    @include r(1200) {
      height: 22px;
      width: 22px;
    }
  }

  &__label_border {
    box-shadow: none;

    &:before {
      content: '';
      transition: $transition-time;
      position: absolute;
      border-radius: 50%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 1px solid $color-grey-second;
    }

    &:hover {
      &:before {
        border-color: $color-dark-blue;
      }
    }
  }

  &__label_wide {
    width: 18px;
    height: 18px;
    font-size: 10px;
    border: 2px solid $color-blue;
    box-shadow: 0 1px 2px rgba(black, .18);
    color: $color-blue;
    font-weight: 400;
    line-height: 1;
    box-sizing: border-box;
  }

  &__info {
    position: absolute;
    width: 204px;
    top: 50%;
    left: 30px;
    z-index: 10;
    box-sizing: border-box;
    padding: 12px 15px 15px 19px;
    color: #0d105b;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 0;
    text-transform: none;
    background-color: #FFFFFF;
    box-shadow: 0 6px 10px 0 rgba(0,0,0,0.08);
    border-radius: 3px;
    transform: translateY(-50%);

    &:before {
      content: '';
      position: absolute;
      top: calc(50% - 2px);
      left: -12px;
      width: 14px;
      height: 14px;
      background-color: #fff;
      transform: rotate(45deg) translateY(-50%);
    }
  }

  &__info_wide {
    right: 30px;
    left: auto;

    &:before {
      left: auto;
      right: -2px;
    }
  }

  &__info_mod {
    left: 50px;
  }
}
</style>




