<template>
  <div
    :class="{ 'e-copy-text_compact': compact }"
    class="e-copy-text"
  >
    <div
      v-if="icon"
      class="e-copy-text__icon"
    >
      <div class="e-copy-text__icon-wrap">
        <e-icon :src="icon"></e-icon>
      </div>
    </div>

    <div
      class="e-copy-text__in"
      :class="{
        'e-copy-text__in_icon': icon,
        'e-copy-text__in-inline': inline,
        [`e-copy-text__in-color-scheme-${colorScheme}`]: colorScheme,
        [`e-copy-text__in-size-${size}`]: size
      }"
    >
      <div
        v-if="qrCodeShow"
        class="e-copy-text__qr-code-show"
      >
        <qriously v-if="theme === 'dark'" :value="hash" :size="116" background="#fff" :backgroundAlpha="0" foreground="#fff" />
        <qriously v-else :value="hash" :size="116" />
      </div>

      <div class="e-copy-text__head">
        <div class="e-copy-text__title" :class="[mod ? 'e-copy-text__title_mod' : '']">{{ title }}</div>

        <div
          v-if="qrCode && !isMasked"
          v-click-outside="() => { qrCodeModal = false }"
          class="e-copy-text__qr"
        >
          <div
            v-if="compact"
            class="e-copy-text__qr-code-wrapper"
            @click="qrCodeModal = !qrCodeModal"
          >
            <div class="e-copy-text__qr-text">
              Show QR code
            </div>
          </div>

          <div
            v-else
            class="e-copy-text__qr-code-wrapper"
            @click="qrCodeModal = !qrCodeModal"
          >
            <div class="e-copy-text__qr-icon">
              <e-icon :src="iconQr"/>
            </div>
            <div class="e-copy-text__qr-text">
              qr code
            </div>
          </div>

          <div
            v-if="qrCodeModal"
            class="e-copy-text__qr-code"
          >
            <qriously :value="hash" :size="qrCodeSize" />
          </div>
        </div>

        <div class="e-copy-text__toggle"
          v-if="!compact && isMasked && !qrCode"
          @click="displayMasked = !displayMasked"
        >
          <div class="e-copy-text__toggle-icon">
            <e-icon :src="iconEye"></e-icon>
          </div>
          <div class="e-copy-text__toggle-text">
            <template v-if="displayMasked">Show key</template>
            <template v-else>Hide key</template>
          </div>
        </div>

        <div class="e-copy-text__toggle"
          v-if="compact && isMasked && !qrCode"
          @click="displayMasked = !displayMasked"
        >
          <div class="e-copy-text__toggle-text">
            <template v-if="displayMasked">Show key</template>
            <template v-else>Hide key</template>
          </div>
        </div>
      </div>

      <div class="e-copy-text__wrapper-btns">
        <div class="e-copy-text__wrapper">
          <span
            class="e-copy-text__hash"
            :class="[smallHashSize ? 'e-copy-text__hash_sm' : '']"
            ref="textHash"
          >
          </span>
          <span
            v-if="walletType"
            class="e-copy-text__type"
            :class="[hintTyping]"
          >
          {{activeWallet.type}}
          </span>
        </div>

        <div class="e-copy-text__btns">
          <div class="e-copy-text__btn"
            :class="[copied ? 'e-copy-text__btn_copy' : '']"
            v-clipboard:copy="hash"
            v-clipboard:success="onCopy"
            >
            <e-button
              :fill="colorScheme === 'blue' ?  'transparent-white' : 'white'"
              size="s"
              width="space"
              >
                <div class="e-copy-text__inside-btn">
                  <div class="e-copy-text__btn-text" :class="!copied ? '' : 'e-copy-text__btn-text_top'">
                    <span>{{ btnName }}</span>
                  </div>

                  <div class="e-copy-text__btn-text e-copy-text__btn-text-copied" :class="!copied ? '' : 'e-copy-text__btn-text_top'">
                    <span><e-icon :src="iconLike"/> <span>Copied</span></span>
                  </div>
                </div>
            </e-button>
          </div>
        </div>
      </div>

      <div
        v-if="qrCodeInline"
        class="e-copy-text__qr-code-inline"
      >
        <qriously :value="hash" :size="116" background="#fff" :backgroundAlpha="0" foreground="#fff" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import debounce from 'lodash/debounce';
import Qriously from 'vue-qriously/src/components/v-qriously';

import iconEye from '@/icons/e-icon_eye.svg';
import iconQr from '@/icons/e-icon_qr_code.svg';
import iconLike from '@/icons/e-icon_emoji_like.svg';

import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';

import { PRIVATE_KEY_TYPE, METAMASK_WALLET_TYPE, LEDGER_WALLET_TYPE } from '@/util/constants';
import TextScramble from './TextScramble';

export default {
  name: 'e-copy-text',

  props: {
    title: {
      type: String,
      default: '',
    },

    hash: {
      type: String,
      default: '',
    },

    icon: {
      type: String,
      default: '',
    },

    qrCode: {
      type: Boolean,
      default: false,
    },

    qrCodeInline: {
      type: Boolean,
      default: false,
    },

    qrCodeShow: {
      type: Boolean,
      default: false,
    },

    btnName: {
      type: String,
      default: '',
    },

    colorScheme: {
      type: String,
      default: '',
    },

    size: {
      type: String,
      default: '',
    },

    inline: {
      type: Boolean,
      default: false,
    },

    isMasked: {
      type: Boolean,
      default: false,
    },

    mod: {
      type: Boolean,
      default: false,
    },

    smallHashSize: {
      type: Boolean,
      default: false,
    },

    walletType: {
      type: Boolean,
      default: false,
    },

    compact: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      displayMasked: this.isMasked,

      iconEye,
      iconQr,
      iconLike,

      copied: false,
      qrCodeModal: false,
      qrCodeSize: 198,
    };
  },

  computed: {
    ...mapGetters({
      windowSize: 'ui/windowSize',
      activeWallet: 'wallets/activeWallet',
      theme: 'ui/getTheme',
    }),

    displayedText() {
      if (this.displayMasked) {
        return '•'.repeat(this.hash.length);
      }
      return this.hash;
    },

    hintTyping() {
      if (this.walletType) {
        return {
          'e-copy-text__type_private': this.activeWallet.type === PRIVATE_KEY_TYPE && this.walletType,
          'e-copy-text__type_metamask': this.activeWallet.type === METAMASK_WALLET_TYPE && this.walletType,
          'e-copy-text__type_ledger': this.activeWallet.type === LEDGER_WALLET_TYPE && this.walletType,
        };
      }
      return {};
    },
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
  },

  mounted() {
    this.textScramble = new TextScramble(this.$refs.textHash, this.displayedText);

    this.onResize();
  },

  watch: {
    displayMasked() {
      this.textScramble.setText(this.displayedText);
    },

    hash() {
      this.textScramble.setText(this.displayedText);
    },

    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },
  },

  methods: {
    onCopy() {
      this.copied = true;
      setTimeout(() => {
        this.copied = false;
      }, 1500);
    },
    onResize() {
      if (this.windowSize.width < 1200) {
        this.qrCodeSize = 125;
      } else {
        this.qrCodeSize = 198;
      }
    },
  },

  components: {
    EIcon,
    EButton,
    Qriously,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-copy-text {
  $this: &;

  display: flex;
  width: 100%;
  position: relative;

  &__icon {
    width: 60px;
    height: 60px;
    min-height: 60px;
    min-width: 60px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0 12px 24px rgba(148, 165, 180, .14);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -14px;
    left: -4px;

    .e-icon {
      position: relative;

      svg {
        fill: #1FCE43;
      }
    }

    @include r(1200) {
      height: 50px;
      width: 50px;
      min-height: 50px;
      min-width: 50px;
      top: -19px;
      left: -8px;
    }
  }

  &__icon-wrap {
    height: 30px;
    position: relative;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    @include r(1200) {
      width: 24px;
      height: 24px;
    }
  }

  &__in {
    margin-top: -2px;
    width: 100%;
    padding-right: 240px;
    box-sizing: border-box;

    &_icon {
      padding-left: 90px;
    }

    @include r(1200) {
      padding-left: 64px;
      padding-right: 170px;
    }
  }

  &__in-inline {
    display: flex;
    align-items: center;
    padding-right: 0;

    #{$this}__title {
      margin-top: 4px;
    }

    #{$this}__btns {
      position: relative;
      top: 0;
      margin-left: auto;
    }

    #{$this}__toggle {
      position: absolute;
      z-index: 10;
      left: 50%;
      transform: translate(-50%, -14px);
    }

    @include r(1200) {
      padding-left: 0;
    }
  }

  &__in-color-scheme-blue {
    #{$this}__title {
      color: #fff;
    }

    #{$this}__hash {
      color: #00E997;
    }

    #{$this}__toggle-text {
      color: #00E997;

      &:hover {
        color: #00d68b;
      }
    }

    #{$this}__toggle:hover #{$this}__toggle-text {
      color: #00d68b;
    }

    #{$this}__btn-text-copied {
      background: #fff;
      color: #010D5F;
    }
  }

  &__in-size-m {
    #{$this}__title {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }

    #{$this}__hash {
      font-size: 22px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: -1px;
    }
    #{$this}__btn {
      min-width: 88px;
    }

    #{$this}__btn-text {
      height: 100%;
    }

    .e-button_size_s {
      overflow: hidden;
      position: relative;
      top: 3px;

      .e-icon {
        margin-right: 0;
      }
    }
  }

  &__in-size-s {
    #{$this}__head{
      margin-bottom: 2px;
      margin-right: 10px;
    }

    #{$this}__title {
      font-size: 13px;
      font-weight: 500;
      line-height: 17px;
    }

    #{$this}__hash {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: -1px;
    }
    #{$this}__btn {
      min-width: 83px;
    }

    #{$this}__btn-text {
      height: 100%;
    }

    .e-button_size_s {
      overflow: hidden;
      height: 34px;
      position: relative;
      top: 3px;

      .e-icon {
        margin-right: 1px;
      }
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 500;
    line-height: 40px;
    display: inline-block;
    margin-right: 7px;
    color: #0D105B;

    @include r(1200) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__title_mod {
    font-weight: 400;
  }

  &__qr-code {
    position: absolute;
    top: 25px;
    height: 198px;
    width: 198px;
    left: 0;
    box-shadow: 0 12px 24px rgba(#94A5B4, .14);
    background-color: #fff;

    @include r(1200) {
      height: 125px;
      width: 125px;
    }
  }

  &__qr-code-inline {
    position: absolute;
    top: -30px;
    right: -30px;
    transform: translate(100%, 0);
  }

  &__head {
    margin-bottom: 5px;
  }

  &__hash {
    color: #09168a;
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
    word-wrap: break-word;

    @include r(1200) {
      font-size: 16px;
    }
  }

  &__hash_sm {
    font-size: 20px;
  }

  &__btns {
    position: absolute;
    top: 38px;
    right: 0;
    display: flex;
    align-items: center;

    @include r(1200) {
      top: 23px;
    }
  }

  &__btn {
    min-width: 170px;

    @include r(1200) {
      min-width: 120px;
    }

    .e-button .e-icon {
      width: 16px;
      height: 16px;
      margin-right: -2px;
    }
  }

  &__btn_copy {
    .e-button {
      color: #0D105B;

      &:hover {
        color: #0D105B;
        background-color: #fff;
        box-shadow: none;

        &:before {
          box-shadow: 0 5px 9px 0 rgba(148, 165, 180, 0.14);
          opacity: 1;
        }
      }
    }
  }

  &__toggle {
    &:hover {
      #{$this}__toggle-icon {
        .e-icon svg {
          fill: darken(#0123E7, 20%);
        }
      }

      #{$this}__toggle-text {
        color: darken(#0123E7, 20%);
      }
    }
  }

  &__qr {
    display: inline-block;
    padding-left: 9px;
    position: relative;
    z-index: 2;

    .e-icon svg {
      fill: #0123E7;
    }

    @include r(1200) {
      padding-left: 4px;
      position: relative;
      top: 1px;
    }
  }

  &__qr-code-wrapper {
    cursor: pointer;

    &:hover {
      #{$this}__qr-icon {
        .e-icon svg {
          fill: darken(#0123E7, 20%);
        }
      }

      #{$this}__qr-text {
        color: darken(#0123E7, 20%);
      }
    }
  }

  &__qr-icon {
    height: 12px;
    width: 12px;
    cursor: pointer;
    display: inline-block;
    vertical-align: top;
    margin-right: 5px;
    position: relative;
    top: 3px;

    @include r(1200) {
      width: 12px;
      height: 12px;
      margin-right: 4px;
    }
  }

  &__qr-text {
    display: inline-block;
    vertical-align: top;
    position: relative;
    top: 2px;
    font-size: 10px;
    color: $color-blue;
    text-transform: uppercase;
    letter-spacing: .83px;

    @include r(1200) {
      letter-spacing: .75px;
      font-size: 9px;
      top: 3px;
    }
  }

  &__toggle {
    display: inline-block;
    cursor: pointer;
  }

  &__toggle-icon {
    height: 21px;
    width: 20px;
    display: inline-block;
    vertical-align: middle;

    .e-icon svg {
      fill: $color-blue;
    }

    @include r(1200) {
      height: 20px;
      width: 18px;
    }
  }

  &__toggle-text {
    display: inline-block;
    vertical-align: middle;
    font-size: 10px;
    color: $color-blue;
    text-transform: uppercase;
    letter-spacing: .83px;
    margin-left: 2px;

    @include r(1200) {
      letter-spacing: .75px;
      font-size: 9px;
      margin-left: 0;
      position: relative;
      top: 1px;
    }
  }

  &__inside-btn {
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
  }

  &__btn-text {
    align-items: center;
    height: 40px;
    display: flex;
    position: relative;
    justify-content: center;
    transition: transform ease 0.3s;

    @include r(1200) {
      height: 30px;
    }
  }

  &__btn-text_top {
    transform: translateY(-100%);
  }

  &__type {
    border-radius: 2px;
    color: $color-white;
    font-size: 10px;
    line-height: 1;
    letter-spacing: 0.67px;
    margin-left: 3px;
    padding: 4px 7px 3px;
    text-transform: uppercase;
    white-space: nowrap;
    display: inline-block;
    position: relative;
    top: -3px;
  }

  &__type_metamask {
    background-color: #ffb516;
  }

  &__type_private {
    background-color: $color-green;
  }

  &__type_ledger {
    background-color: $color-blue;
  }

  &_compact {
    #{$this}__qr-text {
      font-size: 10px;
      color: #0123E7;
      letter-spacing: 0.77px;
      line-height: 2.1;
    }

    #{$this}__toggle {
      font-size: 10px;
      color: #0123E7;
      letter-spacing: 0.77px;
      line-height: 2.1;
    }
  }
}

.text-scramble__temp-char {
  opacity: 0.5;
}
</style>



