/**
 * @module store/trades/helpers
 *
 * @version 1.0.7
 */
import last from 'lodash/last';
import { PAGE_SIZE_TRADES } from '@/config';
import { BigNumber } from 'bignumber.js';
import { BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';
import toDisplayedPrice from '@/util/format/toDisplayedPrice';

/**
 * Хелпер добавляет дельты изменения цены к трейдам
 *
 * @param {array} trades - Массив трейдов
 * @returns {array} Массив трейдов к каждому элементо, которого добавлены поля с изменением цены и маркер растет она или падает
 *
 * @since Version 1.0.1
 */
/* eslint-disable no-param-reassign */
export const addPriceDelta = (trades) => {
  if (trades.length > 0) {
    trades[trades.length - 1].priceDelta = '0%';
    trades[trades.length - 1].increase = 0;

    for (let i = trades.length - 2; i >= 0; i -= 1) {
      const delta = (trades[i].unitPrice.minus(trades[i + 1].unitPrice)).dividedBy(trades[i + 1].unitPrice).times(100).dp(3);

      if (delta.isZero()) {
        trades[i].increase = 0;
        trades[i].priceDelta = '0%';
      } else if (delta.isPositive()) {
        trades[i].increase = 1;
        trades[i].priceDelta = `+${delta.toFixed()}%`;
      } else {
        trades[i].increase = -1;
        trades[i].priceDelta = `${delta.toFixed()}%`;
      }
    }
  }

  return trades;
};
/* eslint-enable */

/**
 * Mutates trade with default price delta, calculated from oldTrade.
 * Doesn't mutate oldTrade.
 *
 * @param newTrade trade for which price delta should be calculated and set
 * @param oldTrade trade that is used to calculate price delta. Is not mutated
 */
/* eslint-disable no-param-reassign */
export const setDefaultPriceDelta = (trade) => {
  trade.increase = 0;
  trade.priceDelta = '0%';
};
/* eslint-enable */

/**
 * Mutates newTrade with price delta, calculated from oldTrade.
 * Doesn't mutate oldTrade.
 *
 * @param newTrade trade for which price delta should be calculated and set
 * @param oldTrade trade that is used to calculate price delta. Is not mutated
 */
/* eslint-disable no-param-reassign */
export const updatePriceDelta = (newTrade, oldTrade) => {
  if (newTrade) {
    if (oldTrade) {
      const delta = newTrade.unitPrice
        .minus(oldTrade.unitPrice)
        .dividedBy(oldTrade.unitPrice)
        .times(100)
        .dp(3);
      if (delta.isZero()) {
        setDefaultPriceDelta(newTrade);
      } else if (delta.isPositive()) {
        newTrade.increase = 1;
        newTrade.priceDelta = `+${delta.toFixed()}%`;
      } else {
        newTrade.increase = -1;
        newTrade.priceDelta = `${delta.toFixed()}%`;
      }
    } else {
      setDefaultPriceDelta(newTrade);
    }
  }
};
/* eslint-enable */


/**
 * Helper: Мепит возвращаемые бекендом данные трейда, на то, что используем мы
 *
 * @param {*} trade - трейд возвращаемый бекендом
 * @param {*} activeWalletAddress - адрес активного кошелька пользователя
 * @param {*} decimals - token decimals
 *
 * @since Version 1.0.0
 */
export const mapTrade = (trade, activeWalletAddress, decimals) => {
  const makerAmountFee = trade.maker_amount_fee != null ? BigNumber(trade.maker_amount_fee) : null;
  const takerAmountFee = trade.taker_amount_fee != null ? BigNumber(trade.taker_amount_fee) : null;
  const unitPrice = toDisplayedPrice(trade.unit_price, decimals);
  return {
    blockNumber: trade.block_number ? trade.block_number : null,
    contractAddress: trade.contract_address,
    createdAt: new Date(trade.created_at),
    gasPrice: BigNumber(trade.gas_price),
    id: trade.id,
    isConfirmed: trade.is_confirmed,
    makerAddress: trade.maker_address,
    makerAmountFilled: BigNumber(trade.maker_amount_filled),
    makerAmountTaken: BigNumber(trade.maker_amount_taken),
    makerAmountFee,
    // makerPctFee: BigNumber(trade.maker_pct_fee),
    takerAmountFilled: BigNumber(trade.taker_amount_filled),
    takerGasFee: BigNumber(trade.taker_gas_fee),
    takerAmountFee,
    // takerGasLimitFee: BigNumber(trade.taker_gas_limit_fee),
    // takerPctFee: BigNumber(trade.taker_pct_fee),
    takerAmountTaken: BigNumber(trade.taker_amount_taken),
    takerAddress: trade.taker_address,
    tokenAddress: trade.token_address,
    transactionHash: trade.transaction_hash ? trade.transaction_hash : null,
    type: trade.is_buy ? BUY_TOKEN_ORDER : SELL_TOKEN_ORDER,
    unitPrice,
    isMy: trade.taker_address === activeWalletAddress || trade.maker_address === activeWalletAddress,
    exchange: trade.exchange,
    isExternal: trade.is_external,
  };
};

