/**
 * Helper: Добавлени трейда в ласттрейдс
 *
 * @since Version 1.0.6
 */
export const addTrade = (latestTrades, newTrade) => {
  const newId = newTrade.id;
  let insertionIndex = latestTrades.findIndex(t => t.id <= newId);
  const allTradesHasBiggerId = insertionIndex === -1;
  const isUpdate = !allTradesHasBiggerId && latestTrades[insertionIndex].id === newId;
  const isPush = allTradesHasBiggerId && latestTrades.length < PAGE_SIZE_TRADES;
  const isInsertInMiddle = !allTradesHasBiggerId;

  if (isUpdate) {
    // Обновление существующего трейда
    Object.assign(latestTrades[insertionIndex], newTrade);
  } else if (isPush) {
    // в сторе нету трейдов, которые бы были выполнены раньше того что получили в качестве параметра
    insertionIndex = latestTrades.length;
    latestTrades.push(newTrade);
  } else if (isInsertInMiddle) {
    latestTrades.splice(insertionIndex, 0, newTrade);
    latestTrades.splice(PAGE_SIZE_TRADES, latestTrades.length - PAGE_SIZE_TRADES); // ensure trades.length <= PAGE_SIZE_TRADES
  }

  // price deltas
  if (isUpdate || isPush || isInsertInMiddle) {
    setDefaultPriceDelta(last(latestTrades));
    updatePriceDelta(latestTrades[insertionIndex], latestTrades[insertionIndex + 1]);
    updatePriceDelta(latestTrades[insertionIndex - 1], latestTrades[insertionIndex]);
  }
};



