/**
 * @module store/tokens/getters
 * @version 1.0.1
 */
/* eslint-disable no-restricted-syntax */

import { ETH_ADDRESS, ETH_TOKEN } from '@/util/constants';
import { BigNumber } from 'bignumber.js';
import find from 'lodash/find';
import get from 'lodash/get';

const tokenMap = (state) => {
  const result = new Map();
  for (const token of state.exchangeTokens) {
    result.set(token.contract, token);
  }
  return result;
};

/**
 * Expects volume24 !== undefined
 * @returns {[object]}
 */
const sortedTokens = state => [...state.exchangeTokens].sort((a, b) => b.volume24h.comparedTo(a.volume24h));

const sortedTokensGainers = state => [...state.exchangeTokens].sort((a, b) => b.priceDiff24InPercent.comparedTo(a.priceDiff24InPercent)).slice(0, 10);
const sortedTokensLoosers = state => [...state.exchangeTokens].sort((a, b) => a.priceDiff24InPercent.comparedTo(b.priceDiff24InPercent)).slice(0, 10);

const ethAndTokens = (state, getters) => [ETH_TOKEN, ...getters.sortedTokens];

const allTokensCount = state => state.exchangeTokens.length;

/**
 * Getter возвращает мои токены (только токены помеченные как мои - звёздочкой)
 */
const myTokens = state => state.exchangeTokens.filter(item => item.isMy);

const recommendedTokens = (state, getters) => getters.sortedTokens.slice(0, 3);

// /**
//  * Getter возвращающий 8 самых популярных токнов для цветка
//  * используется для формрования цветка на главном лендинге
//  */
// const tokensForFlower = (state, getters) => (
//   getters.sortedTokens
//     .map(item => ({
//       contract: item.contract,
//       volume24h: item.volume24h,
//       img: item.img,
//     }))
//     .slice(0, 8)
// );

/**
 * Returns default token contract for exchange page.
 */
const defaultTokenContract = (state, getters) => {
  const activeAddress = getters.activeTokenContract;
  if (activeAddress) {
    return activeAddress;
  }
  // const stored = getStorageValue(ACTIVE_TOKEN_CONTRACT_LOCAL_STORAGE_KEY);
  // if (stored !== null && getters.isTokenContractOnExchange(stored)) {
  //   return stored;
  // }
  const exchangeToken = getters.sortedTokens[0];
  if (exchangeToken) {
    return exchangeToken.contract;
  }
  return null;
};

/**
 * Getter: Получение названия текущего активного токена
 */
const activeTokenContract = state => state.activeToken && state.activeToken.contract;

/**
 * Getter: Получение всей информации по текущему активному токену
 *
 * @returns {object|null}
 */
const activeToken = (state, getters) => {
  const token = state.activeToken;
  if (token) {
    const exchangeToken = getters.tokenByContractSimple(token.contract);
    if (exchangeToken) {
      return exchangeToken;
    }
  }
  return token;
};

/**
 * Получение строки с текущей ценой и шортнеймом активного токена
 * Используется для выведения в title
 *
 * @returns {string} - Строка содержащая 'tokenShortName: tokenPrice'
 *
 * @since Version 1.0.1
 */

const activeTokenTitle = (state, getters) => {
  let title = '';

  if (getters.activeToken) {
    title = `${getters.activeToken.shortName} `;
  }

  if (getters.activeToken && getters.activeToken.price) {
    const growthSymbol = getters.activeToken.increasing ? '▲' : '▼';
    title =
      `${getters.activeToken.shortName}: ${growthSymbol}${getters.activeToken.price.dp(9, BigNumber.ROUND_UP).toFixed()} `;
  }
  return title;
};

/**
 * Getter: Получение общего объема активного токена на биржке в эфире
 *
 * @returns {BigNumber}
 */
const getTotalAmountForActiveToken = (state, getters) => {
  const token = getters.activeToken;
  return token !== null ? token.orderbookDepth : BigNumber(0);
};

/* Non-reactive getters */

/**
 * Возвращает токена по его адресу контракта.
 * Возвращает undefined если передано 0x0, в отличии от tokenByContract
 *
 * @see tokenByContract
 * @returns {Object|null}
 */
const tokenByContractSimple = (state, getters) => contract => (
  contract === getters.activeTokenContract
    ? state.activeToken
    : getters.tokenMap.get(contract)
);

/**
 * Возвращает токена по его адресу контракта.
 * Возвращает данные по Ether-у если передано 0x0, в отличии от tokenByContract
 *
 * @see tokenByContract
 * @returns {Object|null}
 */
const tokenByContract = (state, getters) => (contract) => {
  switch (contract) {
    case ETH_ADDRESS:
      return ETH_TOKEN;
    case getters.activeTokenContract:
      return state.activeToken;
    default:
      return getters.tokenMap.get(contract);
  }
};

/**
 * Checks if the token contract exists on exchange
 *
 * @param {string} contract
 * @returns {boolean}
 */
const isTokenContractOnExchange = (state, getters) => contract => contract != null && getters.tokenMap.has(contract);

const isActiveTokenContractOnExchange = (state, getters) => getters.isTokenContractOnExchange(getters.activeTokenContract);

// Получение URL изображения токена по его названию
const imgOfToken = (state, getters) => (contract) => {
  if (contract === ETH_ADDRESS) {
    return {
      type: 'svg',
      urls: ['/static/img/eth-circle.svg'],
    };
  }

  const token = getters.tokenByContractSimple(contract);

  if (token) {
    const img = token.img;
    if (img) {
      let result = null;
      if (img.svg) {
        result = {
          type: 'svg',
          urls: img.svg,
        };
      } else if (img.png) {
        result = {
          type: 'png',
          urls: img.png,
        };
      }
      return result;
    }
  }

  return null;
};

const getTokenContractByNameAndShortName = state => (nameAndShortName) => {
  const tokenName = nameAndShortName.substring(0, nameAndShortName.lastIndexOf('-'));
  const tokenShortName = nameAndShortName.substring(nameAndShortName.lastIndexOf('-') + 1, nameAndShortName.length);
  const token = state.exchangeTokens.find(item => item.name === tokenName && item.shortName === tokenShortName);
  return token ? token.contract : null;
};

const tokenContracts = (state, getters) => {
  const exchangeAddresses = state.exchangeTokens.map(t => t.contract);
  const activeAddress = getters.activeTokenContract;
  if (activeAddress && !getters.isTokenContractOnExchange(activeAddress)) {
    return [activeAddress, ...exchangeAddresses];
  }
  return exchangeAddresses;
};

const assets = state => state.assets;

const ethPrice = state => get(find(state.assets, asset => asset.name === 'ETHUSD'), 'markPrice');

export default {
  tokenMap,
  sortedTokens,
  sortedTokensGainers,
  sortedTokensLoosers,
  ethAndTokens,
  myTokens,
  allTokensCount,
  recommendedTokens,
  // tokensForFlower,
  defaultTokenContract,
  activeToken,
  activeTokenContract,
  activeTokenTitle,
  getTotalAmountForActiveToken,
  tokenByContractSimple,
  tokenByContract,
  isTokenContractOnExchange,
  isActiveTokenContractOnExchange,
  imgOfToken,
  getTokenContractByNameAndShortName,
  tokenContracts,
  assets,
  ethPrice,
};



