/**
 * Metamask Cancel Transaction Signature
 * {
 *   message: "Error: MetaMask Tx Signature: User denied transaction signature.",
 *   code: -32603
 * }
 */
const ERROR_METAMASK_DENY_TRANSACTION = 'Error: MetaMask Tx Signature: User denied transaction signature.';

/**
 * Metamask Cancel Message Signature
 * {
 *   message: "Error: MetaMask Message Signature: User denied message signature.",
 *   code: -32603
 * }
 */
export const ERROR_METAMASK_DENY_MESSAGE = 'Error: MetaMask Message Signature: User denied message signature.';

// const ERROR_CODE_METAMASK_DENY = -32603;

/**
 * Errors that indicate that user cancelled Metamask transaction.
 *
 * @type {string[]}
 */
export const ERRORS_METAMASK_DENY = [
  ERROR_METAMASK_DENY_TRANSACTION,
  ERROR_METAMASK_DENY_MESSAGE,
];


/**
 * Ledger Nano S Cancel error transaction or signature message:
 * {
 *   message: "Ledger device: Condition of use not satisfied (denied by the user?) (0x6985)",
 *   name: "TransportStatusError",
 *   statusCode: 27013,
 *   statusText: "CONDITIONS_OF_USE_NOT_SATISFIED"
 * }
 */
export const ERROR_LEDGER_DENY = 'Ledger device: Condition of use not satisfied (denied by the user?) (0x6985)';
// export const ERROR_LEDGER_DENY = 'Invalid status 6985';

