<template>
  <transition-modal>
    <e-modal
      class="the-reconnect-popup"
      :class="[ `the-reconnect-popup__${theme}` ]"
      :level="10"
      :btnClose="false"
      :autoHeight="true"
      :secondaryDesign="true"
      :darkBackground="true"
      @close="handleClose"
    >
      <e-modal-trades
        slot="inner"
        custom-header
        @close="handleClose"
      >
        <div
          slot="header"
          class="the-reconnect-popup__title"
        >
          <div class="the-reconnect-popup__icon">
            <e-icon :src="iconConnection" />
          </div>
          Connection lost
        </div>
        <template slot="body">
          <div class="the-reconnect-popup__illustration">
            <e-icon :src="iconGeneration"/>
          </div>
          <div class="the-reconnect-popup__messages">
            <div class="the-reconnect-popup__title-msg">You were Disconnected.</div>
            <div class="the-reconnect-popup__title-msg">
              Please wait to reconnect
              <span class="dot">.</span><span class="dot">.</span><span class="dot">.</span>
            </div>
            <div class="the-reconnect-popup__textual">
              <p>
                Check your internet connection. Maybe some Wi-Fi problems? Try to restart your router or call your internet provider.
              </p>
            </div>
          </div>
        </template>
      </e-modal-trades>
    </e-modal>
  </transition-modal>
</template>

<script>
import { mapGetters } from 'vuex';
import iconGeneration from '@/icons/e-icon_generation.svg';
import iconConnection from '@/icons/e-icon_connection.svg';

import TransitionModal from '@/containers/transitions/TransitionModal';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';

export default {
  name: 'the-reconnect-popup',

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  created() {
    Object.assign(this, {
      iconGeneration,
      iconConnection,
    });
  },

  components: {
    TransitionModal,
    EButton,
    EIcon,
    EModal,
    EModalTrades,
  },

  methods: {
    handleClose() {
      this.$store.commit('ui/CLOSE_DISCONNECT_POPUP');
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-reconnect-popup {
  $this: &;

  padding: 22px 20px 0;

  &__title {
    align-items: center;
    color: $color-red;
    display: flex;
    justify-content: center;
    flex-direction: row;
    font-size: 16px;
    font-weight: 800;
  }

  &__icon {
    height: 10px;
    margin-right: 9.5px;
    width: 14px;
  }

  &__textual {
    color: $color-semi-blue;
    font-size: 14px;
    font-weight: 600;
  }

  &__illustration {
    width: 160px;
    height: 110px;
    margin: 0 auto 17px;
  }

  &__messages {
    margin-bottom: 25px;
  }

  &__title-msg {
    text-align: center;
    font-size: 16px;
    font-weight: 800;
    color: $color-dark-blue;
    line-height: 1;

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  #e-icon_generation__arrow path {
    transform-origin: 10.2px 14px 0;
    animation: rotating 1.2s linear infinite;
  }

  .dot {
    animation: dottes 1.4s linear infinite;
    opacity: 0;

    &:nth-child(2) {
      animation-delay: 0.2s;
    }

    &:nth-child(3) {
      animation-delay: 0.4s;
    }
  }

  &__dark {
    #{$this}__header {
      background: #232323;
    }

    #{$this}__title-msg {
      color: #fff;
    }

    #{$this}__textual {
      color: #8D9098;
    }
  }
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes dottes {
  0% {
    opacity: 1;
  }
  70%, 100% {
    opacity: 0;
  }
}
</style>



