export default {
  computed: {
    $formProgress() {
      const indicators = this.$progressIndicators;
      const totalValid = indicators.reduce((acc, i) => (i ? acc + 1 : acc), 0);
      const total = indicators.length;
      return Math.round((totalValid / total) * 100);
    },
  },

  watch: {
    $progressIndicators() {
      this.$emit('$formProgress', this.$formProgress);
    },
  },
};



