<template>
  <transition-modal>
    <e-modal
      class="the-popup-metamask-network"
      :level="11"
      :secondaryDesign="true"
      :autoHeight="true"
      :btnClose="false"
      :darkBackground="true"
      @close="close"
    >
      <template slot="inner">
        <e-modal-trades
          headerTitle="You are connected to the wrong network"
          @close="close"
        >
          <template slot="body">
            <div class="the-popup-metamask-network__container">
              <div class="the-popup-metamask-network__image">
                <e-icon :src="iconMetamask"/>
              </div>
              <div class="the-popup-metamask-network__main">
                <p>
                  Switch to
                  <span class="the-popup-metamask-network__main-net">
                  <span class="the-popup-metamask-network__main-net-logo"/>
                  {{ APP_ETH_NETWORK_ID === MAIN_NETWORK_ID ? 'Main Ethereum Network' : 'Rinkeby Test Network' }}
                </span>
                  in your MetaMask wallet to continue using DMEX.app.
                </p>
              </div>
            </div>
          </template>
        </e-modal-trades>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
import EModalTrades from '@/components/EModalTrades';
import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';

import iconMetamask from '@/icons/e-icon__metamask-network-error.svg';
import TransitionModal from '@/containers/transitions/TransitionModal';

export default {
  name: 'the-popup-metamask-network',

  created() {
    Object.assign(this, { iconMetamask });
  },

  methods: {
    close() {
      this.$store.commit('ui/CANCEL_METAMASK_NETWORK_POPUP');
    },
  },

  components: { TransitionModal, EModal, EModalTrades, EIcon },
};
</script>

<style lang="scss">
.the-popup-metamask-network {
  .e-modal-trades__header {
    font-weight: bold;
  }

  &__image {
    margin: 24px -6px 35px -30px;
    height: 161px;
    background: linear-gradient(180deg, #FDFDFD 0%, #F6F6F6 99.08%);
    box-shadow: 0 12px 14px rgba(0, 0, 0, 0.0705673);
    border-radius: 0 16px 16px 0;
  }

  &__main {
    font-weight: bold;
    line-height: 21px;
    font-size: 16px;
    text-align: center;
    color: #0D105B;
  }

  &__main-net {
    color: #00898A;
  }

  &__main-net-logo {
    display: inline-block;
    margin-left: 8px;
    width: 12px;
    height: 12px;
    background: #028789;
    border: 1px solid #006465;
    box-sizing: border-box;
    border-radius: 2px;
    transform: rotate(45deg);
  }
}
</style>



