/**
 * @module store/futures-trades/getters
 * @version 1.0.6
 */

// import { BUY_TOKEN_ORDER, ETH_DECIMALS } from '@/util/constants';

/**
 * Getter: Возвращает текущую страницу трейдов для отображения
 *
 * @since Version 1.0.4
 */
const latestTrades = state => state.latestTrades;

/**
 * Getter: Возвращает загружается сейчас информация по странице тредов или нет
 *
 * @since Version 1.0.5
 */
const latestTradesLoading = state => state.latestTradesLoading;


/*
 * Геттер возвращающий детали трейда для отображения в попапе делателй трейда
 *
 * @since Version 1.0.1
 */
const currentTradeDetails = state => state.selectedTrade;

export default {
  currentTradeDetails,
  latestTrades,
  latestTradesLoading,
};



