<template functional>
  <div class="e-svg-from-store" v-on="$listeners">
    <svg v-if="props.icon !== 'preloader'" class="e-svg-from-store__svg">
      <use :xlink:href="`#e-icon__${props.icon}`"/>
    </svg>
    <div v-else class="e-svg-from-store__preloader"></div>
  </div>
</template>

<script>
export default {
  name: 'e-svg-from-store',
  props: {
    icon: {
      type: String,
      required: true,
    },
  },
};
</script>

<style lang="scss">
.e-svg-from-store {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  &__svg {
    max-width: 100%;
    max-height: 100%;
  }

  &__preloader {
    width: 30px;
    height: 30px;
    position: relative;
    display: flex;
    background-color: #E9F1F9;
    border-radius: 50%;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    animation: rotaetockeniconspinting 1s linear infinite;

    &::before {
      content: '';
      width: 10px;
      height: 10px;
      background-image: url(data:image/svg+xml;base64,);
    }
  }
}

@keyframes rotaetockeniconspinting {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>



