import BigNumber from 'bignumber.js';

import backend from '../endpoint';

export const assetTransformer = (a) => {
  const model = {
    id: a.id,
    contractAddress: a.contract_address,
    futuresAssetHash: a.futures_asset_hash,
    name: a.name,
    decimals: a.decimals,
    symbol: a.symbol,
    label: a.label,
    baseToken: a.base_token,
    priceUrl: a.price_url,
    pricePath: a.price_path,
    inversed: a.inversed,
    createdAt: new Date(a.created_at),

    volume24h: BigNumber(a.volume_24h),
    markPrice: BigNumber(a.mark_price),

    lastPrice: a.last_price ? BigNumber(a.last_price) : null,
    closingPrice: a.closing_price ? BigNumber(a.closing_price) : null,

    recommendedContract: a.recommended_contract
      ? {
        id: a.recommended_contract.id,
        futuresContractHash: a.recommended_contract.futures_contract_hash,
      }
      : null,

    maxMargin: a.max_margin,
    notional: +a.notional,

    ticker: {
      volume: BigNumber(a.ticker.volume),
      open: BigNumber(a.ticker.open),
      high: BigNumber(a.ticker.high),
      low: BigNumber(a.ticker.low),
      last: BigNumber(a.ticker.last),
    },
  };

  model.tradePriceDiffVal = BigNumber(0);
  model.tradePriceDiffPct = BigNumber(0);
  model.isTradePriceIncreasing = true;
  if (model.lastPrice && model.closingPrice) {
    model.tradePriceDiffVal = model.lastPrice.minus(model.closingPrice);
    model.tradePriceDiffPct = model.tradePriceDiffVal.times(100).div(model.closingPrice);
    model.isTradePriceIncreasing = model.tradePriceDiffVal.gte(0);
  }

  return model;
};

export const getOneAsset = async (assetHash) => {
  const result = await backend.http.get(`/api/futures/assets/${assetHash}`);
  return assetTransformer(result.data.data);
};

export const getAssets = async ({ contractAddress, baseToken, type = 'crypto', limit = 50, offset = 0 }) => {
  const reqParams = {
    contract_address: contractAddress,
    limit,
    offset,
    type,
  };

  if (baseToken) {
    reqParams.base_token = baseToken;
  }

  const result = await backend.http.get('/api/futures/assets', {
    params: reqParams,
  });
  return result.data.data.map(assetTransformer);
};

export const getBaseToken = async ({ baseToken }) => backend.http.get(`/api/futures/assets?name=ETHUSD&base_token=${baseToken}`);



