<template>
  <div class="the-logo" :class="{centred: !isVersion}">
    <div class="the-logo__dmex">
      <e-icon :src="iconLogo"/>
    </div>
    <!--<div class="the-logo__body" :class="color">-->
      <!--<div class="the-logo__version" v-if="isVersion">{{APP_VERSION}} @ {{APP_BUILD_DATE}}</div>-->
    <!--</div>-->
  </div>
</template>

<script>
import iconLogo from '@/icons/e-icon__dmex-logo.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'the-logo',

  props: {
    color: {
      type: String,
    },
    isVersion: {
      type: Boolean,
    },
  },

  data() {
    return {
      iconLogo,
    };
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-logo {
  $this: &;

  position: relative;
  display: flex;
  text-decoration: none;

  &.centred {
    align-items: center;

    #{$this}__body {
      padding-top: 0;
    }

    #{$this}__text {
      font-size: 24px;
    }
  }

  &__dmex {
    width: 66px;
    height: 16px;
    margin-left: -3px;

    svg .e-logo-dmex__white-text {
      fill: #fff;
    }
  }

  &__text {
    font-size: 24px;
    font-weight: 600;
    color: $color-green;
    line-height: 1;
  }

  &__variety {
    color: #fff;
  }

  &__version {
    font-size: 10px;
    font-weight: 600;
    color: #AFB8BE;
  }

  &__body {
    padding-left: 13px;
    padding-top: 4px;
    font-weight: 600;

    &.blue {
      #{$this}__text {
        color: #0D105B;
      }
      #{$this}__variety {
        color: #0D105B;
      }
    }
  }
}
</style>



