<template>
  <transition-modal>
    <e-modal
      class="the-ledger-popup-sign"
      :secondaryDesign="true"
      :autoHeight="true"
      :btnClose="false"
      :level="1"
      :darkBackground="true"
      @close="close"
    >
      <template slot="inner">
        <e-modal-trades
          headerTitle="Approve transaction in Ledger Nano S"
          @close="close"
        >
          <template slot="body">
            <div class="the-ledger-popup-sign__container">
              <div class="the-ledger-popup-sign__image">
                <e-icon :src="iconLedger" />
              </div>
              <div class="the-ledger-popup-sign__textual">
                <p>
                  Check your Ledger Nano S and approve the transaction.
                </p>
              </div>
            </div>
          </template>
        </e-modal-trades>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
import { mapMutations } from 'vuex';

import TransitionModal from '@/containers/transitions/TransitionModal';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';
import EIcon from '@/components/EIcon';

import iconLedger from '@/icons/e-icon_ledger-full.svg';

export default {
  name: 'the-ledger-popup-sign',

  created() {
    Object.assign(this, {
      iconLedger,
    });
  },

  methods: {
    ...mapMutations({
      close: 'ui/HIDE_LEDGER_SIGN',
    }),
  },

  components: {
    TransitionModal,
    EModal,
    EModalTrades,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-ledger-popup-sign {
  &__header {
    color: $color-dark-blue;
    box-shadow: 0 0 3px 0 rgba(0,0,0,0.2);
    font-size: 16px;
    font-weight: 800;
    line-height: 1.375;
    padding: 25px 10px 21px;
  }

  &__container {
    padding: 24px 0 7px;
  }

  &__image {
    height: 106px;
    position: relative;
  }

  &__textual {
    color: $color-dark-blue;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.312em;
    margin: auto;
    max-width: 296px;
  }
}
</style>



