import backend from '../endpoint';

export const getOrderBook = async ({ futuresContractHash, userAddress }) => {
  const params = {
    futures_contract_hash: futuresContractHash,
  };

  if (userAddress) {
    params.user_address = userAddress;
  }

  const result = await backend.http.get('/api/futures/orderbook', { params });

  return result.data;
};

/**
 * Отписка от обновлений ордербука.
 *
 * @since Version 1.1.1
 */
export const removeOrderBookListener = () => {
  backend.socket.removeAllListeners('futures_orderbook_update');
};

/**
 * Подписка на обновление orderbook
 *
 * @since Version 1.1.1
 */
export const onOrderBookChange = (callback) => {
  removeOrderBookListener();
  backend.socket.on('futures_orderbook_update', callback);
};



