<template>
  <transition name="e-transfer-hint__transition">
    <div class="e-transfer-hint">
      <div class="e-transfer-hint__inner">
        <div class="e-transfer-hint__text">
          <slot/>
        </div>
        <div
          @click="$emit('close')"
          class="e-transfer-hint__close"
        >
          <e-icon
            class="e-transfer-hint__close-icon"
            :src="iconClose"
          />
          <span class="e-transfer-hint__close-label">Ok, got it!</span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconClose from '@/icons/e-icon_close.svg';

export default {
  name: 'e-transfer-hint',

  created() {
    this.iconClose = iconClose;
  },

  components: { EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-transfer-hint {
  $this: &;

  &__transition {
    &-enter-active, &-leave-active {
      transition: opacity $transition-time;
    }
    &-enter, &-leave-to {
      opacity: 0;
    }
  }

  &__inner {
    height: 108px;
    border-radius: 5px 5px 5px 0;
    background-color: $color-blue;
    color: $color-white;
    font-size: 13px;
    font-weight: 600;
    padding: 13px 15px 14px;
    display: flex;
    justify-content: center;
    flex-flow: column;
    box-sizing: border-box;
    transform: translateY(-9px);
  }

  &__inner::after {
    content: '';
    position: absolute;
    bottom: -7px;
    left: 0;
    width: 0;
    height: 0;
    border-top: 7px solid #0123E7;
    border-right: 12px solid transparent;
  }

  &__text {
    margin-bottom: 9px;
  }

  &__close {
    min-height: 18px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__close:hover {

    #{$this}__close-icon {
      fill: rgba(255, 255, 255, 1);
    }
    #{$this}__close-label {
      color: rgba(246, 247, 251, 1);
    }
    #{$this}__close-label::after {
      background-color: rgba(255, 255, 255, 1);
    }
  }

  &__close-icon {
    width: 10px;
    height: 10px;
    fill: rgba(255, 255, 255, 0.5);
    transition: fill $transition-time;
    margin-right: 8px;
  }

  &__close-label {
    position: relative;
    color: rgba(246, 247, 251, 0.7);
    transition: color $transition-time;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  &__close-label::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 79px;
    height: 1px;
    transition: background-color $transition-time;
    background-color: rgba(255, 255, 255, 0.23);
  }
}
</style>



