/**
 * @module store/wallets/helpers
 * @version 1.0.0
 */

import log from '@/util/logger';
import { ACTIVE_WALLET_LOCAL_STORAGE_KEY, WALLETS_LOCAL_STORAGE_KEY } from '@/store/constants';
import { setStorageArray } from '@/util/storage';

const isWalletPresent = ({ address, type }, wallets) => wallets.find(w => w.address === address && w.type === type);

export const isActiveRemoved = (active, oldWallets, newWallets) => (
  active &&
  isWalletPresent(active, oldWallets) &&
  !isWalletPresent(active, newWallets)
);

export const loadPrivateKeyWallets = () => {
  let res = [];

  const localWalletsRaw = window.localStorage.getItem(WALLETS_LOCAL_STORAGE_KEY);
  if (localWalletsRaw) {
    try {
      res = JSON.parse(localWalletsRaw);
    } catch (error) {
      res = [];
      log.error(`Invalid JSON of WALLETS_LOCAL_STORAGE_KEY ${error}`);
    }
  }
  return res;
};

export const loadActiveWallet = () => {
  const loadedWalletRaw = window.localStorage.getItem(ACTIVE_WALLET_LOCAL_STORAGE_KEY);
  if (loadedWalletRaw) {
    try {
      const loadedWallet = JSON.parse(loadedWalletRaw);
      if (loadedWallet && loadedWallet.address) {
        return loadedWallet;
      }
    } catch (e) {
      // invalid JSON
    }
  }
  return null;
};

export const storePrivateKeyWallets = (wallets) => {
  setStorageArray(WALLETS_LOCAL_STORAGE_KEY, wallets);
};



