<template>
  <div class="e-heading">
    <div
      class="e-heading__in"
      :class="[
        btns ? 'e-heading__in_direction-row' : '',
        icon ? 'e-heading__in_padding-left' : '',
        padingIcon ? 'e-heading__in_padding-left' : '',
        classes ? 'e-heading__in_' + classes : '',
      ]"
    >
      <div v-if="hasBack" @click="$router.go(-1)" class="e-heading__back"></div>

      <router-link
        v-if="toFutures"
        :to="{ name: 'futures' }"
        class="e-heading__back"
      ></router-link>

      <div
        class="e-heading__icon"
        :class="iconSize ? 'e-heading__icon_size-' + iconSize : ''"
        v-if="icon"
      >
        <e-icon :src="icon"></e-icon>
      </div>
      <div
        class="e-heading__textual"
        :class="[fullWidth ? 'e-heading__textual_full-width' : '']"
      >
        <component :is="titleTag" class="e-heading__title" v-html="title" />
        <div
          v-if="subtitle"
          class="e-heading__subtitle"
          :class="[
            hint ? 'e-heading__subtitle_hint' : ''
          ]"
          v-html="subtitle"
        />
        <div class="e-heading__content" v-if="hasContent">
          <slot></slot>
        </div>
      </div>
      <div
        class="e-heading__btns"
        v-if="btns"
        :class="[
          btnsInColumn ? 'e-heading__btns_direction-column' : '',
        ]"
      >
        <slot></slot>
      </div>

      <slot name="aside"/>
    </div>

  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconDefault from '@/icons/e-icon_check-list.svg';

export default {
  name: 'e-heading',

  data: () => ({ iconDefault }),

  props: {
    title: {
      type: String,
      default: '',
    },
    titleTag: {
      type: String,
      default: 'div',
    },
    subtitle: {
      type: String,
      default: '',
    },
    hint: {
      type: Boolean,
      default: false,
    },
    icon: {
      type: String,
      default: '',
    },
    btns: {
      type: Boolean,
      default: false,
    },
    hasBack: {
      type: Boolean,
      default: false,
    },
    toFutures: {
      type: Boolean,
      default: false,
    },
    hasContent: {
      type: Boolean,
      default: false,
    },
    btnsInColumn: {
      type: Boolean,
      default: false,
    },
    classes: {
      type: String,
      default: '',
    },
    iconSize: {
      type: String,
      default: '',
    },
    fullWidth: {
      type: Boolean,
      default: false,
    },
    padingIcon: {
      type: Boolean,
      default: false,
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-heading {
  $this: &;

  width: 100%;

  &__in {
    position: relative;
  }

  &__back {
    position: absolute;
    z-index: 1;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 12px;

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }

    @include r(1320) {
      left: -22px;

      &::before, &::after {
        width: 11px;
      }

      &::before {
        top: 9px;
      }
    }

    @include r(640) {
      top: 10px;
    }
  }

  &__in_direction-row {
    display: flex;
    flex-direction: row;
    text-align: left;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__in_padding-left {
    padding-left: 162px;

    @include r(1200) {
      padding-left: 86px;
    }
  }

  &__in_gutters {
    #{$this}__btns {
      align-items: stretch;
      padding-top: 7px;
    }

    #{$this}__title {
      max-width: 350px;
      @include r(1200) {
        max-width: 240px;
      }
    }
  }

  &__title {
    color: #0d105b;
    font-size: 36px;
    font-weight: 600;
    margin: 0;
    line-height: 0.85;
    white-space: nowrap;
    padding-top: 7px;

    @include r(767) {
      font-size: 30px;
    }

    @include r(640) {
      font-size: 22px;
      white-space: normal;
    }
  }

  &__subtitle {
    color: #0d105b;
    font-size: 20px;
    font-weight: 600;
    line-height: 21.5px;
    margin-top: 14px;
    display: inline-block;

    @include r(1200) {
      font-size: 13px;
      margin-top: 4px;
    }
  }

  &__textual {
    max-width: 490px;
  }

  &__textual_full-width {
    max-width: none;
  }

  &__icon {
    position: absolute;
    top: -4px;
    left: -20px;
    height: 140px;
    width: 200px;

    .e-icon {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    @include r(1200) {
      width: 115px;
    }
  }

  &__btns {
    display: flex;
    justify-content: flex-end;
    margin-left: 50px;

    @include r(1200) {
      max-width: 265px;
      margin-left: 20px;
    }
    button {
      margin-right: 15px;
      &:last-of-type {
        margin-right: 0;
      }
    }
    &_direction-column {
      flex-direction: column;
      align-items: flex-end;
      button {
        margin-right: 0;
        margin-bottom: 20px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }

    @include r(640) {
      margin-left: 0;
    }
  }

  &__content {
    color: $color-dark-blue;
    font-size: 16px;
    margin-top: 11px;
  }
}
</style>



