export default {
  methods: {
    $searchTokens(filter, tokens = this.$store.getters['tokens/sortedTokens'], property) {
      if (!filter) return tokens;
      const lowerCasedFilter = filter.toLowerCase();
      return tokens.filter((t) => {
        let name;
        let shortName;
        let contract;
        if (property) {
          name = t[property].name;
          shortName = t[property].shortName;
          contract = t[property].contract;
        } else {
          name = t.name;
          shortName = t.shortName;
          contract = t.contract;
        }
        return (
          (name != null && name.toLowerCase().includes(lowerCasedFilter)) ||
          (shortName != null && shortName.toLowerCase().includes(lowerCasedFilter)) ||
          (contract != null && contract.toLowerCase().includes(lowerCasedFilter))
        );
      });
    },
  },
};



