import { BigNumber } from 'bignumber.js';
import { ETH_ADDRESS, ETH_DECIMALS, BUY_TOKEN_ORDER, SELL_TOKEN_ORDER, ORDER_TYPE_MARKET, ORDER_TYPE_STOP_LIMIT } from '@/util/constants';
import getOrderTotalRoundingMode from '@/util/getOrderTotalRoundingMode';

export const isEnoughBalance = ({ buyOrSell, total, amount }, { ethBalance, tokenBalance }) => {
  if (buyOrSell === BUY_TOKEN_ORDER) {
    return ethBalance.gte(total);
  } else if (buyOrSell === SELL_TOKEN_ORDER) {
    return tokenBalance.gte(amount);
  }
  throw Error('buyOrSell should be specified to check balance before order creation');
};

export const buyOrSellToString = buyOrSell => (
  buyOrSell === BUY_TOKEN_ORDER
    ? 'Buy order'
    : 'Sell order'
);

export const getOrderDetails = ({ order, tokenDecimals, getMarketPrice }) => {
  const buyOrSell = order.buyOrSell;
  const amountNonShifted = BigNumber(order.orderParams.amount);
  const limitPrice = order.orderType === ORDER_TYPE_MARKET
    ? getMarketPrice(buyOrSell).shiftedBy(ETH_DECIMALS)
    : BigNumber(order.orderParams.limitPrice).shiftedBy(ETH_DECIMALS);
  let stopPrice;
  if (order.orderType === ORDER_TYPE_STOP_LIMIT) {
    stopPrice = BigNumber(order.orderParams.stopPrice).shiftedBy(ETH_DECIMALS).toFixed();
  }
  return {
    buyOrSell,
    type: order.orderType,
    amount: amountNonShifted.shiftedBy(tokenDecimals),
    limitPrice,
    stopPrice,
    total: BigNumber(amountNonShifted).times(limitPrice).dp(0, getOrderTotalRoundingMode(buyOrSell)),
  };
};

export const getSignParams = ({ tokenAddress, orderDetails }) => {
  const { amount, total, buyOrSell } = orderDetails;
  if (buyOrSell === BUY_TOKEN_ORDER) {
    return {
      buyAddress: tokenAddress,
      buyAmount: amount.toFixed(),
      sellAddress: ETH_ADDRESS,
      sellAmount: total.toFixed(),
    };
  } else if (buyOrSell === SELL_TOKEN_ORDER) {
    return {
      buyAddress: ETH_ADDRESS,
      buyAmount: total.toFixed(),
      sellAddress: tokenAddress,
      sellAmount: amount.toFixed(),
    };
  }
  throw new Error(`buyOrSell is wrong: ${buyOrSell}`); // development-only error
};



