/**
 * @module store/order-book/mutations
 *
 * @version 1.0.1
 */

import log from '@/util/logger';
import * as types from '@/store/mutation-types';


const mutations = {
  /**
   * Mutation: Начало асинхронной загрузки orderbook
   *
   * @since Version 1.0.0
   */
  [types.GET_ORDER_BOOK_ASYNC.PENDING](state) {
    state.orderBookLoading = true;
  },


  /**
   * Mutation: Обработка ошибки при загрузке orderbook
   *
   * @since Version 1.0.0
   */
  [types.GET_ORDER_BOOK_ASYNC.FAILURE](state, error) {
    state.orderBookLoading = false;
    log.error(`Error loading my orderbook: ${error}`, {
      module: 'orderbook',
      layer: 'backend',
    });
  },


  /**
   * Mutation: Обработка успешной загрузки orderbook
   *
   * @since Version 1.0.0
   */
  [types.GET_ORDER_BOOK_ASYNC.SUCCESS](state, { asks, bids, volume }) {
    state.orderBookLoading = false;
    state.asks = asks;
    state.bids = bids;
    state.volume = volume;
  },
};


export default mutations;



