import { BigNumber } from 'bignumber.js';
import { BUY_TOKEN_ORDER, SELL_TOKEN_ORDER, ORDER_TYPE_STOP_LIMIT, FUTURES_PRICE_DECIMALS, FUTURES_AMOUNT_DECIMALS, BLOCK_TIME_SEC } from '@/util/constants';
import getOrderTotalRoundingMode from '@/util/getOrderTotalRoundingMode';
import futuresCalc from '@/util/futuresCalc';

export const isEnoughBalance = ({ buyOrSell, total, amount }, { ethBalance, tokenBalance }) => {
  if (buyOrSell === BUY_TOKEN_ORDER) {
    return ethBalance.gte(total);
  } else if (buyOrSell === SELL_TOKEN_ORDER) {
    return tokenBalance.gte(amount);
  }
  throw Error('buyOrSell should be specified to check balance before order creation');
};

export const buyOrSellToString = buyOrSell => (
  buyOrSell === BUY_TOKEN_ORDER
    ? 'Buy order'
    : 'Sell order'
);

export const getOrderDetails = ({ order }) => {
  const buyOrSell = order.buyOrSell;
  const amountNonShifted = BigNumber(order.orderParams.amount);
  // const limitPrice = order.orderType === ORDER_TYPE_MARKET
  //   ? getMarketPrice(buyOrSell).shiftedBy(FUTURES_PRICE_DECIMALS)
  //   : BigNumber(order.orderParams.limitPrice).shiftedBy(FUTURES_PRICE_DECIMALS);
  const limitPrice = BigNumber(order.orderParams.limitPrice).shiftedBy(FUTURES_PRICE_DECIMALS);
  let stopPrice;
  if (order.orderType === ORDER_TYPE_STOP_LIMIT) {
    stopPrice = BigNumber(order.orderParams.stopPrice).shiftedBy(FUTURES_PRICE_DECIMALS).toFixed();
  }
  return {
    buyOrSell,
    type: order.orderType,
    side: buyOrSell === BUY_TOKEN_ORDER,
    amount: amountNonShifted.shiftedBy(FUTURES_AMOUNT_DECIMALS).integerValue(), // Decimals are not allowed
    limitPrice: limitPrice.integerValue(), // Decimals are not allowed
    stopPrice,
    total: BigNumber(amountNonShifted).times(limitPrice).dp(0, getOrderTotalRoundingMode(buyOrSell)),
  };
};

export const getContractDetails = ({ contractModel, ethusdRawPrice, currentBlockNumber }) => ({
  creationBlock: currentBlockNumber,
  expirationBlock: Math.round(currentBlockNumber + (contractModel.expiresInSeconds / BLOCK_TIME_SEC)),
  multiplier: futuresCalc.calculateMultiplier(contractModel.baseToken, ethusdRawPrice),
  fundingRate: contractModel.fundingRate,
});

export const getSignParams = ({ orderDetails }) => {
  const { amount, limitPrice, stopPrice, side } = orderDetails;

  return {
    amount: amount.toFixed(),
    price: limitPrice.toFixed(),
    stopPrice,
    side,
  };
};



