import { ETH_DECIMALS } from '@/util/constants';
import { BigNumber } from 'bignumber.js';

export default (price, decimals) => {
  if (price == null) throw new Error('price should be specified');
  if (decimals == null) throw new Error('decimals should be specified');
  return BigNumber(1).div(BigNumber(price).shiftedBy(-decimals)).dp(ETH_DECIMALS, BigNumber.ROUND_UP);
};



