import HookedWalletSubprovider from 'web3-provider-engine/dist/es5/subproviders/hooked-wallet';
import { Transaction } from 'ethereumjs-tx';
import { APP_ETH_NETWORK_ID, TIMEOUT_LEDGER_USER_RESPONSE } from '@/config';
import getLedgerApp from './getLedgerApp';

const networkId = APP_ETH_NETWORK_ID;

const makeError = (msg, id) => {
  const err = new Error(msg);
  err.id = id;
  return err;
};

const signTransaction = async (txData, addressToPathMap) => {
  const path = addressToPathMap[txData.from.toLowerCase()];
  if (!path) throw new Error(`address unknown '${txData.from}'`);
  const eth = await getLedgerApp({ exchangeTimeout: TIMEOUT_LEDGER_USER_RESPONSE });
  const tx = new Transaction(txData, { chain: networkId });

  // Set the EIP155 bits
  tx.raw[6] = Buffer.from([networkId]); // v
  tx.raw[7] = Buffer.from([]); // r
  tx.raw[8] = Buffer.from([]); // s

  // Pass hex-rlp to ledger for signing
  const result = await eth.signTransaction(
    path,
    tx.serialize().toString('hex'),
  );

  // Store signature in transaction
  tx.v = Buffer.from(result.v, 'hex');
  tx.r = Buffer.from(result.r, 'hex');
  tx.s = Buffer.from(result.s, 'hex');

  // EIP155: v should be chain_id * 2 + {35, 36}
  const signedChainId = Math.floor((tx.v[0] - 35) / 2);
  // eslint-disable-next-line no-bitwise
  const validChainId = networkId & 0xff; // FIXME this is to fixed a current workaround that app don't support > 0xff
  if (signedChainId !== validChainId) {
    throw makeError(
      `Invalid networkId signature returned. Expected: ${
        networkId
      }, Got: ${
        signedChainId}`,
      'InvalidNetworkId',
    );
  }

  return `0x${tx.serialize().toString('hex')}`;
};

export default ({ getAddressToPathMap }) => {
  const subprovider = new HookedWalletSubprovider({
    getAccounts: (callback) => {
      callback(null, Object.keys(getAddressToPathMap()));
    },
    signTransaction: (txData, callback) => {
      signTransaction(txData, getAddressToPathMap())
        .then(res => callback(null, res))
        .catch(err => callback(err, null));
    },
  });
  return subprovider;
};



