<template>
  <div id="app">
    <transition-page>
      <router-view/>
    </transition-page>

    <the-metamask-sign-popup v-if="metamaskSignRequired"/>
    <the-reconnect-popup v-if="disconnectPopup || standbyPopup"/>
    <the-maintenance-popup v-if="maintenancePopup"/>
    <the-ledger-popup-sign v-if="ledgerApproveModal"/>
    <the-balance-transfer-popup v-if="tokenTransferActive && !btcBaseToken"/>
    <the-balance-btc-popup v-if="tokenTransferActive && btcBaseToken"/>
    <the-popup-balance-low v-if="balanceEthLowPopup"/>
    <the-popup-metamask-network v-if="showMetamaskNetworkPopup"/>
    <the-password-popup v-if="passwordPrivateKeyModal"/>
    <the-login-flow-popup v-if="loginFlowModal !== null" />
    <the-edit-margin-popup v-if="marginPosition !== null" />
    <the-notifications/>
    <the-svg-store/>
  </div>
</template>

<script>
import { PAGE_TITLE } from '@/config';
import { mapState, mapGetters, mapActions } from 'vuex';
import { getStorageValue } from '@/util/storage';

import TheMaintenancePopup from '@/containers/blocks/TheMaintenancePopup';
import ThePopupBalanceLow from '@/containers/blocks/ThePopupBalanceLow';
import TransitionModal from '@/containers/transitions/TransitionModal';
import TransitionPage from '@/containers/transitions/TransitionPage';
import TheNotifications from '@/containers/blocks/TheNotifications';
import TheBalanceTransferPopup from '@/containers/blocks/TheBalanceTransferPopup';
import TheBalanceBtcPopup from '@/containers/blocks/TheBalanceTransferBtcPopup';
import TheMetamaskSignPopup from '@/containers/blocks/TheMetamaskSignPopup';
import TheLoginFlowPopup from '@/containers/blocks/TheLoginFlowPopup';
import TheEditMarginPopup from '@/containers/blocks/TheEditMarginPopup';
import TheReconnectPopup from '@/containers/blocks/TheReconnectPopup';
import ThePopupMetamaskNetwork from '@/containers/blocks/ThePopupMetamaskNetwork';
import TheLedgerPopupSign from '@/containers/blocks/TheLedgerPopupSign';
import ThePasswordPopup from '@/containers/blocks/ThePasswordPopup';
import TheSvgStore from '@/containers/blocks/TheSvgStore';

export default {
  name: 'App',

  data() {
    return {
    };
  },

  computed: {
    ...mapGetters({
      btcBaseToken: 'balance/btcBaseToken',
      tokenTransferActive: 'deposit/tokenTransferActive',
      btcTransferActive: 'deposit/btcTransferActive',
      // suspendPopup: 'ui/isDisconnectPopupDisplayed',
      maintenancePopup: 'ui/isMaintenancePopupDisplayed',
      marginPosition: 'futuresPositions/marginPosition',
      disconnectPopup: 'ui/isDisconnectPopupDisplayed',
      standbyPopup: 'ui/isStandbyPopupDisplayed',
      metamaskSignRequired: 'ui/metamaskSignRequired',
      loginFlowModal: 'ui/loginFlowModal',
      balanceEthLowPopup: 'ui/balanceEthLowPopup',
      showMetamaskNetworkPopup: 'ui/showMetamaskNetworkPopup',
      passwordPrivateKeyModal: 'ui/passwordPrivateKeyModal',
    }),
    // maintenancePopup: () => true,
    // disconnectPopup: () => true,

    ...mapState({
      ledgerApproveModal: state => state.ui.ledgerApproveModal,
    }),
  },

  created() {
    this.setWindowSize = this.setWindowSize.bind(this);
    this.changeVisibility = this.changeVisibility.bind(this);
  },

  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.setWindowSize);
      document.addEventListener('visibilitychange', this.changeVisibility);

      if (getStorageValue('theme') === 'light') {
        this.$store.commit('ui/SET_THEME_LIGHT');
      }

      if (getStorageValue('theme') === 'dark') {
        this.$store.commit('ui/SET_THEME_DARK');
      }

      // Init
      this.setWindowSize();
    });
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.setWindowSize);
    document.removeEventListener('visibilitychange', this.changeVisibility);
  },

  methods: {
    ...mapActions({
      changeVisibility: 'ui/changeVisibility',
    }),

    /**
     * Функция задания размера окна
     */
    setWindowSize() {
      this.$store.commit('ui/SET_WINDOW_SIZE', {
        width: document.documentElement.clientWidth,
        height: document.documentElement.clientHeight,
      });
    },
  },


  metaInfo: {
    title: PAGE_TITLE,
    meta: [
      {
        name: 'description',
        content: 'DMEX is the First Decentralized Margin Trading Exchange with up to 100X leverage and beautiful user experience. Trade 20+ assets with full security of funds and anonymity.',
      },
    ],
  },

  components: {
    TheMaintenancePopup,
    TheLedgerPopupSign,
    ThePopupBalanceLow,
    TheSvgStore,
    ThePopupMetamaskNetwork,
    TheMetamaskSignPopup,
    TheNotifications,
    TheBalanceTransferPopup,
    TheBalanceBtcPopup,
    TransitionModal,
    TransitionPage,
    TheReconnectPopup,
    ThePasswordPopup,
    TheLoginFlowPopup,
    TheEditMarginPopup,
  },
};
</script>

<style lang="scss">
@import './scss/index';
@import './scss/styles';
</style>



