<template>
  <e-modal
    class="the-maintenance-popup"
    :level="10"
    :btnClose="false"
    :autoHeight="true"
    :secondaryDesign="true"
    :darkBackground="true"
  >
    <div slot="logo" class="the-maintenance-popup__logo">
      <the-logo isVersion />
    </div>
    <e-modal-trades
      slot="inner"
      :btn-cancel="false"
      custom-header
    >
      <div
        slot="header"
        class="the-maintenance-popup__title"
      >
        Maintenance
      </div>
      <template slot="body">
        <div class="the-maintenance-popup__illustration">
          <e-icon :src="iconMaintenance"/>
        </div>
        <div class="the-maintenance-popup__messages">
          <div class="the-maintenance-popup__title-msg">
            We're undergoing a bit of scheduled maintenance
            <span class="dot">.</span><span class="dot">.</span><span class="dot">.</span>
          </div>
          <div class="the-maintenance-popup__textual">
            <p>
              Sorry for the inconvenience. We'll be back up and running as fast as possible.
            </p>
          </div>
        </div>
      </template>
    </e-modal-trades>
  </e-modal>
</template>

<script>
import TheLogo from '@/containers/blocks/TheLogo';
import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';

import iconMaintenance from '@/icons/e-icon_maintenance.svg';

export default {
  name: 'the-maintenance-popup',

  created() {
    this.iconMaintenance = iconMaintenance;
  },

  components: {
    TheLogo,
    EModal,
    EModalTrades,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-maintenance-popup {
  padding: 22px 20px 0;

  &__title {
    align-items: center;
    color: $color-red;
    display: flex;
    justify-content: center;
    flex-direction: row;
    font-size: 16px;
    font-weight: 800;
  }

  &__icon {
    height: 10px;
    margin-right: 9.5px;
    width: 14px;
  }

  &__textual {
    color: $color-semi-blue;
    font-size: 14px;
    font-weight: 600;
  }

  &__illustration {
    width: 127px;
    height: 95px;
    margin: 0 auto 45px;
  }

  &__messages {
    margin-bottom: 25px;
  }

  &__title-msg {
    text-align: center;
    font-size: 16px;
    font-weight: 800;
    color: $color-dark-blue;
    line-height: 1;

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  .dot {
    animation: dottes 1.4s linear infinite;
    opacity: 0;

    &:nth-child(2) {
      animation-delay: 0.2s;
    }

    &:nth-child(3) {
      animation-delay: 0.4s;
    }
  }

  &.e-modal_dark .e-modal__cover {
    background-image: radial-gradient(60% 100%, #0025F7 -15%, #0F0D44 100%);
  }

  &__logo {
    position: absolute;
    z-index: 9990;
    top: 36px;
    left: 24px;
  }
}
</style>



