import { BigNumber } from 'bignumber.js';
import { ETH_ADDRESS, ASYNC_STATES, ETH_BASE_ADDRESS, ETH_DEMO_ADDRESS, ETH_STAGE_ADDRESS } from '@/util/constants';
import { getStorageValue } from '@/util/storage';

const state = {
  ethInitStatus: ASYNC_STATES.INITIAL,
  tokenInitStatus: ASYNC_STATES.INITIAL,
  privateLoaded: false,
  tradableLoaded: false,
  privateLoading: false,
  tradableLoading: false,
  availableLoading: false,

  privateReloading: false,
  tradableReloading: false,

  // privateLoadingWallet: null,
  // privateReloadingWallet: null,
  // privateSubscribedWallet: null,

  privateBalance: {
    eth: BigNumber(0),
    tokens: {},
  },

  tradableBalance: {
    [ETH_ADDRESS]: BigNumber(0),
  },

  availableBalance: BigNumber(0),

  reserved: {
    [ETH_ADDRESS]: BigNumber(0),
  },

  privateListenersCount: 0,
  privateSubscriptionActive: false,
  contractListenersCount: 0,
  contractSubscriptionActive: false,

  baseToken: [ETH_ADDRESS, ETH_DEMO_ADDRESS, ETH_STAGE_ADDRESS].includes(getStorageValue('BASE_TOKEN')) ? ETH_BASE_ADDRESS : getStorageValue('BASE_TOKEN') || ETH_BASE_ADDRESS,
  btcBaseToken: getStorageValue('BASE_TOKEN_BTC'),
};

export default state;



