import { PRIVATE_KEY_TYPE, METAMASK_WALLET_TYPE } from '@/util/constants';
import ethSign from './ethSign';

export default class Signatures {
  constructor({ web3, exchangeContract, futuresContract, user, isMetamaskLedger }) {
    this.web3 = web3;
    this.contract = exchangeContract;
    this.futuresContract = futuresContract;
    this.user = user;
    this.isMetamaskLedger = isMetamaskLedger;
  }

  async signOrder({ buyAddress, buyAmount, sellAddress, sellAmount, nonce }) {
    const orderHash = this.contract.createOrderHash(
      buyAddress,
      buyAmount,
      sellAddress,
      sellAmount,
      nonce,
      this.user.address,
    );
    const signParams = await ethSign({
      web3: this.web3,
      wallet: this.getOverriddenWallet(),
      data: orderHash,
    });
    return { orderHash, nonce, ...signParams };
  }

  async createUpdateMarginHash({ user, futuresContractHash, side, newMargin }) {
    const marginHash = this.futuresContract.createUpdateMarginHash(user, futuresContractHash, side, newMargin);

    const signParams = await ethSign({
      web3: this.web3,
      wallet: this.getOverriddenWallet(),
      data: marginHash,
    });
    return { marginHash, ...signParams };
  }

  async signCancelOrder({ orderHash, nonce }) {
    const cancelHash = this.contract.createCancelOrderHash(
      orderHash,
      this.user.address,
      nonce,
    );
    const signParams = await ethSign({
      web3: this.web3,
      wallet: this.getOverriddenWallet(),
      data: cancelHash,
    });
    return { cancelHash, nonce, ...signParams };
  }

  async signWithdrawal({ tokenAddress, value, nonce, withdrawFee }) {
    const withdrawHash = this.contract.createWithdrawHash(
      tokenAddress,
      value,
      this.user.address,
      nonce,
      withdrawFee,
    );

    const signParams = await ethSign({
      web3: this.web3,
      wallet: this.getOverriddenWallet(),
      data: withdrawHash,
    });
    return { withdrawHash, nonce, ...signParams };
  }

  async signPTokenRedeem({ tokenAddress, value, nonce, destinationAddress, withdrawFee }) {
    if (typeof this.contract.createPTokenRedeemHash === 'undefined') {
      throw new Error('Current base contract does not support ptoken redeem');
    }

    const withdrawHash = this.contract.createPTokenRedeemHash(
      tokenAddress,
      value,
      this.user.address,
      nonce,
      destinationAddress,
      withdrawFee,
    );

    const signParams = await ethSign({
      web3: this.web3,
      wallet: this.user,
      data: withdrawHash,
    });
    return { withdrawHash, nonce, ...signParams };
  }

  signData(data) {
    return ethSign({
      web3: this.web3,
      wallet: this.getOverriddenWallet(),
      data,
    });
  }

  getOverriddenWallet() {
    const type = this.user.type === METAMASK_WALLET_TYPE && this.isMetamaskLedger
      ? PRIVATE_KEY_TYPE
      : this.user.type;

    return { ...this.user, type };
  }

  setIsMetamaskLedger(isMetamaskLedger) {
    this.isMetamaskLedger = isMetamaskLedger;
  }
}



