import BigNumber from 'bignumber.js';

import backend from '../endpoint';

export const positionTransformer = p => ({
  id: p.id,
  side: p.side,
  label: p.futures_asset.label,
  decimals: p.futures_asset.decimals,
  symbol: p.futures_asset.symbol,
  baseToken: p.futures_asset.base_token,
  contractAddress: p.contract_address,
  futuresContractHash: p.futures_contract_hash,
  userAddress: p.user_address,
  positionHash: p.position_hash,
  size: BigNumber(p.size),
  price: p.price ? BigNumber(p.price) : null,
  expiresInSeconds: p.expires_in_seconds,
  unrealizedPL: BigNumber(p.pnl),
  realizedPL: BigNumber(p.realized_pnl),
  collateral: BigNumber(p.collateral),
  markPrice: BigNumber(p.mark_price),
  liqPrice: BigNumber(p.liq_price),
  leverage: BigNumber(p.leverage),
  value: p.value,
  fee: BigNumber(p.fee).shiftedBy(-10),
  fundingCost: BigNumber(p.funding_cost),
  netPL: BigNumber(p.pnl).minus(BigNumber(p.fee).shiftedBy(-10)).minus(p.funding_cost),
  closedAt: p.closed_at !== null ? new Date(p.closed_at) : null,
  futuresContract: {
    multiplier: BigNumber(p.futures_contract.multiplier),
    expiresInSeconds: p.futures_contract.expires_in_seconds,
  },
});

export const getOpenPositions = async ({ userAddress }) => {
  const params = {
    user_address: userAddress,
  };

  const resp = await backend.http.get('/api/futures/positions-v2/open', { params });

  return resp.data;
};

export const getClosedPositions = async ({ userAddress }) => {
  const params = {
    user_address: userAddress,
  };

  const resp = await backend.http.get('/api/futures/positions-v2/closed', { params });

  return resp.data;
};



