const SEPARATOR = '/';

export default {
  computed: {
    $closedPopupRoute() {
      let nextName;
      const curName = this.$route.name;
      if (curName == null) {
        nextName = 'index';
      } else {
        const separatorIndex = curName.indexOf(SEPARATOR);
        nextName = separatorIndex === -1 ? curName : curName.substring(0, separatorIndex);
      }
      return { name: nextName, params: this.$route.params };
    },
  },

  methods: {
    $popupRoute(routeDescriptor) {
      const { popupPage, ...popupPageParams } = typeof routeDescriptor === 'object'
        ? routeDescriptor
        : { popupPage: routeDescriptor };
      let nextName;
      const curName = this.$route.name;
      if (curName == null) {
        nextName = `index${SEPARATOR}${popupPage}`;
      } else {
        const separatorIndex = curName.indexOf(SEPARATOR);
        nextName = separatorIndex === -1
          ? `${curName}${SEPARATOR}${popupPage}`
          : `${curName.substring(0, separatorIndex)}${SEPARATOR}${popupPage}`;
      }
      return {
        name: nextName,
        params: { ...this.$route.params, ...popupPageParams },
      };
    },
  },
};



