import ERC20Contract from 'erc20-contract-js';
import getNameForNamelessToken from '@/util/getNameForNamelessToken';

/**
 * Rejects if no decimals.
 *
 * @param contract
 * @returns {Promise<number>}
 */
const getDecimals = async (contract) => {
  const decimalsStr = await contract.decimals().call({});
  return parseInt(decimalsStr, 10);
};

const getSymbol = async (contract) => {
  try {
    return await contract.symbol().call({});
  } catch (errSymbol) {
    return getNameForNamelessToken(contract.getAddress());
  }
};

const getName = async (contract) => {
  try {
    return await contract.name().call({});
  } catch (e) {
    return getNameForNamelessToken(contract);
  }
};

export default class TokenContracts {
  constructor({ web3 }) {
    this.web3 = web3;
  }

  /**
   * Doesn't throw exception.
   *
   * @param address
   * @returns {Promise<{contract: *, decimals: any, name: any, shortName: any}>}
   */
  async getTokenData(address) {
    const contract = new ERC20Contract(this.web3, address);
    const [decimals, shortName, name] = await Promise.all([
      getDecimals(contract),
      getSymbol(contract),
      getName(contract),
    ]);

    return {
      contract: address,
      decimals,
      name,
      shortName,
    };
  }
}



