import LedgerAppEth from '@ledgerhq/hw-app-eth';
import { TIMEOUT_LEDGER_USER_RESPONSE } from '@/config';
import TransportU2F from '@ledgerhq/hw-transport-u2f';
import log from '@/util/logger';

const getTransport = async () => {
  try {
    return await TransportU2F.create();
  } catch (e) {
    log.debug(`Error: Create Ledger TransportU2F: ${e}`);
    throw e;
  }
};

export default async ({ exchangeTimeout = TIMEOUT_LEDGER_USER_RESPONSE } = {}) => {
  try {
    const transport = await getTransport();
    transport.setExchangeTimeout(exchangeTimeout); // Ledger interaction timeouts (including user interactions)
    return new LedgerAppEth(transport);
  } catch (e) {
    return null;
  }
};



