<template>
  <div
    class="e-select"
    :class="selectClass"
  >
    <div class="e-select__option-icon-wrap">
      <div class="e-select__option-icon">
        <e-icon
          v-show="displayedIcon"
          :src="displayedIcon"
        />
      </div>
    </div>

    <div
      class="e-select__box"
      tabindex="0"
      @focus="isFocused = true"
      @blur="isFocused = false"
      v-click-outside="handleClickOutside"
    >
      <div class="e-select__selected" @click="toggleOptions">
        <div class="e-select__content">
          <div v-if="label" class="e-select__label">
            {{ label }}
          </div>

          <div class="e-select__selected-label">
            {{ selected ? selected.label : '' }}
          </div>
        </div>

        <div class="e-select__dropdown-icon">
          <e-icon :src="dropdownIcon"/>
        </div>
      </div>

      <div class="e-select__options" :class="{ isLedger }">
        <div
          class="e-select__options-item"
          v-for="(option, i) of withPagination ? options.slice(pageNumber*pageCountNum, pageNumber*pageCountNum + pageCountNum) : options"
          :key="option.value"
        >
          <div
            class="e-select__option"
            :class="[getSelectedOptionClass(option), withPagination ? 'with-pagination' : '' ]"
            @click="select(option)"
          >
            {{ option.label }}
          </div>
          <div
            class="e-select__option-separator"
            v-if="i < options.length - 1"
          />
        </div>

        <e-preloader v-if="isLedger && !options.length && !ledgerError"/>

        <div v-if="ledgerError" class="e-select__ledger-error">Something went wrong… Please try again.</div>

        <e-pagination
          v-if="pageCount > 1 && withPagination"
          v-model="pageNumber"
          isSelectPagination
          :page-count="pageCount"
          class="e-select__pagination"
          type="select"
        />
      </div>

      <e-pagination
        v-if="isLedger && optionsDisplayed"
        isLedger
        v-model="ledgerPageNumber"
        isSelectPagination
        :page-count="999"
        class="e-select__pagination"
        type="select"
      />
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';
import EPagination from '@/components/EPagination';
import EPreloader from '@/components/EPreloader';
import dropdownIcon from '@/icons/e-icon_arrow-down-b.svg';

export default {
  name: 'e-select',

  props: {
    isLedger: Boolean,
    value: {
      required: true,
    },
    withPagination: Boolean,
    label: String,
    icon: String,
    options: {
      type: Array,
      required: true,
    },
    pageCountNum: Number,
  },

  data() {
    return {
      dropdownIcon,
      optionsDisplayed: false,
      isFocused: false,
      pageNumber: 0,
      ledgerPageNumber: 0,
      ledgerError: false,
    };
  },

  watch: {
    ledgerPageNumber(val) {
      this.$emit('ledgerPageNumber', val);
    },

    optionsDisplayed() {
      if (this.isLedger) {
        this.ledgerError = false;

        setTimeout(() => {
          if (this.options.length === 0) {
            this.ledgerError = true;
          }
        }, 30 * 1000);
      }
    },
  },

  computed: {
    pageCount() {
      return Math.ceil(this.options.length / this.pageCountNum);
    },

    selected() {
      if (this.value === undefined || this.options.length === 0) return undefined;
      return this.options.find(o => o.value === this.value);
    },

    displayedIcon() {
      const selected = this.selected;
      if (selected && selected.icon) return selected.icon;
      return this.icon || null;
    },

    selectClass() {
      return {
        selecting: !this.selected && (this.isFocused || this.optionsDisplayed),
        selected: this.selected,
        expanded: this.optionsDisplayed,
      };
    },
  },

  methods: {
    getSelectedOptionClass({ value }) {
      return { selected: this.selected && this.selected.value === value };
    },

    select({ value }) {
      this.closeOptions();
      this.$emit('input', value);
    },

    toggleOptions() {
      this.optionsDisplayed = !this.optionsDisplayed;
    },

    closeOptions() {
      this.$nextTick(() => { this.optionsDisplayed = false; });
    },

    handleClickOutside() {
      this.optionsDisplayed = false;
    },
  },

  components: {
    EPreloader,
    EPagination,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';
$e-input-border-size: 2px;

.e-select {
  $this: &;

  display: flex;
  max-width: 450px;
  min-height: 60px;
  user-select: none;
  fill: #dadada;
  position: relative;
  z-index: 1;

  @include r(1200) {
    min-height: 50px;
    max-width: 430px;
  }

  &__label {
    position: absolute;
    top: 17px;
    left: 4px;
    font-size: 16px;
    margin: 0 7px 0;
    color: black;
    transform: scale(1) translate(0);
    transform-origin: top left;
    transition: .3s ease;
    pointer-events: none;

    @include r(1200) {
      top: 12px;
    }
  }

  &.selected &__label {
    transform: scale(.75) translate(2px, -9px);
  }

  &__selected-label {
    position: relative;
    top: 28px;
  }

  &__box {
    position: relative;
    font-size: 16px;
    width: 400px;
    box-sizing: border-box;
    min-height: 60px;
    border: $e-input-border-size solid #ebedf0;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.03);
    border-radius: 5px;
    background: white;
    color: #0d105b;
    transition: .3s ease;

    &:focus {
      outline: 0;
    }

    @include r(1200) {
      min-height: 50px;
      width: 379px;
    }

    @include r(640) {
      width: 100%;
    }
  }

  &.selecting &__box {
    border: $e-input-border-size solid $color-blue;
    @include r(1200) {
      min-height: 50px;
    }
  }

  &.selected &__box {
    margin-left: 0;
    border: $e-input-border-size solid #1fce43;
  }

  &__selected {
    cursor: pointer;
    height: 60px - 2 * $e-input-border-size;
    display: flex;
    padding: 0 7px;
    border-radius: 5px 5px 0 0;

    @include r(1200) {
      height: 51px - 2 * $e-input-border-size;
    }
  }

  &__content {
    flex: 1;
    margin: 0 7px 0;
  }

  &__option-icon-wrap {
    margin: auto 15px auto 0;
    min-width: 35px;
    width: 35px;
    max-width: 35px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;

    & .e-icon svg {
      top: 50%;
      transform: translateY(-50%);
    }

    @include r(1200) {
      height: 50px;
    }
  }

  &__option-icon {
    fill: #dadada;
    width: 27px;
    height: 27px;
  }

  &.selected &__option-icon {
    fill: #1fce43;
    width: 27px;
    height: 27px;
  }

  &__dropdown-icon {
    display: flex;
    align-items: center;
    fill: #dadada;
    width: 21px;
    padding: 0 7px;
    transition: .3s ease;

    & .e-icon svg {
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &.selecting &__dropdown-icon {
    fill: $color-blue;
  }

  &.expanded &__dropdown-icon {
    transform: rotate(180deg);
  }

  &.selected &__dropdown-icon {
    fill: #1fce43;
  }

  /* dropdown */

  &__options {
    display: none;
    z-index: 1;
    max-height: 4 * (60px - $e-input-border-size);
    overflow-y: auto;
    margin-right: 4px;
    border-radius: 0 0 4px 4px;

    &::-webkit-scrollbar {
      width: 2px;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }

    &.isLedger {
      min-height: 54px;
    }
  }

  &__option {
    display: flex;
    align-items: center;
    height: 50px;
    padding-left: 14px;
    cursor: pointer;
    transition: $transition-time;
  }

  &__options-item {
    transition: $transition-time;
    background-color: transparent;
    border-radius: 3px;

    &:hover {
      background-color: #fcfcfc;

      #{$this}__option {
        color: $color-green-bright;
      }
    }
  }

  &__option.selected {
    color: $color-green-bright;
  }

  &__option-separator {
    height: 2px;
    margin-left: 5px;
    background: #f8f9fc;
    border-radius: 1px;
  }

  &.expanded {
    position: relative;
  }

  &.expanded &__box {
    z-index: 1;
    position: absolute;
    right: 0;
    top: 0;
  }

  &.expanded &__selected {
    background: #f8f9fc;
    height: 60px - 2 * $e-input-border-size;
    padding-bottom: $e-input-border-size;
  }

  &.expanded &__options {
    display: flex;
    flex-direction: column;
  }

  &__pagination {
    padding: 10px 0;
  }

  &__ledger-error {
    font-size: 13px;
    color: $color-red;
    padding-top: 5px;
    margin-bottom: -5px;
  }
}
</style>



