/* eslint-disable no-restricted-syntax */
import EventEmitter from 'eventemitter3';

const BEFORE_CHANGE = 'before';
const AFTER_CHANGE = 'after';

const emitter = new EventEmitter();

const beforeWalletListenerMap = new Map();
const afterWalletListenerMap = new Map();

export const emitBeforeTokenChange = () => {
  emitter.emit(BEFORE_CHANGE);
};

export const emitAfterTokenChange = () => {
  emitter.emit(AFTER_CHANGE);
};

export const beforeTokenChange = (key, callback) => {
  let value = beforeWalletListenerMap.get(key);
  if (!value) {
    value = [];
    beforeWalletListenerMap.set(value);
  }
  value.push(callback);
  emitter.on(BEFORE_CHANGE, callback);
};

export const afterTokenChange = (key, callback) => {
  let value = afterWalletListenerMap.get(key);
  if (!value) {
    value = [];
    afterWalletListenerMap.set(value);
  }
  value.push(callback);
  emitter.on(AFTER_CHANGE, callback);
};

export const removeBeforeTokenListener = (key) => {
  let listeners = beforeWalletListenerMap.get(key);
  if (!listeners) {
    listeners = [];
    beforeWalletListenerMap.set(listeners);
  }
  beforeWalletListenerMap.set(key, []);
  for (const listener of listeners) {
    emitter.removeListener(BEFORE_CHANGE, listener);
  }
};

export const removeAfterTokenListener = (key) => {
  let listeners = afterWalletListenerMap.get(key);
  if (!listeners) {
    listeners = [];
    afterWalletListenerMap.set(listeners);
  }
  afterWalletListenerMap.set(key, []);
  for (const listener of listeners) {
    emitter.removeListener(AFTER_CHANGE, listener);
  }
};



