import * as types from '@/store/mutation-types';
import log from '@/util/logger';
import { getAffiliatesClicks, getTrades, saveAffiliatesClick } from '@/api/backend';
import { getStoredReferrer, persistReferrer, mapTradeForAffiliate } from './helpers';

/**
 * Action: Synchronous
 * Loads referrer from localstorage to vuex. Saves in vuex in case it's valid Ethererum address.
 * Referrer is an address that send affiliate link to current user.
 */
const loadReferrer = ({ commit }) => {
  const storedReferrer = getStoredReferrer();
  commit(types.UPDATE_REFERRER, storedReferrer);
};

/**
 * Action: Synchronous
 * Updates referrer both in localstorage and vuex.
 * Referrer is an address that send affiliate link to current user.
 *
 * @param referrerAddress address that send affiliate link to the user
 */
const updateReferrer = async ({ commit, getters }, referrerAddress) => {
  if (referrerAddress !== getters.referrerAddress) {
    persistReferrer(referrerAddress);

    commit(types.UPDATE_REFERRER, referrerAddress);
  }
};

/*
 * Экшин для получения информации о кликах
 */
const getAffiliateClicksCount = async ({ commit, rootGetters }) => {
  try {
    const clickCount = await getAffiliatesClicks(rootGetters['wallets/activeWalletAddress']);
    if (!isNaN(clickCount)) {
      commit(types.UPDATE_AFFILIATES_CLICKS, parseInt(clickCount, 10));
    } else {
      log.error('Error resiving affiliate clicks', {
        module: 'affiliate',
        layer: 'backend',
      });
    }
  } catch (e) {
    log.error(`Error resiving affiliate clicks: ${e}`, {
      module: 'affiliate',
      layer: 'backend',
    });
  }
};

/**
 * Экшин для сохранения информации о кликах
 */
const affiliateClick = async ({ rootGetters }, referrer) => {
  try {
    await saveAffiliatesClick({
      affiliateAddress: referrer,
      userAddress: rootGetters['wallets/activeWalletAddress'] || undefined,
    });
  } catch (err) {
    log.error(`Error: Affiliates - Save click. Name: ${err.name}. Type: ${err.type}. Message: ${err.message}`, {
      module: 'affiliate',
      layer: 'backend',
    });
  }
};

/**
 * Экшин для получения аффлированных трейдов
 */
const getAffiliateTrades = async ({ commit, rootGetters }) => {
  commit(types.GET_AFFILIATE_TRADES_ASYNC.PENDING);

  const contractAddress = rootGetters['contracts/activeMainContractAddress'];
  if (!contractAddress) {
    log.error('contractAddress must not be null', {
      module: 'affiliate',
      layer: 'store',
    });
    return;
  }

  try {
    const affiliateAddress = rootGetters['wallets/activeWalletAddress'];

    const res = await getTrades({
      filters: {
        affiliate_address: affiliateAddress,
        has_synced_affiliate_fees: true,
        contract_address: contractAddress,
      },
    });
    commit(types.GET_AFFILIATE_TRADES_ASYNC.SUCCESS, res.data.map(mapTradeForAffiliate(affiliateAddress)));
  } catch (error) {
    commit(types.GET_AFFILIATE_TRADES_ASYNC.FAILURE, error);
  }
};

export default {
  loadReferrer,
  updateReferrer,
  getAffiliateClicksCount,
  affiliateClick,
  getAffiliateTrades,
};



