<template>
  <div
    class="the-notifications"
    ref="root"
  >
    <transition-modal
      v-for="(notification, i) of notifications"
      :key="notification.key"
    >
      <component
        ref="notifications"
        v-bind="withoutType(notification)"
        :vertical-offset="offsets[i]"
        :is="notification.type || 'ENotification'"
        @close="remove(notification)"
        @auto-close="remove(notification)"
      />
    </transition-modal>
  </div>
</template>

<script>
/* eslint-disable no-restricted-syntax */
import { mapState, mapMutations } from 'vuex';
import { notificationComponents } from '@/util/notifications';

import ENotification from '@/components/ENotification';

import TransitionModal from '@/containers/transitions/TransitionModal';

const START_MARGIN = 10;
const DEFAULT_MARGIN = 7;

export default {
  name: 'the-notifications',

  data: () => ({ offsets: [] }),

  computed: {
    ...mapState({
      notifications: state => state.ui.notifications,
    }),
  },

  watch: { notifications: 'updateOffset' },

  async mounted() {
    await this.$nextTick();
    this.updateOffset();
  },

  methods: {
    ...mapMutations({ remove: 'ui/REMOVE_NOTIFICATION' }),

    updateOffset() {
      let offsets;
      if (this.notifications.length) {
        let curOffset = START_MARGIN + START_MARGIN;
        offsets = [curOffset];
        for (const notification of this.$refs.notifications || []) {
          curOffset += DEFAULT_MARGIN + notification.$el.offsetHeight;
          offsets.push(curOffset);
        }
      }
      this.offsets = offsets || [];
    },

    withoutType: ({ type, ...rest }) => rest,
  },

  components: {
    TransitionModal,
    ENotification,
    ...notificationComponents,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-notifications {
  position: fixed;
  z-index: 20020;
  right: 0;
  width: 0;
  height: 0;
  bottom: 0;
}
</style>



