/* eslint-disable no-restricted-syntax */
import last from 'lodash/last';
import { ZERO } from '@/util/numbers';
import { BigNumber } from 'bignumber.js';
import { ORDERS_IN_ORDERBOOK } from '@/config';

/**
 * Used to group orders by price
 * @param {Array} orderBookElements Order book elements where 0:qty, 1:price
 * @return {Array}
 */
export const compressOrderBookElements = (orderBookElements) => {
  if (!orderBookElements.length) return [];
  const compressed = [
    [ZERO, orderBookElements[0][1]],
  ];
  for (const [curAmount, curPrice, isMy] of orderBookElements) {
    const [prevAmount, prevPrice] = last(compressed);
    if (curPrice.eq(prevPrice)) {
      compressed.pop();
      compressed.push([curAmount.plus(prevAmount), curPrice, isMy]);
    } else {
      compressed.push([curAmount, curPrice, isMy]);
    }
  }
  return compressed;
};

export const mapOrderInOrderBook = order => ([
  BigNumber(order[0]),
  BigNumber(order[1]),
  order[2],
]);

export const transformOrderBookElements = (items, side) => {
  const res = [];

  // items = compressOrderBookElements(items);

  // If number of items is more than needed, delete them
  if (items.length > ORDERS_IN_ORDERBOOK) {
    /* eslint-disable-next-line */
    items = items.slice(0, ORDERS_IN_ORDERBOOK);
  }

  const averageSize = items.reduce((acc, current) => acc.plus(current[0]), BigNumber(0)).dividedBy(items.length);

  let volumeSum = BigNumber(0);
  for (let i = 0; i < items.length; i += 1) {
    const size = items[i][0];
    const price = items[i][1];
    const isMy = items[i][2];
    const volume = size.times(price);
    const proportion = size.dividedBy(averageSize).toNumber();

    volumeSum = volumeSum.plus(volume);

    // true = long
    const itemObj = {
      size,
      price,
      proportion,
      volumeSum,
      isMy,
    };

    if (side) {
      res.push(itemObj);
    } else {
      res.unshift(itemObj);
    }
  }

  return res;
};



