import { ASYNC_STATES } from '@/util/constants';

const state = {
  latestTrades: [],

  // latestTradesIsListened: false,

  latestTradesListenersCount: 0,

  /**
   * Флаг устанавливается в значение true когда трейды загружаются, и в false когда они загружены
   */
  latestTradesLoading: false,

  /**
   * Указывает загружено ли значение volumes
   */
  volumesInitState: ASYNC_STATES.INITIAL,

  /**
   * Total traded ETH volumes in exchange
   * @type {BigNumber|null}
   */
  volumeTotal: null,

  tradeVolumesListenersCount: false,

  selectedTrade: null,

  /**
   * Флаг указывающй, какие треды следует показывать все (false) или только внутренние (true)
   * @type {boolean}
   */
  showOnlyInternalTrades: false,


  /**
   * Буфер для трейдов
   */
  tradesBuffer: [],
};

export default state;



