<template>
  <div
    v-if="displayed"
    class="e-modal"
    :class="{
      'e-modal_secondary': secondaryDesign,
      'e-modal_wide': wideDesign,
      'e-modal_auto-height': autoHeight,
      'e-modal_dark' : darkBackground,
    }"
  >
    <slot name="logo"/>
    <div class="e-modal__body" :class="[ `e-modal__theme-${theme}` ]">
      <div class="e-modal__inner">
        <slot name="inner"/>
      </div>
      <div class="e-modal__close" @click="close" v-if="btnClose"><e-icon :src="iconClose"/></div>
    </div>
    <div
      v-if="!noBackground"
      class="e-modal__cover"
      :class="[ `e-modal__cover-${theme}` ]"
      @click="closeBackgroundClick"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import iconClose from '@/icons/e-icon_close.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-modal',

  props: {
    level: {
      type: Number,
      default: 0,
    },

    secondaryDesign: {
      type: Boolean,
      default: false,
    },

    wideDesign: {
      type: Boolean,
      default: false,
    },

    autoHeight: {
      type: Boolean,
      default: false,
    },
    btnClose: {
      type: Boolean,
      default: true,
    },

    darkBackground: {
      type: Boolean,
      default: false,
    },

    noBackground: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    displayed() {
      return this.level === this.$store.getters['ui/topModalLevel'];
    },

    ...mapGetters({
      orderConfirmationModal: 'futuresActiveOrder/orderConfirmation',
      theme: 'ui/getTheme',
    }),
  },

  created() {
    Object.assign(this, { iconClose });
    this.$store.commit('ui/SHOW_MODAL', this.level);
  },

  beforeDestroy() {
    this.$store.commit('ui/HIDE_MODAL', this.level);
  },

  methods: {
    close() {
      this.$emit('close');
    },

    closeBackgroundClick() {
      if (!this.orderConfirmationModal) {
        this.$emit('close');
      }
    },
  },

  components: { EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-modal {
  $this: &;

  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1001;
  display: flex;
  justify-content: center;
  align-items: center;

  &_secondary {
    #{$this}__body {
      width: 369px;
    }

    #{$this}__close {
      top: -5px;
      right: -5px;
      height: 10px;
      width: 10px;

      .e-icon svg {
        fill: #fff;
      }

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #545575;
        border-radius: 50%;
        transform: scale(3);
      }
    }
  }

  &_wide {
    #{$this}__close {
      display: none;
    }

    #{$this}__body {
      width: 100%;
      max-width: 540px;

      /*@include r(580) {
        #{$this}__close {
          position: absolute;
          top: 19px;
          right: 18px;
          width: 12px;
          height: 16px;

          &:before {
            display: none;
          }
        }
      }*/
    }
  }

  &_auto-height {
    #{$this}__body {
      min-height: auto;
    }
  }

  &_dark {
    #{$this}__cover {
      background-color: rgba(#595959, 0.76);
    }
  }

  &__body {
    position: relative;
    width: 390px;
    min-height: 360px;
    box-sizing: border-box;
    z-index: 1;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 3px 10px rgba(#94A5B4, .14);
  }

  &__inner {
    background-color: #FFF;
    border-radius: 3px;
    overflow: hidden;
  }

  &__cover {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(#0D105B, .46);
  }

  &__close {
    position: absolute;
    top: 19px;
    right: 18px;
    width: 12px;
    height: 16px;
    cursor: pointer;

    .e-icon svg {
      fill: #8889AE;
    }
  }

  &__theme-dark {
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.14);

    #{$this}__inner {
      background: $dark-background;
    }

    #{$this}__close .e-icon svg {
      fill: #fff;
    }

    .the-delete-order-popup__title {
      color: #9a9ea7;

      &::before {
        background-color: #9a9ea7;
      }
    }

    .e-receipt__header {
      background: $dark-background-lighter;
      color: #FFFFFF;
    }

    .e-receipt-item_num .e-receipt-item__token-name {
      color: #9a9ea7;
    }

    .e-receipt__body-wrapper {
      background: $dark-background;
      color: #9a9ea7;
    }

    .e-receipt-item__icon .e-icon svg g path:nth-child(2) {
      fill: $dark-background;
    }

    .e-big-number,
    .e-receipt-item_num .e-big-number {
      color: #D9D9D9;
    }

    .e-receipt-item_num .e-big-number__leading-zeros {
      color: #D9D9D9;
    }

    .e-receipt-item_sell .e-big-number__leading-zeros {
      color: rgba(218, 72, 48, 0.75);
    }

    .e-receipt-item_buy .e-big-number__leading-zeros {
      color: rgba(31, 206, 67, 0.75);
    }

    .e-receipt__link.e-receipt__pin {
      background: $color-blue-dark-mode;
      color: #fff;

      .e-receipt__link-icon .e-icon svg {
        fill: #fff;
      }
    }

    .e-receipt__btn.e-receipt__btn_cancel.e-receipt__pin {
      background: $dark-background-lighter;
      color: #9a9ea7;

      &:hover {
        color: #fff;
      }
    }

    .e-receipt__btn.e-receipt__btn_confirm.e-receipt__pin {
      color: $dark-background-lighter;
    }

    .e-popup-info__details,
    .e-popup-info__message {
      color: $color-blue-dark-mode;
    }
  }

  .e-modal__cover-dark {
    background: rgba(0,0,0, .65);
  }
}
</style>



