import { Accounts } from '@/api/ethereum';
import { APP_ETH_NETWORK_ID } from '@/config';
import { LEDGER_WALLET_TYPE, PRIVATE_KEY_TYPE } from '@/util/constants';

/**
 * Adapts few common web3 calls for simple access from app.
 * Includes account management and network selection.
 * @property web3
 */
export default class ProviderAdapter {
  /**
   *
   * @param web3
   */
  constructor({ web3 = null } = {}) {
    this.web3 = web3;
  }

  /**
   * Проверяет выбранную пользователем сеть в Metamask или его аналоге.
   * Если пользователь не залогинен, то возвращает false.
   *
   * @returns {boolean|Promise<boolean>}
   */
  isCorrectNetworkSelected() {
    return !!(Accounts.isEthereumCompatibleBrowser && this.web3) &&
      this.web3.eth.net.getId().then(n => n === APP_ETH_NETWORK_ID);
  }

  /**
   * Этот метод необхидимо вызвать после переключения на другой кошелёк, для того чтобы обновить данные по кошелькам,
   * которые храняться в web3.
   *
   * @param address {string}
   * @param type {string}
   * @param privateKey {string|null|undefined} только для кошельков с private key
   * @param derivationPath {string|null|undefined} только для кошельков с ledger
   * @returns {void}
   */
  updateActiveWallet({ address, type, privateKey, derivationPath }) {
    if (!this.web3) return;
    const web3Wallets = this.web3.eth.accounts.wallet;
    web3Wallets.clear();
    if (type === PRIVATE_KEY_TYPE && !web3Wallets[address]) {
      web3Wallets.add(privateKey);
    }
    if (type === LEDGER_WALLET_TYPE) {
      // eslint-disable-next-line no-underscore-dangle,no-restricted-syntax
      for (const subprovider of this.web3.currentProvider._providers) {
        if (subprovider.setGetAddressToPathMap) {
          subprovider.setGetAddressToPathMap(() => ({
            [address.toLowerCase()]: derivationPath,
          }));
        }
      }
    }
    this.web3.eth.defaultAccount = address;
  }

  /**
   * Этот метод необходимо вызывать после переключения в режим без кошелька,
   * для того чтобы очистить данные по privateKey кошелькам внутри web3.
   *
   * @returns {void}
   */
  clearActiveWallet() {
    if (!this.web3) return;
    this.web3.eth.accounts.wallet.clear();
    this.web3.eth.defaultAccount = undefined;
  }
}



