const state = {
  /**
   * Ордера покупки
   * Sorted by [1] (aka 'unitPrice') ascending (smallest first, biggest last)
   */
  asks: [],

  /**
   * Ордера продажи
   * Sorted by [1] (aka 'unitPrice') descending (biggest first, smallest last)
   */
  bids: [],

  /**
   * Флаг, показывающий загружается ли в текущий момент ордербук
   */
  orderBookLoading: false,
};

export default state;



