import arrayEqualsShallow from 'shallow-equal/arrays';

const walletToAddress = w => w.address;

export default (wallets1, wallets2) => {
  const addresses1 = wallets1.map(walletToAddress);
  const addresses2 = wallets2.map(walletToAddress);
  return arrayEqualsShallow(addresses1, addresses2);
};



