import { CREATE_ORDER_SUCCESS } from '@/store/constants';
import { BUY_TOKEN_ORDER, ORDER_TYPE_MARKET } from '@/util/constants';
import { BigNumber } from 'bignumber.js';

const state = {
  orderType: ORDER_TYPE_MARKET,

  buyOrSell: BUY_TOKEN_ORDER,

  orderParams: {
    amount: '',
  },

  status: CREATE_ORDER_SUCCESS,

  orderConfirmation: null,

  orderMinEthValue: BigNumber(NaN), // NaN while not init

  minOrderError: false,

  ethUsdPrice: 0,
};

export default state;



