const state = {
  /**
   * Массив, в котором хранится списк доступных контрактов
   */
  exchangeContracts: [],
  futuresContracts: [],
  dmexOracleContracts: [],

  /**
   * Флаг, который true если загрузка Exchange Contracts провалилась
   */
  exchangeContractsFailure: false,
  futuresContractsFailure: false,
  dmexOracleContractsFailure: false,

  /**
   * Флаг, который показывает загружается в текущий момент спосок контрактов или нет
   */
  contractsLoading: false,

  contractsLoaded: false,

  /**
   * Флаг: true если активный контракт биржи загружается
   */
  activeExchangeContractLoading: false,
  activeFuturesContractLoading: false,
  activeDmexOracleContractLoading: false,


  /**
   * Флаг: true если активный контракт биржи загружен успешно
   */
  activeExchangeContractLoaded: false,
  activeFuturesContractLoaded: false,
  activeDmexOracleContractLoaded: false,

  /**
   * Активный главный контракт
   */
  activeMainContract: null,
  activeFuturesContract: null,
  activeDmexOracleContract: null,

  /**
   * Флаг, который показывает идет ли в данный момент подписка на контракт
   */
  contractSubscribing: false,

  affiliateContract: null,
};

export default state;



