<script>
import VueRangeSlider from 'vue-range-slider';

export default {
  name: 'e-range-slider',

  extends: VueRangeSlider,
  mounted() {
    const dragHelper = this.$children[0];
    dragHelper.$on('dragstart', () => {
      this.$emit('dragstart');
    });
    dragHelper.$on('dragend', () => {
      this.$emit('dragend');
    });
    // dragHelper.$on('drag', () => {
    //   this.$emit('drag');
    // });
  },
};
</script>

<style lang="scss">
@import '~vue-range-slider/dist/vue-range-slider.scss';
</style>



