/**
 * @module store/orders/actions
 *
 * @version 1.0.5
 */

import { BigNumber } from 'bignumber.js';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import log from '@/util/logger';
import * as types from '@/store/mutation-types';
import {
  getTrades, onReconnect, removeReconnectListener,
  getOrderHistoryPage, getActiveOrdersPage, cancelOrder as sendCancelOrder, onOrderChange, removeOrderListener,
  ACTION_CREATE_ORDER, ACTION_CANCEL_ORDER, ACTION_INTERNAL_TRADE, ACTION_TRADE_CONFIRMATION,
} from '@/api/backend';
import { MY_ORDERS_MODE_ACTIVE } from '@/store/constants';
import { BUY_TOKEN_ORDER, METAMASK_WALLET_TYPE, ETH_ADDRESS, LEDGER_WALLET_TYPE } from '@/util/constants';
import { ERROR_METAMASK_DENY_MESSAGE, ERROR_LEDGER_DENY } from '@/util/errorMessages';
import { mapTrade } from '@/store/modules/trades/helpers';
import { mapOrder } from './helpers';

let ordersReconnectHandler;

/**
 * Action: Загрузка страницы моих ордеров
 * Should be called only after activeToken is set
 *
 * @param {number} pageNumber - номер запрашиваемой страницы
 * @param {number} itemsPerPage - количество элементов на странице
 *
 * @since Version 1.0.3
 */
const loadMyOrdersPage = async ({ state, commit, rootGetters, dispatch }) => {
  await Promise.all([
    dispatch('contracts/subscribeToContract', undefined, { root: true }), // ensures active contract loaded
    dispatch('tokens/subscribeToToken', undefined, { root: true }),
    dispatch('wallets/subscribeToAccount', undefined, { root: true }), // ensures wallets loaded
    dispatch('updateAppConfig', undefined, { root: true }), // for tradeTakerGasLimit
  ]);
  commit(types.GET_MY_ORDERS_PAGE_ASYNC.PENDING);
  const userAddress = rootGetters['wallets/activeWalletAddress'];
  const activeToken = rootGetters['tokens/activeToken'];
  const contractAddress = rootGetters['contracts/activeMainContractAddress'];
  if (userAddress == null) {
    commit(types.GET_MY_ORDERS_PAGE_ASYNC.SUCCESS, {
      orders: [],
      total: 0,
    });
  } else {
    const getOrders = state.myOrdersMode === 0
      ? getActiveOrdersPage
      : getOrderHistoryPage;
    let orders;
    let total;
    try {
      const response = await getOrders({
        userAddress,
        contractAddress,
        pageNumber: state.activePageNumber,
      });
      orders = response.data.map(item => mapOrder(item, { activeToken }));
      total = response.total;

      // // Проверка не уменьшьлось ли количество страниц, а существует ли страница с текущим номером
      // if (state.activePageNumber !== 0 && data.length === 0) {
      //   // Повторный запрос последней из существующих страниц
      //   // TODO: Нужно релазовать юзкейс, когда во время вотрого запроса перестала и эта страница сущесовавать в этом случае нужно запрашивать первую страницу
      //   commit('SET_MY_ORDERS_PAGE_NUMBER', Math.ceil(total / state.ordersPerPage) - 1);
      //   commit('SET_TOTAL_NUMBER_OF_ORDERS', total);
      //   ({ data, total } = await getOrders({
      //     filters,
      //     limit: state.ordersPerPage,
      //     offset: state.activePageNumber * state.ordersPerPage,
      //   }));
      // }
    } catch (error) {
      if (rootGetters.isConnected) {
        commit(types.GET_MY_ORDERS_PAGE_ASYNC.FAILURE, error);
      }
    }

    if (orders) {
      commit(types.GET_MY_ORDERS_PAGE_ASYNC.SUCCESS, {
        orders,
        total,
      });
    }
  }
};

/**
 * Action: Synchronous
 * Производит подписку на обновление информации по ордерам
 * Should be called only after activeToken is set
 *
 * @todo ensure gas price loaded
 * @since Version 1.0.1
 */
const listenToOrdersChanges = ({ commit, dispatch, rootGetters }) => {
  const orderEventHandler = async (data) => {
    // Футкция формирующая строку информации об операции
    const transactionToString = (amount, unitPrice) => (
      `${
        amount.shiftedBy(-rootGetters['tokens/activeToken'].decimals).dp(3, BigNumber.ROUND_UP).toFixed()
      } ${
        rootGetters['tokens/activeToken'].shortName
      } @ ${
        unitPrice.dp(9, BigNumber.ROUND_UP).toFixed()
      } ETH`
    );

    // Функция для поиска трейда по айди и формрования строки с информацией по нему
    const infoForTrade = async (id) => {
      let trade = rootGetters['trades/latestTrades'].find(item => item.id === id);
      if (!trade) {
        const contractAddress = rootGetters['contracts/activeMainContractAddress'];
        if (!contractAddress) {
          log.error('contractAddress must not be null', {
            module: 'orders',
            layer: 'store',
          });
        }

        try {
          const fetchedTrade = await getTrades({
            filters: {
              token_address: rootGetters['tokens/activeTokenContract'],
              id,
              contract_address: contractAddress,
            },
          });
          if (fetchedTrade.length === 1) {
            trade = mapTrade(fetchedTrade[0], rootGetters['wallets/activeWalletAddress'], rootGetters['tokens/activeToken'].decimals);
          }
        } catch (e) {
          // backend trade unavailable
          if (rootGetters.isConnected) {
            log.error(`backend trades unavailable: ${e}`, {
              module: 'orders',
              layer: 'backend',
            });
          }
        }
      }

      return trade
        ? `${transactionToString(
          trade.type === BUY_TOKEN_ORDER ? trade.takerAmountFilled : trade.makerAmountFilled,
          trade.unitPrice,
        )}`
        : '';
    };

    await Promise.all([
      dispatch('contracts/loadActiveExchangeContract', undefined, { root: true }),
      dispatch('updateAppConfig', undefined, { root: true }), // for tradeTakerGasLimit
    ]);

    const newOrder = mapOrder(data, { activeToken: rootGetters['tokens/activeToken'] });
    const event = data.event;

    if (newOrder.userAddress === rootGetters['wallets/activeWalletAddress']) {
      dispatch('updateMyOrdersPage', {
        order: newOrder,
        action: event.action,
      });

      // async balance update
      const balanceApi = rootGetters['blockchain/balanceApi'];
      balanceApi
        .getReserved({ tokenAddress: newOrder.type === BUY_TOKEN_ORDER ? ETH_ADDRESS : newOrder.tokenAddress })
        .then((newBalance) => {
          commit('balance/UPDATE_TRADABLE_BALANCE', newBalance, { root: true });
        })
        .catch((error) => {
          log.warn(`Error on reserved balance update after order event: ${error}`);
        });

      // Notification
      switch (event.action) {
        // Нотификация в случае отмены ордера
        case ACTION_CANCEL_ORDER:
          commit('ui/ADD_NOTIFICATION', {
            status: 'success',
            headline: 'Order was canceled',
            message: `Order for ${data.is_buy ? 'buy' : 'sell'} ${transactionToString(newOrder.amount, newOrder.unitPrice)} was canceled.`,
          }, { root: true });
          break;

        // Нотификация в случае подтверждения трейда блокчейном
        case ACTION_TRADE_CONFIRMATION: {
          infoForTrade(event.data.trade_id).then((tradeMessage) => {
            commit('ui/ADD_NOTIFICATION', {
              status: 'success',
              headline: 'Trade confirmation',
              message: `${data.is_buy ? 'Bought' : 'Sold'}  ${tradeMessage}`,
            }, { root: true });
          });
          break;
        }
        default: // nothing
      }
    }
  };
  ordersReconnectHandler = () => {
    dispatch('loadMyOrdersPage');
  };
  onOrderChange(orderEventHandler);
  onReconnect(ordersReconnectHandler);
  onResume(ordersReconnectHandler);
};

const stopListeningToOrdersChanges = () => {
  removeOrderListener();
  removeReconnectListener(ordersReconnectHandler);
  removeResumeListener(ordersReconnectHandler);
};

/**
 * Action: Отмена ордера
 *
 * @since Version 1.0.1
 */
const submitCancelOrder = async ({ commit, state, rootGetters }) => {
  const { address: userAddress, type: walletType } = rootGetters['wallets/activeWallet'];
  const { orderHash, orderType } = state.cancelledOrder;
  const balanceApi = rootGetters['blockchain/balanceApi'];
  const tokenAddress = rootGetters['tokens/activeTokenContract'];
  const contractAddress = rootGetters['contracts/activeMainContractAddress'];
  const signService = rootGetters['blockchain/signService'];
  const isMetamask = walletType === METAMASK_WALLET_TYPE;
  const isLedger = walletType === LEDGER_WALLET_TYPE;

  let signature;
  commit(types.SUBMIT_CANCEL_ORDER);
  if (isMetamask) {
    commit('ui/SHOW_METAMASK_SIGN', undefined, { root: true });
  } else if (isLedger) {
    commit('ui/SHOW_LEDGER_SIGN', undefined, { root: true });
  }
  try {
    signature = await signService.signCancelOrder({ orderHash });
  } catch (e) {
    if (
      (isMetamask && e.message.includes(ERROR_METAMASK_DENY_MESSAGE)) ||
      (isLedger && e.message.includes(ERROR_LEDGER_DENY))
    ) {
      // User manually cancelled transaction in Metamask or Ledger
    } else {
      commit('ui/ADD_NOTIFICATION', {
        status: 'error',
        headline: 'Order Cancellation Error',
        message: e.message,
      }, { root: true });
      log.error(`Cancel Order Sign error: ${e.message}`, {
        module: 'orders',
        layer: 'sign',
      });
    }
  } finally {
    if (isMetamask) {
      commit('ui/HIDE_METAMASK_SIGN', undefined, { root: true });
    } else if (isLedger) {
      commit('ui/HIDE_LEDGER_SIGN', undefined, { root: true });
    }
  }

  if (signature) {
    const cancelOrderPromise = sendCancelOrder({ contractAddress, orderHash, userAddress, ...signature });

    try {
      await cancelOrderPromise;
    } catch (e) {
      commit('ui/ADD_NOTIFICATION', {
        status: 'error',
        headline: 'Order Cancellation Error',
        message: e.message,
      }, { root: true });
      log.error(`Error: Cancel order. Name: ${e.name}. Type: ${e.type}. Message: ${e.message}`, {
        module: 'orders',
        layer: 'backend',
      });
    }

    await balanceApi
      .getReserved({ tokenAddress: orderType === BUY_TOKEN_ORDER ? ETH_ADDRESS : tokenAddress })
      .then((newBalance) => {
        commit('balance/UPDATE_TRADABLE_BALANCE', newBalance, { root: true });
      })
      .catch((e) => {
        log.warn(`Reserved balance update error after cancel order: ${e.message}`, {
          module: 'orders',
          layer: 'store',
        });
      });
  }
};


/**
 * Action: Обработка получения информациио новом моем ордере.
 * После получения ивента о новом ордере, нужно проверить нужно ли нам по новой запрашивать информацию о моих ордерах для отборажения текущей странциы страницы
 */
const updateMyOrdersPage = async ({ state, dispatch }, { action }) => {
  // Случай создания нового ордера
  if (action === ACTION_CREATE_ORDER) {
    if (state.myOrdersMode === MY_ORDERS_MODE_ACTIVE) {
      // Если отображатся активные ордера
      dispatch('loadMyOrdersPage');
    }
  }

  // Случай отмены ордера
  if (action === ACTION_CANCEL_ORDER) {
    dispatch('loadMyOrdersPage');
  }

  // Случай создания внутреннего трейда
  if (action === ACTION_INTERNAL_TRADE) {
    dispatch('loadMyOrdersPage');
  }

  // Случай подтверждения трейда
  if (action === ACTION_TRADE_CONFIRMATION) {
    dispatch('loadMyOrdersPage');
  }
};


/**
 * Экшин обновляющий мои ордера при переключении кошелька
 *
 */
const initMyOrders = async ({ commit, dispatch }) => {
  commit('SET_MY_ORDERS_MODE', MY_ORDERS_MODE_ACTIVE);
  commit('SET_MY_ORDERS_PAGE_NUMBER', 0);
  commit('SET_TOTAL_NUMBER_OF_ORDERS', 0);
  dispatch('loadMyOrdersPage');
};


export default {
  initMyOrders,
  loadMyOrdersPage,
  submitCancelOrder,
  listenToOrdersChanges,
  stopListeningToOrdersChanges,
  updateMyOrdersPage,
};



