import { BigNumber } from 'bignumber.js';

/**
 * 3 * 10 ** 18 wei -> 3 Ether
 *
 * @param amount (i.e. wei)
 * @param decimals (i.e. 18 for Ether)
 * @returns {string}
 */
export default (amount, decimals) => BigNumber(amount).shiftedBy(-decimals).toFixed();



