import { userAndContractScoped } from '@/util/scopes';
import { BalanceBlockchain, Signatures } from '@/api/ethereum';
import BalanceMixedConstructor from './BalanceMixed';
import SignaturesServiceConstructor from './SignaturesService';

const BalanceMixedScoped = userAndContractScoped(
  BalanceMixedConstructor,
  ({ user, exchangeContract, numberOfConfirmations }) => new BalanceMixedConstructor({
    balanceBlockchain: BalanceBlockchain({ user, exchangeContract, numberOfConfirmations }),
    exchangeContractAddress: exchangeContract.contractAddress,
    userAddress: user.address,
  }),
);

const SignaturesServiceScoped = userAndContractScoped(
  SignaturesServiceConstructor,
  ({ user, exchangeContract, futuresContract, isMetamaskLedger }) => new SignaturesServiceConstructor({
    signatures: Signatures({ user, exchangeContract, futuresContract, isMetamaskLedger }),
  }),
);

export {
  BalanceMixedScoped as BalanceMixed,
  SignaturesServiceScoped as SignaturesService,
};



