/**
 * Selects subset of own properties from the object
 *
 * @param {Object} object from which properties are taken
 * @param {String[]} props array of properties
 * @returns {*}
 */
export default (object, props) => props.reduce((acc, prop) => {
  if (object && Object.prototype.hasOwnProperty.call(object, prop)) {
    acc[prop] = object[prop];
  }
  return acc;
}, {});



