// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill';
import Vue from 'vue';
import VueClipboard from 'vue-clipboard2';
import vClickOutside from 'v-click-outside';
import VueAnalytics from 'vue-analytics';
import VueScrollTo from 'vue-scrollto';
import VueCarousel from 'vue-carousel';
import VueYouTubeEmbed from 'vue-youtube-embed';

// import Raven from 'raven-js';
// import RavenVue from 'raven-js/plugins/vue';
import { BigNumber } from 'bignumber.js';

import { APP_VERSION, APP_ENV } from './config';
import { EConstantsPlugin, EUtilsPlugin } from './util/plugins';
import App from './App';
import router from './router';
import store from './store';

BigNumber.config({ DECIMAL_PLACES: 19 });

// Инициализация библиотеки централизованного хранения логов
// if (process.env.NODE_ENV === 'production') {
//   Raven
//     .config('https://7cf1c65036e74bdea5f7f6e23791f6f8@sentry.io/1479578', {
//       release: `${APP_VERSION}(${APP_ENV})`,
//     })
//     .addPlugin(RavenVue, Vue)
//     .install();
// }

Vue
  .use(vClickOutside)
  .use(VueClipboard)
  .use(EConstantsPlugin)
  .use(EUtilsPlugin)
  .use(VueScrollTo, { duration: 800 })
  .use(VueCarousel)
  .use(VueAnalytics, {
    id: 'UA-118596258-2',
  })
  .use(VueYouTubeEmbed);

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,

  created() {
    this.$store.dispatch('initStore');
  },

  mounted() {
    // eslint-disable-next-line no-console
    console.log(`DMEX.app v${APP_VERSION}(${APP_ENV})`);
  },

  render() {
    return <App/>;
  },
});



