/**
 * @module store/contracts/blockchain
 *
 * @version 1.2.9
 */

import { METAMASK_WALLET_TYPE } from '@/util/constants';
import log from '@/util/logger';
import * as types from '@/store/mutation-types';
import { getGasPrice, onGasPrice, removeGasPriceListener, onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';

/**
 * Action: checks selected network type in Metamask or analog.
 *
 * @since 1.2.9
 */
const checkNetworkType = async ({ state, getters, rootGetters, commit }) => {
  if (rootGetters['wallets/activeWalletType'] === METAMASK_WALLET_TYPE) {
    if (!state.networkChecked) {
      commit(types.CHECK_ETH_NETWORK.PENDING);
      try {
        commit(types.CHECK_ETH_NETWORK.SUCCESS, await getters.accountsService.isCorrectNetworkSelected());
      } catch (e) {
        commit(types.CHECK_ETH_NETWORK.FAILURE);
        log.error(e.message);
      }
    } else {
      commit('ui/RESET_METAMASK_NETWORK_POPUP', undefined, { root: true });
    }
  }
};

/**
 * Action: Fetches Gas Price from backend
 *
 * @since 1.2.9
 */
const fetchGasPrice = async ({ rootState, commit, dispatch }) => {
  await dispatch('updateAppConfig', undefined, { root: true });
  const prices = await getGasPrice({ gasPriceType: rootState.gasPriceType });
  commit(types.GAS_PRICE_UPDATE, prices);
};

/**
 * Action: Subscribe on Gas Price changes from backend
 *
 * @since 1.2.9
 */

let gasReconnectHandler;

const listenToGasPriceChanges = async ({ state, rootState, commit, dispatch }) => {
  commit(types.GAS_PRICE_ADD_LISTENER);

  if (state.gasPriceListenersCount === 1) {
    await dispatch('fetchGasPrice');
    onGasPrice({
      getParams: async () => {
        await dispatch('updateAppConfig', undefined, { root: true });
        return {
          gasPriceType: rootState.gasPriceType,
        };
      },
      handler: (gasPrice) => {
        commit(types.GAS_PRICE_UPDATE, gasPrice);
      },
    });

    gasReconnectHandler = () => {
      dispatch('fetchGasPrice');
    };

    onReconnect(gasReconnectHandler);
    onResume(gasReconnectHandler);
  }
};

const stopListeningToGasPriceChanges = ({ state, commit }) => {
  commit(types.GAS_PRICE_REMOVE_LISTENER);
  if (state.gasPriceListenersCount === 0) {
    removeGasPriceListener();

    if (gasReconnectHandler) {
      removeReconnectListener(gasReconnectHandler);
      removeResumeListener(gasReconnectHandler);
    }
  }
};

export default {
  checkNetworkType,
  fetchGasPrice,
  listenToGasPriceChanges,
  stopListeningToGasPriceChanges,
};



