<script>
import { BigNumber } from 'bignumber.js';
import findLastIndex from 'lodash/findLastIndex';

const NON_ZERO_DIGITS = [1, 2, 3, 4, 5, 6, 7, 8, 9].map(d => `${d}`);

const firstNonZeroIndex = (str) => {
  const index = str.split('').findIndex(c => NON_ZERO_DIGITS.includes(c));
  return index !== -1 ? index : str.length;
};

const firstTrailingZeroIndex = str => 1 + findLastIndex(str.split(''), c => NON_ZERO_DIGITS.includes(c));

const getDecimalPlaces = ({ decimalPlaces, minDecimalPlaces, maxDecimalPlaces }, minPrecision, maxPrecision) => {
  if (decimalPlaces !== undefined) return decimalPlaces;
  if (minDecimalPlaces != null || maxDecimalPlaces != null) return undefined;
  // default cutting mode
  if (maxPrecision === undefined && minPrecision === undefined) return 3;
  return undefined;
};

const normalize = (bigNumber, decimals, maxPrecision, maxDecimalPlaces, roundingMode) => {
  const nonRounded = bigNumber.shiftedBy(-decimals);

  // precision-based rounding
  if (maxPrecision != null) {
    return nonRounded.sd(maxPrecision, roundingMode);
  }

  // decimal places based rounding
  if (maxDecimalPlaces != null) {
    return nonRounded.dp(maxDecimalPlaces, roundingMode);
  }

  // no rounding
  return nonRounded;
};

const getSignStr = (bigNumber, signRequired) => {
  const negative = bigNumber.s < 0; // return "-" for -0
  switch (signRequired) {
    case true: return negative ? '-' : '+';
    case false: return '';
    default: return negative ? '-' : '';
  }
};

const getFractionalStr = (intPart, fractionalPart, minPrecision, minDecimalPlaces) => {
  let expectedDp = fractionalPart.dp();

  // extending to match precision
  if (minPrecision != null) {
    expectedDp = minPrecision - intPart.sd(true);
  }

  // extending to match decimal places
  if (minDecimalPlaces != null) {
    expectedDp = minDecimalPlaces;
  }

  return fractionalPart.toFormat().substring(2).padEnd(expectedDp, '0');
};

const propsToNumber = (props) => {
  const minPrecision = props.minPrecision !== undefined ? props.minPrecision : props.precision;
  const maxPrecision = props.maxPrecision !== undefined ? props.maxPrecision : props.precision;
  const decimalPlaces = getDecimalPlaces(props, minPrecision, maxPrecision);
  const minDecimalPlaces = props.minDecimalPlaces !== undefined ? props.minDecimalPlaces : decimalPlaces;
  const maxDecimalPlaces = props.maxDecimalPlaces !== undefined ? props.maxDecimalPlaces : decimalPlaces;
  const bigNumber = BigNumber(props.value);
  const normalized = normalize(bigNumber, props.decimals, maxPrecision, maxDecimalPlaces, props.roundingMode);
  const normalizedAbs = normalized.abs();
  const intPart = normalizedAbs.integerValue(BigNumber.ROUND_DOWN);
  const fractionalPart = normalizedAbs.minus(intPart);
  const fractionalStr = getFractionalStr(intPart, fractionalPart, minPrecision, minDecimalPlaces);
  return {
    positive: normalized.gt(0),
    isZero: normalized.isZero(),
    negative: normalized.lt(0),
    parts: [
      getSignStr(normalized, props.sign),
      intPart.toFormat(),
      fractionalStr && '.',
      fractionalStr,
    ],
  };
};

const renderWithoutZeroClasses = (h, sign, intPart, decimalPoint, fractionalPart) => [
  sign && <span class="e-big-number__sign">{sign}</span>,
  intPart && <span class="e-big-number__int">{intPart}</span>,
  decimalPoint && <span class="e-big-number__separator">{decimalPoint}</span>,
  fractionalPart && <span class="e-big-number__fraction">{fractionalPart}</span>,
];

const renderWithLeadingAndTrailingZeros = (h, sign, intPart, decimalPoint, fractionalPart) => {
  const intLeadingZeros = intPart === '0' ? intPart : '';
  const fractionalLeadingZeros = intLeadingZeros.length === intPart.length
    ? fractionalPart.slice(0, firstNonZeroIndex(fractionalPart))
    : '';
  const fractionalTrailingZeros = fractionalPart.slice(firstTrailingZeroIndex(fractionalPart), fractionalPart.length);
  const fractionalTrailingZerosFirsts = fractionalTrailingZeros.slice(0, 2);
  const fractionalTrailingZerosLasts = fractionalTrailingZeros.slice(2, fractionalTrailingZeros.length);

  const intTrailingZeros = fractionalTrailingZeros.length === fractionalPart.length
    ? intPart.slice(firstTrailingZeroIndex(intPart), intPart.length)
    : '';
  const intTrailingZerosFirsts = intTrailingZeros.slice(0, 2);
  const intTrailingZerosLasts = intTrailingZeros.slice(2, intTrailingZeros.length);

  const intMain = intPart.slice(intLeadingZeros.length, intPart.length - intTrailingZeros.length);
  const fractionalMain = fractionalPart.slice(
    fractionalLeadingZeros.length,
    fractionalPart.length - fractionalTrailingZeros.length,
  );

  const fractionalMainFirsts = fractionalMain.slice(0, 2);
  const fractionalMainLasts = fractionalMain.slice(2, fractionalMain.length);

  const decimalPointClass = {
    'e-big-number__sign': true,
    'e-big-number__leading-zeros': intLeadingZeros && fractionalLeadingZeros,
    'e-big-number__trailing-zeros': intTrailingZeros && fractionalTrailingZeros,
  };

  return [
    (sign &&
      <span class={{
        'e-big-number__sign': true,
        'e-big-number__leading-zeros': intLeadingZeros,
      }}>{sign}</span>
    ),
    (
      <span class="e-big-number__int">
        {intLeadingZeros &&
          <span class="e-big-number__leading-zeros">{intLeadingZeros}</span>
        }
        {intMain &&
          <span class="e-big-number__main">{intMain}</span>
        }
        {intTrailingZeros &&
          <span class="e-big-number__trailing-zeros">
            <span class="e-big-number__main-firsts">{intTrailingZerosFirsts}</span>
            <span class="e-big-number__main-lasts">{intTrailingZerosLasts}</span>
          </span>
        }
      </span>
    ),
    (decimalPoint &&
      <span class={decimalPointClass}>{decimalPoint}</span>
    ),
    (fractionalPart &&
      <span class="e-big-number__fraction">
        {[
          (fractionalLeadingZeros &&
            <span class="e-big-number__leading-zeros">{fractionalLeadingZeros}</span>
          ),
          (fractionalMain &&
            <span class="e-big-number__main">
              <span class="e-big-number__main-firsts">{fractionalMainFirsts}</span>
              <span class="e-big-number__main-lasts">{fractionalMainLasts}</span>
            </span>
          ),
          (fractionalTrailingZeros &&
            <span class="e-big-number__trailing-zeros">
              <span class="e-big-number__main-firsts">{fractionalTrailingZerosFirsts}</span>
              <span class="e-big-number__main-lasts">{fractionalTrailingZerosLasts}</span>
            </span>
          ),
        ]}
      </span>
    ),
  ];
};

const getContentRenderer = (isZero, leadingZerosClass, trailingZerosClass) => {
  if (isZero) {
    return renderWithoutZeroClasses;
  } else if (leadingZerosClass && trailingZerosClass) {
    return renderWithLeadingAndTrailingZeros;
  } else if (leadingZerosClass) {
    // TODO:
  } else if (trailingZerosClass) {
    // TODO:
  }
  return renderWithoutZeroClasses;
};

export default {
  name: 'e-big-number',
  functional: true,

  props: {
    value: {
      type: [Number, String, BigNumber],
      default: () => BigNumber(0),
    },

    decimals: {
      type: Number,
      default: 0,
    },

    sign: {
      type: Boolean,
      default: null,
    },

    // количество цифр в числе (prop-ы взаимоисключают decimalPlaces)
    precision: Number,
    minPrecision: Number,
    maxPrecision: Number,

    // количество цифр после точки (prop-ы взаимоисключают precision)
    decimalPlaces: Number,
    minDecimalPlaces: Number,
    maxDecimalPlaces: Number,

    // TODO: add 'start' support if needed
    // specifies where to append extra 0 digits (for minPrecision)
    // padZeros: {
    //   type: String,
    //   default: 'end',
    //   validator: v => ['start', 'end'].includes(v),
    // },

    // http://mikemcl.github.io/bignumber.js/#constructor-properties
    roundingMode: {
      type: Number,
      default: BigNumber.ROUND_DOWN,
      validator: v => [0, 1, 2, 3, 4, 5, 6, 7, 8].includes(v),
    },

    leadingZerosClass: {
      type: Boolean,
      default: true,
    },

    trailingZerosClass: {
      type: Boolean,
      default: true,
    },
  },

  render(h, { props }) {
    const { isZero, positive, negative, parts } = propsToNumber(props);
    const renderContent = getContentRenderer(isZero, props.leadingZerosClass, props.trailingZerosClass);
    return (
      <span class={[
        'e-big-number',
        isZero && 'e-big-number_zero', // if ±0
        positive && 'e-big-number_positive', // if > 0
        negative && 'e-big-number_negative', // if < 0
      ]}>
        {renderContent(h, ...parts)}
      </span>
    );
  },
};
</script>



