/**
 * @module store/futures-contracts/mutations
 * @version 1.0.0
 */

import * as types from '@/store/mutation-types';
import { ASYNC_STATES } from '@/util/constants';

// mutations
const mutations = {
  [types.FETCH_FUTURES_ACTIVE_CONTRACT_ASYNC.PENDING](state) {
    state.getContractState = ASYNC_STATES.PENDING;
  },

  [types.FETCH_FUTURES_ACTIVE_CONTRACT_ASYNC.SUCCESS](state, contract) {
    state.activeContract = contract;
    state.getContractState = ASYNC_STATES.SUCCESS;
  },

  [types.FETCH_FUTURES_FIRST_CONTRACT_ASYNC.SUCCESS](state, firstContractHash) {
    state.firstContractHash = firstContractHash;
  },

  [types.FETCH_FUTURES_ACTIVE_CONTRACT_ASYNC.FAILURE](state) {
    state.activeContract = null;
    state.getContractState = ASYNC_STATES.ERROR;
  },

  [types.SET_ACTIVE_CONTRACT](state, contract) {
    state.activeContract = contract;
  },

  [types.SET_ACTIVE_CONTRACT_BASE_TOKEN](state, baseToken) {
    state.activeContract = { ...state.activeContract, baseToken };
  },
};

export default mutations;



