// eslint-disable-next-line import/prefer-default-export
export const NOT_ENOUGH_BALANCE = { message: 'Not enough balance' };
export const NO_ZERO_TRANSACTION = { message: 'Transactions with 0 value forbidden' };
export const ESTIMATION_FAILED = { message: 'Gas estimation failed' };

export class TimeoutError extends Error {
  constructor(message = 'Timeout occured', timeout) {
    super(message);
    this.timeout = timeout;
  }
}



