/**
 * @module store/contracts/getters
 * @version 1.0.2
 */


/**
 * Getter: Возвращает список доступных версий главного контракта
 *
 * @returns {array}
 * @since Version 1.0.1
 */
const exchangeContracts = (state) => {
  let contracts = [...state.exchangeContracts];
  if (contracts.length > 0) {
    contracts = contracts.map((item, index) => ({ ...item, isCurrent: index === 0 }));
  }
  return contracts;
};

/**
 * Getter: Возвращает список доступных версий futures контрактов
 *
 * @returns {array}
 * @since Version 1.0.1
 */
const futuresContracts = (state) => {
  let contracts = [...state.futuresContracts];
  if (contracts.length > 0) {
    contracts = contracts.map((item, index) => ({ ...item, isCurrent: index === 0 }));
  }
  return contracts;
};

/**
 * Getter: Возвращает список доступных версий dmexOracle контрактов
 *
 * @returns {array}
 * @since Version 1.0.1
 */
const dmexOracleContracts = (state) => {
  let contracts = [...state.dmexOracleContracts];
  if (contracts.length > 0) {
    contracts = contracts.map((item, index) => ({ ...item, isCurrent: index === 0 }));
  }
  return contracts;
};


/**
 * Getter: Возвращает активную версию главного контракта
 *
 * @returns {object | null}
 * @since Version 1.0.1
 */
const activeMainContract = state => state.activeMainContract;
const activeFuturesContract = state => state.activeFuturesContract;
const activeDmexOracleContract = state => state.activeDmexOracleContract;

/**
 * Getter: Возвращает адрес активной версии главного контракта
 *
 * @returns {string}
 * @since Version 1.0.2
 */
const activeMainContractAddress = state => state.activeMainContract && state.activeMainContract.contractAddress;
const activeFuturesContractAddress = state => state.activeFuturesContract && state.activeFuturesContract.contractAddress;
const activeDmexOracleContractAddress = state => state.activeDmexOracleContract && state.activeDmexOracleContract.contractAddress;

/**
 * Getter: Возвращает:
 * true если активна последняя версия контракта
 * false если активна не последняя версия контракта
 * null - если список контрактов ещё не загружен
 *
 * @returns {boolean|null}
 * @since Version 1.0.1
 */
const isLatestExchangeContractActive = (state) => {
  const active = state.activeMainContract;
  const latest = state.exchangeContracts[0]; // latest is first
  return !!(active && latest && active.id === latest.id);
};

const isLatestFuturesContractActive = (state) => {
  const active = state.activeFuturesContract;
  const latest = state.futuresContracts[0]; // latest is first
  return !!(active && latest && active.id === latest.id);
};

const isLatestDmexOracleContractActive = (state) => {
  const active = state.activeDmexOracleContract;
  const latest = state.dmexOracleContracts[0]; // latest is first
  return !!(active && latest && active.id === latest.id);
};

const affiliateContract = state => state.affiliateContract;

const getExchangeContractById = (state, getters) => (id) => {
  const filtered = getters.exchangeContracts.filter(c => c.id === id);
  return filtered.length > 0 ? filtered[0] : null;
};
const getFuturesContractById = (state, getters) => (id) => {
  const filtered = getters.futuresContracts.filter(c => c.id === id);
  return filtered.length > 0 ? filtered[0] : null;
};
const getDmexOracleContractById = (state, getters) => (id) => {
  const filtered = getters.dmexOracleContracts.filter(c => c.id === id);
  return filtered.length > 0 ? filtered[0] : null;
};

export default {
  exchangeContracts,
  futuresContracts,
  dmexOracleContracts,
  activeMainContract,
  activeFuturesContract,
  activeDmexOracleContract,
  activeMainContractAddress,
  activeFuturesContractAddress,
  activeDmexOracleContractAddress,
  isLatestExchangeContractActive,
  isLatestFuturesContractActive,
  isLatestDmexOracleContractActive,
  affiliateContract,
  getExchangeContractById,
  getFuturesContractById,
  getDmexOracleContractById,
};



