/**
 * @module store/wallets/getters
 * @version 1.0.0
 */

import { METAMASK_WALLET_TYPE, LEDGER_WALLET_TYPE, PRIVATE_KEY_TYPE } from '@/util/constants';
import walletsEqual from '@/util/walletsEqual';

/**
 * Getter: Возвращает является ли пользователь залогиненным (есть ли у него активных кошелек)
 *
 * @since Version 1.0.0
 */
const isLogin = (state, getters) => getters.activeWallet != null;

const activeWallet = (state, getters) => (
  state.activeWallet &&
  (getters.allWallets.find(w => walletsEqual(w, state.activeWallet)) || null)
);

const activeWalletAddress = state => (state.activeWallet && state.activeWallet.address) || null;

const activeWalletType = state => (state.activeWallet && state.activeWallet.type) || null;

const isActivePrivateKey = state => state.activeWallet && state.activeWallet.type === PRIVATE_KEY_TYPE;

const isActiveMetamask = state => state.activeWallet && state.activeWallet.type === METAMASK_WALLET_TYPE;

const isMetamaskLedger = state => state.activeWallet && state.isMetamaskLedger;

const isActiveLedger = state => state.activeWallet && state.activeWallet.type === LEDGER_WALLET_TYPE;

const walletBalanceName = (state, getters) => (
  {
    [PRIVATE_KEY_TYPE]: 'Wallet',
    [METAMASK_WALLET_TYPE]: 'Metamask',
    [LEDGER_WALLET_TYPE]: 'Ledger',
  }[getters.activeWalletType]
  || 'Wallet'
);


/**
 * Getter: Provided wallets are first
 * @return {[]}
 */
const allWallets = (state) => {
  const browserProvided = state.providedWallets;
  const ledger = state.ledgerWallets;
  const privateKey = state.localWallets;
  return [...ledger, ...browserProvided, ...privateKey];
};

const isActiveWalletNew = (state, getters) => state.newWalletAddress !== null && state.newWalletAddress === getters.activeWalletAddress;

const totalWallets = state => state.totalWallets;

// non-reactive-getters

const hasPrivateKeyWallet = state => address => !!state.localWallets.find(w => w.address === address);

const ledgerWallets = state => state.ledgerWallets;

export default {
  isLogin,
  activeWallet,
  activeWalletAddress,
  activeWalletType,
  isActivePrivateKey,
  isMetamaskLedger,
  isActiveMetamask,
  isActiveLedger,
  walletBalanceName,
  allWallets,
  isActiveWalletNew,
  totalWallets,
  hasPrivateKeyWallet,
  ledgerWallets,
};



