import { BASE_TOKEN_DAI_TEST, BASE_TOKEN_DAI_MAIN, BASE_TOKEN_DAI_ROPSTEN } from '@/util/constants';
import { BigNumber } from 'bignumber.js';

export default class FuturesCalc {
  /**
   * Calculate fee
   *
   * function calculateFee (
   *      uint256 qty, uint256 tradePrice, uint256 fee,  bytes32 futuresContractHash
   * ) returns (uint256)
   *
   * @param {String|Number|BigNumber} qty
   * @param {String|Number|BigNumber} tradePrice
   * @param {String|Number|BigNumber} fee
   * @param {String|Number|BigNumber} multiplier
   * @returns {BigNumber}
   */
  static calculateFee(qty, tradePrice, fee, multiplier) {
    return FuturesCalc.calculateTradeValue(qty, tradePrice, multiplier)
      .times(BigNumber(fee).idiv('1e10'))
      .idiv('1e18');
  }

  /**
   * Calculate trade value
   *
   * function calculateTradeValue(uint256 qty, uint256 price, bytes32 futuresContractHash) returns (uint256)
   *
   * @param {String|Number|BigNumber} qty
   * @param {String|Number|BigNumber} price
   * @param {String|Number|BigNumber} multiplier
   * @returns {BigNumber}
   */
  static calculateTradeValue(qty, price, multiplier) {
    return BigNumber(qty).times(price)
      .shiftedBy(2)
      .times(multiplier)
      .idiv('1e8');
  }

  /**
   * Calculate liquidation price V7
   *
   * @param {Object} pos
   * @param {String|Number|BigNumber} pos.size
   * @param {String|Number|BigNumber} pos.price
   * @param {String|Number|BigNumber} pos.side
   * @param {String|Number|BigNumber} pos.entryBlock
   * @param {String|Number|BigNumber} pos.collateral
   * @param {Object} details
   * @param {String|Number|BigNumber} details.fundingBlocks
   * @param {String|Number|BigNumber} details.fundingRate
   * @param {String|Number|BigNumber} details.maintenanceMargin
   * @param {String|Number|BigNumber} details.multiplier
   * @returns {{liquidationPrice: BigNumber, collateral: BigNumber}}
   */
  static calculateLiquidationPriceV7(pos, details) {
    // pos
    // [0] size
    // [1] price
    // [2] side
    // [3] entryBlock
    // [4] collateral

    // values
    // [0] fundingBlocks
    // [1] fundingRate
    // [2] maintenanceMargin
    // [3] multiplier

    // LiquidationPriceValues memory v = LiquidationPriceValues({
    //  size: pos[0],
    //  price: pos[1],
    //  baseCollateral: pos[4]
    // });

    // uint256 collateral = safeMul(v.baseCollateral, 1e8) / values[3];
    const collateral = BigNumber(pos.collateral).times('1e8').idiv(details.multiplier);

    // uint256 leverage = safeMul(v.price,v.size)/collateral/1e8;
    const leverage = BigNumber(pos.price).times(pos.size).idiv(collateral).idiv('1e8');

    // uint256 coef = safeMul(values[2], 1e10)/leverage;
    const coef = BigNumber(details.maintenanceMargin).times('1e10').idiv(leverage);

    // uint256 fundingCost = safeMul(
    //  safeMul(
    //    safeMul(v.size, v.price) / 1e8,
    //    values[0]
    //  ),
    //  values[1]
    // ) / 1e18;
    const fundingCost = BigNumber(pos.size).times(pos.price).idiv('1e8')
      .times(details.fundingBlocks)
      .times(details.fundingRate)
      .idiv('1e18');

    // uint256 netLiqPrice;
    // uint256 liquidationPrice;

    // uint256 movement = safeMul(safeSub(collateral, fundingCost), 1e8) / v.size;
    const movement = BigNumber(collateral).minus(fundingCost)
      .times('1e8')
      .idiv(pos.size);

    // if (pos[2] == 0)
    // {
    //  netLiqPrice = safeAdd(v.price, movement);
    //  liquidationPrice = safeSub(netLiqPrice, safeMul(v.price, coef)/1e18);
    // }
    // else
    // {
    //  netLiqPrice = safeSub(v.price, movement);
    //  liquidationPrice = safeAdd(netLiqPrice, safeMul(v.price, coef)/1e18);
    // }
    let liquidationPrice;
    if (BigNumber(pos.side).eq(0)) {
      liquidationPrice = BigNumber(pos.price).plus(movement)
        .minus(BigNumber(pos.price).times(coef).idiv('1e18'));
    } else {
      liquidationPrice = BigNumber(pos.price).minus(movement)
        .plus(BigNumber(pos.price).times(coef).idiv('1e18'));
    }

    return {
      liquidationPrice,
      collateral,
    };
  }

  /**
   * Calculate liquidation price
   *
   * @param {Object} pos
   * @param {String|Number|BigNumber} pos.size
   * @param {String|Number|BigNumber} pos.price
   * @param {String|Number|BigNumber} pos.side
   * @param {String|Number|BigNumber} pos.entryBlock
   * @param {String|Number|BigNumber} pos.collateral
   * @param {Object} details
   * @param {String|Number|BigNumber} details.fundingBlocks
   * @param {String|Number|BigNumber} details.fundingRate
   * @param {String|Number|BigNumber} details.maintenanceMargin
   * @param {String|Number|BigNumber} details.multiplier
   * @param {String|Number|BigNumber} details.fundingInterval
   * @returns {{liquidationPrice: BigNumber, collateral: BigNumber}}
   */
  static calculateLiquidationPriceV8(pos, details) {
    // pos
    // [0] size
    // [1] price
    // [2] side
    // [3] entryBlock
    // [4] collateral

    // values
    // [0] fundingBlocks
    // [1] fundingRate
    // [2] maintenanceMargin
    // [3] multiplier

    // LiquidationPriceValues memory v = LiquidationPriceValues({
    //  size: pos[0],
    //  price: pos[1],
    //  baseCollateral: pos[4]
    // });

    // adjust funding blocks to funding interval
    // values[0] = safeMul(values[0]/fundingInterval, fundingInterval);
    const adjustedFundingBlocks = BigNumber(details.fundingBlocks)
      .idiv(details.fundingInterval)
      .times(details.fundingInterval);

    // uint256 collateral = safeMul(v.baseCollateral, 1e8) / values[3];
    const collateral = BigNumber(pos.collateral).times('1e8').idiv(details.multiplier);

    // uint256 leverage = safeMul(v.price,v.size)/collateral/1e6;
    const leverage = BigNumber(pos.price).times(pos.size).idiv(collateral).idiv('1e6');

    // uint256 coef = safeMul(safeMul(values[2], 1e10)/leverage, 1e2);
    const coef = BigNumber(details.maintenanceMargin).times('1e10').idiv(leverage).times('1e2');

    // uint256 fundingCost = safeMul(
    //  safeMul(
    //    safeMul(v.size, v.price) / 1e8,
    //    values[0]
    //  ),
    //  values[1]
    // ) / 1e18;
    const fundingCost = BigNumber(pos.size).times(pos.price).idiv('1e8')
      .times(adjustedFundingBlocks)
      .times(details.fundingRate)
      .idiv('1e18');

    // uint256 netLiqPrice;
    // uint256 liquidationPrice;

    // uint256 movement = safeMul(safeSub(collateral, min(collateral, fundingCost)), 1e8)/v.size;
    const movement = collateral.minus(BigNumber.min(collateral, fundingCost))
      .times('1e8')
      .idiv(pos.size);

    // if (pos[2] == 0)
    // {
    //   netLiqPrice = safeAdd(v.price, movement);
    //   liquidationPrice = safeSub(netLiqPrice, min(netLiqPrice, safeMul(v.price, coef)/1e18));
    // }
    // else
    // {
    //   netLiqPrice = safeSub(v.price, movement);
    //   liquidationPrice = safeAdd(netLiqPrice, safeMul(v.price, coef)/1e18);
    // }
    let liquidationPrice;
    let netLiqPrice;
    if (BigNumber(pos.side).eq(0)) {
      netLiqPrice = BigNumber(pos.price).plus(movement);
      liquidationPrice = netLiqPrice.minus(
        BigNumber.min(
          netLiqPrice,
          BigNumber(pos.price)
            .times(coef)
            .idiv('1e18'),
        ),
      );
    } else {
      netLiqPrice = BigNumber(pos.price).minus(movement);
      liquidationPrice = netLiqPrice.plus(
        BigNumber(pos.price)
          .times(coef)
          .idiv('1e18'),
      );
    }

    return {
      liquidationPrice,
      collateral,
    };
  }

  /**
   * Calculate collateral V7
   *
   * calculateCollateral (uint256 qty, uint256 price, uint256 leverage, bytes32 futuresContractHash) view returns (uint256)
   *
   * @param {String|Number|BigNumber} qty
   * @param {String|Number|BigNumber} price
   * @param {String|Number|BigNumber} leverage
   * @param {String|Number|BigNumber} multiplier
   * @returns {BigNumber}
   */
  static calculateCollateralV7(qty, price, leverage, multiplier) {
    return BigNumber(price).times(qty)
      .times(multiplier)
      .idiv('1e16')
      .idiv(leverage);
  }

  /**
   * Calculate collateral V8
   *
   * calculateCollateral (uint256 qty, uint256 price, uint256 leverage, bytes32 futuresContractHash) view returns (uint256)
   *
   * @param {String|Number|BigNumber} qty
   * @param {String|Number|BigNumber} price
   * @param {String|Number|BigNumber} leverage
   * @param {String|Number|BigNumber} multiplier
   * @param {String|Number|BigNumber} [fee=0]
   * @returns {BigNumber}
   */
  static calculateCollateralV8(qty, price, leverage, multiplier, fee = 0) {
    return BigNumber(price).times(qty)
      .times(multiplier)
      .idiv('1e8')
      .idiv(leverage)
      .plus(FuturesCalc.calculateFee(qty, price, fee, multiplier));
  }

  /**
   * Calculate multiplier
   *
   * @param {String} baseToken
   * @param {String|Number|BigNumber} ethusdRawPrice
   * @return {BigNumber}
   */
  static calculateMultiplier(baseToken, ethusdRawPrice) {
    // Calculate ETH multiplier
    if (baseToken === BASE_TOKEN_DAI_TEST || baseToken === BASE_TOKEN_DAI_MAIN || baseToken === BASE_TOKEN_DAI_ROPSTEN) {
      // Non ETH multiplier is 1e8
      return BigNumber('1e8');
    }

    return BigNumber(1).div(ethusdRawPrice).times('1e8').integerValue();
  }
}



