import { BigNumber } from 'bignumber.js';
import * as types from '@/store/mutation-types';
import { createOrder, createOrderWithContract } from '@/api/backend/futures/orders';
import { getOneExternalTicker, backendErrorToString } from '@/api/backend';
import {
  ETH_ADDRESS,
  BASE_TOKEN_BTC,
  ETH_BASE_ADDRESS,
  METAMASK_WALLET_TYPE,
  LEDGER_WALLET_TYPE,
  BUY_TOKEN_ORDER,
  ORDER_TYPE_LIMIT,
  ORDER_TYPE_MARKET,
  ORDER_TYPE_STOP_LIMIT,
  FUTURES_PRICE_DECIMALS,
  FUTURES_AMOUNT_DECIMALS,
} from '@/util/constants';
import {
  ERROR_METAMASK_DENY_MESSAGE,
  ERROR_LEDGER_DENY,
} from '@/util/errorMessages';
import log from '@/util/logger';
import { getSignParams, buyOrSellToString, getOrderDetails, getContractDetails } from './helpers';


/**
 * Action: Задает параметры формы
 */
const fillOrder = ({ state, commit, rootState }, { buyOrSell, amount, limitPrice }) => {
  const orderType = state.orderType;
  const formattedAmount = amount ? amount.shiftedBy(-FUTURES_AMOUNT_DECIMALS).toFixed() : null;
  const formattedLimitPrice = limitPrice ? limitPrice.shiftedBy(-FUTURES_PRICE_DECIMALS).toFixed() : null;
  if (orderType === ORDER_TYPE_LIMIT || orderType === ORDER_TYPE_STOP_LIMIT) {
    commit(types.FILL_CREATE_ORDER, {
      buyOrSell,
      amount: rootState.futuresPositions.positionToClose ? undefined : formattedAmount,
      limitPrice: formattedLimitPrice,
    });
  } else if (orderType === ORDER_TYPE_MARKET) {
    commit(types.FILL_CREATE_ORDER, {
      buyOrSell,
      amount: formattedAmount,
    });
  }
};

const currencyTicker = (baseToken) => {
  if (baseToken === ETH_BASE_ADDRESS) {
    return 'ETHUSD';
  } else if (baseToken === BASE_TOKEN_BTC) {
    return 'BTCUSD';
  }

  return 'DAI';
};

const startOrderConfirmation = async ({ getters, rootGetters, rootState, commit }, order) => {
  const blockObserver = rootGetters['blockchain/blockObserver'];
  const activeContract = rootState.futuresActiveContract.activeContract;

  const orderDetails = getOrderDetails({
    order,
    getMarketPrice: getters.getMarketPrice,
  });

  const currentBlockNumber = await blockObserver.getLatest();
  const curTicker = currencyTicker(activeContract.baseToken);

  const contractDetails = getContractDetails({
    contractModel: activeContract,
    ethusdRawPrice: curTicker === 'DAI'
      ? BigNumber(1)
      : (await getOneExternalTicker(curTicker)).lastPrice,
    currentBlockNumber,
  });

  const isInvalidMarketOrder = () => false;
  // {
  //   if (orderDetails.type === ORDER_TYPE_MARKET) {
  //     const marketTotal = getters.getMarketTotal(orderDetails);
  //     return marketTotal.isNaN() || marketTotal.lt(state.orderMinEthValue);
  //   }
  //   return false;
  // };

  // TODO: Reactivate validation (it does not work fine with TOKEN)
  /*
  if (isInvalidMarketOrder()) {
    commit(types.SHOW_MIN_ORDER_ERROR);
  } else if (orderDetails.type !== ORDER_TYPE_MARKET && orderDetails.total.lt(state.orderMinEthValue)) {
    commit(types.SHOW_MIN_ORDER_ERROR);
  } else if (!isEnoughBalance(orderDetails, {
    ethBalance: rootGetters['balance/contractEthAvailable'],
    tokenBalance: rootGetters['balance/activeTokenContractBalance'],
  })) {
    commit('ui/SHOW_ERROR_ORDER_BALANCE', undefined, { root: true });
  } else if (!isEnoughBalance(orderDetails, {
    ethBalance: rootGetters['balance/contractEthNotInOrders'],
    tokenBalance: rootGetters['balance/activeTokenContractBalanceNotInOrders'],
  })) {
    commit('ui/SHOW_ERROR_ORDER_BALANCE_RESERVED', undefined, { root: true });
  } else {
    commit(types.ORDER_ASK_CONFIRMATION, orderDetails);
  }
  */
  if (isInvalidMarketOrder()) {
    commit(types.SHOW_MIN_ORDER_ERROR);
  } else {
    commit(types.ORDER_ASK_CONFIRMATION, { orderDetails, contractDetails });
  }
};

const submitOrder = async ({ rootState, rootGetters, commit }, { orderDetails, contractDetails }) => {
  const activeContract = rootState.futuresActiveContract.activeContract;
  const baseToken = rootGetters['futuresActiveContract/contractBaseToken'];
  const activeFuturesSc = rootGetters['contracts/activeFuturesContract'];
  const web3Instance = rootGetters['blockchain/web3Instance'];
  const wallet = rootGetters['wallets/activeWallet'];
  const { address: userAddress, type: walletType } = wallet;
  const isMetamask = walletType === METAMASK_WALLET_TYPE;
  const isLedger = walletType === LEDGER_WALLET_TYPE;
  const balanceApi = rootGetters['blockchain/balanceApi'];
  const signService = rootGetters['blockchain/signService'];
  const { buyOrSell, stopPrice } = orderDetails;
  const signParams = getSignParams({ orderDetails });

  const nonce = Date.now();
  const leverage = BigNumber(activeContract.margin)
    .shiftedBy(activeFuturesSc.version <= 7 ? 0 : 8)
    .toFixed(0);

  let contractHash;
  if (activeFuturesSc.version < 7) {
    contractHash = web3Instance.utils.soliditySha3(
      { type: 'address', value: activeContract.contractAddress },
      { type: 'bytes32', value: activeContract.futuresAssetHash },
      { type: 'uint256', value: contractDetails.expirationBlock },
      { type: 'uint256', value: contractDetails.multiplier.toFixed(0) },
      { type: 'uint256', value: contractDetails.fundingRate.toFixed(0) },
      { type: 'bool', value: false },
    );
  } else {
    contractHash = web3Instance.utils.soliditySha3(
      { type: 'address', value: activeContract.contractAddress },
      { type: 'bytes32', value: activeContract.futuresAssetHash },
      { type: 'uint256', value: contractDetails.expirationBlock },
      { type: 'uint256', value: contractDetails.multiplier.toFixed(0) },
      { type: 'uint256', value: contractDetails.fundingRate.toFixed(0) },
      { type: 'bool', value: false },
      { type: 'uint256', value: activeContract.maintenanceMargin.toFixed(0) },
    );
  }

  const orderHash = web3Instance.utils.soliditySha3(
    { type: 'address', value: activeContract.contractAddress },
    { type: 'bytes32', value: activeContract.isModel && !activeContract.perpetual ? contractHash : activeContract.futuresContractHash },
    { type: 'address', value: userAddress },
    { type: 'uint256', value: signParams.amount },
    { type: 'uint256', value: signParams.price },
    { type: 'bool', value: signParams.side },
    { type: 'uint256', value: nonce },
    { type: 'uint256', value: leverage },
  );

  // TODO: Reactivate validation (it does not work fine with TOKEN)
  /*
  if (!isEnoughBalance(orderDetails, {
    ethBalance: rootGetters['balance/contractEthNotInOrders'],
    tokenBalance: rootGetters['balance/activeTokenContractBalanceNotInOrders'],
  })) {
    commit('ui/SHOW_ERROR_ORDER_BALANCE', undefined, { root: true });
  }
  */

  commit(types.CREATE_ORDER_ASYNC.PENDING);

  let signature;
  if (isMetamask) {
    commit('ui/SHOW_METAMASK_SIGN', undefined, { root: true });
  } else if (isLedger) {
    commit('ui/SHOW_LEDGER_SIGN', undefined, { root: true });
  }

  try {
    signature = await signService.signData(orderHash);
  } catch (e) {
    if (
      (isMetamask && e.message.includes(ERROR_METAMASK_DENY_MESSAGE)) ||
      (isLedger && e.message.includes(ERROR_LEDGER_DENY))
    ) {
      // User manually cancelled transaction in Metamask
    } else {
      commit(types.CREATE_ORDER_ASYNC.FAILURE);
      commit('ui/ADD_NOTIFICATION', {
        status: 'error',
        headline: 'Order Creation Error',
        message: e.message,
      }, { root: true });
      log.error(`Failed to create ${buyOrSellToString(buyOrSell)}: ${e.message}`, {
        module: 'orders',
        layer: 'sign',
      });
    }
  } finally {
    if (isMetamask) {
      commit('ui/HIDE_METAMASK_SIGN', undefined, { root: true });
    } else if (isLedger) {
      commit('ui/HIDE_LEDGER_SIGN', undefined, { root: true });
    }
  }

  const createContractObj = {
    contractHash,
    contractModelHash: activeContract.futuresContractHash,
    expirationBlock: contractDetails.expirationBlock,
    multiplier: contractDetails.multiplier.toFixed(0),
    fundingRate: contractDetails.fundingRate.toFixed(0),
    perpetual: false,
    maintenanceMargin: activeContract.maintenanceMargin.toFixed(0),
  };

  const createOrderObj = {
    contractAddress: activeContract.contractAddress,
    futuresContractHash: activeContract.isModel && !activeContract.perpetual ? contractHash : activeContract.futuresContractHash,
    userAddress,
    ...signParams,
    signature: { ...signature, nonce, orderHash },
    stopPrice,
    leverage,
  };

  if (signature) {
    let orderCreated = false;
    try {
      if (activeContract.isModel && !activeContract.perpetual) {
        await createOrderWithContract({
          order: createOrderObj,
          contract: createContractObj,
        });
      } else {
        await createOrder(createOrderObj);
      }
      orderCreated = true;
    } catch (e) {
      commit(types.CREATE_ORDER_ASYNC.FAILURE);
      commit('ui/ADD_NOTIFICATION', {
        status: 'error',
        headline: 'Order Creation Error',
        message: backendErrorToString(e),
      }, { root: true });
      log.warn(`Error: Create order. Name: ${e.name}. Type: ${e.type}. Message: ${e.message}`, {
        module: 'orders',
        layer: 'backend',
      });
    }

    if (orderCreated) {
      commit('ui/ADD_NOTIFICATION', {
        status: 'success',
        headline: 'Order Created',
        message: `${buyOrSellToString(buyOrSell)} successfully created`,
      }, { root: true });
      commit(types.CREATE_ORDER_ASYNC.SUCCESS); // обнуление данных в форме происходит только, если создание ордера прошло успешно

      let reserved;
      try {
        reserved = await balanceApi.getReserved({ tokenAddress: buyOrSell === BUY_TOKEN_ORDER ? ETH_ADDRESS : baseToken.contract });
      } catch (err) {
        // TODO: balance error notification
        log.warn(`Error: Reserved balance after Create order: Name: ${err.name}. Type: ${err.type}. Message: ${err.message}`, {
          module: 'orders',
          layer: 'backend',
        });
      }
      if (reserved) {
        commit('balance/UPDATE_TRADABLE_BALANCE', reserved, { root: true });
      }
    }
  }
};

export default {
  fillOrder,
  startOrderConfirmation,
  submitOrder,
};



