/**
 * @module store/order-book/actions
 *
 * @version 1.0.2
 */
import { BigNumber } from 'bignumber.js';
import * as types from '@/store/mutation-types';
import { getOrderBook, onOrderBookChange, removeOrderBookListener } from '@/api/backend/futures/orderbook';
import { mapOrderInOrderBook } from './helpers';


const loadOrderBook = async ({ commit, rootState, rootGetters }) => {
  // await Promise.all([
  //   dispatch('contracts/subscribeToContract', undefined, { root: true }), // ensures active contract loaded
  //   dispatch('tokens/subscribeToToken', undefined, { root: true }),
  // ]);
  const activeContract = rootState.futuresActiveContract.activeContract;
  if (!activeContract) {
    return;
  }

  const userAddress = rootGetters['wallets/activeWalletAddress'];

  commit(types.GET_ORDER_BOOK_ASYNC.PENDING);
  try {
    const { data } = await getOrderBook({
      futuresContractHash: activeContract.futuresContractHash,
      userAddress,
    });


    const mappingFn = o => mapOrderInOrderBook(o);

    commit(types.GET_ORDER_BOOK_ASYNC.SUCCESS, {
      asks: data.asks.map(mappingFn),
      bids: data.bids.map(mappingFn),
      volume: BigNumber(data.volume),
    });
  } catch (e) {
    if (rootGetters.isConnected) {
      commit(types.GET_ORDER_BOOK_ASYNC.FAILURE, e);
    }
  }
};


const listenToOrderBookChanges = ({ commit }) => {
  onOrderBookChange((data) => {
    const mappingFn = o => mapOrderInOrderBook(o);
    commit(types.GET_ORDER_BOOK_ASYNC.SUCCESS, {
      asks: data.data.asks.map(mappingFn),
      bids: data.data.bids.map(mappingFn),
      volume: BigNumber(data.data.volume),
    });
  });
};


const stopListeningToOrderBookChanges = () => {
  removeOrderBookListener();
};

export default {
  listenToOrderBookChanges,
  loadOrderBook,
  stopListeningToOrderBookChanges,
};



