import { INTERVAL_DESTROY_COMPONENT_RESOURCES } from '@/config';

/* eslint-disable no-restricted-syntax */
import * as types from '@/store/mutation-types';
import { afterWalletChange, beforeWalletChange, removeBeforeWalletListener, removeAfterWalletListener } from '@/util/events/wallet';
import { afterTokenChange, removeAfterTokenListener } from '@/util/events/token';
import { onReconnect, removeReconnectListener } from '@/api/backend';
import { onIdle, onResume, removeIdleListener, removeResumeListener } from '@/util/IdleEmitter';
import {
  onPrivateBalanceLoadFail,
  onTradableBalanceLoadFail,
  onPrivateBalanceReloadFail,
  onTradableBalanceReloadFail,
  subscribePrivateBalance,
  unsubscribePrivateBalance,
} from './helpers';

const loadContractBalance = async ({ state, rootGetters, commit, dispatch }) => {
  if (state.tradableLoading || state.tradableLoaded) return;
  commit(types.SET_TRADABLE_BALANCE_ASYNC.PENDING);
  if (!rootGetters['wallets/isLogin'] || rootGetters['wallets/isActiveWalletNew']) {
    commit(types.SET_TRADABLE_BALANCE_ASYNC.SUCCESS);
  } else {
    await Promise.all([
      dispatch('contracts/loadActiveExchangeContract', undefined, { root: true }),
      dispatch('tokens/loadTokens', undefined, { root: true }),
    ]);
    const activeWalletWhenRequestWasSent = rootGetters['wallets/activeWallet'];
    const balanceApi = rootGetters['blockchain/balanceApi'];
    const tokens = rootGetters['tokens/tokenContracts'];
    if (activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
      let newBalance;
      try {
        newBalance = await balanceApi.getTradableAndReservedEthAndTokens({ tokens });
      } catch (e) {
        onTradableBalanceLoadFail({ rootGetters, commit }, e, activeWalletWhenRequestWasSent);
      }
      if (newBalance && activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
        commit(types.SET_TRADABLE_BALANCE_ASYNC.SUCCESS, {
          balance: newBalance.tradableBalance,
          reserved: newBalance.reserved,
        });
      }
    }
  }
};

const refreshContractBalance = async ({ rootGetters, commit, dispatch }) => {
  commit(types.RELOAD_TRADABLE_BALANCE_ASYNC.PENDING);
  if (!rootGetters['wallets/isLogin'] || rootGetters['wallets/isActiveWalletNew']) {
    commit(types.RELOAD_TRADABLE_BALANCE_ASYNC.SUCCESS);
  } else {
    await Promise.all([
      dispatch('contracts/loadActiveExchangeContract', undefined, { root: true }),
      dispatch('tokens/loadTokens', undefined, { root: true }),
    ]);
    const activeWalletWhenRequestWasSent = rootGetters['wallets/activeWallet'];
    const balanceApi = rootGetters['blockchain/balanceApi'];
    const tokens = rootGetters['tokens/tokenContracts'];
    if (activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
      let newBalance;
      try {
        newBalance = await balanceApi.getTradableAndReservedEthAndTokens({ tokens });
      } catch (e) {
        onTradableBalanceReloadFail({ rootGetters, commit }, e, activeWalletWhenRequestWasSent);
      }
      if (newBalance && activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
        commit(types.RELOAD_TRADABLE_BALANCE_ASYNC.SUCCESS, {
          balance: newBalance.tradableBalance,
          reserved: newBalance.reserved,
        });
      }
    }
  }
};

