const refreshPrivateBalance = async ({ rootGetters, commit, dispatch }) => {
  commit(types.RELOAD_PRIVATE_BALANCE_ASYNC.PENDING);
  if (!rootGetters['wallets/isLogin']) {
    commit(types.RELOAD_PRIVATE_BALANCE_ASYNC.SUCCESS);
  } else {
    await dispatch('tokens/loadTokens', undefined, { root: true });
    const activeWalletWhenRequestWasSent = rootGetters['wallets/activeWallet'];
    const balanceApi = rootGetters['blockchain/balanceApi'];
    const tokens = rootGetters['tokens/tokenContracts'];
    if (activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
      let fetchedBalance;
      try {
        fetchedBalance = await balanceApi.getPrivateEthAndTokens({ tokens });
      } catch (e) {
        onPrivateBalanceReloadFail({ rootGetters, commit }, e, activeWalletWhenRequestWasSent);
      }
      if (fetchedBalance && activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
        commit(types.RELOAD_PRIVATE_BALANCE_ASYNC.SUCCESS, fetchedBalance.privateBalance);
      }
    }
  }
};

let privateBalanceStandbyHandler;
let privateBalanceResumeHandler;
// let tradableBalanceStandbyHandler;
let contractBalanceResumeHandler;
let contractBalanceReconnectHandler;

/**
 * Action should be called when component needs access to private balance
 * @param context
 * @returns {Promise<void>}
 */
const listenToPrivateBalanceChanges = async (context) => {
  const { state, rootGetters, commit, dispatch } = context;
  commit(types.ADD_PRIVATE_BALANCE_LISTENER);
  await dispatch('tokens/loadTokens', undefined, { root: true });
  await dispatch('loadPrivateBalance');
  if (!state.privateSubscriptionActive && state.privateListenersCount > 0) {
    commit(types.ACTIVATE_PRIVATE_BALANCE_SUBSCRIPTION);
    subscribePrivateBalance(context);

    // wallet change
    removeBeforeWalletListener('private');
    beforeWalletChange('private', () => {
      unsubscribePrivateBalance(context);
    });
    removeAfterWalletListener('private');
    afterWalletChange('private', async () => {
      commit(types.SET_BALANCE_IRRELEVANT);
      const activeWalletWhenRequestWasSent = rootGetters['wallets/activeWallet'];
      await dispatch('loadPrivateBalance');
      if (activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
        subscribePrivateBalance(context);
      }
    });
    removeAfterTokenListener('private');
    afterTokenChange('private', async () => {
      if (!rootGetters['tokens/isActiveTokenContractOnExchange']) {
        commit(types.SET_BALANCE_IRRELEVANT);
        const activeWalletWhenRequestWasSent = rootGetters['wallets/activeWallet'];
        await dispatch('loadPrivateBalance');
        if (activeWalletWhenRequestWasSent === rootGetters['wallets/activeWallet']) {
          subscribePrivateBalance(context);
        }
      }
    });

    // standby mode
    removeIdleListener(privateBalanceStandbyHandler);
    privateBalanceStandbyHandler = () => { unsubscribePrivateBalance(context); };
    onIdle(privateBalanceStandbyHandler);

    removeResumeListener(privateBalanceResumeHandler);
    privateBalanceResumeHandler = async () => {
      await dispatch('refreshPrivateBalance');
      if (state.privateListenersCount > 0) {
        subscribePrivateBalance(context);
      }
    };
    onResume(privateBalanceResumeHandler);
  }
};

/**
 * Action should be called when component no longer needs to access to private balance
 * @param context
 * @returns {Promise<void>}
 */
const stopListeningToPrivateBalanceChanges = (context) => {
  const { state, commit } = context;
  commit(types.REMOVE_PRIVATE_BALANCE_LISTENER);
  setTimeout(() => {
    if (state.privateSubscriptionActive && state.privateListenersCount === 0) {
      commit(types.DEACTIVATE_PRIVATE_BALANCE_SUBSCRIPTION);
      unsubscribePrivateBalance(context);

      // wallet change
      removeBeforeWalletListener('private');
      removeAfterWalletListener('private');
      removeAfterTokenListener('private');

      // standby mode
      removeIdleListener(privateBalanceStandbyHandler);
      removeResumeListener(privateBalanceResumeHandler);
    }
  }, INTERVAL_DESTROY_COMPONENT_RESOURCES);
};

/**
 * Action should be called when component needs access to contract balance
 */
const listenToContractBalanceChanges = async ({ state, rootGetters, commit, dispatch }) => {
  commit(types.ADD_CONTRACT_BALANCE_LISTENER);
  await Promise.all([
    dispatch('contracts/loadActiveExchangeContract', undefined, { root: true }),
    dispatch('tokens/loadTokens', undefined, { root: true }),
  ]);
  if (!state.tradableLoading && !state.tradableLoaded) {
    await dispatch('loadContractBalance');
  }
  if (!state.contractSubscriptionActive && state.contractListenersCount > 0) {
    commit(types.ACTIVATE_CONTRACT_BALANCE_SUBSCRIPTION);
    // Wallet change
    removeAfterWalletListener('tradable');
    afterWalletChange('tradable', () => {
      commit(types.SET_BALANCE_IRRELEVANT);
      dispatch('loadContractBalance');
    });
    removeAfterTokenListener('tradable');
    afterTokenChange('tradable', () => {
      if (!rootGetters['tokens/isActiveTokenContractOnExchange']) {
        commit(types.SET_BALANCE_IRRELEVANT);
        dispatch('loadContractBalance');
      }
    });

    // Standby mode
    removeResumeListener(contractBalanceResumeHandler);
    contractBalanceResumeHandler = () => { dispatch('refreshContractBalance'); };
    onResume(contractBalanceResumeHandler);

    // Backend Reconnect
    removeReconnectListener(contractBalanceReconnectHandler);
    contractBalanceReconnectHandler = () => { dispatch('refreshContractBalance'); };
    onReconnect(contractBalanceReconnectHandler);
  }
};

/**
 * Action should be called when component no longer needs to access to contract balance
 * @param context
 * @returns {Promise<void>}
 */
const stopListeningToContractBalanceChanges = ({ state, commit }) => {
  commit(types.REMOVE_CONTRACT_BALANCE_LISTENER);
  setTimeout(() => {
    if (state.contractSubscriptionActive && state.contractListenersCount === 0) {
      commit(types.DEACTIVATE_CONTRACT_BALANCE_SUBSCRIPTION);
      removeAfterWalletListener('tradable');
      removeAfterTokenListener('tradable');
      removeResumeListener(contractBalanceResumeHandler);
      removeReconnectListener(contractBalanceReconnectHandler);
    }
  }, INTERVAL_DESTROY_COMPONENT_RESOURCES);
};

export default {
  loadContractBalance,
  refreshContractBalance,
  loadPrivateBalance,
  refreshPrivateBalance,
  listenToPrivateBalanceChanges,
  stopListeningToPrivateBalanceChanges,
  listenToContractBalanceChanges,
  stopListeningToContractBalanceChanges,
};



