import { getReservedFunds } from '@/api/backend';
import { ETH_ADDRESS } from '@/util/constants';

/**
 * Высокоуровневый сервис, который абстрагирует доступ к балансу из бекенда и блокчейна.
 *
 * Balance shape
 * {
 *   privateBalance: {
 *     '0x0': BigNumber(1),
 *     '0x99...999': BigNumber(1),
 *   },
 *   tradableBalance: {
 *     '0x0': 0
 *   },
 *   reservedBalance: {
 *     '0x0': 0
 *   },
 *   privateFails: [],
 *   tradableFails: [],
 *   reservedFails: []
 * }
 *
 * @property {BalanceBlockchain} balanceBlockchain
 * @property {string} userAddress
 * @property {string} exchangeContractAddress
 */
export default class BalanceMixed {
  /**
   * @param {{balanceBlockchain, userAddress, exchangeContractAddress}} params
   */
  constructor(params) {
    Object.assign(this, params);
  }

  getPrivateEth(args) {
    return this.balanceBlockchain.getPrivateEth(args);
  }

  getPrivateToken(args) {
    return this.balanceBlockchain.getPrivateToken(args);
  }

  getAvailableBalance(args) {
    return this.balanceBlockchain.getAvailableBalance(args);
  }

  getPrivateEthAndToken(args) {
    return this.balanceBlockchain.getPrivateEthAndToken(args);
  }

  /**
   *
   * @param {{blockNumber: number, tokens: Array}} args
   * @returns {Promise<{privateBalance, privateFailed: Array}>}
   */
  getPrivateEthAndTokens(args) {
    return this.balanceBlockchain.getPrivateEthAndTokens(args);
  }

  getTradableEth(args) {
    return this.balanceBlockchain.getTradableEth(args);
  }

  getTradableToken(args) {
    return this.balanceBlockchain.getTradableToken(args);
  }

  /**
   * Returns both tradable and reserved balance
   *
   * @param tokens
   * @returns {Promise<{tradableBalance: Object, tradableFailed: number, reserved: Object}>}
   */
  async getTradableAndReservedEthAndTokens({ tokens }) {
    const userAddress = this.userAddress;
    const { tradableBalance: contractBalance, tradableFailed } = await this.balanceBlockchain.getTradableEthAndTokens({ tokens });
    const reserved = {};
    await Promise.all(
      Object.keys(contractBalance)
        .map(async (tokenAddress) => {
          reserved[tokenAddress] = await getReservedFunds({
            userAddress,
            tokenAddress,
            contractAddress: this.exchangeContractAddress,
          });
        }),
    );
    return {
      tradableBalance: contractBalance,
      tradableFailed,
      reserved,
    };
  }

  /**
   * Returns tradable and reserved balance for 1 specific token and ETH.
   *
   * @param tokenAddress
   * @param blockNumber
   * @returns {Promise<{reserved, balance}>}
   */
  async getTradableEthAndToken({ tokenAddress, blockNumber }) {
    const reserved = {};
    const reservedPromise = Promise.all([
      getReservedFunds({
        tokenAddress: ETH_ADDRESS,
        userAddress: this.userAddress,
        contractAddress: this.exchangeContractAddress,
      }),
      getReservedFunds({
        tokenAddress,
        userAddress: this.userAddress,
        contractAddress: this.exchangeContractAddress,
      }),
    ]).then(([ethBalance, tokenBalance]) => {
      reserved[ETH_ADDRESS] = ethBalance;
      reserved[tokenAddress] = tokenBalance;
    });
    const contractBalancePromise = this.balanceBlockchain.getTradableEthAndToken({ tokenAddress, blockNumber });
    const [{ balance }] = await Promise.all([contractBalancePromise, reservedPromise]);
    return { balance, reserved };
  }

  async getReserved({ tokenAddress }) {
    const reserved = await getReservedFunds({
      userAddress: this.userAddress,
      contractAddress: this.exchangeContractAddress,
      tokenAddress,
    });
    return {
      reserved: { [tokenAddress]: reserved },
    };
  }

  getBalanceAfterOperation(args) {
    return this.balanceBlockchain.getBalanceAfterOperation(args);
  }
}




