/**
 * @module store/ui/getters
 *
 * @version 1.0.2
 */

import { CONNECTION_STATUS, CONNECTION_STATUSES_SERVICE_DOWN } from '@/store/constants';
import { METAMASK_WALLET_TYPE } from '@/util/constants';

/*
 * Getter:
 *
 * @since Version 1.0.0
 */
const windowSize = state => state.windowSize;


/*
 * Getter:
 *
 * @since Version 1.0.0
 */
const isPopupShowing = state => state.isPopup;

const demoStep = state => state.demoStep;


/*
 * Getter:
 *
 * @since Version 1.0.0
 */
const displayedPopup = state => state.displayedPopup;

/*
 * Getter:
 *
 * @since Version 1.0.0
 */
const metamaskSignRequired = state => state.metamaskSignRequired;

const passwordPrivateKeyModal = state => state.passwordPrivateKeyModal;


/*
 * Getter:
 *
 * @since Version 1.0.0
 */
const showMetamaskNetworkPopup = (state, getters, rootState, rootGetters) => (
  !state.metamaskNetworkPopupCancelled &&
  rootGetters['blockchain/wrongNetworkSelected'] &&
  rootGetters['wallets/activeWalletType'] === METAMASK_WALLET_TYPE
);

/**
 * Return example: { operationType: 'deposit', tokenName: 'KEK', fee: BigNumber(1) }
 *
 * @param state
 * @returns {?{operationType: string, tokenName: string, fee: BigNumber}}
 */
const balanceEthLowPopup = state => state.balanceEthLowPopup;

/*
 * Getter:
 *
 * @since Version 1.0.0
 */
const topModalLevel = state => Math.max(...state.displayedModalLevels);

/*
 * Getter: Возвращающий дату последнего логина пользователя на данной машине
 *
 * @since Version 1.0.0
 */
const getLastLogin = state => state.lastLoginTime;

const loginFlowModal = state => state.loginFlowModal;

/**
 * Getter: Возвращает активна вкладка или нет
 *
 * @since Version 1.0.2
 */
const tabVisibility = state => state.tabVisibility;

/**
 * Getter: Возвращает текущую цветовую тему
 *
 * @since Version 1.4.37
 */
const getTheme = state => state.theme;

/*
 * Getter: true if disconnect popup should be displayed.
 * This popup indicates lack of internet connection.
 */
const isDisconnectPopupDisplayed = (state, getters, rootState) => (
  rootState.internetConnectionStatus === CONNECTION_STATUS.ERROR_SERVICE_DOWN &&
  CONNECTION_STATUSES_SERVICE_DOWN.includes(rootState.backendConnectionStatus) &&
  !state.isNetworkStatusPopupClosed
);

const isStandbyPopupDisplayed = (state, getters, rootState) => (
  rootState.backendConnectionStatus === CONNECTION_STATUS.MANUAL_DISCONNECT &&
  !state.isNetworkStatusPopupClosed
);

/*
 * Getter: true if disconnect popup should be displayed.
 * This popup indicates lack of backend server connection, while Internet connection is OK.
 * These 3 conditions must be seen
 * - соединение с бекендом разорвано
 * - хотя бы одна из 3 проверок на наличие Интернета удалась
 * - 3 раза провалилась попытка переподключиться к бекенду
 */
const isMaintenancePopupDisplayed = (state, getters, rootState) => (
  rootState.internetConnectionStatus === CONNECTION_STATUS.OK &&
  CONNECTION_STATUSES_SERVICE_DOWN.includes(rootState.backendConnectionStatus) &&
  !state.isNetworkStatusPopupClosed
);

const assetType = state => state.assetType;

export default {
  balanceEthLowPopup,
  displayedPopup,
  getLastLogin,
  isPopupShowing,
  demoStep,
  metamaskSignRequired,
  loginFlowModal,
  passwordPrivateKeyModal,
  showMetamaskNetworkPopup,
  tabVisibility,
  getTheme,
  topModalLevel,
  windowSize,
  isDisconnectPopupDisplayed,
  isStandbyPopupDisplayed,
  isMaintenancePopupDisplayed,
  assetType,
};



