import Vue from 'vue';
import { BigNumber } from 'bignumber.js';

/**
 * Converts payload to operation params for submitDeposit, submitWithdrawal, submitTransfer methods.
 *
 * @param tokenAddress token address
 * @param value - value of
 * @param payload - payload for the submit operation action
 * @returns {{tokenAddress: *, value: string | *}}
 */
export const mapPayloadToOperationParams = (tokenAddress, { value, ...rest }) => ({
  tokenAddress,
  value: BigNumber(value).toFixed(),
  ...rest,
});

const findBy = idField => (operations, { userAddress, [idField]: expectedId }) => {
  const userOps = operations[userAddress];
  if (userOps) {
    const index = userOps.findIndex(o => expectedId === o[idField]);
    if (index !== -1) {
      return [userOps, index];
    }
  }
  return [];
};

/**
 * Returns a function that can be used to search operation by transaction hash in specified operations object.
 *
 * @returns function that can be used to search operation by transaction hash in specified operations object
 */
export const findIndexByHash = findBy('transactionHash');

/**
 * Returns a function that can be used to search operation by key in specified operations object.
 *
 * @returns function that can be used to search operation by key in specified operations object
 */
export const findIndexByKey = findBy('key');

/**
 * Adds pending operation to Vuex state.
 *
 * @param state - Vuex state
 * @param userAddress - wallet address that sent a transaction
 * @param operation - pending operation details
 */
export const addPending = (state, userAddress, operation) => {
  const userOps = state.pendingOperations[userAddress];
  if (userOps) {
    userOps.push(operation);
  } else {
    Vue.set(state.pendingOperations, userAddress, [operation]);
  }
};

/**
 * Adds or updates pending operation in Vuex state.
 *
 * @param state - Vuex state
 * @param userAddress - wallet address that sent a transaction
 * @param operation - pending operation details
 */
export const upsertPending = (state, userAddress, operation) => {
  const { transactionHash } = operation;
  const [oldArray, i] = findIndexByHash(state.pendingOperations, { userAddress, transactionHash });
  if (oldArray) {
    Object.assign(oldArray[i], operation);
  } else {
    addPending(state, userAddress, operation);
  }
};



