/**
 * @module store/trades/helpers
 *
 * @version 1.0.7
 */
import last from 'lodash/last';
import { PAGE_SIZE_TRADES } from '@/config';

/**
 * Хелпер добавляет дельты изменения цены к трейдам
 *
 * @param {array} trades - Массив трейдов
 * @returns {array} Массив трейдов к каждому элементо, которого добавлены поля с изменением цены и маркер растет она или падает
 *
 * @since Version 1.0.1
 */
/* eslint-disable no-param-reassign */
export const addPriceDelta = (trades) => {
  if (trades.length > 0) {
    trades[trades.length - 1].priceDelta = '0%';
    trades[trades.length - 1].increase = 0;

    for (let i = trades.length - 2; i >= 0; i -= 1) {
      const delta = (trades[i].price.minus(trades[i + 1].price)).dividedBy(trades[i + 1].price).times(100).dp(3);

      if (delta.isZero()) {
        trades[i].increase = 0;
        trades[i].priceDelta = '0%';
      } else if (delta.isPositive()) {
        trades[i].increase = 1;
        trades[i].priceDelta = `+${delta.toFixed()}%`;
      } else {
        trades[i].increase = -1;
        trades[i].priceDelta = `${delta.toFixed()}%`;
      }
    }
  }

  return trades;
};
/* eslint-enable */

/**
 * Mutates trade with default price delta, calculated from oldTrade.
 * Doesn't mutate oldTrade.
 *
 * @param newTrade trade for which price delta should be calculated and set
 * @param oldTrade trade that is used to calculate price delta. Is not mutated
 */
/* eslint-disable no-param-reassign */
export const setDefaultPriceDelta = (trade) => {
  trade.increase = 0;
  trade.priceDelta = '0%';
};
/* eslint-enable */

/**
 * Mutates newTrade with price delta, calculated from oldTrade.
 * Doesn't mutate oldTrade.
 *
 * @param newTrade trade for which price delta should be calculated and set
 * @param oldTrade trade that is used to calculate price delta. Is not mutated
 */
/* eslint-disable no-param-reassign */
export const updatePriceDelta = (newTrade, oldTrade) => {
  if (newTrade) {
    if (oldTrade) {
      const delta = newTrade.price
        .minus(oldTrade.price)
        .dividedBy(oldTrade.price)
        .times(100)
        .dp(3);
      if (delta.isZero()) {
        setDefaultPriceDelta(newTrade);
      } else if (delta.isPositive()) {
        newTrade.increase = 1;
        newTrade.priceDelta = `+${delta.toFixed()}%`;
      } else {
        newTrade.increase = -1;
        newTrade.priceDelta = `${delta.toFixed()}%`;
      }
    } else {
      setDefaultPriceDelta(newTrade);
    }
  }
};
/* eslint-enable */

/**
 * Helper: Добавлени трейда в ласттрейдс
 *
 * @since Version 1.0.6
 */
export const addTrade = (latestTrades, newTrade) => {
  const newId = newTrade.id;
  let insertionIndex = latestTrades.findIndex(t => t.id <= newId);
  const allTradesHasBiggerId = insertionIndex === -1;
  const isUpdate = !allTradesHasBiggerId && latestTrades[insertionIndex].id === newId;
  const isPush = allTradesHasBiggerId && latestTrades.length < PAGE_SIZE_TRADES;
  const isInsertInMiddle = !allTradesHasBiggerId;

  if (isUpdate) {
    // Обновление существующего трейда
    Object.assign(latestTrades[insertionIndex], newTrade);
  } else if (isPush) {
    // в сторе нету трейдов, которые бы были выполнены раньше того что получили в качестве параметра
    insertionIndex = latestTrades.length;
    latestTrades.push(newTrade);
  } else if (isInsertInMiddle) {
    latestTrades.splice(insertionIndex, 0, newTrade);
    latestTrades.splice(PAGE_SIZE_TRADES, latestTrades.length - PAGE_SIZE_TRADES); // ensure trades.length <= PAGE_SIZE_TRADES
  }

  // price deltas
  if (isUpdate || isPush || isInsertInMiddle) {
    setDefaultPriceDelta(last(latestTrades));
    updatePriceDelta(latestTrades[insertionIndex], latestTrades[insertionIndex + 1]);
    updatePriceDelta(latestTrades[insertionIndex - 1], latestTrades[insertionIndex]);
  }
};



