import { getStorageValue } from '@/util/storage';
import BalanceWorkerBatched from './BalanceWorkerBatched.worker';

let cache;

const APP_ENV = getStorageValue('APP_ENV') || process.env.PROD_ENV;
export const NETWORK_TYPE = process.env.NETWORK_TYPE;

export default () => {
  if (!cache) {
    cache = new BalanceWorkerBatched();
  }

  cache.setEnv(APP_ENV, NETWORK_TYPE);
  return cache;
};



