<template>
  <transition
    name="transition-modal"
    v-on="$listeners"
    :duration="{ enter: enter, leave: leave }"
    appear
  >
    <slot></slot>
  </transition>
</template>

<script>
export default {
  name: 'transition-modal',

  props: {
    enter: {
      type: Number,
      default: 300,
    },

    leave: {
      type: Number,
      default: 300,
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.transition-modal-enter-active, .transition-modal-leave-active {
  .e-modal__body, .e-modal__cover, &.e-alert {
    transition: 0.3s;
    transition-property: transform, opacity;
    will-change: transform, opacity;
  }
}

.transition-modal-enter, .transition-modal-leave-to {
  .e-modal__body, .e-modal__cover, &.e-alert {
    opacity: 0;
  }

  .e-modal__body {
    opacity: 0;
    transform: scale(0.5);
  }

  &.e-alert {
    transform: scale(0.5);
  }
}
</style>



