<template>
  <div class="the-balance-empty">
    <div class="the-balance-empty__title">
      <div class="the-balance-empty__title-icon-wrap">
        <div class="the-balance-empty__title-icon">
          <e-icon :src="iconBalance"/>
        </div>
      </div>
      {{
        {
          [PRIVATE_KEY_TYPE]: 'Wallet',
          [METAMASK_WALLET_TYPE]: 'MetaMask',
          [LEDGER_WALLET_TYPE]: 'Ledger',
        }[walletType]
      }} Balance
    </div>

    <div class="the-balance-empty__main">
      <div class="the-balance-empty__balance">
        <div class="the-balance-empty__balance-icon">
          <e-icon :src="iconEth"/>
          <div class="the-balance-empty__balance-icon-label">ETHEREUM</div>
        </div>

        <div class="the-balance-empty__in">
          <div class="the-balance-empty__section">
            <div class="the-balance-empty__wrapper">
              <div class="the-balance-empty__amount">
                <e-big-number :value="0"/>
              </div>
            </div>
          </div>

          <div class="the-balance-empty__section">
            <div class="the-balance-empty__balance-icon-wrap">
              <div class="the-balance-empty__token-icon">0</div>
              <div class="the-balance-empty__balance-icon-label">ERC 20 Tokens</div>
            </div>

            <div class="the-balance-empty__wrapper">
              <div class="the-balance-empty__amount">
                <e-big-number :value="0"/>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="the-balance-empty__tip">
        <e-icon :src="iconMagnifier"/>
        <div class="the-balance-empty__tip-textual">
          Tip: Deposit your first tokens directly to your Wallet Address
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';

import iconEth from '@/icons/e-icon_eth-value.svg';
import iconBalance from '@/icons/e-icon__balance.svg';
import iconMagnifier from '@/icons/e-icon_magnifier.svg';

export default {
  name: 'the-balance-empty',

  computed: {
    ...mapGetters({
      walletType: 'wallets/activeWalletType',
    }),
  },

  created() {
    Object.assign(this, {
      iconEth,
      iconBalance,
      iconMagnifier,
    });
  },

  components: {
    EBigNumber,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-balance-empty {
  $this: &;
  margin-bottom: 8px;

  &__title {
    align-items: center;
    color: $color-dark-blue;
    display: flex;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 7px;

    @include r(1200) {
      font-size: 16px;
      margin-bottom: 9px;
    }
  }

  &__title-icon-wrap {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0 12px 24px rgba(148, 165, 180, 0.14);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-right: 30px;
    top: -3px;
    left: -4px;

    @include r(1200) {
      height: 50px;
      width: 50px;
      margin-right: 15px;
      left: -8px;
    }
  }

  &__title-icon {
    height: 25px;
    width: 25px;

    @include r(1200) {
      height: 20px;
      width: 20px;
    }
  }

  &__main {
    padding-left: 92px;
    display: flex;
    height: 200px;

    @include r(1200) {
      padding-left: 69px;
    }
  }

  &__balance {
    position: relative;
    padding-left: 84px;

    @include r(1300) {
      padding-left: 70px;
    }

    @include r(1200) {
      padding-left: 63px;
    }
  }

  &__balance-icon {
    position: absolute;
    top: 0;
    left: 0;
    height: 70px;
    width: 70px;
    box-shadow: 0 5px 9px rgba(13, 16, 91, 0.0486583);
    border-radius: 50%;

    @include r(1300) {
      width: 60px;
      height: 60px;
    }

    .e-icon svg {
      /*fill: #BBC6F0;*/
      fill: rgba(13, 16, 91, 0.2);
    }
  }

  &__balance-icon-wrap {
    max-width: 70px;
    margin-right: 11px;

    @include r(1300) {
      margin-right: 0;
    }

    @include r(1200) {
      max-width: 66px;
    }
  }

  &__balance-icon-label {
    padding-top: 13px;
    color: #9ca0a9;
    font-size: 14px;
    line-height: 1.3em;
    text-align: center;
    position: relative;
    left: -3px;

    @include r(1200) {
      font-size: 12px;
      letter-spacing: .86px;
      padding-top: 8px;
    }
  }

  &__in {
    display: flex;
  }

  &__amount {
    .e-big-number {
      color: #0d105b;
      font-size: 40px;
      line-height: 1;
      font-weight: 500;

      @include r(1400) {
        font-size: 30px;
        position: relative;
        top: 5px;
      }

      @include r(1300) {
        font-size: 25px;
        top: 0;
      }

      &__sign,
      &__fraction,
      &__separator {
        position: relative;
        top: -13px;
        left: 1px;
        color: #8384ab;
        font-size: 20px;

        @include r(1200) {
          font-size: 13px;
          left: 3px;
          top: -7px;
        }
      }
    }
  }

  &__section {
    display: flex;
    flex-direction: row;

    &:not(:last-child) {
      margin-right: 38px;

      @include r(1300) {
        margin-right: 15px;
      }

      @include r(1200) {
        margin-right: 45px;
      }
    }

    #{$this}__balance-icon-label {
      left: -2px;
    }
  }

  &__wrapper {
    padding-top: 15px;

    @include r(1200) {
      padding-top: 18px;
    }
  }

  &__token-icon {
    width: 70px;
    height: 70px;
    margin-right: 30px;
    border-radius: 50%;
    font-size: 32px;
    font-weight: 900;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 5px 9px rgba(13, 16, 91, 0.0486583);

    @include r(1300) {
      width: 60px;
      height: 60px;
    }

    @include r(1200) {
      font-size: 26px;
    }
    /*background-color: #0123E7;*/
    /*color: #17DA9D;*/
    background-color: rgba(13, 16, 91, 0.2);
    color: $color-white;
  }

  &__tip {
    max-width: 284px;
    position: relative;
    top: -80px;
    width: 100%;

    .e-icon {
      height: 233px;
      margin: auto;
      width: 233px;

      svg {
        width: 100%;
      }
    }
  }

  &__tip-textual {
    color: $color-dark-blue;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
  }
}
</style>



