const state = {
  // Данные текущей страницы
  activePage: [],

  // Номер текущей отображаемой страницы
  activePageNumber: 0,

  // Общее количество страниц
  numberOfPages: 0,

  /**
   * Флаг, который устанавлвается в `true` когда происходит загрузка страницы истории
   */
  historyPageLoading: false,
};

export default state;



