/**
 * @module store/actions
 *
 * @version 1.1.0
 */

import { APP_VERSION } from '@/config';
import { APP_VERSION_STORAGE_KEY, CONNECTION_STATUS, CONNECTION_STATUSES_SERVICE_DOWN } from '@/store/constants';
import { onIdle, onResume } from '@/util/IdleEmitter';
import { testConnection } from '@/api/frontend';
import { BigNumber } from 'bignumber.js';
import {
  getConfigs,
  onReconnect,
  onDisconnect,
  onConnect,
  onConnectError,
  reconnect as restoreSocket,
  close as closeSocket,
} from '@/api/backend';
import { clearCachedServices } from '@/util/scopes';
import log from '@/util/logger';
import * as types from './mutation-types';
import { wrapCachedAction, resetCachedAction } from './utils';

/**
 * Action: Update app config
 *
 * @since Version 1.0.1
 */
const updateAppConfig = wrapCachedAction('updateAppConfig', async ({ commit, rootGetters, rootState }) => {
  let configs;

  try {
    configs = await getConfigs();
  } catch (err) {
    if (rootGetters.isConnected) {
      log.error(`Error: Get config. Name: ${err.name}. Type: ${err.type}. Message: ${err.message}`, {
        module: 'config',
        layer: 'backend',
      });
    }
    return;
  }

  commit(types.UPDATE_APP_CONFIG, {
    serverTimestamp: configs.server_timestamp,
    gasPriceType: configs.gasPriceType,
  });
  const orderMinEthValue = BigNumber(configs.order_min_eth);
  if (!orderMinEthValue.eq(rootState.activeOrder.orderMinEthValue)) {
    commit('activeOrder/UPDATE_ORDER_MIN_ETH_VALUE', orderMinEthValue);
  }
  commit('blockchain/SET_BACKEND_GAS_LIMITS', {
    withdrawal: parseInt(configs.withdraw_gas_limit_fee, 10),
    tradeTaker: parseInt(configs.trade_taker_gas_limit_fee, 10),
  });
  const fundConfirmations = parseInt(configs.fund_confirmations, 10);
  if (rootGetters['deposit/fundConfirmations'] !== fundConfirmations) {
    commit('deposit/UPDATE_FUND_CONFIRMATIONS', fundConfirmations);
  }
});

// Инициализация стора при загрузке приложения
const initStore = ({ state, commit, dispatch }) => {
  window.localStorage.setItem(APP_VERSION_STORAGE_KEY, APP_VERSION);
  dispatch('ui/getAndUpdateLastLogin');
  dispatch('affiliate/loadReferrer');

  const reconnectHandler = () => {
    resetCachedAction('subscribeToContract');
    resetCachedAction('subscribeToToken');
    resetCachedAction('subscribeToAccount');
    clearCachedServices();
    restoreSocket();
  };
  /* reumse after leaving and returning to tab */
  onResume(reconnectHandler);
  onReconnect(reconnectHandler);
  onIdle(() => {
    commit(types.BACKEND_MANUAL_DISCONNECT);
    if (state.ui.isNetworkStatusPopupClosed) commit('ui/SET_DISCONNECT_POPUP_NOT_CLOSED');
    closeSocket();
  });
  const testConnectionConfigured = async () => {
    const testResult = await testConnection();
    if (testResult && state.internetConnectionStatus !== CONNECTION_STATUS.OK) {
      commit(types.SET_INTERNET_CONNECTED);
    } else if (!testResult && state.internetConnectionStatus !== CONNECTION_STATUS.ERROR_SERVICE_DOWN) {
      commit(types.SET_INTERNET_DISCONNECTED);
      if (state.ui.isNetworkStatusPopupClosed) commit('ui/SET_DISCONNECT_POPUP_NOT_CLOSED');
    }
  };

  // Инициализация соединения с бекендом после разрыва
  onConnect(() => {
    if (state.backendConnectionStatus !== CONNECTION_STATUS.OK) {
      commit(types.SET_BACKEND_CONNECTED);
    }
  });

  onConnectError(async () => {
    if (!CONNECTION_STATUSES_SERVICE_DOWN.includes(state.backendConnectionStatus)) {
      commit(types.BACKEND_CONNECT_ERROR);
      if (state.ui.isNetworkStatusPopupClosed) commit('ui/SET_DISCONNECT_POPUP_NOT_CLOSED');
    }
    await testConnectionConfigured();
  });

  onReconnect(() => {
    if (state.backendConnectionStatus !== CONNECTION_STATUS.OK) {
      commit(types.SET_BACKEND_CONNECTED);
    }
  });

  // both manual and unexpected reconnects
  onDisconnect(async () => {
    commit(types.SET_INTERNET_UNKNOWN);
    if (state.backendConnectionStatus !== CONNECTION_STATUS.MANUAL_DISCONNECT) {
      commit(types.BACKEND_UNEXPECTED_DISCONNECT);
      // no popup while internet connection is checked
      await testConnectionConfigured();
    }
  });
};

export default {
  updateAppConfig,
  initStore,
};



