/**
 * Модуль связывает все Blockchain сервисы, инициализирует их зависимости а так же кеширует их.
 * Для каждого сервиса применяются свои правила кеширования.
 *
 * @module api/ethereum/services
 */
import etmCommonJs from 'ethermium-common-js';
import TimeoutConfirmServiceBasic from '@/api/ethereum/TimeoutConfirmService';
import {
  globalScoped,
  contractScoped,
  userAndContractScoped,
  walletTypeScoped,
  walletTypeAndContractScoped,
} from '@/util/scopes';
import { PRIVATE_KEY_TYPE, METAMASK_WALLET_TYPE, LEDGER_WALLET_TYPE } from '@/util/constants';

import { getWeb3, getWeb3WithEthereumNodeProvider } from './web3';
import ProviderAdapterBasic from './ProviderAdapter';
import BlockObserverBasic from './BlockObserver';
import BalanceBlockchainBasic from './BalanceBlockchain';
import TransactionsBasic from './Transactions';
import ContractsBasic from './Contracts';
import SignaturesBasic from './Signatures';
import PrivateBalanceListenerBasic from './PrivateBalanceListener';
import TokenContractsBasic from './TokenContracts';
import { NonceGeneratorInfura, NonceGeneratorMetamask } from './NonceGenerator';

/**
 * ReadOnlyExchangeContract is not exported outside. ExchangeContract is other's services dependency.
 */
const ReadOnlyExchangeContractCached = contractScoped(
  'ReadOnlyExchangeContract',
  ({ exchangeContract }) => {
    const ExchangeContract = etmCommonJs.contracts.getExchange(exchangeContract.version);
    return new ExchangeContract(
      getWeb3WithEthereumNodeProvider(),
      exchangeContract.contractAddress,
    );
  },
);

const Web3Cached = walletTypeScoped('web3', ({ type, ...params }) => getWeb3(type, params));

const ProviderAdapterCached = walletTypeScoped(
  ProviderAdapterBasic,
  ({ type, ...params }) => {
    const web3 = type ? Web3Cached({ type, ...params }) : undefined;
    return new ProviderAdapterBasic({ web3 });
  },
);

/**
 * ExchangeContract is not exported outside. ExchangeContract is other's services dependency.
 */
const ExchangeContractCached = walletTypeAndContractScoped(
  'ExchangeContract',
  ({ type, exchangeContract }) => {
    const ExchangeContract = etmCommonJs.contracts.getExchange(exchangeContract.version);
    return new ExchangeContract(
      Web3Cached({ type }),
      exchangeContract.contractAddress,
    );
  },
);

const FuturesContractCached = walletTypeAndContractScoped(
  'FuturesContract',
  ({ type, futuresContract }) => {
    const FuturesContract = etmCommonJs.contracts.getFutures(futuresContract.version);
    return new FuturesContract(
      Web3Cached({ type }),
      futuresContract.contractAddress,
    );
  },
);

const BlockObserverCached = globalScoped(
  BlockObserverBasic,
  ({ numberOfConfirmations }) =>
    new BlockObserverBasic({
      web3: getWeb3WithEthereumNodeProvider(),
      numberOfConfirmations,
    }),
);

const ContractsCached = walletTypeAndContractScoped(
  ContractsBasic,
  ({ type, exchangeContract }) =>
    new ContractsBasic({
      exchangeContract: ReadOnlyExchangeContractCached({ type, exchangeContract }),
    }),
);

const ConfirmServiceCached = globalScoped(
  'ConfirmService',
  () => {
    const web3 = getWeb3WithEthereumNodeProvider();
    return new TimeoutConfirmServiceBasic(web3);
  },
);

const userTypeToNonceMap = {
  [PRIVATE_KEY_TYPE]: NonceGeneratorInfura,
  [METAMASK_WALLET_TYPE]: NonceGeneratorMetamask,
  [LEDGER_WALLET_TYPE]: NonceGeneratorInfura,
};

const TransactionsCached = userAndContractScoped(
  TransactionsBasic,
  ({ user, exchangeContract, numberOfConfirmations }) => {
    const type = user.type;
    const web3 = Web3Cached({ type });
    const NonceGenerator = userTypeToNonceMap[user.type];
    return new TransactionsBasic({
      web3,
      user,
      exchangeContract: ExchangeContractCached({ type, exchangeContract }),
      nonceGenerator: new NonceGenerator({ web3, user }),
      confirmService: ConfirmServiceCached(),
      numberOfConfirmations,
    });
  },
);

const BalanceBlockchainCached = userAndContractScoped(
  BalanceBlockchainBasic,
  ({ user, exchangeContract }) => {
    const type = user.type;
    return new BalanceBlockchainBasic({
      userAddress: user.address,
      exchangeContract: ReadOnlyExchangeContractCached({ type, exchangeContract }),
    });
  },
);

const SignaturesCached = userAndContractScoped(
  SignaturesBasic,
  ({ user, exchangeContract, futuresContract, isMetamaskLedger }) => {
    const type = user.type;
    return new SignaturesBasic({
      web3: Web3Cached({ type }),
      user,
      exchangeContract: ExchangeContractCached({ type, exchangeContract }),
      futuresContract: FuturesContractCached({ type, futuresContract }),
      isMetamaskLedger,
    });
  },
);

const PrivateBalanceListenerCached = userAndContractScoped(
  PrivateBalanceListenerBasic,
  ({ user, exchangeContract, numberOfConfirmations }) =>
    new PrivateBalanceListenerBasic({
      balanceApi: BalanceBlockchainCached({
        user,
        exchangeContract,
        numberOfConfirmations,
      }),
      blockObserver: BlockObserverCached({
        numberOfConfirmations,
      }),
    }),
);

const TokenContractCached = globalScoped(
  TokenContractsBasic,
  () => {
    const web3 = getWeb3WithEthereumNodeProvider();
    return new TokenContractsBasic({ web3 });
  },
);

export {
  Web3Cached as Web3,
  ProviderAdapterCached as ProviderAdapter,
  ContractsCached as Contracts,
  TransactionsCached as Transactions,
  BalanceBlockchainCached as BalanceBlockchain,
  BlockObserverCached as BlockObserver,
  SignaturesCached as Signatures,
  PrivateBalanceListenerCached as PrivateBalanceListener,
  ConfirmServiceCached as ConfirmService,
  TokenContractCached as TokenContracts,
};



