/**
 * @module store/trades/getters
 * @version 1.0.6
 */

import { ASYNC_STATES, BUY_TOKEN_ORDER, ETH_DECIMALS } from '@/util/constants';

/**
 * Getter: Возвращает текущую страницу трейдов для отображения
 *
 * @since Version 1.0.4
 */
const latestTrades = state => state.latestTrades;

/**
 * Getter: Возвращает загружается сейчас информация по странице тредов или нет
 *
 * @since Version 1.0.5
 */
const latestTradesLoading = state => state.latestTradesLoading;


/*
 * Геттер возвращающий детали трейда для отображения в попапе делателй трейда
 *
 * @since Version 1.0.1
 */
const currentTradeDetails = (state, getters, rootState, rootGetters) => {
  const trade = state.selectedTrade;
  if (trade == null) return null;

  const token = rootGetters['tokens/tokenByContract'](trade.tokenAddress);
  const isTaker = trade.takerAddress === rootGetters['wallets/activeWalletAddress'];
  return {
    id: trade.id,
    type: trade.type,
    isConfirmed: trade.isConfirmed,
    token,
    unitPrice: trade.unitPrice,
    tokenAmount: trade.type === BUY_TOKEN_ORDER ? trade.takerAmountFilled : trade.makerAmountFilled,
    ethAmount: trade.type === BUY_TOKEN_ORDER ? trade.makerAmountFilled : trade.takerAmountFilled,
    transactionHash: trade.transactionHash,
    isTaker,
    maker: {
      give: {
        amount: trade.takerAmountFilled,
        tokenShortName: trade.type === BUY_TOKEN_ORDER ? token.shortName : 'ETH',
        decimals: trade.type === BUY_TOKEN_ORDER ? token.decimals : ETH_DECIMALS,
      },
      take: {
        amount: trade.makerAmountTaken,
        tokenShortName: trade.type === BUY_TOKEN_ORDER ? 'ETH' : token.shortName,
        fee: trade.makerAmountFee || trade.makerAmountFilled.minus(trade.makerAmountTaken),
        decimals: trade.type === BUY_TOKEN_ORDER ? ETH_DECIMALS : token.decimals,
      },
    },
    taker: {
      give: {
        amount: trade.makerAmountFilled,
        tokenShortName: trade.type === BUY_TOKEN_ORDER ? 'ETH' : token.shortName,
        decimals: trade.type === BUY_TOKEN_ORDER ? ETH_DECIMALS : token.decimals,
      },
      take: {
        amount: trade.takerAmountTaken,
        tokenShortName: trade.type === BUY_TOKEN_ORDER ? token.shortName : 'ETH',
        fee: trade.takerAmountFee || trade.takerAmountFilled.minus(trade.takerAmountTaken).minus(trade.takerGasFee),
        gasFee: trade.takerGasFee,
        decimals: trade.type === BUY_TOKEN_ORDER ? token.decimals : ETH_DECIMALS,
      },
    },
  };
};

/**
 * Getter: Возвращает какие треды отображаем все или отлько внутренние
 *
 * @returns {boolean}
 *
 * @since Version 1.0.6
 */
const showOnlyInternalTrades = state => state.showOnlyInternalTrades;

const isVolumesInitSuccess = state => state.volumesInitState === ASYNC_STATES.SUCCESS;

const isVolumesInitError = state => state.volumesInitState === ASYNC_STATES.ERROR;

export default {
  currentTradeDetails,
  latestTrades,
  latestTradesLoading,
  showOnlyInternalTrades,
  isVolumesInitSuccess,
  isVolumesInitError,
};



