<template>
  <e-notification
    status="success"
    :headline="`${capitalizeFirstLetter(operation)} confirmed`"
    v-bind="$attrs"
    v-on="$listeners"
  >
    {{ capitalizeFirstLetter(operation) }} of
    <e-big-number
      :value="value"
      :decimals="token.decimals"
      :max-decimal-places="token.contract === ETH_ADDRESS ? ETH_DISPLAYED_DECIMALS : null"
    />
    {{ token.shortName }} confirmed
  </e-notification>
</template>

<script>
import { BigNumber } from 'bignumber.js';

import EBigNumber from '@/components/EBigNumber';
import ENotification from '@/components/ENotification';
import { capitalizeFirstLetter } from '@/util/filters';

export default {
  name: 'e-notification-balance-success',
  inheritAttrs: false,

  props: {
    operation: {
      type: String,
      required: true,
    },
    value: {
      type: [Number, String, BigNumber],
      required: true,
    },
    token: {
      type: Object,
      required: true,
      validator: token => token && token.contract && token.decimals != null && token.shortName,
    },
  },

  methods: {
    capitalizeFirstLetter,
  },

  components: { ENotification, EBigNumber },
};
</script>



