<template>
  <button
    class="e-button"
    :type="htmlType"
    :disabled="disabled"
    :class="[
      type ? 'e-button_type_' + type : 'e-button_type_' + 'default',
      size ? 'e-button_size_' + size : 'e-button_size_' + 'default',
      padding ? 'e-button_padding_' + padding : 'e-button_padding_' + 'default',
      fill ? 'e-button_fill_' + fill : 'e-button_fill_' + 'default',
      iconMargin ? 'e-button_icon-margin_' + iconMargin : 'e-button_icon-margin_' + 'default',
      fontWeight ? 'e-button_font-weight_' + fontWeight : 'e-button_font-weight_' + 'default',
      fontSize ? 'e-button_font-size_' + fontSize : 'e-button_font-size_' + 'default',
      textTransform ? 'e-button_text-transform_' + textTransform : 'e-button_text-transform_' + 'default',
      iconPosition ? 'e-button_icon-position_' + iconPosition : '',
      width ? 'e-button_width_' + width : '',
      letterSpacing ? 'e-button_letter-spacing' : '',
      adaptiveMod ? 'e-button_adaptive_' + adaptiveMod : '',
      iconRight ? 'e-button_icon_right' : '',
      iconSize ? 'e-button_icon_size_' + iconSize : '',
      color ? 'e-button_color_' + color : '',
      error ? 'e-button_error' : '',
      `e-button__theme-${theme}`
    ]"
    v-on="$listeners"
  >
    <e-icon :src="icon" v-if="icon" @click="handleInnerClick"/>
    <slot></slot>
    <e-icon :src="iconRight" v-if="iconRight" @click="handleInnerClick"/>
  </button>
</template>

<script>
/**
 * Component: EButton
 *
 * @version 1.0.0
 */

import { mapGetters } from 'vuex';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-button',

  props: {
    htmlType: {
      type: String,
      default: 'button',
    },

    /**
     * Тип кнопки
     *
     */
    type: {
      type: String,
      default: 'default',
    },

    size: {
      type: String,
      default: 'default',
    },

    padding: {
      type: String,
      default: 'default',
    },

    fill: {
      type: String,
      default: 'default',
    },

    fontWeight: {
      type: String,
      default: 'default',
    },

    fontSize: {
      type: String,
      default: 'default',
    },

    textTransform: {
      type: String,
      default: 'default',
    },

    iconPosition: {
      type: String,
      default: null,
    },

    width: {
      type: String,
      default: null,
    },

    icon: {
      type: String,
      default: null,
    },

    iconRight: {
      type: String,
      default: null,
    },

    mod: {
      type: String,
      default: null,
    },

    iconMargin: {
      type: String,
      default: 'default',
    },

    letterSpacing: {
      type: Boolean,
      default: false,
    },

    adaptiveMod: {
      type: String,
      default: '',
    },

    iconSize: {
      type: String,
      default: '',
    },

    color: {
      type: String,
      default: '',
    },

    disabled: {
      type: Boolean,
      default: false,
    },

    error: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  methods: {
    handleClick(evt) {
      this.$emit('click', evt);
    },

    handleInnerClick(evt) {
      if (this.disabled) {
        evt.stopPropagation();
      }
    },
  },

  components: {
    EIcon,
  },
};
</script>

