/* eslint-disable no-restricted-syntax */
import Vue from 'vue';
import { BigNumber } from 'bignumber.js';
import { ETH_ADDRESS } from '@/util/constants';
import { ZERO } from '@/util/numbers';
import * as types from '@/store/mutation-types';

const updatePrivateBalance = (state, { eth, tokens } = {}) => {
  if (eth != null) {
    state.privateBalance.eth = eth;
  }
  if (tokens) {
    for (const [token, balance] of Object.entries(tokens)) {
      if (BigNumber(balance).isZero()) {
        Vue.delete(state.privateBalance.tokens, token);
      } else {
        Vue.set(state.privateBalance.tokens, token, balance);
      }
    }
  }
};

const updateTradableBalance = (state, { balance, reserved } = {}) => {
  if (balance) {
    for (const [token, tokenBalance] of Object.entries(balance)) {
      if (BigNumber(tokenBalance).isZero()) {
        Vue.delete(state.tradableBalance, token);
      } else {
        Vue.set(state.tradableBalance, token, tokenBalance);
      }
    }
  }

  if (reserved) {
    for (const [token, tokenReserved] of Object.entries(reserved)) {
      if (BigNumber(tokenReserved).isZero()) {
        Vue.delete(state.reserved, token);
      } else {
        Vue.set(state.reserved, token, tokenReserved);
      }
    }
  }
};

const updateAvailableBalance = (state, { balance } = {}) => {
  if (balance) {
    Vue.set(state, 'availableBalance', balance.availableBalance);
  }
};

const mutations = {
  [types.SET_PRIVATE_BALANCE_ASYNC.PENDING]: (state) => {
    state.privateLoading = true;
    state.privateBalance.eth = BigNumber(0);
    state.privateBalance.tokens = {};
  },

  [types.SET_PRIVATE_BALANCE_ASYNC.SUCCESS]: (state, { eth, tokens } = {}) => {
    state.privateLoaded = true;
    state.privateLoading = false;
    state.privateBalance.eth = eth || BigNumber(0);
    state.privateBalance.tokens = tokens || {};
  },

  [types.SET_PRIVATE_BALANCE_ASYNC.FAILURE]: (state) => {
    state.privateLoaded = true;
    state.privateLoading = false;
    state.privateBalance.eth = BigNumber(0);
    state.privateBalance.tokens = {};
  },

  [types.SET_TRADABLE_BALANCE_ASYNC.PENDING]: (state) => {
    state.availableLoading = true;
    state.tradableLoading = true;
    state.tradableBalance = { [ETH_ADDRESS]: ZERO };
    state.reserved = { [ETH_ADDRESS]: ZERO };
  },

  [types.SET_TRADABLE_BALANCE_ASYNC.SUCCESS]: (state, payload) => {
    state.availableLoading = false;
    state.tradableLoaded = true;
    state.tradableLoading = false;
    state.tradableBalance = (payload && payload.balance) || { [ETH_ADDRESS]: ZERO };
    state.reserved = (payload && payload.reserved) || { [ETH_ADDRESS]: ZERO };
  },

  [types.SET_TRADABLE_BALANCE_ASYNC.FAILURE]: (state) => {
    state.availableLoading = false;
    state.tradableLoaded = true;
    state.tradableLoading = false;
    state.tradableBalance = { [ETH_ADDRESS]: ZERO };
    state.reserved = { [ETH_ADDRESS]: ZERO };
  },

  [types.RELOAD_PRIVATE_BALANCE_ASYNC.PENDING]: (state) => {
    state.privateReloading = true;
  },

  [types.RELOAD_PRIVATE_BALANCE_ASYNC.FAILURE]: (state) => {
    state.privateLoaded = true;
    state.privateReloading = false;
    state.privateBalance.eth = BigNumber(0);
    state.privateBalance.tokens = {};
  },

  [types.RELOAD_PRIVATE_BALANCE_ASYNC.SUCCESS]: (state, { eth, tokens } = {}) => {
    state.privateLoaded = true;
    state.privateReloading = false;
    state.privateBalance.eth = eth || BigNumber(0);
    state.privateBalance.tokens = tokens || {};
  },

  [types.RELOAD_TRADABLE_BALANCE_ASYNC.PENDING]: (state) => {
    state.tradableReloading = true;
  },

  [types.RELOAD_TRADABLE_BALANCE_ASYNC.SUCCESS]: (state, payload) => {
    state.tradableLoaded = true;
    state.tradableReloading = false;
    state.tradableBalance = (payload && payload.balance) || { [ETH_ADDRESS]: ZERO };
    state.reserved = (payload && payload.reserved) || { [ETH_ADDRESS]: ZERO };
  },

  [types.RELOAD_TRADABLE_BALANCE_ASYNC.FAILURE]: (state) => {
    state.tradableLoaded = true;
    state.tradableReloading = false;
    state.tradableBalance = { [ETH_ADDRESS]: ZERO };
    state.reserved = { [ETH_ADDRESS]: ZERO };
  },

  [types.UPDATE_PRIVATE_BALANCE]: updatePrivateBalance,

  [types.UPDATE_TRADABLE_BALANCE]: updateTradableBalance,

  [types.UPDATE_AVAILABLE_BALANCE]: updateAvailableBalance,

  [types.UPDATE_BALANCE]: (state, { privateBalance, tradableBalance, availableBalance }) => {
    updatePrivateBalance(state, privateBalance);
    updateTradableBalance(state, tradableBalance);
    updateAvailableBalance(state, availableBalance);
  },

  [types.SET_BALANCE_IRRELEVANT](state) {
    state.privateLoading = false;
    state.privateReloading = false;
    state.privateLoaded = false;
    state.tradableLoaded = false;
    state.tradableReloading = false;
    state.tradableLoaded = false;
  },

  [types.ADD_PRIVATE_BALANCE_LISTENER](state) {
    state.privateListenersCount += 1;
  },

  [types.REMOVE_PRIVATE_BALANCE_LISTENER](state) {
    state.privateListenersCount += 1;
  },

  [types.ACTIVATE_PRIVATE_BALANCE_SUBSCRIPTION](state) {
    state.privateSubscriptionActive = true;
  },

  [types.DEACTIVATE_PRIVATE_BALANCE_SUBSCRIPTION](state) {
    state.privateSubscriptionActive = false;
  },

  [types.ADD_CONTRACT_BALANCE_LISTENER](state) {
    state.contractListenersCount += 1;
  },

  [types.REMOVE_CONTRACT_BALANCE_LISTENER](state) {
    state.contractListenersCount -= 1;
  },

  [types.ACTIVATE_CONTRACT_BALANCE_SUBSCRIPTION](state) {
    state.contractSubscriptionActive = true;
  },

  [types.DEACTIVATE_CONTRACT_BALANCE_SUBSCRIPTION](state) {
    state.contractSubscriptionActive = false;
  },

  [types.SET_BALANCE_ETH_INIT_STATUS](state, status) {
    state.ethInitStatus = status;
  },

  [types.SET_BALANCE_TOKEN_INIT_STATUS](state, status) {
    state.tokenInitStatus = status;
  },

  [types.SET_BASE_TOKEN](state, baseToken) {
    state.baseToken = baseToken;
  },

  [types.SET_BASE_BTC_TOKEN](state, btcBaseToken) {
    state.btcBaseToken = btcBaseToken;
  },
};

export default mutations;



