import * as types from '@/store/mutation-types';
import { getHistoryPage } from '@/api/backend';

/**
 * Экшин для получения аффлированных трейдов
 *
 * @param {number} pageNumber - Номер страницы для отображения
 * @param {section} section - секция для отображения
 */
// TODO: N/A prices
const getHistory = async ({ commit, rootGetters, dispatch }, { pageNumber = 0, section }) => {
  commit(types.GET_HISTORY_ASYNC.PENDING);
  await dispatch('contracts/loadActiveExchangeContract', undefined, { root: true });
  const userAddress = rootGetters['wallets/activeWalletAddress'];
  const contractAddress = rootGetters['contracts/activeMainContractAddress'];

  if (userAddress) {
    let pageCount;
    let pageData;
    try {
      ({ pageData, pageCount } = await getHistoryPage(
        { userAddress, contractAddress, section, pageNumber },
        { activeToken: rootGetters['tokens/activeToken'] },
      ));
      commit(types.GET_HISTORY_ASYNC.SUCCESS, { pageNumber, pageData, pageCount });
    } catch (error) {
      commit(types.GET_HISTORY_ASYNC.FAILURE, error);
    }
  } else {
    commit(types.GET_HISTORY_ASYNC.SUCCESS, { pageNumber: 0, pageData: [], pageCount: 0 });
  }
};

export default {
  getHistory,
};



