import { BigNumber } from 'bignumber.js';

export const MARKET_PRICE_PERCENT_DELTA = BigNumber(0.3);

/**
 * Перед тем как отправить deposit/withdrawal/transfer в блокчейн, мы на FrontEnd оцениваем сколько газа она будет потреблять.
 * Чтобы транзакция успешно завершилась пользователь должен иметь достаточно ETH чтобы заплатить за газ.
 * Полученное в результате предварительной оценки количество газа умножаем на это число.
 * Для estimatedGas Fee + 20% => 1.2
 */
export const GAS_LIMIT_MULTIPLIER = BigNumber(1.2);

// Orders

// Множитель для основной суммы ордера 0.3% => 0.003
export const SERVICE_FEE_MULTIPLIER = BigNumber(0.003);

/**
 * Сумма в wei, снимаемая блокчейном за проведение транзакции.
 * На данный момент отсутствует.
 */
export const TRANSACTION_FEE = BigNumber(0);

// List Token

/**
 * List Token
 * @type {number}
 */
export const LIST_TOKEN_PRICE = BigNumber('5e18');

/**
 * Prevent auto deposit if value < fee * AUTO_DEPOSIT_MIN_FEES_IN_VALUE.
 */
export const AUTO_DEPOSIT_MIN_FEES_IN_VALUE = BigNumber(1);



