<template>
  <e-popup-info
    class="the-popup-balance-low"
    :header="params.header"
    @close="close"
  >
    <template slot="message">
      <div class="the-popup-balance-low__icon">
        <e-icon :src="iconEthLarge"/>
      </div>
      {{ params.title }}
    </template>
    <template slot="details">
      {{ params.message }}
    </template>
    <template slot="info">
      <p>Make sure you cancel your orders before withdrawing.</p>
    </template>
  </e-popup-info>
</template>

<script>
/**
 * Component: Компонент попапа с ошбками проведения операций депозита, видровла, трансфера
 *
 * @version 1.0.2
 */

import { mapGetters, mapMutations } from 'vuex';
import iconEthLarge from '@/icons/e-icon_eth-large.svg';
import { operationTypeAction } from '@/util/filters';

// Components
import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';
import EBigNumber from '@/components/EBigNumber';
import EPopupInfo from '@/components/EPopupInfo';

// Tansitions
import TransitionModal from '@/containers/transitions/TransitionModal';


export default {
  name: 'ThePopupBalanceLow',
  computed: {
    operationType: vm => operationTypeAction(vm.params.operationType),

    ...mapGetters({
      params: 'ui/balanceEthLowPopup',
    }),
  },

  created() {
    Object.assign(this, { iconEthLarge });
  },

  methods: {
    ...mapMutations({
      close: 'ui/HIDE_BALANCE_ETH_LOW_POPUP',
    }),
  },
  components: {
    EBigNumber,
    EIcon,
    EModal,
    EModalTrades,
    EPopupInfo,
    TransitionModal,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-popup-balance-low {
  &__icon {
    width: 90px;
    height: 90px;
    margin: 0 auto 24px;
  }

  &__min-balance {
    color: $color-blue;
    font-weight: 700;
  }
}
</style>



