/* eslint-disable no-restricted-syntax */
import toDisplayedPrice from '@/util/format/toDisplayedPrice';
import last from 'lodash/last';
import { ZERO } from '@/util/numbers';
import { BigNumber } from 'bignumber.js';

export const compressOrderBookElements = (orderBookElements) => {
  if (!orderBookElements.length) return [];
  const compressed = [
    [ZERO, orderBookElements[0][1]],
  ];
  for (const [curAmount, curPrice] of orderBookElements) {
    const [prevAmount, prevPrice] = last(compressed);
    if (curPrice.eq(prevPrice)) {
      compressed.pop();
      compressed.push([curAmount.plus(prevAmount), curPrice]);
    } else {
      compressed.push([curAmount, curPrice]);
    }
  }
  return compressed;
};

export const mapOrderInOrderBook = (order, decimals) => ([
  BigNumber(order[0]),
  toDisplayedPrice(order[1], decimals),
  order[2],
]);



