import { CONNECTION_STATUS, MAINTENANCE_CONNECTION_ATTEMPTS } from './constants';
import * as types from './mutation-types';

export default {
  [types.UPDATE_APP_CONFIG](state, configs) {
    Object.assign(state, configs);
  },

  [types.SET_INTERNET_UNKNOWN](state) {
    state.internetConnectionStatus = CONNECTION_STATUS.ERROR_UNSPECIFIED;
  },

  [types.SET_INTERNET_CONNECTED](state) {
    state.internetConnectionStatus = CONNECTION_STATUS.OK;
  },

  [types.SET_INTERNET_DISCONNECTED](state) {
    state.internetConnectionStatus = CONNECTION_STATUS.ERROR_SERVICE_DOWN;
  },

  [types.SET_BACKEND_CONNECTED](state) {
    state.internetConnectionStatus = CONNECTION_STATUS.OK;
    state.backendConnectionStatus = CONNECTION_STATUS.OK;
    state.backendConnectionFailedAttempts = 0;
  },

  /**
   * Backend connection attempt failed
   * @param state
   */
  [types.BACKEND_CONNECT_ERROR](state) {
    state.backendConnectionFailedAttempts += 1;
    if (state.backendConnectionFailedAttempts === MAINTENANCE_CONNECTION_ATTEMPTS) {
      if (state.backendConnectionStatus === CONNECTION_STATUS.NOT_CONNECTED) {
        state.backendConnectionStatus = CONNECTION_STATUS.ERROR_ON_INIT;
      } else {
        state.backendConnectionStatus = CONNECTION_STATUS.ERROR_SERVICE_DOWN;
      }
    }
  },

  /**
   * Backend is manually disconnected. I.e. because of standby more.
   * @param state
   */
  [types.BACKEND_MANUAL_DISCONNECT](state) {
    state.backendConnectionStatus = CONNECTION_STATUS.MANUAL_DISCONNECT;
    state.backendConnectionFailedAttempts = 0;
  },

  /**
   * Backend is unexpectedly disconnected. I.e. because of user Internet problems.
   * @param state
   */
  [types.BACKEND_UNEXPECTED_DISCONNECT](state) {
    state.backendConnectionStatus = CONNECTION_STATUS.ERROR_UNSPECIFIED;
    state.backendConnectionFailedAttempts = 0;
  },
};



