import { BigNumber } from 'bignumber.js';
import { helpers } from 'vuelidate/lib/validators';

export default helpers.withParams(
  { type: 'greaterThanZero' },
  (value) => {
    const isEmpty = !helpers.req(value);
    const num = BigNumber(value);
    return isEmpty || (!num.isNaN() && num.gt(0));
  },
);



