import { withRetryForAsync } from '@/util/withRetry';

/**
 * @property web3 {Web3}
 * @property numberOfConfirmations {number}
 */
export default class BlockObserver {
  constructor({ web3, numberOfConfirmations }) {
    this.web3 = web3;
    this.numberOfConfirmations = numberOfConfirmations;
    this.getLatestUnstable = this.getLatestUnstable.bind(this);
  }

  /**
   * Returns latest approved mined block
   * Approved is a block that was mined N blocks before latest.
   * N - is confirmation number.
   *
   * @returns {Promise<number>}
   */
  async getApprovedBlockNumber() {
    return this.getLatest();
  }

  /**
   * Returns latest mined block
   *
   * @returns {Promise<number>}
   */
  getLatest() {
    return withRetryForAsync(this.getLatestUnstable);
  }

  /**
   * Returns latest mined block
   *
   * @private
   * @returns {Promise<number>}
   */
  getLatestUnstable() {
    return this.web3.eth.getBlockNumber();
  }
}



