/* eslint-disable no-restricted-syntax */
import { BigNumber } from 'bignumber.js';

export default (balance) => {
  const { privateBalance, tradableBalance } = balance;
  if (privateBalance) {
    const eth = privateBalance.eth;
    if (eth) {
      privateBalance.eth = BigNumber(eth);
    }
    const tokens = privateBalance.tokens;
    if (tokens) {
      for (const contract of Object.keys(tokens)) {
        tokens[contract] = BigNumber(tokens[contract]);
      }
    }
  }
  // if (tradableBalance) {
  //   const eth = tradableBalance.eth;
  //   if (eth) {
  //     tradableBalance.eth = BigNumber(eth);
  //   }
  //   const tokens = tradableBalance.tokens;
  //   if (tokens) {
  //     for (const contract of Object.keys(tokens)) {
  //       tokens[contract] = BigNumber(tokens[contract]);
  //     }
  //   }
  // }
  if (tradableBalance) {
    for (const contract of Object.keys(tradableBalance)) {
      tradableBalance[contract] = BigNumber(tradableBalance[contract]);
    }
  }
  return balance;
};



