import Vue from 'vue';
import Router from 'vue-router';
import Meta from 'vue-meta';
import get from 'lodash/get';
import { isValidHex } from '@/util/crypto';
import store from '@/store';
import { APP_ENV } from '@/config';
import log from '@/util/logger';
import { NOT_FOUND_PAGE } from '@/util/constants';
import { CONNECTION_STATUS } from '@/store/constants';
import { setStorageValue, getStorageValue } from '@/util/storage';
import getPopupPages from './getPopupPages';
import fetchDefaultToken from './fetchDefaultToken';
import fetchDefaultContract from './fetchDefaultContract';
import affiliate from './affiliate';
import utm from './utm';

const PageFutures = () => import(/* webpackChunkName: "futures" */ '@/containers/pages/PageFutures');
const Page404 = () => import(/* webpackChunkName: "page404" */ '@/containers/pages/Page404');
const PageNews = () => import(/* webpackChunkName: "PageNews" */ '@/containers/pages/PageNews');
const PagePostSecond = () => import(/* webpackChunkName: "PagePostSecond" */ '@/containers/pages/PagePostSecond');
const PageNewsFuturesContract = () => import(/* webpackChunkName: "PageNewsFuturesContract" */ '@/containers/pages/PageNewsFuturesContract');
const PageSmartContracts = () => import(/* webpackChunkName: "PageSmartContracts" */ '@/containers/pages/PageSmartContracts');
const PageAboutUs = () => import(/* webpackChunkName: "PageAboutUs" */ '@/containers/pages/PageAboutUs');
const PageAbout = () => import(/* webpackChunkName: "PageAbout" */ '@/containers/pages/PageAbout');
const PageAboutEthermium = () => import(/* webpackChunkName: "PageAboutEthermium" */ '@/containers/pages/PageAboutEthermium');
const PageAboutDecentralization = () => import(/* webpackChunkName: "PageAboutDecentralization" */ '@/containers/pages/PageAboutDecentralization');
const PageSupport = () => import(/* webpackChunkName: "PageSupport" */ '@/containers/pages/PageSupport');
const PageFees = () => import(/* webpackChunkName: "PageFees" */ '@/containers/pages/PageFees');
const PageNewsTradingView = () => import(/* webpackChunkName: "PageFees" */ '@/containers/pages/PageNewsTradingView');
const PageNewsDarkMode = () => import(/* webpackChunkName: "PageFees" */ '@/containers/pages/PageNewsDarkMode');
const PageLogin = () => import(/* webpackChunkName: "PageLogin" */ '@/containers/pages/PageLogin');
const PageGenerateWallet = () => import(/* webpackChunkName: "PageGenerateWallet" */ '@/containers/pages/PageGenerateWallet');
const PageWallet = () => import(/* webpackChunkName: "PageWallet" */ '@/containers/pages/PageWallet');
const PageFaq = () => import(/* webpackChunkName: "PageFees" */ '@/containers/pages/PageFaq');
const PageHelp = () => import(/* webpackChunkName: "PageHelp" */ '@/containers/pages/PageHelp');
const PageHistory = () => import(/* webpackChunkName: "PageHelp" */ '@/containers/pages/PageHistory');
const PageAffiliateHistory = () => import(/* webpackChunkName: "PageHelp" */ '@/containers/pages/PageAffiliateHistory');
const PageContracts = () => import(/* webpackChunkName: "PageHelp" */ '@/containers/pages/PageContracts');
const PageIndexLanding = () => import(/* webpackChunkName: "PageHelp" */ '@/containers/pages/PageIndexLanding');

Vue.use(Router);
Vue.use(Meta);

/**
 * Список роутов для которых может возникать попап
 */
const BACKGROUND_ROUTES_FOR_POPUP = ['index', 'landing', 'tokens', 'token', 'main', 'signup', 'create-wallet', 'wallet', 'help', 'history', 'contracts', 'news', 'postsecond', 'aboutus', 'about', 'source-code', 'fees', 'faq', 'futures-contract', 'tradingview', 'dark-mode', 'aboutethermium', 'aboutdecenralization', 'support', 'future'];

const routes = [
  {
    path: '/',
    name: 'index',
    component: PageIndexLanding,
    children: getPopupPages('index'),
    // beforeEnter: (to, from, next) => {
    //   if (from.name !== 'future') {
    //     next({ name: 'futures' });
    //   }
    //
    //   // Try to send UTM request by using query string
    //   utm(to.query).catch((err) => {
    //     log.error(`Error: UTM request failed. Name: ${err.name}. Type: ${err.type}. Message: ${err.message}`, {
    //       module: 'router',
    //       layer: 'backend',
    //     });
    //   });
    //
    //   // On test env. redirect user to trading page
    //   if (APP_ENV === 'test') {
    //     // next({ name: 'futures' });
    //     next();
    //   } else {
    //     next();
    //   }
    // },
  },
  {
    path: '/404',
    name: NOT_FOUND_PAGE,
    component: Page404,
  },
  {
    path: '/history',
    name: 'history',
    component: PageHistory,
    children: getPopupPages('history'),
  },
  {
    path: '/affiliate-history',
    name: 'affiliate-history',
    component: PageAffiliateHistory,
  },
  {
    path: '/contracts',
    name: 'contracts',
    component: PageContracts,
    children: getPopupPages('contracts'),
    beforeEnter: async (to, from, next) => {
      await fetchDefaultContract();
      next();
    },
  },
  {
    path: '/signup',
    name: 'signup',
    component: PageLogin,
    children: getPopupPages('signup'),
  },
  {
    path: '/create-wallet',
    name: 'create-wallet',
    component: PageGenerateWallet,
    children: getPopupPages('create-wallet'),
  },
  {
    path: '/wallet/:walletToken?',
    name: 'wallet',
    component: PageWallet,
    props: route => ({ tokenAddress: route.params.walletToken }),
    children: getPopupPages('wallet'),
  },
  {
    path: '/news',
    name: 'news',
    component: PageNews,
    children: getPopupPages('news'),
  },
  {
    path: '/faq',
    name: 'faq',
    component: PageFaq,
    children: getPopupPages('faq'),
  },
  {
    path: '/help',
    name: 'help',
    component: PageHelp,
    children: getPopupPages('help'),
  },
  {
    path: '/ledger-nano-s-wallet-now-supported',
    name: 'postsecond',
    component: PagePostSecond,
    children: getPopupPages('postsecond'),
  },
  {
    path: '/about-us',
    name: 'aboutus',
    component: PageAboutUs,
    children: getPopupPages('aboutus'),
  },
  {
    path: '/about',
    name: 'about',
    component: PageAbout,
    children: getPopupPages('about'),
  },
  {
    path: '/source-code',
    name: 'source-code',
    component: PageSmartContracts,
    children: getPopupPages('source-code'),
  },
  {
    path: '/what-is-dmex',
    name: 'aboutethermium',
    component: PageAboutEthermium,
    children: getPopupPages('aboutethermium'),
  },
  {
    path: '/what-is-decentralization',
    name: 'aboutdecenralization',
    component: PageAboutDecentralization,
    children: getPopupPages('aboutdecenralization'),
  },
  {
    path: '/futures-contract',
    name: 'futures-contract',
    component: PageNewsFuturesContract,
    children: getPopupPages('futures-contract'),
  },
  {
    path: '/tradingview',
    name: 'tradingview',
    component: PageNewsTradingView,
    children: getPopupPages('tradingview'),
  },
  {
    path: '/dark-mode',
    name: 'dark-mode',
    component: PageNewsDarkMode,
    children: getPopupPages('dark-mode'),
  },
  {
    path: '/contact-us',
    name: 'support',
    component: PageSupport,
    children: getPopupPages('support'),
  },
  {
    path: '/fees',
    name: 'fees',
    component: PageFees,
    children: getPopupPages('fees'),
  },
  // {
  //   path: '/a/:aff/t/:token',
  //   beforeEnter: (to, from, next) => {
  //     const { aff, token } = to.params;
  //     affiliate(aff); // async
  //     next({ name: 'token', params: { token } });
  //   },
  // },
  {
    path: '/a/:aff',
    beforeEnter: (to, from, next) => {
      affiliate(to.params.aff); // async
      next({ name: 'index' });
    },
  },
  {
    path: '/f/:contract',
    name: 'future',
    component: PageFutures,
    beforeEnter: async (to, from, next) => {
      // TODO: Try to remove this portion of code
      const token = await fetchDefaultToken();
      await store.dispatch('tokens/setActiveToken', token);

      const { contract: paramContract } = to.params;
      if (isValidHex(paramContract)) {
        if (await store.dispatch('futuresActiveContract/fetchContract', paramContract)) {
          await Promise.all([
            store.dispatch('futuresActiveContract/subscribeToSmartContract'),
            store.dispatch('futuresActiveContract/subscribeToFuturesContract'),
            store.dispatch('futuresActiveContract/listenToContractChanges'),
          ]);
          next();
        } else {
          next({ name: NOT_FOUND_PAGE });
        }
      } else {
        next({ name: NOT_FOUND_PAGE });
      }
    },
    children: getPopupPages('future'),
  },
  {
    path: '/f',
    name: 'futures',
    beforeEnter: async (to, from, next) => {
      // For new users:generate a wallet and redirect to futures page
      if (!getStorageValue('activewallet') && !getStorageValue('isNotFirstTime')) {
        /* setStorageValue('APP_ENV', 'test'); */
        setStorageValue('isNotFirstTime', true);
        /* setStorageValue('demoStep', 1); */
        /* store.dispatch('wallets/createWallet', { ga: this.$ga, referrerAddress: this.referrerAddress }); */

        // Reload to handle APP_ENV = test
        window.location.href = '/f';
        return;
      }

      const contract = await fetchDefaultContract();

      if (contract) {
        next({
          name: 'future',
          params: { contract },
          // replace: true,
        });
      } else {
        // TODO: smarter maintenance page
        next({
          name: NOT_FOUND_PAGE,
          // replace: true,
        });
      }
    },
  },
  {
    path: '*',
    beforeEnter(to, from, next) {
      next({
        name: NOT_FOUND_PAGE,
        // replace: true,
      });
    },
  },
];

const router = new Router({
  mode: 'history',
  routes,
});

// Prevent user navigation if disconnected
router.beforeEach((to, from, next) => {
  if (store.state.backendConnectionStatus === CONNECTION_STATUS.OK) {
    next();
  } else if (from.name !== null) {
    next(false);
  } else {
    next();
  }
});

router.beforeEach((to, from, next) => {
  if (!getStorageValue('utm_source')) { setStorageValue('utm_source', get(to.query, 'utm_source')); }
  if (!getStorageValue('utm_campaign')) { setStorageValue('utm_campaign', get(to.query, 'utm_campaign')); }
  if (!getStorageValue('utm_medium')) { setStorageValue('utm_medium', get(to.query, 'utm_medium')); }

  // For new users:generate a wallet and redirect to futures page
  // if (getStorageValue('activewallet')) {
  //   setStorageValue('isNotFirstTime', true);
  // }

  if (store.getters['ui/isPopupShowing'] && BACKGROUND_ROUTES_FOR_POPUP.includes(to.name)) {
    store.commit('ui/HIDE_POPUP');
  }

  next();
});

export default router;



