const affiliateClicks = state => state.clicks;
const affiliateEth = state => state.eth;
const affiliateTrades = state => state.trades;
const allTotalEarned = state => state.all;
const referrerAddress = state => state.referrerAddress;

export default {
  affiliateClicks,
  affiliateEth,
  affiliateTrades,
  allTotalEarned,
  referrerAddress,
};



