import { getWeb3WithoutProvider } from '@/util/web3';

/**
 * True if address is valid Ethereum address with checksum letter casing
 *
 * @param address
 * @returns {boolean}
 */
export default address => getWeb3WithoutProvider().utils.isAddress(address);



