import { CONNECTION_STATUS } from './constants';

export default {
  // app configs

  serverTimestamp: null,
  gasPriceType: null,

  // connection

  /**
   * State of Internet connection.
   * OK
   * ERROR_UNSPECIFIED - Internet connection status is unknown at the moment.
   * ERROR_SERVICE_DOWN - Internet connection checked and is absent
   */
  internetConnectionStatus: CONNECTION_STATUS.OK,

  /**
   * State of Internet connection.
   * OK
   * NOT_CONNECTED - Backend WS connection is off. It still needs some time to
   * MANUAL_DISCONNECT - Backend WS connection is off and this is intentional. I.e. to conserve resources in standby mode.
   * ERROR_UNSPECIFIED - Backend WS connection is lost. Attempting to reconnect
   * ERROR_SERVICE_DOWN - Backend WS connection is lost. Reconnect attempts failed.
   * ERROR_ON_INIT - Backend WS connection is lost. Reconnect attempts failed. Connect was never established.
   */
  backendConnectionStatus: CONNECTION_STATUS.NOT_CONNECTED,
  /**
   * Connection attempts while in NOT_CONNECTED or ERROR_UNSPECIFIED state.
   * We do not display
   */
  backendConnectionFailedAttempts: 0,
};



