/**
 * @module store/wallets/actions
 * @version 1.0.0
 */
// import Raven from 'raven-js';
import { wrapCachedAction, resetCachedAction } from '@/store/utils';
import { APP_ETH_NETWORK_ID } from '@/config';
import { getTotalWallets, accountSubscribe as sendAccountSubscription } from '@/api/backend';
import { faucet } from '@/api/backend/futures/others';
import { putGAClientID } from '@/api/backend/ga';
import { Accounts, checkLedgerSupport, getLedgerAccounts } from '@/api/ethereum';
import log from '@/util/logger';
import * as types from '@/store/mutation-types';
import { ACTIVE_WALLET_LOCAL_STORAGE_KEY } from '@/store/constants';
import { emitAfterWalletChange, emitBeforeWalletChange } from '@/util/events/wallet';
import { LEDGER_WALLET_TYPE, METAMASK_WALLET_TYPE, PRIVATE_KEY_TYPE, TEST_NETWORK_ID } from '@/util/constants';
import equalWalletArrays from '@/util/equalWalletArrays';
import walletEqual from '@/util/walletsEqual';
import { privateKeyToAddress } from '@/util/crypto';
import { loadActiveWallet, loadPrivateKeyWallets, storePrivateKeyWallets } from './helpers';

const createWallet = ({ state, commit, dispatch, rootGetters }, { ga, referrerAddress }) => {
  const wallet = Accounts.createAccount();
  commit(types.CREATE_WALLET, wallet);
  storePrivateKeyWallets(state.localWallets);
  dispatch('setActiveWallet', wallet);
  const cid = window.ga && window.ga.getAll() && window.ga.getAll()[0] ? window.ga.getAll()[0].get('clientId') : undefined;

  setTimeout(() => {
    putGAClientID({ walletAddress: wallet.address, clientID: cid, ga, ref: referrerAddress, login_type: 'privateKey' });
  }, 100);

  if (APP_ETH_NETWORK_ID === TEST_NETWORK_ID) {
    const contractAddress = rootGetters['contracts/activeMainContractAddress'];
    faucet({ userAddress: wallet.address, contractAddress })
      .then(() => {
        commit('ui/ADD_NOTIFICATION', {
          status: 'success',
          headline: 'Faucet',
          message: [
            'We have sent you 100 test-ETH to try out DMEX.',
            'Please wait a minute for the funds to reach your account.',
            'If the balance doesn’t update automatically press the refresh button.',
          ].join(' '),
        }, { root: true });
      })
      .catch((e) => {
        if (rootGetters.isConnected) {
          log.error(e.message);
        }
      });
  }
};

const importWallet = ({ state, rootGetters, commit, dispatch }, { privateKey }) => {
  const address = privateKeyToAddress(privateKey);
  if (!rootGetters['wallets/hasPrivateKeyWallet'](address)) {
    commit(types.UPSERT_WALLET, {
      address,
      privateKey,
      type: PRIVATE_KEY_TYPE,
    });
    storePrivateKeyWallets(state.localWallets);
  }
  dispatch('setActiveWallet', {
    address,
    type: PRIVATE_KEY_TYPE,
  });
};

const setActiveWallet = ({ commit, dispatch, getters, rootGetters }, walletInfo) => {
  if (walletEqual(getters.activeWallet, walletInfo)) return;

  emitBeforeWalletChange();
  commit(types.SET_ACTIVE_WALLET_NOT_NEW);
  if (walletInfo) {
    const wallet = getters.allWallets.find(w => walletEqual(w, walletInfo)); // provided wallets first
    commit(types.SET_ACTIVE_WALLET, { address: wallet.address, type: wallet.type });
    window.localStorage.setItem(ACTIVE_WALLET_LOCAL_STORAGE_KEY, JSON.stringify(wallet));
    rootGetters['blockchain/accountsService'].updateActiveWallet(getters.activeWallet);

    if (APP_ETH_NETWORK_ID === TEST_NETWORK_ID) {
      const contractAddress = rootGetters['contracts/activeMainContractAddress'];
      faucet({ userAddress: walletInfo.address, contractAddress })
        .then(() => {
          commit('ui/ADD_NOTIFICATION', {
            status: 'success',
            headline: 'Faucet',
            message: [
              'We have sent you 100 test-ETH to try out DMEX.',
              'Please wait a minute for the funds to reach your account.',
              'If the balance doesn’t update automatically press the refresh button.',
            ].join(' '),
          }, { root: true });
        });
    }
  } else {
    commit(types.SET_ACTIVE_WALLET, null);
    window.localStorage.removeItem(ACTIVE_WALLET_LOCAL_STORAGE_KEY);
    rootGetters['blockchain/accountsService'].clearActiveWallet();
  }
  // Notify about wallet change
  if (process.env.NODE_ENV === 'production') {
    // Raven.setUserContext(wallet
    //   ? { id: wallet.address, type: wallet.type }
    //   : undefined,
    // ); // Задаем контекст юзера или перестаем его трекать
  }
  emitAfterWalletChange();

  // async, but without await, because notify asynchronously
  // Promise.resolve to be sure that all of the further code is executed async
  // TODO: work on subcribe on account
  Promise.resolve().then(() => {
    resetCachedAction('subscribeToAccount');
    Promise.all([
      dispatch('subscribeToAccount'), // Подписка только на события по текущему пользователю
      dispatch('blockchain/checkNetworkType', undefined, { root: true }), // Проверить на правильность выбранной сети Metamask
    ]);
  });
};

const loadPrivateWallets = wrapCachedAction('loadPrivateWallets', async ({ state, commit, dispatch }) => {
  commit(types.SET_LOCAL_WALLETS, loadPrivateKeyWallets());
  const storageWallet = loadActiveWallet();

  if (storageWallet) {
    if (storageWallet.type === PRIVATE_KEY_TYPE) {
      const privateKeyWalletExists = state.localWallets.some(w => storageWallet.address === w.address);
      if (privateKeyWalletExists) {
        dispatch('setActiveWallet', storageWallet);
      } else {
        window.localStorage.removeItem(ACTIVE_WALLET_LOCAL_STORAGE_KEY);
      }
    } else if (storageWallet.type === METAMASK_WALLET_TYPE) {
      commit(types.SET_PROVIDED_WALLETS, [storageWallet]);
      dispatch('setActiveWallet', storageWallet);
      await dispatch('loadMetamaskWallets');
    } else if (storageWallet.type === LEDGER_WALLET_TYPE) {
      commit(types.SET_LEDGER_WALLETS, [storageWallet]);
      dispatch('setActiveWallet', storageWallet);
    }
  } else {
    dispatch('selectWalletIfAbsent');
  }
});

const loadMetamaskWallets = wrapCachedAction('loadMetamaskWallets', async ({ state, commit, dispatch }) => {
  if (Accounts.isEthereumCompatibleBrowser) {
    const accounts = await Accounts.getBrowserProviderAccounts();
    Accounts.onBrowserProviderAccountChange((nextAccounts) => {
      if (!equalWalletArrays(state.providedWallets, nextAccounts)) {
        commit(types.SET_PROVIDED_WALLETS, nextAccounts);
        dispatch('selectWalletIfAbsent');
      }
    });
    if (!equalWalletArrays(state.providedWallets, accounts)) {
      commit(types.SET_PROVIDED_WALLETS, accounts);
      dispatch('selectWalletIfAbsent');
    }
  }
});

const unloadMetamaskWallets = async ({ commit, dispatch }) => {
  resetCachedAction('loadMetamaskWallets');
  if (Accounts.isEthereumCompatibleBrowser) {
    Accounts.stopListenToBrowserProviderChange();
    commit(types.SET_PROVIDED_WALLETS, []);
    dispatch('selectWalletIfAbsent');
  }
};

const loadLedgerWallets = async ({ commit, dispatch }) => {
  const isSupported = await checkLedgerSupport();
  if (isSupported) {
    const ledgerAccounts = await getLedgerAccounts();
    commit(types.SET_LEDGER_WALLETS, ledgerAccounts);
    dispatch('selectWalletIfAbsent');
  }
};

/**
 * Action: Удаляет активный кошелек из локального стора
 */
const forgetPrivateWallet = ({ state, commit, dispatch }, address) => {
  commit(types.FORGET_WALLET, address);
  storePrivateKeyWallets(state.localWallets);
  dispatch('selectWalletIfAbsent');
};

const loadTotalWallets = async ({ rootGetters, commit }) => {
  try {
    const totalWallets = await getTotalWallets();
    commit(types.SET_TOTAL_WALLETS, totalWallets);
  } catch (e) {
    if (rootGetters.isConnected) {
      log.error(e.message);
    }
  }
};

// helper actions

/**
 * Action: Synchronous: selects wallet if active wallet doesn't exist.
 * Intended to be used in other actions.
 */
const selectWalletIfAbsent = ({ state, getters, dispatch }) => {
  if (!getters.activeWallet) {
    if (state.ledgerWallets.length) {
      dispatch('setActiveWallet', state.ledgerWallets[0]);
    } else if (state.providedWallets.length) {
      dispatch('setActiveWallet', state.providedWallets[0]);
    } else if (state.localWallets.length) {
      dispatch('setActiveWallet', state.localWallets[0]);
    } else {
      window.localStorage.removeItem(ACTIVE_WALLET_LOCAL_STORAGE_KEY);
    }
  }
};

const subscribeToAccount = wrapCachedAction('subscribeToAccount', async ({ getters, rootGetters }) => {
  // Оптимизация: Promise resolve-иться как только запрос на подписку отправлен, не дожидаясь ответа.
  (async () => {
    try {
      await sendAccountSubscription(getters.activeWalletAddress);
    } catch (error) {
      if (rootGetters.isConnected) {
        log.error(`Error subscribing to account: ${error}`, {
          module: 'wallets',
          layer: 'backend',
        });
      }
    }
  })();
});

export default {
  selectWalletIfAbsent,
  createWallet,
  importWallet,
  loadTotalWallets,
  loadPrivateWallets,
  loadMetamaskWallets,
  unloadMetamaskWallets,
  loadLedgerWallets,
  forgetPrivateWallet,
  setActiveWallet,
  subscribeToAccount,
};



