import { BigNumber } from 'bignumber.js';

import { REFERRER_LOCAL_STORAGE_KEY } from '@/store/constants';
import { isEthereumAddress } from '@/util/validators';
import { ETH_ADDRESS } from '@/util/constants';

export const getStoredReferrer = () => window.localStorage.getItem(REFERRER_LOCAL_STORAGE_KEY);

export const persistReferrer = referrer => window.localStorage.setItem(REFERRER_LOCAL_STORAGE_KEY, referrer);

export const clearStoredReferrer = () => window.localStorage.removeItem(REFERRER_LOCAL_STORAGE_KEY);

export const validReferrer = (referrer, active) => referrer !== active && isEthereumAddress(referrer);

/**
 * Функция меппинга треда для отборажения инфомрации об афилированных коммисиях
 */
export const mapTradeForAffiliate = affiliateAddress => (trade) => {
  const traded = [];
  const affiliates = [];

  // Если мейкер был рефералом
  if (affiliateAddress === trade.maker_affiliate_address) {
    traded.push({
      amount: BigNumber(trade.maker_amount_filled),
      tokenAddress: trade.is_buy ? trade.token_address : ETH_ADDRESS,
    });

    affiliates.push({
      amount: BigNumber(trade.maker_affiliate_fee),
      tokenAddress: trade.is_buy ? trade.token_address : ETH_ADDRESS,
    });
  }

  // Если тейкер біл рефералом
  if (affiliateAddress === trade.taker_affiliate_address) {
    traded.push({
      amount: BigNumber(trade.maker_amount_filled),
      tokenAddress: trade.is_buy ? ETH_ADDRESS : trade.token_address,
    });

    affiliates.push({
      amount: BigNumber(trade.maker_affiliate_fee),
      tokenAddress: trade.is_buy ? ETH_ADDRESS : trade.token_address,
    });
  }


  return {
    id: trade.id, // id трейда, не выводится нужно для отдладочных целей
    transactionHash: trade.transaction_hash, // hash-трейда
    createdAt: new Date(trade.created_at), // дата создания трейда
    traded, // Массив сумм с которых брались  коммисии
    affiliates, // Коммиссии, которые получает
  };
};



