import { BigNumber } from 'bignumber.js';
import { MARKET_PRICE_PERCENT_DELTA } from '@/config';
import { BUY_TOKEN_ORDER, ETH_DECIMALS, SELL_TOKEN_ORDER } from '@/util/constants';

/**
 * Market price for new buy orders.
 * Returns NaN when no number.
 * Active token must be set.
 * @returns {BigNumber|null}
 */
const buyMarketPrice = (state, getters, rootState, rootGetters) => {
  const bestAsk = rootGetters['orderBook/getAsks'][0];
  return bestAsk
    ? bestAsk.unitPrice.times(BigNumber(1).plus(MARKET_PRICE_PERCENT_DELTA)) // TODO ensureCorrectPrice
    : BigNumber(NaN);
};

/**
 * Market price for new sell orders.
 * Returns NaN when no number.
 * Active token must be set.
 * @returns {BigNumber}
 */
const sellMarketPrice = (state, getters, rootState, rootGetters) => {
  const bestBid = rootGetters['orderBook/getBids'][0];
  return bestBid
    ? bestBid.unitPrice.times(BigNumber(1).minus(MARKET_PRICE_PERCENT_DELTA)) // TODO ensureCorrectPrice
    : BigNumber(NaN);
};

const getMarketPrice = (state, getters) => buyOrSell => ({
  [BUY_TOKEN_ORDER]: () => getters.buyMarketPrice,
  [SELL_TOKEN_ORDER]: () => getters.sellMarketPrice,
}[buyOrSell]());

const orderConfirmation = state => state.orderConfirmation;

const orderParams = state => state.orderParams;

const minOrderError = state => state.minOrderError;

const ethUsdPrice = state => state.ethUsdPrice;

const orderMinEthValue = state => state.orderMinEthValue;

/**
 * Precondition: active token is set.
 */
const getMarketTotal = (state, getters, rootState, rootGetters) => ({ buyOrSell, amount }) => {
  if (amount.isNaN() || amount.isZero()) return BigNumber(NaN); // неправильный amount

  const orders = buyOrSell === BUY_TOKEN_ORDER
    ? [...rootGetters['orderBook/getAsks']].reverse()
    : rootGetters['orderBook/getBids'];

  let total = BigNumber(0);
  let remains = amount;
  let i = 0;
  while (!remains.isZero() && i < orders.length) {
    const curOrderAmount = orders[i].amount;
    if (remains.gt(curOrderAmount)) {
      remains = remains.minus(curOrderAmount);
      total = orders[i].ethAmount;
    } else {
      const lastOrderValue = remains.times(orders[i].unitPrice).shiftedBy(ETH_DECIMALS - rootGetters['tokens/activeToken'].decimals);
      total = total.plus(lastOrderValue);
      remains = BigNumber(0);
    }
    i += 1;
  }

  if (!remains.isZero()) return BigNumber(NaN); // not enough orders in order book
  return total;
};

export default {
  buyMarketPrice,
  sellMarketPrice,
  getMarketPrice,
  orderConfirmation,
  minOrderError,
  orderMinEthValue,
  getMarketTotal,
  orderParams,
  ethUsdPrice,
};



