import debounce from 'lodash/debounce';
import { withRetryForAsync } from '@/util/withRetry';
import { NONCE_ERROR_RECOVERY_DELAY } from '../constants';

/**
 * @property web3
 * @property {{ address, type }} user
 */
export default class NonceGeneratorInfura {
  /**
   * @param {{ web3, user: { address, type } }} params
   */
  constructor(params) {
    Object.assign(this, params);
    this.prevNonce = null;
    this.debouncedResetLocalNonce = debounce(
      this.resetLocalNonce.bind(this),
      NONCE_ERROR_RECOVERY_DELAY,
    );
  }

  async nextTransactionNonce() {
    const blockchainNonce = await this.getBlockchainNonce();
    this.debouncedResetLocalNonce();
    this.prevNonce = this.prevNonce !== null
      ? Math.max(blockchainNonce, this.prevNonce + 1)
      : blockchainNonce;
    return this.prevNonce;
  }

  getBlockchainNonce() {
    return withRetryForAsync(() =>
      this.web3.eth.getTransactionCount(this.user.address, 'pending'),
    );
  }

  resetLocalNonce() {
    this.prevNonce = null;
  }
}



