/**
 * @module store/order-book/getters
 *
 * @version 1.0.0
 */
import { transformOrderBookElements } from './helpers';

/**
 * Getter: Возвращает загружаются сейчас данные ордербука или нет
 *
 * @since Version 1.0.4
 */
const orderBookLoading = state => state.orderBookLoading;

/**
 * Getter: Возвращает все предложения продажи
 *
 * @since Version 1.0.4
 */
const getAsks = state => transformOrderBookElements(state.asks, false).slice(0, 30);

const getBestAskPrice = (state) => {
  const asks = getAsks(state);
  return asks[asks.length - 1].price;
};


/**
 * Getter: Возвращает все предложения покупки
 *
 * @since Version 1.0.4
 */
const getBids = state => transformOrderBookElements(state.bids, true).slice(0, 30);
const getBestBidPrice = state => getBids(state)[0].price;


export default {
  getAsks,
  getBestAskPrice,
  getBids,
  getBestBidPrice,
  orderBookLoading,
};



