/**
 * @module store/futures-positions/mutations
 * @version 1.0.0
 */

import * as types from '@/store/mutation-types';
import { ASYNC_STATES } from '@/util/constants';

// mutations
const mutations = {
  [types.FETCH_FUTURES_POSITIONS_ASYNC.PENDING](state) {
    state.fetchPositionsState = ASYNC_STATES.PENDING;
  },

  [types.FETCH_FUTURES_POSITIONS_ASYNC.SUCCESS](state, positions) {
    state.positions = positions;
    state.fetchPositionsState = ASYNC_STATES.SUCCESS;
  },

  [types.FETCH_FUTURES_POSITIONS_ASYNC.FAILURE](state) {
    state.activeContract = null;
    state.fetchPositionsState = ASYNC_STATES.ERROR;
  },

  [types.FETCH_FUTURES_CLOSED_POSITIONS_ASYNC.PENDING](state) {
    state.fetchClosedPositionsState = ASYNC_STATES.PENDING;
  },

  [types.FETCH_FUTURES_CLOSED_POSITIONS_ASYNC.SUCCESS](state, positions) {
    state.closedPositions = positions;
    state.fetchClosedPositionsState = ASYNC_STATES.SUCCESS;
  },

  [types.FETCH_FUTURES_CLOSED_POSITIONS_ASYNC.FAILURE](state) {
    state.activeContract = null;
    state.fetchClosedPositionsState = ASYNC_STATES.ERROR;
  },

  [types.SET_POSITION_TO_CLOSE](state, position) {
    state.positionToClose = position;
  },

  [types.SET_PREVIOUS_CONTRACT_HASH](state, previousContractHash) {
    state.previousContractHash = previousContractHash;
  },

  [types.SET_MARGIN_POSITION](state, marginPosition) {
    state.marginPosition = marginPosition;
  },
};

export default mutations;



