import { getWeb3WithoutProvider } from '@/util/web3';
import { helpers } from 'vuelidate/lib/validators';
import { normalizePrivateKey } from '@/util/crypto';

export default helpers.withParams(
  { type: 'validPrivateKey' },
  (privateKey) => {
    const web3 = getWeb3WithoutProvider();

    const isEmpty = !helpers.req(privateKey);
    if (isEmpty) return true;

    const normalized = normalizePrivateKey(privateKey);
    if (normalized.length !== 66) return false;

    let wallet;
    try {
      wallet = web3.eth.accounts.privateKeyToAccount(normalized);
    } catch (error) {
      return false;
    }

    return web3.utils.isAddress(wallet.address);
  },
);



