import ProviderEngine from 'web3-provider-engine';
import RpcSubprovider from 'web3-provider-engine/subproviders/rpc';
import { ETHEREUM_NODE_HTTP } from '@/config';
import { LEDGER_WALLET_TYPE, METAMASK_WALLET_TYPE, PRIVATE_KEY_TYPE } from '@/util/constants';
import { getWeb3WithoutProvider, Web3 } from '@/util/web3';
import isEthereumCompatibleBrowser from './isEthereumCompatibleBrowser';
import { getSubprovider } from './ledger';

let web3WithEthereumNodeProvider = null;
let web3WithBrowserProvider = null;
let web3WithLedgerProvider = null;

/**
 * Web3 instance with Provider that uses Infura Cloud as Ethereum Node.
 *
 * @returns {*}
 */
const getWeb3WithEthereumNodeProvider = () => {
  if (!web3WithEthereumNodeProvider) {
    web3WithEthereumNodeProvider = new Web3(ETHEREUM_NODE_HTTP);
    if (process.env.NODE_ENV !== 'production') window.localWeb3 = web3WithEthereumNodeProvider; // test global var
  }
  return web3WithEthereumNodeProvider;
};

/**
 * Web3 instance with Provider that uses browser Ethereum provider (i.e. MetaMask).
 *
 * @returns {*}
 */
const getWeb3WithBrowserProvider = () => {
  if (!web3WithBrowserProvider && isEthereumCompatibleBrowser) {
    const provider = (
      window.ethereum || // only in modern dapp browsers
      window.web3.currentProvider // legacy dapp browsers
    );
    web3WithBrowserProvider = new Web3(provider);
    if (process.env.NODE_ENV !== 'production') window.givenWeb3 = web3WithBrowserProvider; // test global var
  }
  return web3WithBrowserProvider;
};

/**
 * Web3 instance with Provider that uses Ledger Nano S.
 *
 * @returns {*}
 */
const getWeb3WithLedgerProvider = ({ getAddressToPathMap }) => {
  if (!web3WithLedgerProvider) {
    const engine = new ProviderEngine();
    engine.addProvider(getSubprovider({ getAddressToPathMap }));
    engine.addProvider(new RpcSubprovider({ rpcUrl: ETHEREUM_NODE_HTTP }));
    engine.start();
    web3WithLedgerProvider = new Web3(engine);
    if (process.env.NODE_ENV !== 'production') window.web3WithLedgerProvider = web3WithLedgerProvider; // test global var
  }
  return web3WithLedgerProvider;
};

/**
 * Returns web3 instance. If type is not specified than returns the instance based on current active wallet.
 *
 * @param type type of web3 instance to be returned
 * @param params {{ getAddressToPathMap: function }} for Ledger wallets. Maps wallet address to derivation path
 * @returns {Web3}
 */
const getWeb3 = (type = null, params) => {
  switch (type) {
    case PRIVATE_KEY_TYPE: return getWeb3WithEthereumNodeProvider();
    case METAMASK_WALLET_TYPE: return getWeb3WithBrowserProvider();
    case LEDGER_WALLET_TYPE: return getWeb3WithLedgerProvider(params);
    case null: return getWeb3WithoutProvider();
    default: throw new Error(`Invalid wallet type: ${type}`);
  }
};

export {
  getWeb3WithEthereumNodeProvider,
  getWeb3WithBrowserProvider,
  getWeb3,
};



