<template>
  <div class="the-wallet-pages-wallet">
    <the-popup-wallet/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { routesMixin } from '@/util/mixins';
import ThePopupWallet from '@/containers/blocks/ThePopupWallet';

export default {
  name: 'the-wallet-pages-wallet',

  computed: {
    ...mapGetters({
      wallet: 'wallets/activeWallet',
    }),
  },

  watch: {
    async wallet() {
      if (!this.$store.getters['wallets/isLogin']) {
        this.$router.push(this.$closedPopupRoute);
      }
    },
  },

  created() {
    this.$store.dispatch('balance/listenToPrivateBalanceChanges');
    this.$store.dispatch('balance/listenToContractBalanceChanges');
  },

  beforeDestroy() {
    this.$store.dispatch('balance/stopListeningToPrivateBalanceChanges');
    this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
  },

  beforeRouteLeave(to, from, next) {
    this.$store.commit('wallets/SET_ACTIVE_WALLET_NOT_NEW');
    next();
  },

  components: {
    ThePopupWallet,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-pages-wallet {
  height: 100%;
  margin-bottom: 0;
}
</style>



