/**
 * Время по прошествию которого приложение считается idle и подлежит входу в "спящий" режим.
 * @type {number}
 */
export const TIMEOUT_IDLE = 30 * 1000;

/**
 * Срок существование нотификаций, если на них не делать ховер
 *
 * @type {number}
 */
export const DEFAULT_NOTIFICATION_DURATION = 9000;

export const INTERVAL_DESTROY_COMPONENT_RESOURCES = 3000;

export const INTERVAL_DESTROY_CHAT = 2500;

export const TIMEOUT_BACKEND_REQUEST = 90 * 1000;

/**
 * Период обновления изменинй приватного баланса из внешних источников.
 * Задаеться в миллисекундах.
 * @type {number}
 */
export const INTERVAL_BALANCE_CHECK = 10000;

/**
 * Максимальная частота обновления Total Volume на бирже
 * @type {number}
 */
export const THROTTLE_DELAY_VOLUMES = 2000;

/**
 * Timeout получения адреса кошелька из Metamask
 * @type {number}
 */
export const TIMEOUT_METAMASK_INIT = 1000;

/**
 * Timeout получения адреса кошелька из Metamask
 * @type {number}
 */
export const TIMEOUT_METAMASK_UPDATE = 9000;

/**
 * Указывает как часто приложения проверяет переключение кошельков в Metamask
 * @type {number}
 */
export const INTERVAL_METAMASK_ACCOUNT_CHECK = 650;

/**
 * Timeout операций которые включают взаимодействие пользователя с интерфейсом MetaMask
 * @type {number}
 */
export const TIMEOUT_METAMASK_USER_RESPONSE = 60 * 1000;

/**
 * Timeout получения адреса кошелька из Ledger Nano S.
 */
export const TIMEOUT_LEDGER_GET_ACCOUNTS = 5000;

/**
 * Timeout для операций, которые включают взаимодействие пользователя с интерфейсом Ledger Nano S.
 * Default is 30000.
 * @type {number}
 */
export const TIMEOUT_LEDGER_USER_RESPONSE = 35000;

export const WALLET_CREATION_TIME = 2 * 1000;



