import { getStorageValue, setStorageValue } from '@/util/storage';

export const APP_ENV = getStorageValue('APP_ENV') || process.env.PROD_ENV;
export const NETWORK_TYPE = process.env.NETWORK_TYPE;

// wallet types
export const PRIVATE_KEY_TYPE = 'private key';
export const METAMASK_WALLET_TYPE = 'MetaMask';
export const LEDGER_WALLET_TYPE = 'Ledger Nano S';

// Balance operation types
export const DEPOSIT_OPERATION = 'deposit';
export const WITHDRAWAL_OPERATION = 'withdrawal';
export const TRANSFER_OPERATION = 'transfer';
// export const INCOMING_TRANSFER_OPERATION = 'INCOMING_TRANSFER';

// Orders types (buy or sell)
export const BUY_TOKEN_ORDER = 'buyToken';
export const SELL_TOKEN_ORDER = 'sellToken';

// Order types
export const ORDER_TYPE_LIMIT = 'limit';
export const ORDER_TYPE_MARKET = 'market';
export const ORDER_TYPE_STOP_LIMIT = 'stop-limit';

// Ethereum operations statuses
export const PENDING_STATUS = 'pending';
export const APPROVED_STATUS = 'confirmed';
export const DECLINED_STATUS = 'declined';

// Ethereum networks
export const MAIN_NETWORK_ID = 1;
export const TEST_NETWORK_ID = NETWORK_TYPE === 'ropsten' ? 3 : 4;

// Exchange symbols
export const EXCHANGE_ETHERMIUM = 'ETM';
export const EXCHANGE_BINANCE = 'BNC';
export const EXCHANGE_HUOBI = 'HBI';
export const EXCHANGE_IDEX = 'IDX';
export const EXCHANGE_OKEX = 'OKX';
export const EXCHANGE_FORKDELTA = 'FDT';

/**
 * Async operations statuses.
 * INITIAL - value not initialized yet.
 * PENDING - value is in initialization process.
 * ERROR - initialization failed.
 * SUCCESS - value is initialized successfully.
 * UNSYNC - value was initialized, but the value may be not up to date.
 *
 * @type {{SUCCESS: string, INITIAL: string, ERROR: string, PENDING: string}}
 */
export const ASYNC_STATES = {
  INITIAL: 'initial',
  PENDING: 'loading',
  ERROR: 'error',
  SUCCESS: 'success',
  UNSYNC: 'unsync',
};

/**
 * Максимальное количество знаков после запятой у эфира.
 *
 * 1 Ether = 10 ** 18 wei
 * This constant is the power of ten.
 *
 * @type {number}
 */
export const ETH_DECIMALS = 18;

export const DEFAULT_DECIMALS = 18;

// Futures constants
export const FUTURES_PRICE_DECIMALS = 8;
export const FUTURES_AMOUNT_DECIMALS = 8;

/**
 * Максимальное количество знаков после запятой у GWEI.
 *
 * 1 Gwei = 10 ** 9 wei
 * 1 Ether = 10 ** 9 Gwei
 * This constant is the power of ten.
 *
 * @type {number}
 */
export const GWEI_DECIMALS = 9;

/**
 * Максимальное количество знаков после запятой у эфира,
 * которое мы отображаем в приложении.
 *
 * 1 Ether = 10 ** 18 wei
 * This constant is the power of ten.
 *
 * @type {number}
 */
export const ETH_DISPLAYED_DECIMALS = 9;

export const DEFAULT_DISPLAYED_DECIMALS = 8;

export const DEFAULT_MIN_DECIMALS = 3;

export const DEFAULT_MAX_DECIMALS = 8;

/**
 * We use 0x0 contract address as Ether token address for convenience.
 *
 * @type {string}
 */
export const ETH_ADDRESS = '0x0';
export const ETH_DEMO_ADDRESS = '0xF5593f40DD1a872466885ab9fE8eb8E9668628bE';
export const ETH_STAGE_ADDRESS = '0xC93dCe4D4985Bf5dfa7a85498f56AeC33A6e0Ef2';

/* eslint-disable-next-line */
export const ETH_BASE_ADDRESS = NETWORK_TYPE === 'ropsten' ? ETH_STAGE_ADDRESS : APP_ENV === 'test' ? ETH_DEMO_ADDRESS : '0x0';

/**
 * We use 0x0000000000000000000000000000000000000000 contract address as Ether token address for convenience.
 *
 * @type {string}
 */
export const ETH_ADDRESS_LONG = '0x0000000000000000000000000000000000000000';

export const ETH_SHORT_NAME = 'ETH';

export const ETH_TOKEN = {
  contract: ETH_ADDRESS,
  name: 'Ethereum',
  shortName: ETH_SHORT_NAME,
  decimals: ETH_DECIMALS,
};

/**
 * Роут который должен отображатся, если страица не найдена
 */
export const NOT_FOUND_PAGE = 'page404';

/**
 * Default token icon path.
 * Used when token icon fetching failed or icon is absent.
 *
 * @type {string}
 */
export const DEFAULT_TOKEN_ICON = '/static/img/ethereum.svg';

/**
 * Default token icon path for flower animation.
 * Used when token icon fetching failed or icon is absent.
 *
 * @type {string}
 */
export const DEFAULT_TOKEN_ICON_CIRCLED = '/static/img/ethereum-circled.svg';

/**
 * Block time in seconds used to do calculations (aprox.)
 *
 * @type {number}
 */
export const BLOCK_TIME_SEC = 14;

export const BASE_TOKEN_DAI_TEST = '0xcFA321eBeD2cfc5Add3Ef833de324f7C2b1a310B';
export const BASE_TOKEN_DAI_MAIN = '0x6B175474E89094C44Da98b954EedeAC495271d0F';
export const BASE_TOKEN_DAI_ROPSTEN = '0x8B2B269047CEfEA181C66E4aa5A33013bBcd987a';

/* eslint-disable-next-line */
export const DAI_BASE_ADDRESS = NETWORK_TYPE === 'ropsten' ? BASE_TOKEN_DAI_ROPSTEN : APP_ENV === 'test' ? BASE_TOKEN_DAI_TEST : BASE_TOKEN_DAI_MAIN;

export const BASE_TOKEN_BTC_TEST = '0xcFA321eBeD2cfc5Add3Ef833de324f7C2b1a310B';
export const BASE_TOKEN_BTC_MAIN = '0x5228a22e72ccC52d415EcFd199F99D0665E7733b';
export const BASE_TOKEN_BTC_ROPSTEN = '0xe83BD3e427463Aa394E26Cd49eca8D364e5aB934';

if (APP_ENV === 'test' && getStorageValue('BASE_TOKEN') === BASE_TOKEN_DAI_MAIN) {
  if (NETWORK_TYPE === 'ropsten') {
    setStorageValue('BASE_TOKEN', BASE_TOKEN_DAI_ROPSTEN);
  } else {
    setStorageValue('BASE_TOKEN', BASE_TOKEN_DAI_TEST);
  }
}

if (APP_ENV === 'main' && [BASE_TOKEN_DAI_TEST, BASE_TOKEN_DAI_ROPSTEN].includes(getStorageValue('BASE_TOKEN'))) {
  setStorageValue('BASE_TOKEN', BASE_TOKEN_DAI_MAIN);
}


if (NETWORK_TYPE === 'ropsten') {
  if (getStorageValue('BASE_TOKEN_BTC')) {
    setStorageValue('BASE_TOKEN', BASE_TOKEN_BTC_ROPSTEN);
  } else {
    setStorageValue('BASE_TOKEN', BASE_TOKEN_DAI_ROPSTEN);
  }
}

/* eslint-disable-next-line */
export const BASE_TOKEN_DAI = NETWORK_TYPE === 'ropsten' ? BASE_TOKEN_DAI_ROPSTEN : APP_ENV === 'test' ? BASE_TOKEN_DAI_TEST : BASE_TOKEN_DAI_MAIN;
/* eslint-disable-next-line */
export const BASE_TOKEN_BTC = NETWORK_TYPE === 'ropsten' ? BASE_TOKEN_BTC_ROPSTEN : APP_ENV === 'test' ? BASE_TOKEN_BTC_TEST : BASE_TOKEN_BTC_MAIN;

export const ACTIVE_TOKEN_DAI = {
  contract: BASE_TOKEN_DAI,
  decimals: 18,
  amount_dec: 2,
  shortName: 'DAI',
};

export const ACTIVE_TOKEN_BTC = {
  contract: BASE_TOKEN_BTC,
  decimals: 18,
  amount_dec: 4,
  shortName: 'BTC',
};

export const ACTIVE_TEST_ETH = {
  contract: ETH_DEMO_ADDRESS,
  decimals: 18,
  amount_dec: 2,
  shortName: 'ETH',
};



