import { addHexPrefix } from 'ethereumjs-util';
import { getWeb3WithoutProvider } from '@/util/web3';

export default (privateKey) => {
  const normalizedKey = addHexPrefix(privateKey);
  return getWeb3WithoutProvider().eth.accounts.privateKeyToAccount(normalizedKey).address;
};



