import { PRIVATE_KEY_TYPE, METAMASK_WALLET_TYPE, LEDGER_WALLET_TYPE } from '@/util/constants';
import {
  TIMEOUT_METAMASK_INIT,
  TIMEOUT_METAMASK_UPDATE,
  INTERVAL_METAMASK_ACCOUNT_CHECK,
  TIMEOUT_METAMASK_USER_RESPONSE,
} from '@/config';
import { getLedgerAddresses } from '@/api/ethereum/ledger';
import withTimeout from '@/util/withTimeout';
import { getWeb3WithoutProvider } from '@/util/web3';
import generateId from '@/util/generateId';
import {
  getWeb3WithBrowserProvider,
} from './web3';
import isEthereumCompatibleBrowser from './isEthereumCompatibleBrowser';

/**
 * Возвращает масив кошельков пользователя у провайдера Ethereum (напр. Metamask).
 * У текущей версии Metamask в масиве 1 (выбранный) или 0 елементов (для не залогиненого пользователя).
 *
 * @returns {Promise<string[]>}
 */
const getEthereumBrowserAddresses = async () => {
  if (!isEthereumCompatibleBrowser) return [];
  const web3 = getWeb3WithBrowserProvider();
  return web3.eth.getAccounts();
};

const toBrowserProviderWallets = addresses => addresses.map(address => ({ address, type: METAMASK_WALLET_TYPE }));

/**
 * Работа с кошельками Ethereum.
 * Список обязанностей:
 * - создание private key кошельков.
 * - доступ к списку кошельков в Metamask.
 * - поддержку соединения с кошельками Metamask и уведомляет в случае потери доступа к ним.
 */
export default class Accounts {
  /**
   * Создаёт новый Ethereum кошелёк
   *
   * @returns {{address: string, privateKey: string, type: string}}
   */
  static createAccount() {
    const { address, privateKey } = getWeb3WithoutProvider().eth.accounts.create();
    return { address, privateKey, type: PRIVATE_KEY_TYPE };
  }

  /**
   * Возвращает масив кошельков пользователя у браузерного провайдера Ethereum (напр. Metamask).
   * У текущей версии Metamask в масиве:
   * 1 елемент - пользователь залогинился, и предоставил доступ к его кошелькам
   *   (либо private mode выключен, либо пользователь явно разрешил доступ к его кошелькам для этого приложения)
   *
   * 0 елементов - для пользователя, который не залогинился в Metamask или ограничил доступ к его кошелькам
   *   (включил private mode и не предоставил разрешения для этого приложения).
   *
   * @returns {Promise<{ address: string, type: string }[]>}
   */
  static async getBrowserProviderAccounts() {
    let accounts;
    try {
      await this.getBrowserProviderPermission();
      accounts = await withTimeout(getEthereumBrowserAddresses(), TIMEOUT_METAMASK_INIT);
    } catch (e) {
      accounts = [];
    }
    this.wasBrowserAccountsInit = true;
    return toBrowserProviderWallets(accounts);
  }

  static onBrowserProviderAccountChange(handler) {
    const loop = async (subscriptionId) => {
      // break out of the loop if browser permission rejected
      if (subscriptionId !== this.browserAccountsSubscriptionId) return;
      // call handler only after getBrowserProviderAccounts returned a value
      if (this.wasBrowserAccountsInit) {
        let nextAddresses;
        try {
          nextAddresses = await withTimeout(getEthereumBrowserAddresses(), TIMEOUT_METAMASK_UPDATE);
        } catch (error) {
          // just don't notify about update TODO: add standby mode and enable notifications
          // log.warn(`Metamask couldn't load wallets: ${error.message}`);
        }
        if (subscriptionId !== this.browserAccountsSubscriptionId) return;
        handler(toBrowserProviderWallets(nextAddresses));
      }
      this.isBrowserAccountsChangeTimer = setTimeout(loop.bind(null, subscriptionId), INTERVAL_METAMASK_ACCOUNT_CHECK);
    };
    const subscriptionId = generateId();
    this.browserAccountsSubscriptionId = subscriptionId;
    this.isBrowserAccountsChangeTimer = loop(subscriptionId);
  }

  static stopListenToBrowserProviderChange() {
    clearTimeout(this.isBrowserAccountsChangeTimer);
    this.isBrowserAccountsChangeTimer = null;
    this.browserAccountsSubscriptionId = null;
  }

  /**
   * Ask Browser to grant this dapp a permission to access to wallets.
   * Permission is required only if provider's "private mode" is active.
   *
   * @returns {Promise<void>}
   */
  static async getBrowserProviderPermission() {
    if (window.ethereum) {
      await withTimeout(window.ethereum.enable(), TIMEOUT_METAMASK_USER_RESPONSE);
    }
  }
}

Accounts.wasBrowserAccountsInit = false;
// * @property browserProviderPermissionRejected - true если пользователь включил private mode и не предоставил разрешения для этого dapp
// Accounts.browserProviderPermissionRejected = false; // TODO: optimisation
Accounts.isBrowserAccountsChangeTimer = null;
Accounts.browserAccountsSubscriptionId = null;
Accounts.isEthereumCompatibleBrowser = isEthereumCompatibleBrowser;

const toLedgerWallets = addresses => addresses.map(({ address, derivationPath }) => ({ address, derivationPath, type: LEDGER_WALLET_TYPE }));

/**
 * Возвращает масив кошельков пользователя в Ledger Nano C.
 * Don't throw exceptions.
 *
 * @returns {Promise<{ address: string, type: string }[]>}
 */
export const getLedgerAccounts = async (derivationPath, start) => toLedgerWallets(await getLedgerAddresses(derivationPath, start));

export { checkLedgerSupport } from './ledger';



