import { DEPOSIT_OPERATION, WITHDRAWAL_OPERATION, TRANSFER_OPERATION } from '../constants';

const map = {
  [DEPOSIT_OPERATION]: 'Deposit',
  [WITHDRAWAL_OPERATION]: 'Withdraw',
  [TRANSFER_OPERATION]: 'Transfer',
};

export default o => map[o];



