/**
 * @module store/futures-contracts/actions
 * @version 1.0.3
 */

import * as types from '@/store/mutation-types';
import log from '@/util/logger';
import { getContracts, getOneContract, contractTransformer, onFuturesContracts, offFuturesContracts } from '@/api/backend/futures/contracts';
import { getAssets } from '@/api/backend/futures/assets';
import { futuresAssetSubscribe, futuresContractSubscribe, smartContractSubscribe } from '@/api/backend/futures/subscribe';
import { onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import { withRetryForTask } from '@/util/withRetry';

let subscribeFuturesContractReconnectHandler;
let subscribeSmartContractReconnectHandler;
let onFuturesContractsHandler;

const setFutureFirstContractHash = async ({ commit, rootGetters }) => {
  if (!rootGetters['futuresActiveContract/firstContractHash'] && rootGetters['contracts/activeFuturesContractAddress']) {
    // Select recommended contract from first asset
    const assets = await getAssets({ contractAddress: rootGetters['contracts/activeFuturesContractAddress'], baseToken: rootGetters['balance/baseToken'], limit: 1 });
    if (assets.length > 0 && assets[0].recommendedContract) {
      commit(types.FETCH_FUTURES_FIRST_CONTRACT_ASYNC.SUCCESS, assets[0].recommendedContract.futuresContractHash);
      return;
    }

    // Backend/Previous method to select first contract
    const contracts = await getContracts({ contractAddress: rootGetters['contracts/activeFuturesContractAddress'], isModel: true, isHidden: false, margin: 3 });
    commit(types.FETCH_FUTURES_FIRST_CONTRACT_ASYNC.SUCCESS, contracts[0].futuresContractHash);
  }
};

const fetchContract = async ({ commit, rootGetters }, contractHash) => {
  commit(types.FETCH_FUTURES_ACTIVE_CONTRACT_ASYNC.PENDING);
  try {
    const contract = await withRetryForTask(() => getOneContract(contractHash));
    commit(types.FETCH_FUTURES_ACTIVE_CONTRACT_ASYNC.SUCCESS, contract);
    return true;
  } catch (error) {
    commit(types.FETCH_FUTURES_ACTIVE_CONTRACT_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`backend futures contract unavailable: ${error}`, {
        module: 'futures-contracts',
        layer: 'backend',
      });
    }
    return false;
  }
};

const unsubscribeFromFuturesContract = () => {
  removeReconnectListener(subscribeFuturesContractReconnectHandler);
  removeResumeListener(subscribeFuturesContractReconnectHandler);
};

const subscribeToFuturesContract = async ({ state, dispatch, rootGetters }) => {
  if (!state.activeContract) {
    return;
  }

  unsubscribeFromFuturesContract();

  futuresContractSubscribe(state.activeContract.futuresContractHash)
    .catch((error) => {
      if (rootGetters.isConnected) {
        log.error(`backend futures contract subscription error: ${error}`, {
          module: 'futures-contracts',
          layer: 'backend',
        });
      }
    });

  futuresAssetSubscribe(state.activeContract.futuresAssetHash)
    .catch((error) => {
      if (rootGetters.isConnected) {
        log.error(`backend futures asset subscription error: ${error}`, {
          module: 'futures-assets',
          layer: 'backend',
        });
      }
    });

  subscribeFuturesContractReconnectHandler = () => {
    dispatch('subscribeToFuturesContract');
  };

  onReconnect(subscribeFuturesContractReconnectHandler);
  onResume(subscribeFuturesContractReconnectHandler);
};

const unsubscribeFromSmartContract = () => {
  removeReconnectListener(subscribeSmartContractReconnectHandler);
  removeResumeListener(subscribeSmartContractReconnectHandler);
};

const subscribeToSmartContract = async ({ state, dispatch, rootGetters }) => {
  if (!state.activeContract) {
    return;
  }

  unsubscribeFromSmartContract();

  smartContractSubscribe(state.activeContract.contractAddress)
    .catch((error) => {
      if (rootGetters.isConnected) {
        log.error(`backend futures smart contract subscription error: ${error}`, {
          module: 'futures-contracts',
          layer: 'backend',
        });
      }
    });

  subscribeSmartContractReconnectHandler = () => dispatch('subscribeToSmartContract');

  onReconnect(subscribeSmartContractReconnectHandler);
  onResume(subscribeSmartContractReconnectHandler);
};

const stopListeningToContractChanges = () => {
  offFuturesContracts(onFuturesContractsHandler);
};

const listenToContractChanges = ({ state, commit }) => {
  stopListeningToContractChanges();

  onFuturesContractsHandler = ({ data }) => {
    const contract = contractTransformer(data);

    if (contract.futuresContractHash !== state.activeContract.futuresContractHash) {
      return;
    }

    commit(types.FETCH_FUTURES_ACTIVE_CONTRACT_ASYNC.SUCCESS, contract);
  };

  onFuturesContracts(onFuturesContractsHandler);
};
export default {
  setFutureFirstContractHash,
  fetchContract,

  subscribeToFuturesContract,
  unsubscribeFromFuturesContract,

  subscribeToSmartContract,
  unsubscribeFromSmartContract,

  listenToContractChanges,
  stopListeningToContractChanges,
};



