import Vue from 'vue';
import Vuex from 'vuex';

import state from './state';
import getters from './getters';
import mutations from './mutations';
import actions from './actions';

// Modules
import activeOrder from './modules/active-order';
import affiliate from './modules/affiliate';
import balance from './modules/balance';
import blockchain from './modules/blockchain';
import contracts from './modules/contracts';
import deposit from './modules/deposit';
import faq from './modules/faq';
import history from './modules/history';
import orderBook from './modules/order-book';
import orders from './modules/orders';
import tokens from './modules/tokens';
import trades from './modules/trades';
import ui from './modules/ui';
import wallets from './modules/wallets';
import futuresActiveContract from './modules/futures-active-contract';
import futuresActiveOrder from './modules/futures-active-order';
import futuresOrderBook from './modules/futures-order-book';
import futuresTrades from './modules/futures-trades';
import futuresOrders from './modules/futures-orders';
import futuresPositions from './modules/futures-positions';

Vue.use(Vuex);

const debug = false;// process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions,
  modules: {
    activeOrder,
    affiliate,
    balance,
    blockchain,
    contracts,
    deposit,
    faq,
    history,
    orderBook,
    orders,
    tokens,
    trades,
    ui,
    wallets,
    futuresActiveContract,
    futuresActiveOrder,
    futuresOrderBook,
    futuresTrades,
    futuresOrders,
    futuresPositions,
  },
  strict: debug,
});



