// gas limits
// TODO choose smarter limits

/**
 * Gas limit for Ethermium contract deposit method with param referrerAddress != 0x0
 * @type {number}
 */
export const GAS_LIMIT_DEPOSIT_ETH_REF = 250000;

/**
 * Gas limit for Ethermium contract depositToken method.
 * @type {number}
 */
export const GAS_LIMIT_DEPOSIT_TOKEN = 250000;

/**
 * Gas limit for ERC20 contract approve method.
 * @type {number}
 */
export const GAS_LIMIT_TOKEN_APPROVE = 200000;

/**
 * Gas limit for ERC20 contract approve method.
 * @type {number}
 */
export const GAS_LIMIT_TOKEN_TRANSFER = 200000;

/**
 * Gas limit for transfer Ether from current private balance to another private balance.
 * Minimal gas limit for Ethereum blockchain.
 * @type {number}
 */
export const GAS_LIMIT_TRANSFER_ETH = 21000;

/**
 * Maximal gas limit for operations with dynamic gas limit calculation.
 *
 * @type {number}
 */
export const GAS_LIMIT_MAX = 9e6;

/**
 * Время по истечению которого транзакция считается отменённой (Rejected), если не пришло событие о её успешном завершении.
 *
 * @type {number}
 */
export const TRANSACTION_CONFIRM_TIMEOUT = 5 * 60 * 1000;

/**
 * Время на которое приостанавливается генерация nonce в случае ошибки майнинга какой-либо из блокчейн операций.
 * @type {number}
 */
export const NONCE_ERROR_RECOVERY_DELAY = 15000;



