/* eslint-disable no-restricted-syntax */

import Vue from 'vue';
import * as types from '@/store/mutation-types';
import { PENDING_STATUS } from '@/util/constants';
import { findIndexByHash, findIndexByKey, addPending, upsertPending } from './helpers';

const mutations = {

  // current balance deposit / withdrawal / transfer operation

  /**
   * Mutation: Opens balance transfer dialog
   *
   * @param operation - selected operation (deposit/withdrawal/transfer)
   * @param tokenContract - token to be transferred
   */
  [types.START_TOKEN_TRANSFER](state, { operation, tokenContract }) {
    state.tokenTransfer = { operation, tokenContract };
  },

  /**
   * Mutation: Changes balance transfer dialog state.
   *
   * @param operation - selected operation (deposit/withdrawal/transfer)
   */
  [types.UPDATE_TOKEN_TRANSFER](state, { operation }) {
    state.tokenTransfer.operation = operation;
  },

  [types.BTC_TRANSFER](state, operation) {
    state.btcTransferActive = operation;
  },

  /**
   * Mutation: Closes balance transfer dialog and and submits token transfer.
   */
  [types.SUBMIT_TOKEN_TRANSFER](state) {
    state.tokenTransfer.operation = null;
  },

  /**
   * Mutation: Closes balance transfer dialog without submitting token transfer.
   */
  [types.CANCEL_TOKEN_TRANSFER](state) {
    state.tokenTransfer.operation = null;
  },
  [types.CANCEL_BTC_TRANSFER](state) {
    state.btcTransferActive = null;
  },

  // New and Pending operations

  /**
   * Mutation: Adding operation that has no transaction hash yet.
   * This operation should be stored in store in order to be displayed
   * while transaction hash from blockchain is not recieved.
   *
   * @param userAddress - wallet address that sent a transaction
   * @param operation - rest of operation details
   */
  [types.ADD_HASHLESS_OPERATION](state, { userAddress, ...operation }) {
    const userOps = state.newOperations[userAddress];
    if (userOps) {
      userOps.push(operation);
    } else {
      Vue.set(state.newOperations, userAddress, [operation]);
    }
  },

  /**
   * Mutation: Update operation that has no transaction hash yet.
   *
   * @param userAddress - wallet address of operation
   * @param key - key to identify operation
   * @param {object} updated - update diffs to previous operation
   */
  [types.UPDATE_HASHLESS_OPERATION](state, { userAddress, key, ...updated }) {
    const [ops, i] = findIndexByKey(state.newOperations, { userAddress, key });
    for (const [property, value] of Object.entries(updated)) {
      Vue.set(ops[i], property, value);
    }
  },

  /**
   * Mutation: Adding operation that has no transaction hash yet.
   * This operation should be stored in store in order to be displayed
   * while transaction hash from blockchain is not recieved.
   *
   * @param {{ userAddress, key }} params - args that identify operation
   */
  [types.REMOVE_HASHLESS_OPERATION](state, params) {
    const [ops, i] = findIndexByKey(state.newOperations, params);
    ops.splice(i, 1);
  },

  /**
   * Mutation: Sets transactionHash to a transaction that was without it (new transaction).
   *
   * @param transactionHash - assigned transaction hash (provided by blockchain)
   * @param userAddress - wallet address that sent a transaction
   * @param key - used to identify transaction.
   */
  [types.SET_OPERATION_HASH](state, { transactionHash, userAddress, key }) {
    const [ops, i] = findIndexByKey(state.newOperations, { userAddress, key });
    const [{ key: _, ...oldOperation }] = ops.splice(i, 1);
    addPending(state, userAddress, { transactionHash, ...oldOperation });
  },

  /**
   * Mutation: Adds or updates pending operation
   *
   * @todo rename to UPSERT, because this operation can both update and add values
   * @param userAddress - wallet address that sent a transaction
   * @param operation - rest of operation details
   */
  [types.ADD_PENDING_OPERATION](state, { userAddress, ...operation }) {
    upsertPending(state, userAddress, operation);
  },

  /**
   * Mutation: Removes pending operation. Operation is identified by transactionHash.
   *
   * @param userAddress - wallet address that sent a transaction
   * @param transactionHash - transaction hash of deposit/withdrawal/transfer balance operation. Used to identify transaction.
   */
  [types.REMOVE_PENDING_OPERATION](state, { userAddress, transactionHash }) {
    const [ops, i] = findIndexByHash(state.pendingOperations, { userAddress, transactionHash });
    ops.splice(i, 1);
  },

  // operations history

  /**
   * Mutation: Adds or updates operation that might be pending, confirmed or rejected.
   * Confirmed and rejected operations are used in deposit history that is not displayed anymore.
   *
   * @todo do not add confirmed or rejected operations to history, because it is not displayed anymore
   * @param userAddress - wallet address that sent a transaction
   * @param operation - rest of operation details
   */
  [types.UPSERT_OPERATION](state, { userAddress, ...operation }) {
    if (operation.status === PENDING_STATUS) {
      upsertPending(state, userAddress, operation);
    } else {
      const { operations: history, pendingOperations } = state;
      const { transactionHash } = operation;
      const [oldArray, i] = findIndexByHash(pendingOperations, { userAddress, transactionHash });
      if (oldArray) {
        oldArray.splice(i, 1);
      }
      if (history[userAddress]) {
        const existing = history[userAddress].find(o => o.transactionHash === transactionHash);
        if (existing) {
          Object.assign(existing, operation);
        } else {
          history[userAddress].push(operation);
        }
      } else {
        Vue.set(history, userAddress, [operation]);
      }
    }
  },

  /**
   * Set all user balance transfer operations history such as deposit/withdrawal/transfer.
   * Only non-pending operations are added here.
   * Should be used on initial loading, user wallet change or after connection problems.
   *
   * @todo do not add confirmed or rejected operations to history, because it is not displayed anymore
   * @param userAddress - wallet address that sent a transaction
   * @param operations - an array with non-pending balance transfer operations (deposit/withdrawal/transfer)
   */
  [types.UPDATE_OPERATIONS](state, { userAddress, operations }) {
    Vue.set(state.operations, userAddress, operations);
  },

  /**
   * Sets fund confirmation number. This number is count of blocks that should be mined after the block with balance operation (deposit/withdrawal/transfer)
   * in order to mark operation as confirmed. This is needed to reduce risk of operation to end up being in uncle block after it is confirmed.
   *
   * @param fundConfirmations count of blocks that should be mined after transaction in order to treat transaction as confirmed
   */
  [types.UPDATE_FUND_CONFIRMATIONS](state, fundConfirmations) {
    state.fundConfirmations = fundConfirmations;
  },
};

export default mutations;



