/**
 * @module store/tokens/mutations
 * @version 1.1.1
 */

import * as types from '@/store/mutation-types';
import log from '@/util/logger';
import { MY_TOKENS_LOCAL_STORAGE_KEY } from '@/store/constants';
import { setStorageArray } from '@/util/storage';
import { ASYNC_STATES } from '@/util/constants';

// mutations
const mutations = {
  [types.SET_TOKENS](state, tokens) {
    state.exchangeTokens = tokens;
    state.tokensLoaded = true;
  },

  [types.SET_ASSETS](state, assets) {
    state.assets = assets;
  },

  [types.SET_ACTIVE_TOKEN.PENDING](state) {
    state.setActiveTokenState = ASYNC_STATES.PENDING;
  },

  [types.SET_ACTIVE_TOKEN.SUCCESS](state, token) {
    state.activeToken = token;
    state.setActiveTokenState = ASYNC_STATES.SUCCESS;
  },

  [types.SET_ACTIVE_TOKEN.FAILURE](state) {
    state.activeToken = null;
    state.setActiveTokenState = ASYNC_STATES.ERROR;
  },

  [types.CHANGE_TOKEN_SELECTED](state, contract) {
    const indexInTokens = state.exchangeTokens.findIndex(item => item.contract === contract);

    if (indexInTokens !== -1) {
      state.exchangeTokens[indexInTokens].isMy = !state.exchangeTokens[indexInTokens].isMy;
    }

    const index = state.selectedTokens.indexOf(contract);

    if (index !== -1) {
      state.selectedTokens.splice(index, 1);
    } else {
      state.selectedTokens.push(contract);
    }

    setStorageArray(MY_TOKENS_LOCAL_STORAGE_KEY, state.selectedTokens);
  },

  [types.LOAD_SELECTED_TOKENS](state) {
    // Обнуление статусов МойТокен для всех токенов
    for (let i = 0; i < state.exchangeTokens.length; i += 1) {
      state.exchangeTokens[i].isMy = false;
    }

    const selectedTokens = window.localStorage.getItem(MY_TOKENS_LOCAL_STORAGE_KEY);
    if (selectedTokens) {
      state.selectedTokens = JSON.parse(selectedTokens);

      for (let i = 0; i < state.selectedTokens.length; i += 1) {
        const index = state.exchangeTokens.findIndex(item => item.contract === state.selectedTokens[i]);

        if (index !== -1) {
          state.exchangeTokens[index].isMy = true;
        }
      }
    }
  },

  [types.SET_TOKEN_2](state, { prevTokenState, nextTokenState }) {
    if (prevTokenState) {
      Object.assign(prevTokenState, nextTokenState);
    } else {
      state.exchangeTokens.push(nextTokenState);
    }
  },

  /**
   * Mutation: Начало асинхронной загрузки числовых данных по токенам
   *
   * @since Version 1.1.0
   */
  [types.GET_TOKENS_NUMBERS_ASYNC.PENDING](state) {
    state.tokensNumbersLoading = true;
  },


  /**
   * Mutation: Обработка ошибки при асинхронной загрузке числовых данных по токенам
   *
   * @since Version 1.1.0
   */
  [types.GET_TOKENS_NUMBERS_ASYNC.FAILURE](state, error) {
    state.tokensNumbersLoading = false;
    log.error(`Error loading token numbers from backend: ${error}`, {
      module: 'contracts',
      layer: 'backend',
    });
  },


  /**
   * Mutation: Обработка успешной асинхронной загрузки числовых данных по токенам
   *
   * @since Version 1.1.0
   */
  [types.GET_TOKENS_NUMBERS_ASYNC.SUCCESS](state) {
    state.tokensNumbersLoading = false;
  },

  [types.TOKEN_NUMBERS_ADD_LISTENER](state) {
    state.tokenNumbersListenerCount += 1;
  },

  [types.TOKEN_NUMBERS_REMOVE_LISTENER](state) {
    state.tokenNumbersListenerCount -= 1;
  },
};

export default mutations;



