import log from '@/util/logger';
import * as types from '@/store/mutation-types';

export default {
  // Загрузка старницы с историей
  [types.GET_HISTORY_ASYNC.SUCCESS](state, { pageNumber, pageData, pageCount }) {
    state.historyPageLoading = false;
    state.activePage = pageData;
    state.numberOfPages = pageCount;
    state.activePageNumber = pageNumber;
  },

  [types.GET_HISTORY_ASYNC.PENDING](state) {
    state.historyPageLoading = true;
  },

  [types.GET_HISTORY_ASYNC.FAILURE](state, error) {
    state.historyPageLoading = false;
    log.error(`Backend history unavailable: ${error}`, {
      module: 'history',
      layer: 'backend',
    });
  },
  // ----------------------------------------------
};



