<template>
  <div
    class="e-text-input-length-validated"
    :class="inputClass"
  >
    <e-text-input
      v-model="actualValue"
      v-bind="{ label, width, maxlength, ...$attrs }"
      class="e-text-input-length-validated__input"
      :left-outer-icon="icon"
      :left-outer-icon-size="iconSize"
      :right-inner-icon="validationIcon"
      @focus="isFocused = true"
      @blur="isFocused = false"
    />
    <div class="e-text-input-length-validated__counter">
      <span class="e-text-input-length-validated__counter-text">
        {{ valueLength }}/{{ maxlength }}
      </span>
    </div>
  </div>
</template>

<script>
import validIcon from '@/icons/e-icon_checkbox-round.svg';
import ETextInput from '@/components/ETextInput';

export default {
  name: 'e-text-input-length-validated',
  components: { ETextInput },

  props: {
    maxlength: {
      type: Number,
      required: true,
    },

    vuelidateProperty: {
      type: Object,
      required: true,
    },

    value: {
      type: String,
      default: '',
    },

    label: String,
    icon: String,
    iconSize: String,

    width: {
      type: String,
      default: 'xl',
    },
  },

  data() {
    return {
      actualValue: this.value,
      isFocused: false,
    };
  },

  computed: {
    isDirty() {
      return this.vuelidateProperty.$dirty;
    },

    valueLength: ({ actualValue }) => actualValue.length,

    validationIcon() {
      return this.isValid ? validIcon : null;
    },

    inputClass() {
      if (this.isValid) return 'valid';
      if (this.isOverflow) return 'error';
      if (this.isFocused) return 'in-progress';
      return this.isDirty ? 'error' : 'untouched';
    },

    isOverflow: ({ valueLength, maxlength }) => valueLength > maxlength,

    isValid: ({ vuelidateProperty }) => !vuelidateProperty.$invalid,
  },

  watch: {
    actualValue(val) {
      this.$emit('input', val);
      this.vuelidateProperty.$touch();
    },
  },

  methods: {
    blurHandler(val) {
      this.$emit('blur', val);
    },
    focusHandler(val) {
      this.$emit('focus', val);
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-text-input-length-validated {
  $this: &;

  display: flex;

  &__counter {
    display: none;
    height: 60px;
    border-radius: 0 30px 30px 0;
    min-width: 80px;
    font-weight: 400;
    line-height: 30px;
    font-size: 15px;
    margin-left: -4px;

    @include r(1200) {
      height: 50px;
    }
  }

  &__counter-text {
    mix-blend-mode: normal;
    opacity: 0.5;
  }

  // fixing

  .e-text-input {
    &__left-outer-icon-wrap {
      position: absolute;
      left: -15px + -44px;
      z-index: 1;
      width: 44px;
      min-width: 44px;
      max-width: 44px;
    }
  }

  .e-icon_wallet-adress_darked {
    fill: #878794;
  }

  // in-progress

  &.in-progress {
    #{$this}__input {
      fill: #647AFD;
    }

    .e-icon_wallet-adress_darked {
      fill: $color-blue;
    }
  }

  &.in-progress .e-text-input__box {
    border-color: #0123e7;
  }

  &.in-progress .e-text-input__input {
    color: #0123e7;
  }

  &.in-progress &__counter {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #0123e7;
    background: #f8f9fc;
  }

  // valid

  &.valid {
    #{$this}__input {
      fill: #1fce43;
    }

    .e-icon_wallet-adress_darked {
      fill: #0D8E22;
    }
  }

  &.valid .e-text-input__box {
    border-color: #1fce43;
  }

  &.valid .e-text-input__input {
    color: #1fce43;
  }

  &.valid &__counter {
    display: none;
  }

  // overflow

  &.error {
    #{$this}__input {
      fill: #da4830;
    }

    .e-icon_wallet-adress_darked {
      fill: #9B1600;
    }
  }

  &.error .e-text-input__box {
    border-color: #da4830;
  }

  &.error input {
    color: #da4830;
  }

  &.error &__counter {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #da4830;
    background: rgba(218, 72, 48, 0.08);
  }

  &.focused {
    fill: #647AFD;

    .e-icon_wallet-adress_darked {
      fill: #0123E7;
    }
  }
}
</style>



