/* eslint-disable no-restricted-syntax */
import {
  GAS_LIMIT_DEPOSIT_ETH_REF,
  GAS_LIMIT_DEPOSIT_TOKEN,
  GAS_LIMIT_TOKEN_APPROVE,
} from '@/api/ethereum';
import { AUTO_DEPOSIT_MIN_FEES_IN_VALUE } from '@/config';
import { ETH_ADDRESS, PENDING_STATUS } from '@/util/constants';

// deposit

const ethDepositGasFee = (state, getters, rootState, rootGetters) => {
  const gasPrice = rootGetters['blockchain/backendGasPrice'];
  if (gasPrice == null) return null;
  const gas = GAS_LIMIT_DEPOSIT_ETH_REF;
  return gasPrice.times(gas);
};

const tokenDepositGasFee = (state, getters, rootState, rootGetters) => {
  const gasPrice = rootGetters['blockchain/backendGasPrice'];
  if (gasPrice == null) return null;
  const gas = GAS_LIMIT_TOKEN_APPROVE + GAS_LIMIT_DEPOSIT_TOKEN;
  return gasPrice.times(gas);
};

const isAbleToDepositEth = (state, getters, rootState, rootGetters) => {
  const gasPrice = rootGetters['blockchain/backendGasPrice'];
  if (gasPrice == null) return false;
  const balance = rootGetters['balance/privateEthAvailable'];
  const gas = GAS_LIMIT_DEPOSIT_ETH_REF;
  const fee = gasPrice.times(gas);
  const minValue = fee.times(AUTO_DEPOSIT_MIN_FEES_IN_VALUE);
  return balance.minus(minValue).minus(fee).gte(0);
};

/**
 * Getter with param: Checks if user has enough ETH to deposit token.
 */
const isAbleToPayTokenFee = (state, getters, rootState, rootGetters) => {
  const gasPrice = rootGetters['blockchain/backendGasPrice'];
  if (gasPrice == null) return false;
  const balance = rootGetters['balance/privateEthAvailable'];
  const gas = GAS_LIMIT_TOKEN_APPROVE + GAS_LIMIT_DEPOSIT_TOKEN;
  const value = balance.minus(gasPrice.times(gas));
  return value.gte(0);
};

// current balance deposit / withdrawal / transfer operation

/**
 * Getter: For balance transfer dialog.
 * Flag that specifies if the deposit/withdrawal/transfer dialog is active (e.g. displayed) at the moment.
 *
 * @returns {boolean}
 */
const tokenTransferActive = state => state.tokenTransfer.operation !== null;

const btcTransferActive = state => state.btcTransferActive;

/**
 * Getter: For balance transfer dialog.
 * Balance operation is selected in balance transfer dialog: deposit/withdrawal/transfer.
 * Returns null if dialog is closed at the moment.
 *
 * @returns {'deposit'|'withdrawal'|'transfer'|null}
 */
const tokenTransferOperation = state => state.tokenTransfer.operation;

/**
 * Getter: For balance transfer dialog.
 * Returns data about token selected to be transferred in balance transfer dialog.
 * It includes token's contract address, name, decimals, etc.
 *
 * @returns {{ contract, name, shortName, decimals, balance }}
 */
const transferredToken = ({ tokenTransfer }, getters, rootState, rootGetters) => {
  const contract = tokenTransfer.tokenContract;
  const token = rootGetters['tokens/tokenByContract'](contract);
  if (tokenTransferActive && token) {
    let balance;
    if (contract === ETH_ADDRESS) {
      balance = {
        private: rootGetters['balance/privateEthAvailable'],
        tradable: rootGetters['balance/contractEthAvailable'],
      };
    } else {
      const privateBalance = rootGetters['balance/getPrivateBalanceFor'](contract);
      const contractBalance = rootGetters['balance/getContractBalanceFor'](contract);
      balance = {
        private: privateBalance,
        tradable: contractBalance,
      };
    }
    const { name, shortName, decimals } = token;
    return { contract, name, shortName, decimals, balance };
  }
  return null;
};

// other balance deposit / withdrawal / transfer operations

/**
 * Getter: All active user new and pending balance transfer operations in object (hash/hashmap) for active user.
 * Key is token contract address and value is array of associated balance transfer (deposit/withdrawal/transfer) operations.
 */
const pendingOperationsMap = (state, getters, rootState, { 'wallets/activeWalletAddress': user }) => {
  const result = {};
  const concatToMap = (operations) => {
    for (const { tokenAddress, ...operation } of operations) {
      const tokenOps = result[tokenAddress];
      if (tokenOps) {
        tokenOps.push(operation);
      } else {
        result[tokenAddress] = [operation];
      }
    }
  };
  concatToMap(state.pendingOperations[user] || []);
  concatToMap(state.newOperations[user] || []);
  return result;
};

/**
 * Getter: All active user new and pending balance transfer operations in array for active user.
 * All of the new transactions has status 'pending' but transactionHash == null.
 * All of the pending transactions has status 'pending' and transactionHash != null.
 */
const newAndPendingOperations = (state, getters, rootState, { 'wallets/activeWalletAddress': user }) => (
  (state.pendingOperations[user] || [])
    .concat(state.newOperations[user] || [])
);

/**
 * Getter: All active user pending balance transfer operations in array for active user.
 * All of the pending transactions has status 'pending' and transactionHash != null.
 * Used only in other getters.
 */
const pendingOperations = (state, getters, rootState, { 'wallets/activeWalletAddress': user }) => (
  (state.pendingOperations[user] || [])
    .map(o => ({ ...o, status: PENDING_STATUS }))
);

/**
 * Getter: Count of blocks that should be mined after the block with balance operation (deposit/withdrawal/transfer)
 * in order to mark operation as confirmed. This is needed to reduce risk of operation to end up being in uncle block after it is confirmed.
 */
const fundConfirmations = state => state.fundConfirmations;

