export default {
  ethDepositGasFee,
  tokenDepositGasFee,
  isAbleToDepositEth,
  isAbleToPayTokenFee,
  tokenTransferActive,
  btcTransferActive,
  tokenTransferOperation,
  transferredToken,
  pendingOperationsMap,
  newAndPendingOperations,
  pendingOperations,
  // userOperations,
  fundConfirmations,
};



