<template>
  <div class="the-token-search">
    <div class="the-token-search__search">
      <input
        class="the-token-search__search-input"
        :value="value"
        @input="updateValue($event.target.value)"
        placeholder="Search..."
        ref="inputField"
      >
      <e-icon :src="iconSearch"/>
    </div>
  </div>
</template>

<script>
import iconSearch from '@/icons/e-icon_search_wide.svg';
import iconClose from '@/icons/e-icon_close.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'the-token-search',

  data() {
    return {
      iconSearch,
      iconClose,
    };
  },

  props: {
    value: {
      type: String,
      default: '',
    },
    isVisible: {
      type: Boolean,
      default: false,
    },
  },

  watch: {
    isVisible: 'inputFocusOn',
  },

  methods: {
    hide() {
      this.$emit('hide', this.active);
      this.updateValue('');
    },

    updateValue(value) {
      this.$emit('input', value);
    },

    inputFocusOn() {
      if (this.isVisible) {
        this.$refs.inputField.focus();
      }
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-token-search {
  width: 100%;

  &__search {
    position: relative;
    height: 24px;
    width: 100%;

    .e-icon {
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
      height: 12px;
      width: 12px;
      fill: $color-blue;
    }
  }

  &__search-input {
    height: 100%;
    box-shadow: inset 0 0 3px rgba(72, 78, 84, 0.13);
    box-sizing: border-box;
    border-radius: 3px;
    padding-left: 29px;
    font-weight: bold;
    line-height: 14px;
    font-size: 11px;
    width: 100%;
    background-color: transparent;
    color: rgba($color-dark-blue, 0.5);

    &::placeholder {
      color: rgba($color-dark-blue, 0.5);
    }
  }
}

</style>




