import { TimeoutError } from './errors';

/**
 * Оборачивает Promise в такой, который будет rejected после заданного timeout-а.
 *
 * @param {Promise<any>} promiseToWait
 * @param {number} maxDelay max millis to wait before reject
 * @return {Promise<any>}
 */
export default (promiseToWait, maxDelay) => new Promise(async (resolve, reject) => {
  const timer = setTimeout(
    () => reject(new TimeoutError(undefined, maxDelay)),
    maxDelay,
  );
  promiseToWait.then(resolve, reject).finally(() => clearTimeout(timer));
});



