import { TIMEOUT_LEDGER_GET_ACCOUNTS } from '@/config';
import getLedgerApp from './getLedgerApp';

const getStartingIndex = (path) => {
  const [lvl5] = path.split(/\//).slice(5);
  return parseInt(lvl5 || '0', 10);
};

const getDerivationPathWithoutIndex = (path) => {
  const [lvl3, lvl4] = path.split(/\//).slice(3);
  return lvl4
    ? `m/44'/60'/${lvl3}/${lvl4}/`
    : `m/44'/60'/${lvl3}/`;
};

const ADDRESS_COUNT = 3;

export default async (derivationPathPrefix = "m/44'/60'/0'/0", start = 0) => {
  const ledgerApp = await getLedgerApp({ exchangeTimeout: TIMEOUT_LEDGER_GET_ACCOUNTS });
  const derivationPathWithoutIndex = getDerivationPathWithoutIndex(derivationPathPrefix);
  const accounts = [];
  const startingIndex = getStartingIndex(derivationPathPrefix + start);
  for (let i = startingIndex; i < startingIndex + ADDRESS_COUNT; i += 1) {
    const derivationPath = `${derivationPathWithoutIndex}${i}`;
    // eslint-disable-next-line no-await-in-loop
    const { address } = await ledgerApp.getAddress(derivationPath);
    accounts.push({ address, derivationPath });
  }
  return accounts;
};



