/**
 * @module store/tokens/helpers
 * @version 1.1.1
 */

import { BigNumber } from 'bignumber.js';
import toDisplayedPrice from '@/util/format/toDisplayedPrice';
import { ETH_DECIMALS } from '@/util/constants';

/**
 * Функция вычисления marketcap
 *
 * @since Version 1.0.2
 */
const marketcap = (lastPrice, totalSupply, tokenDecimals) => {
  let res = null;
  if (lastPrice !== null && totalSupply !== null) {
    res = totalSupply.shiftedBy(ETH_DECIMALS - tokenDecimals).times(lastPrice);
  }
  return res;
};

/**
 * Мепит числвые данные возвращаемые бекендом в процессе обновления информации по токену.
 *
 * @param {Object} token - Числовые данные токена возвращаемая бекендом.
 */
export const mapTokenNumbers = (token) => {
  const decimals = token.decimals;
  const price = token.numbers.last_price ? toDisplayedPrice(token.numbers.last_price, decimals) : BigNumber(0);
  const price24hBefore = token.numbers.price_24h_before ? toDisplayedPrice(token.numbers.price_24h_before, decimals) : BigNumber(0);
  const priceDiff24 = price.minus(price24hBefore);
  const priceDiff24InPercent = price24hBefore.isZero() || price.isZero() ? BigNumber(0) : priceDiff24.times(100).div(price24hBefore);
  const totalSupply = token.total_supply ? BigNumber(token.total_supply) : null;

  return {
    contract: token.contract_address,
    id: token.id,
    total: BigNumber(token.numbers.orderbook_qty),
    orderbookDepth: BigNumber(token.numbers.orderbook_depth),
    volume24h: BigNumber(token.numbers.volume_24h),
    price,
    increasing24: priceDiff24.gte(0),
    priceDiff24InPercent,
    marketcap: marketcap(price.isZero() ? null : price, totalSupply, decimals),
    candles24h: token.numbers.prices_24h.map(({ price: priceToken, at }) => ({ price: priceToken, time: at })), // TODO: remove
  };
};

/**
 * Мепит данные возвращаемые бекендом по токену.
 * @param {Object} token - Информация по токену возвращаемая бекендом.
 *
 * @since 1.0.0
 */
export const mapToken = token => ({
  createdAt: new Date(token.created_at),
  decimals: token.decimals,
  img: token.images,
  isMy: false,
  name: token.name,
  shortName: token.symbol,
  twitterUrl: token.twitter_url,
  websiteUrl: token.website_url,
  isWebsiteVerified: Boolean(token.is_website_verified),
  ...mapTokenNumbers(token),
});



