/* eslint-disable no-restricted-syntax */
/**
 * @module store/balance/getters
 * @version 1.0.1
 */

import { BigNumber } from 'bignumber.js';
import pick from '@/util/pick';
import { ZERO } from '@/util/numbers';
import { ETH_DECIMALS, ETH_ADDRESS } from '@/util/constants';
import {
  TOKEN_PROPS,
  outgoingPrivateFunds,
  privateFees,
  outgoingTradableFunds,
  tradableFees,
  reservedTradableFunds,
} from './helpers';

//
// Private balance
//

/**
 * Getter: Private Ether balance in wei.
 * Excludes Ether that is used in pending transactions.
 *
 * @see src/store/modules/balance/README.md Private BalanceWithoutPending
 * @returns {BigNumber}
 */
const privateEthAvailable = (state, getters, rootState, {
  'deposit/newAndPendingOperations': operations,
  'deposit/pendingOperationsMap': operationsMap,
}) => {
  if (state.privateLoading) return ZERO;
  const blockchainBalance = state.privateBalance.eth;
  const outgoingEth = outgoingPrivateFunds(ETH_ADDRESS, operationsMap);
  const ethReservedForGasFees = privateFees(operations);
  return BigNumber.max(0, blockchainBalance.minus(outgoingEth).minus(ethReservedForGasFees));
};

/**
 * Getter with param:
 * Tradable balance for specified tokenContract
 *
 * @param tokenContract contract of token
 * @returns {BigNumber}
 */
const getPrivateBalanceFor = (state, getters, rootState, rootGetters) => (tokenContract) => {
  const blockchainBalance = state.privateBalance.tokens[tokenContract] || ZERO;
  const pending = outgoingPrivateFunds(tokenContract, rootGetters['deposit/pendingOperationsMap']);
  return BigNumber.max(0, blockchainBalance.minus(pending));
};

/**
 * Getter: User private token balance.
 * Excludes balance that is used in pending transactions (unavailable).
 * Tokens, which has all their balance unavailabe will be also returned with privateBalance = 0.
 * Used only in other getters
 *
 * @private
 * @returns {{contract: string, privateBalance: BigNumber}[]}
 * @since Version 1.0.1
 */
const privateTokens = (state, getters, rootState, {
  'tokens/tokenByContractSimple': getToken,
  'deposit/pendingOperationsMap': operationsMap,
}) => {
  if (state.privateLoading) return [];
  const tokensBalance = state.privateBalance.tokens;
  return Object.keys(tokensBalance)
    .filter((contract) => {
      const tokenBalance = tokensBalance[contract];
      return tokenBalance && !tokenBalance.isZero();
    })
    .map(contract => getToken(contract))
    .filter(token => token)
    .map((token) => {
      const { contract } = token;
      const blockchainBalance = tokensBalance[contract];
      const pending = outgoingPrivateFunds(contract, operationsMap);
      return {
        privateBalance: BigNumber.max(0, blockchainBalance.minus(pending)),
        ...pick(token, TOKEN_PROPS),
      };
    });
};

/**
 * True if there are any token count in blockchain balance.
 * False if there are still tokens in blockchain account, but all of them are in pending transfer (0 tokens available).
 *
 * @returns {boolean}
 */
const privateTokensAbsent = state => Object.values(state.privateBalance.tokens).every(v => v.isZero());

//
// Contract balance
//

/**
 * Getter: Contract Ether in wei
 * Excludes Ether that is used in pending transactions.
 *
 * @see src/store/modules/balance/README.md Tradable BalanceWithoutPending
 * @returns {BigNumber}
 */
const contractEthAvailable = (state, getters, rootState, {
  'deposit/newAndPendingOperations': operations,
  'deposit/pendingOperationsMap': operationsMap,
}) => {
  if (state.tradableLoading) return ZERO;
  const blockchainBalance = state.tradableBalance[ETH_ADDRESS] || ZERO;
  const outgoingEth = outgoingTradableFunds(ETH_ADDRESS, operationsMap);
  const reservedForWithdrawFee = tradableFees(operations);
  return BigNumber.max(0, blockchainBalance.minus(outgoingEth).minus(reservedForWithdrawFee));
};

/**
 * Getter: Tradable Ether balance in wei.
 * Excludes Ether that is used in pending transactions and Ether that is reserved for orders execution.
 *
 * @see src/store/modules/balance/README.md Private BalanceWithoutPending
 * @returns {BigNumber}
 */
const contractEthNotInOrders = (state, getters) => {
  const availablePlusInOrders = getters.contractEthAvailable;
  const reserved = reservedTradableFunds(ETH_ADDRESS, state.reserved);
  return BigNumber.max(0, availablePlusInOrders.minus(reserved));
};

/**
 * Getter: Available Ether balance
 * @returns {BigNumber}
 */
const availableBalance = state => BigNumber.max(0, state.availableBalance);

/**
 * Баланс токенов у пользователя в контракте.
 * Возвращает токены, весь баланс которых переводиться куда-то (не доступен). У них будет tradableBalance = 0.
 *
 * @private
 * @returns {{contract: string, tradableBalance: BigNumber}[]}
 */
const contractTokens = (state, getters, rootState, { 'tokens/tokenByContractSimple': getToken }) => {
  if (state.tradableLoading) return [];
  const tokensBalance = state.tradableBalance;
  return Object.keys(tokensBalance)
    .filter((contract) => {
      const tokenBalance = tokensBalance[contract];
      return tokenBalance && !tokenBalance.isZero();
    })
    .map(contract => getToken(contract))
    .filter(token => token != null)
    .map((token) => {
      const balance = getters.getContractBalanceFor(token.contract);
      return {
        tradableBalance: balance,
        ...pick(token, TOKEN_PROPS),
      };
    });
};

/**
 * Баланс токенов у пользователя в контракте с дополнительной статистикой
 * Не возвращает токены, если их весь баланс есть, но куда-то переводиться (не доступен).
 *
 * @returns {{contract: string, tradableBalance: BigNumber}[]}
 */
const contractTokensBalanceAndStats = (
  state,
  getters,
  rootState,
  { 'tokens/tokenByContractSimple': getToken },
) => getters.contractTokens
  .filter(t => !t.tradableBalance.isZero())
  .map((tokenBalance) => {
    const { contract, tradableBalance } = tokenBalance;
    const token = getToken(contract);
    return {
      ...token,
      ...tokenBalance,
      tradableEthValue: BigNumber(tradableBalance).times(token.price).shiftedBy(ETH_DECIMALS - token.decimals),
    };
  })
  .sort((o1, o2) => {
    const n1 = o1.tradableBalance.shiftedBy(-o1.decimals);
    const n2 = o2.tradableBalance.shiftedBy(-o2.decimals);
    const numComparisonResult = -n1.comparedTo(n2);
    return numComparisonResult === 0
      ? o1.contract.localeCompare(o2.contract)
      : numComparisonResult;
  });

/**
 * Getter: суммирует все доступные tradable токены и возвращает их сумму.
 * Токены, которые куда-то переводиться (не доступены) не беруться в расчёт (отнимаются от баланса в блокчейне).
 *
 * @returns {BigNumber}
 */
const contractTokenBalanceSum = (state, getters) => getters.contractTokens.reduce(
  (acc, t) => acc.plus(t.tradableBalance.shiftedBy(-t.decimals)),
  ZERO,
);

/**
 * Getter: Private balance for active token
 * Не учитывает токены, которые куда-то переводиться (не доступены).
 *
 * @returns {BigNumber}
 */
const activeTokenPrivateBalance = (state, getters, rootState, {
  'tokens/activeTokenContract': tokenContract,
}) => (
  getters.getPrivateBalanceFor(tokenContract)
);

/**
 * Getter: Tradable balance for active token
 * Не учитывает токены, которые куда-то переводиться (не доступены).
 *
 * @returns {BigNumber}
 */
const activeTokenContractBalance = (state, getters, rootState, {
  'tokens/activeTokenContract': tokenContract,
}) => (
  getters.getContractBalanceFor(tokenContract)
);

/**
 * Getter: Tradable balance for active token
 * Не учитывает токены, которые куда-то переводиться (не доступены).
 *
 * @returns {BigNumber}
 */
const activeTokenContractBalanceNotInOrders = (state, getters, rootState, {
  'tokens/activeTokenContract': tokenContract,
}) => getters.getContractBalanceNotInOrdersFor(tokenContract);

/**
 * Getter with param:
 * Tradable balance for specified tokenContract
 *
 * @param tokenContract contract of token
 * @returns {BigNumber}
 */
const getContractBalanceFor = (state, getters, rootState, rootGetters) => (tokenContract) => {
  const blockchainBalance = state.tradableBalance[tokenContract] || ZERO;
  const pending = outgoingTradableFunds(tokenContract, rootGetters['deposit/pendingOperationsMap']);
  return BigNumber.max(0, blockchainBalance.minus(pending));
};

/**
 * Getter with param:
 * Tradable balance for specified tokenContract
 *
 * @param tokenContract contract of token
 * @returns {BigNumber}
 */
const getContractBalanceNotInOrdersFor = (state, getters) => (tokenContract) => {
  const blockchainBalance = getters.getContractBalanceFor(tokenContract);
  const reserved = reservedTradableFunds(tokenContract, state.reserved);
  return BigNumber.max(0, blockchainBalance.minus(reserved));
};

//
// Both tradable and private balance getters
//

/**
 * Getter: Возвращает флаг загружается в текущий момент (true) баланс или нет (false).
 * True даже если это не первая загрузка, а перезагрузка (например, когда приложение вышло из спящего режима).
 *
 * @returns {boolean}
 * @since Version 1.0.1
 */
const isBalanceRefreshing = state => state.privateLoading ||
  state.tradableLoading ||
  state.privateReloading ||
  state.tradableReloading;

const isPrivateBalanceRefreshing = state => state.privateLoading || state.privateReloading;

const isContractBalanceRefreshing = state => state.tradableLoading || state.tradableReloading;

/**
 * Getter with param: Brief description of single token balance.
 * Accepts token contract address as parameter.
 * All numbers are whole (ETH, not wei).
 *
 * @returns {function(string): ({privateBalance: BigNumber, contractBalance: BigNumber, inOrders: BigNumber, sum: BigNumber})}
 */
const getUserBalanceByToken = (state, getters, rootState, rootGetters) => (contract) => {
  const pendingOperations = rootGetters['deposit/pendingOperationsMap'];
  if (contract === ETH_ADDRESS) {
    const ethAvailableBalance = state.availableLoading ? ZERO : getters.availableBalance;
    const ethPrivateBalance = state.privateLoading ? ZERO : getters.privateEthAvailable;
    const ethContractBalance = state.tradableLoading ? ZERO : getters.contractEthAvailable;
    const ethInOrders = state.tradableLoading ? ZERO : reservedTradableFunds(ETH_ADDRESS, state.reserved);

    return {
      availableBalance: ethAvailableBalance.shiftedBy(-ETH_DECIMALS),
      privateBalance: ethPrivateBalance.shiftedBy(-ETH_DECIMALS),
      contractBalance: ethContractBalance.shiftedBy(-ETH_DECIMALS),
      inOrders: ethInOrders.shiftedBy(-ETH_DECIMALS),
      sum: ethPrivateBalance.plus(ethContractBalance).shiftedBy(-ETH_DECIMALS),
      operations: pendingOperations[contract] || [],
    };
  }

  const tokenData = rootGetters['tokens/tokenByContractSimple'](contract);
  if (tokenData) {
    const { decimals } = tokenData;
    const contractBalance = state.tradableLoading ? ZERO : (state.tradableBalance[contract] || ZERO);
    const tokenAvailableBalance = state.availableLoading ? ZERO : (state.availableBalance || ZERO);
    const inOrders = state.tradableLoading ? ZERO : reservedTradableFunds(contract, state.reserved);
    let privateFunds;
    if (state.privateLoading) {
      privateFunds = ZERO;
    } else {
      const outgoingFunds = outgoingPrivateFunds(contract, rootGetters['deposit/pendingOperationsMap']);
      const privateBlockchainBalance = state.privateBalance.tokens[contract] || ZERO;
      privateFunds = BigNumber.max((privateBlockchainBalance).minus(outgoingFunds), ZERO);
    }
    return {
      availableBalance: tokenAvailableBalance.shiftedBy(-decimals),
      contractBalance: contractBalance.shiftedBy(-decimals),
      inOrders: inOrders.shiftedBy(-decimals),
      privateBalance: privateFunds.shiftedBy(-decimals),
      sum: privateFunds.plus(contractBalance).shiftedBy(-decimals),
      operations: pendingOperations[contract] || [],
    };
  }
  return null;
};

/**
 * Returns an array of owned currencies.
 * User owns all tokens that has non-zero tradable or private balance and the ones which are used in pending transactions.
 * Ether comes first. Followed by ERC20 Tokens.
 *
 * @returns {{contract: string, name: string}[]}
 */
const ethAndOwnedTokens = (state, getters, rootState, rootGetters) => (
  [
    ...new Set([
      ETH_ADDRESS,
      ...Object.keys(state.privateBalance.tokens).filter((contract) => {
        const tokenBalance = state.privateBalance.tokens[contract];
        return tokenBalance && !tokenBalance.isZero();
      }),
      ...Object.keys(state.tradableBalance).filter((contract) => {
        const tokenBalance = state.tradableBalance[contract];
        return tokenBalance && !tokenBalance.isZero();
      }),
    ]),
  ]
    .map(c => rootGetters['tokens/tokenByContract'](c))
    .filter(c => c != null)
    .map(token => pick(token, ['contract', 'name', 'shortName']))
);

/**
 * Tokens, that are not used in pending transactions and have both their tradable and private balance === 0.
 * @returns {Token[]}
 */
const missingTokens = (state, getters, rootState, rootGetters) => {
  const all = rootGetters['tokens/sortedTokens'];
  if (state.privateLoading || state.tradableLoading) return all;
  const positiveBalanceTokens = getters.userEthAndTokens.map(t => t.contract);
  return all.filter(t => !positiveBalanceTokens.includes(t.contract));
};

/**
 * Getter:
 * Balance of all tokens that user owns.
 * User owns all tokens that has non-zero tradable or private balance and the ones which are used in pending transactions.
 * Ether comes first. Followed by ERC20 Tokens.
 *
 * @returns {*[]}
 */
const userEthAndTokens = (state, getters, rootState, rootGetters) => {
  if (state.privateLoading || state.tradableLoading) {
    return [
      {
        ...ETH_ADDRESS,
        privateBalance: ZERO,
        tradableBalance: ZERO,
        operations: [],
      },
    ];
  }
  const pendingOperations = rootGetters['deposit/pendingOperationsMap'];
  const tokensArr = [];
  const contractToElementMap = {}; // optimizes access

  getters.contractTokens.forEach((element) => {
    const { contract } = element;
    const nextVal = {
      ...element,
      privateBalance: ZERO,
      operations: pendingOperations[contract] || [],
    };
    contractToElementMap[contract] = nextVal;
    tokensArr.push(nextVal);
  });

  getters.privateTokens.forEach((element) => {
    const { contract, privateBalance } = element;
    if (contractToElementMap[contract]) {
      contractToElementMap[contract].privateBalance = privateBalance;
    } else {
      const nextVal = {
        ...element,
        tradableBalance: ZERO,
        operations: pendingOperations[contract] || [],
      };
      tokensArr.push(nextVal);
    }
  });

  tokensArr
    .sort((o1, o2) => {
      const tb1 = o1.tradableBalance.shiftedBy(-o1.decimals);
      const tb2 = o2.tradableBalance.shiftedBy(-o2.decimals);
      let result = -tb1.comparedTo(tb2);
      if (result === 0) {
        const pb1 = o1.privateBalance.shiftedBy(-o1.decimals);
        const pb2 = o2.privateBalance.shiftedBy(-o2.decimals);
        result = -pb1.comparedTo(pb2);
        if (result === 0) {
          result = o1.contract.localeCompare(o2.contract);
        }
      }
      return result;
    });

  const ethBalance = {
    ...ETH_ADDRESS,
    privateBalance: getters.privateEthAvailable,
    tradableBalance: getters.contractEthAvailable,
    operations: pendingOperations[ETH_ADDRESS] || [],
  };
  return [ethBalance, ...tokensArr];
};

/**
 * True if there are any token count in blockchain balance.
 * False if there are still tokens in blockchain account, but all of them are in pending transfer (0 tokens available).
 *
 * @returns {boolean}
 */
const userBalanceAbsent = (state, getters, rootState, rootGetters) => (
  state.privateLoading ||
  state.tradableLoading ||
  !rootGetters['wallets/isLogin'] ||
  (
    state.privateBalance.eth.isZero() &&
    getters.privateTokensAbsent &&
    Object.values(state.tradableBalance).every(v => v.isZero())
  )
);

const baseToken = state => state.baseToken;
const btcBaseToken = state => state.btcBaseToken;

