<template>
  <div
    class="e-preloader"
    title="1"
    :class="smallPreloader ? 'e-preloader_small' : ''"
  >
  </div>
</template>

<script>
/**
 * Компонент прелоудера
 *
 * @version 1.0.1
 */
export default {
  name: 'e-preloader',
  data() {
    return {
      raf: '',
    };
  },

  props: {
    smallPreloader: {
      type: Boolean,
      default: false,
    },
  },
  mounted() {
    const that = this;
    this.$nextTick(() => {
      const borderWidth = 3;
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const visibleSize = this.smallPreloader ? 13 : 40;
      canvas.width = 80;
      canvas.height = 80;
      canvas.style.width = `${visibleSize}px`;
      canvas.style.height = `${visibleSize}px`;
      ctx.lineWidth = borderWidth * 2;
      this.$el.appendChild(canvas);
      let angle;
      function draw(t) {
        that.$data.raf = undefined;
        ctx.clearRect(0, 0, 80, 80);
        angle = (t / 100) % (2 * Math.PI);
        ctx.strokeStyle = '#17da9d'; // $color-green variable from sass
        ctx.beginPath();
        ctx.arc(40, 40, 40 - borderWidth - 4, angle, angle + (Math.PI / 2));
        ctx.stroke();
        that.$data.raf = window.requestAnimationFrame(draw);
      }
      draw();
    });
  },

  beforeDestroy() {
    window.cancelAnimationFrame(this.$data.raf);
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 40px;
  width: 40px;
  text-align: center;
  display: inline-block;
  vertical-align: top;
  transform: translate(-50%, -50%);
  z-index: 20;

  &_small {
    height: 8px;
    width: 8px;
  }
}
</style>



