/**
 * @module store/trades/actions
 * @version 1.0.5
 */

import { ASYNC_STATES } from '@/util/constants';
import log from '@/util/logger';
import * as types from '@/store/mutation-types';
import { getTrades, getVolumes, onTradeChange, removeTradeChangeHandler, onTradeVolumesChange, removeTradeVolumesListener, onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import { wrapCachedAction, resetCachedAction } from '@/store/utils';
import { mapTrade } from './helpers';

let tradeChangeHandler;
let tradeUnsyncHandler;
let tradeVolumesReconnectHandler;

/**
 * Action: Synchronous
 * Создает подписку на обновление информации по трейдам (и соответственно обрабатывает все изменения)
 * Should be called only after activeToken is set
 *
 * @since Version 1.0.3
 * @todo ensure contract loaded
 */
const listenToTradesChanges = async ({ state, rootGetters, commit, dispatch }) => {
  commit(types.TRADES_ADD_LISTENER);
  if (state.latestTradesListenersCount === 1) {
    tradeChangeHandler = (data) => {
      const trade = mapTrade(data, rootGetters['wallets/activeWalletAddress'], rootGetters['tokens/activeToken'].decimals);
      if (
        trade.contractAddress === rootGetters['contracts/activeMainContractAddress'] &&
        trade.tokenAddress === rootGetters['tokens/activeTokenContract']
      ) {
        // Adding to trades page
        if (!state.latestTradesLoading) {
          commit(types.UPSERT_LATEST_TRADE, trade);
        } else {
          commit(types.PUSH_TO_TRADES_BUFFER, trade);
        }

        // Добавление точки с ценой на график
        // if (!rootGetters['prices/pricesLoading']) {
        //   // Случай если сейчас не загружаются данные по свечам
        //   dispatch('prices/pushPrice', {
        //     id: trade.id,
        //     price: priceToNumber(trade.unitPrice),
        //     createdAt: trade.createdAt,
        //     action: data.event.action,
        //     isConfirmed: trade.isConfirmed,
        //   }, { root: true });
        // } else {
        //   // Случай когда сейчас загружаются данные по свечкам
        //   commit('prices/PUSH_TO_PRICES_BUFFER', trade, { root: true });
        // }

        // async balance update - only for non-internal trades
        if (trade.isConfirmed && trade.isMy && trade.transactionHash) {
          const balanceApi = rootGetters['blockchain/balanceApi'];
          balanceApi.getTradableEthAndToken({
            tokenAddress: trade.tokenAddress,
            blockNumber: trade.blockNumber,
          })
            .then((balance) => {
              commit('balance/UPDATE_TRADABLE_BALANCE', balance, { root: true });
            })
            .catch((error) => {
              log.warn(`Error on reserved balance update after trade event: ${error}`);
            });
        }
      }
    };
    tradeUnsyncHandler = () => {
      dispatch('loadLatestTrades');
    };
    onTradeChange(tradeChangeHandler);
    onReconnect(tradeUnsyncHandler);
    onResume(tradeUnsyncHandler);
  }
};

/**
 * Action: Synchronous
 * отписка от событий по trades
 *
 * @since Version 1.0.3
 */
const stopListeningToTradesChanges = ({ state, commit }) => {
  commit(types.TRADES_REMOVE_LISTENER);
  if (state.latestTradesListenersCount === 0) {
    removeTradeChangeHandler(tradeChangeHandler);
    removeReconnectListener(tradeUnsyncHandler);
    removeResumeListener(tradeUnsyncHandler);
  }
};

/**
 * Action: Загрузка страницы тредов
 * Should be called only after activeToken is set
 *
 * @since Version 1.0.4
 */
const loadLatestTrades = async ({ state, rootGetters, commit, dispatch }) => {
  commit(types.GET_LATEST_TRADES_ASYNC.PENDING);
  await Promise.all([
    dispatch('contracts/subscribeToContract', undefined, { root: true }), // ensures active contract loaded
    dispatch('tokens/subscribeToToken', undefined, { root: true }),
  ]);
  const filters = {
    token_address: rootGetters['tokens/activeTokenContract'],
    contract_address: rootGetters['contracts/activeMainContractAddress'],
  };

  // Если режим отображения трейдов "только внутренние"
  if (state.showOnlyInternalTrades) {
    filters.is_external = false;
  }

  try {
    const { data } = await getTrades({ filters });
    const trades = data.map(trade => mapTrade(trade, rootGetters['wallets/activeWalletAddress'], rootGetters['tokens/activeToken'].decimals));
    commit(types.GET_LATEST_TRADES_ASYNC.SUCCESS, trades);
  } catch (error) {
    commit(types.GET_LATEST_TRADES_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`Error loading trades page: ${error.message}`, {
        module: 'trades',
        layer: 'backend',
      });
    }
  }
};

const loadVolumes = wrapCachedAction('loadVolumes', async ({ commit, rootGetters }) => {
  commit(types.SET_VOLUMES_ASYNC.PENDING);
  try {
    commit(types.SET_VOLUMES_ASYNC.SUCCESS, await getVolumes());
  } catch (error) {
    commit(types.SET_VOLUMES_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`backend volumes unavailable: ${error.message}`, {
        module: 'trades',
        layer: 'backend',
      });
    }
  }
});

/**
 * Action: Synchronous
 * @todo check if volumes is loading at the moment and cancel event handling in that case
 */
const listenToTradeVolumesChange = ({ state, commit, dispatch }) => {
  commit(types.TRADE_VOLUMES_ADD_LISTENER);
  if (state.tradeVolumesListenersCount === 1) {
    const handler = (nextVolumes) => {
      if (state.volumesInitState !== ASYNC_STATES.PENDING) {
        commit(types.SET_VOLUMES_ASYNC.SUCCESS, nextVolumes);
      }
    };
    tradeVolumesReconnectHandler = () => {
      resetCachedAction('loadVolumes');
      dispatch('loadVolumes');
    };
    onTradeVolumesChange(handler);
    onReconnect(tradeVolumesReconnectHandler);
    onResume(tradeVolumesReconnectHandler);
  }
};

const stopListeningToTradeVolumesChanges = ({ state, commit }) => {
  commit(types.TRADE_VOLUMES_REMOVE_LISTENER);
  if (state.tradeVolumesListenersCount === 0) {
    removeTradeVolumesListener();
    removeReconnectListener(tradeVolumesReconnectHandler);
    removeResumeListener(tradeVolumesReconnectHandler);
  }
};

const showDetailsIfTradeIsMine = ({ commit, rootGetters }, trade) => {
  const userAddress = rootGetters['wallets/activeWalletAddress'];
  if (trade.makerAddress === userAddress || trade.takerAddress === userAddress) { // TODO: trade.isMy
    commit(types.SHOW_TRADE_DETAILS, trade);
  }
};

export default {
  loadLatestTrades,
  listenToTradesChanges,
  stopListeningToTradesChanges,
  loadVolumes,
  listenToTradeVolumesChange,
  stopListeningToTradeVolumesChanges,
  showDetailsIfTradeIsMine,
};



