import { BigNumber } from 'bignumber.js/bignumber';

import getNameForNamelessToken from '@/util/getNameForNamelessToken';
import toDisplayedPrice from '@/util/format/toDisplayedPrice';

import backend from './endpoint';

const mapSearchTokens = result => result.data.map(t => ({
  contract: t.token_address,
  name: t.name || getNameForNamelessToken(t.token_address),
  shortName: t.symbol || getNameForNamelessToken(t.token_address),
  websiteUrl: t.website_url,
  isWebsiteVerified: Boolean(t.is_website_verified),
}));

const mapSearchTokensStats = result => result.data.map((token) => {
  const decimals = token.decimals;
  const price = token.numbers.last_price ? toDisplayedPrice(token.numbers.last_price, decimals) : BigNumber(0);
  const price24hBefore = token.numbers.price_24h_before ? toDisplayedPrice(token.numbers.price_24h_before, decimals) : BigNumber(0);
  const priceDiff24 = price.minus(price24hBefore);
  const priceDiff24InPercent = price24hBefore.isZero() || price.isZero() ? BigNumber(0) : priceDiff24.times(100).div(price24hBefore);

  return {
    contract: token.contract_address,
    name: token.name || getNameForNamelessToken(token.contract_address),
    shortName: token.symbol || getNameForNamelessToken(token.contract_address),
    websiteUrl: token.website_url,
    isWebsiteVerified: Boolean(token.is_website_verified),
    price,
    increasing24: priceDiff24.gte(0),
    priceDiff24InPercent,
    decimals,
    prices24h: token.numbers.prices_24h.map(({ price: priceToken, at }) => ({ price: priceToken, time: at })),
    volume24h: BigNumber(token.numbers.volume_24h),
  };
});

// eslint-disable-next-line import/prefer-default-export
export const searchTokens = async (term) => {
  const result = await backend.http.get('/api/tokens/external-search', {
    params: { term },
  });
  return mapSearchTokens(result.data);
};

// eslint-disable-next-line import/prefer-default-export
export const searchTokensStats = async (contractAddress, term) => {
  const result = await backend.http.get('/api/tokens/search', {
    params: {
      contract_address: '0xa5CC679A3528956E8032df4F03756C077C1eE3F4',
      term,
    },
  });
  return mapSearchTokensStats(result.data);
};



