/**
 * @module store/ui/mutations
 *
 * @version 1.0.2
 */

import * as types from '@/store/mutation-types';
import { setStorageValue } from '@/util/storage';

let lastKey = -1;
const generateKey = () => {
  lastKey += 1;
  return lastKey;
};

const mutations = {
  // Задать размер окна для отслеживания ресайза
  [types.SET_WINDOW_SIZE](state, { width, height }) {
    state.windowSize.width = width;
    state.windowSize.height = height;
  },

  // Показать попап дисконекта
  [types.SET_DISCONNECT_POPUP_NOT_CLOSED](state) {
    state.isNetworkStatusPopupClosed = false;
  },

  // Скрыть попап дисконекта
  [types.CLOSE_DISCONNECT_POPUP](state) {
    state.isNetworkStatusPopupClosed = true;
  },

  // Скрыть попап
  [types.HIDE_POPUP](state) {
    state.isPopup = false;
  },

  // Задание какой из попапов показывать
  [types.SET_DISPLAYED_POPUP](state, popupId) {
    state.displayedPopup = popupId;
    state.isPopup = true;
  },

  // Demo step
  [types.SET_DEMO_STEP](state, demoStep) {
    setStorageValue('demoStep', demoStep);
    state.demoStep = demoStep;
  },

  // Задание последнего времени логина
  [types.SET_LAST_LOGIN_TIME](state, time) {
    state.lastLoginTime = time;
  },

  [types.SHOW_METAMASK_SIGN](state) {
    state.metamaskSignRequired = true;
  },

  [types.HIDE_METAMASK_SIGN](state) {
    state.metamaskSignRequired = false;
  },

  [types.SHOW_LEDGER_SIGN](state) {
    state.ledgerApproveModal = true;
  },

  [types.HIDE_LEDGER_SIGN](state) {
    state.ledgerApproveModal = false;
  },

  [types.SHOW_LOGIN_FLOW](state) {
    state.loginFlowModal = 0;
  },

  [types.SET_LOGIN_FLOW](state, num) {
    state.loginFlowModal = num;
  },

  [types.HIDE_LOGIN_FLOW](state) {
    state.loginFlowModal = null;
  },

  [types.SHOW_PASSWORD_PRIVATE_KEY](state) {
    state.passwordPrivateKeyModal = true;
  },

  [types.HIDE_PASSWORD_PRIVATE_KEY](state) {
    state.passwordPrivateKeyModal = false;
  },

  [types.RESET_METAMASK_NETWORK_POPUP](state) {
    state.metamaskNetworkPopupCancelled = false;
  },

  [types.CANCEL_METAMASK_NETWORK_POPUP](state) {
    state.metamaskNetworkPopupCancelled = true;
  },

  [types.SHOW_BALANCE_ETH_LOW_POPUP](state, type) {
    state.balanceEthLowPopup = type;
  },

  [types.HIDE_BALANCE_ETH_LOW_POPUP](state) {
    state.balanceEthLowPopup = null;
  },

  [types.SHOW_MODAL](state, level) {
    state.displayedModalLevels.push(level);
  },

  [types.HIDE_MODAL](state, level) {
    const arr = state.displayedModalLevels;
    const index = arr.indexOf(level);
    if (index !== -1) arr.splice(index, 1);
  },

  [types.ADD_NOTIFICATION](state, notification) {
    const key = generateKey();
    state.notifications.push({ ...notification, key });
  },

  [types.REMOVE_NOTIFICATION](state, { key }) {
    const index = state.notifications.findIndex(n => n.key === key);
    state.notifications.splice(index, 1);
  },

  /**
   * Mutation: Изменяет флаг активна вкладка с приложением или нет
   *
   * @since Version 1.0.2
   */
  [types.CHANGE_TAB_VISIBILITY](state, tabVisibility) {
    state.tabVisibility = tabVisibility;
  },

  [types.SET_NEWS_AND_ORDERS_TAB](state, tab) {
    state.newsAndMyOrdersTab = tab;
  },

  [types.SET_POSITIONS_TAB](state, tab) {
    state.positionsTab = tab;
  },

  [types.SHOW_ERROR_ORDER_BALANCE](state) {
    state.errorOrderBalance = true;
  },

  [types.HIDE_ERROR_ORDER_BALANCE](state) {
    state.errorOrderBalance = false;
  },

  [types.SHOW_ERROR_ORDER_BALANCE_RESERVED](state) {
    state.errorOrderBalanceReserved = true;
  },

  [types.HIDE_ERROR_ORDER_BALANCE_RESERVED](state) {
    state.errorOrderBalanceReserved = false;
  },

  [types.SET_THEME_LIGHT](state) {
    setStorageValue('theme', 'light');
    state.theme = 'light';
  },

  [types.SET_THEME_DARK](state) {
    setStorageValue('theme', 'dark');
    state.theme = 'dark';
  },

  [types.SET_ASSET_TYPE](state, assetType) {
    state.assetType = assetType;
  },
};

export default mutations;



