import { getWeb3WithoutProvider } from '@/util/web3';
import isEthereumAddress from './isEthereumAddress';

export default (wallet, key) => {
  if (!key || !isEthereumAddress(wallet)) return false;
  try {
    return getWeb3WithoutProvider().eth.accounts.privateKeyToAccount(key).address === wallet;
  } catch (error) {
    return false;
  }
};



