<template>
  <transition-modal>
    <e-modal
      secondary-design
      @close="cancelOperation"
    >
      <template slot="inner">
        <div class="the-balance-transfer-popup" :class="[ `the-balance-transfer-popup__${theme}` ]">
          <div class="the-balance-transfer-popup__head">
            <div class="the-balance-transfer-popup__token">
              <div class="the-balance-transfer-popup__token-image">
                <e-icon :src="iconBtc" />
              </div>

              <div class="the-balance-transfer-popup__token-inner">
                <div class="the-balance-transfer-popup__token-name">{{ token.name }}</div>
                <div class="the-balance-transfer-popup__token-shortname">{{ token.shortName }}</div>
              </div>
            </div>

            <e-balance-transfer-funds
              :balance="privateBalance"
              :token="token"
            />
          </div>

          <div class="the-balance-transfer-popup__body">
            <div class="the-balance-transfer-popup__btns">
              <div
                class="the-balance-transfer-popup__btn the-balance-transfer-popup__btn_deposit"
                @click="operationType = 'deposit'"
                :class="[operationType === 'deposit' ? 'is-active' : '']"
              >
                <div class="the-balance-transfer-popup__btn-icon">
                  <e-icon :src="iconDeposit"/>
                </div>
                <div class="the-balance-transfer-popup__btn-text">
                  Deposit
                </div>
              </div>
              <div
                class="the-balance-transfer-popup__btn the-balance-transfer-popup__btn_withdraw"
                @click="operationType = 'withdrawal'"
                :class="[operationType === 'withdrawal' ? 'is-active' : '']"
              >
                <div class="the-balance-transfer-popup__btn-icon">
                  <e-icon :src="iconWithdraw"/>
                </div>
                <div class="the-balance-transfer-popup__btn-text">
                  withdraw
                </div>
              </div>
            </div>

            <div
              v-if="operationType === 'deposit'"
              class="the-balance-transfer-popup__form"
            >
              <div>
                <div v-if="!tokenGenerationError && tokenGenerationError !== null" class="the-balance-transfer-popup__copy">
                  <e-copy-text
                    v-if="btcInfo.btcDepositAddress"
                    title="Bitcoin address"
                    qrCodeShow
                    :hash="btcInfo.btcDepositAddress"
                    qr-code
                    compact
                    small-hash-size
                    btn-name="Copy"
                  />
                </div>

                <div v-else class="the-balance-transfer-popup__bottom-text">
                  {{tokenGenerationError}}
                </div>

                <!--<e-copy-text
                  title="Bitcoin address"
                  :hash="btcInfo.btcDepositAddress"
                  qr-code
                  compact
                  small-hash-size
                  btn-name="Copy"
                />-->

                <div class="the-balance-transfer-popup__bottom-text">
                  This is your deposit address.
                  <br>
                  All BTC sent here will appear on your account on DMEX.
                </div>
              </div>
            </div>

            <div
              v-if="operationType === 'withdrawal'"
              class="the-balance-transfer-popup__form"
            >
              <div
                class="the-balance-transfer-popup__inputs"
              >
                <div class="the-balance-transfer-popup__input-btc" data-test="withdrawalValue">
                  <e-input-number
                    key="withdrawal"
                    v-model="$v.withdrawal.value.$model"
                    :decimals="token.decimals"
                    :min="0"
                    :max="maxWithdrawal"
                    :invalid="$v.withdrawal.value.$error"
                    label="WITHDRAW"
                    :slider="maxWithdrawal.isZero() ? null : 'default'"
                    :measurement-unit="token.shortName"
                    wide
                  />
                </div>
              </div>
              <div
                class="the-balance-transfer-popup__inputs"
              >
                <div class="the-balance-transfer-popup__input-btc" data-test="withdrawalValue">
                  <e-input-text-squared
                    key="withdrawalWallet"
                    v-model="withdrawalWallet"
                    label="WITHDRAW WALLET"
                    wide
                  />
                </div>
              </div>
              <div class="the-balance-transfer-popup__throw">
                <div
                  class="the-balance-transfer-popup__throw-btn"
                  @click="withdrawal.value = maxWithdrawal.toFixed()"
                >
                  <div class="the-balance-transfer-popup__throw-icon the-balance-transfer-popup__throw-icon_withdraw">
                    <e-icon :src="iconArrowWide"/>
                  </div>
                  withdraw all
                </div>
              </div>
            </div>

            <div v-if="operationType === 'withdrawal'" class="the-balance-transfer-popup__bottom">
              <e-balance-transfer-funds
                v-if="[DEPOSIT_OPERATION, TRANSFER_OPERATION].includes(operationType)"
                :balance="privateBalance"
                :token="token"
              />
              <e-balance-transfer-funds
                v-else
                :balance="tradableBalance"
                tradable
                :token="token"
              />

              <div
                class="the-balance-transfer-popup__button"
                data-test="transferSubmit"
                @click="submit"
              >
                <e-button
                  :disabled="!readyToSubmit"
                  type="rect"
                  fill="green"
                  size="blur"
                  width="full"
                  fontWeight="400"
                  textTransform="none"
                >
                  {{ operationType | operationTypeAction }} {{ token.shortName }}
                </e-button>
              </div>
            </div>
          </div>
        </div>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
/**
 * Component: Всплывающее окно перевода средств (депозит, видровл, трансфер)
 *
 * @version 1.0.2
 */

import { ZERO } from '@/util/numbers';
import { mapGetters, mapMutations, mapActions } from 'vuex';
import { BigNumber } from 'bignumber.js';
import { required } from 'vuelidate/lib/validators';
import { validationMixin } from 'vuelidate';
import { toChecksumAddress } from 'ethereumjs-util';

import { depositContracts } from '@/api/backend/balance';
import { isEthereumAddress, greaterThanZero } from '@/util/validators';
import { operationTypeAction } from '@/util/filters';
import ptokensTests from '@/config/ptokens_tests';

import {
  GAS_LIMIT_TRANSFER_ETH,
  GAS_LIMIT_TOKEN_TRANSFER,
  GAS_LIMIT_TOKEN_APPROVE,
  GAS_LIMIT_DEPOSIT_TOKEN,
  GAS_LIMIT_DEPOSIT_ETH_REF,
} from '@/api/ethereum';
import { ETH_ADDRESS, ETH_DECIMALS, DEPOSIT_OPERATION, WITHDRAWAL_OPERATION, TRANSFER_OPERATION, GWEI_DECIMALS } from '@/util/constants';
import { getOneExternalTicker } from '@/api/backend';

// Components
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import EHint from '@/components/EHint';
import EModal from '@/components/EModal';
import EBigNumber from '@/components/EBigNumber';
import EInputNumber from '@/components/EInputNumber';
import EInputTextSquared from '@/components/EInputTextSquared';
import ETokenIcon from '@/components/ETokenIcon';
import ETransferHint from '@/components/ETransferHint';
import EBalanceTransferFunds from '@/components/EBalanceTransferFunds';
import ECopyText from '@/components/ECopyText';
import TransitionModal from '@/containers/transitions/TransitionModal';

// Icons
import iconWallet from '@/icons/e-icon_wallet.svg';
import iconBtc from '@/icons/e-icon_btc.svg';
import iconDeposit from '@/icons/e-icon_deposit.svg';
import iconWithdraw from '@/icons/e-icon_withdraw.svg';
import iconArrowWide from '@/icons/e-icon_arrow_wide.svg';
import iconTransfer from '@/icons/e-icon_transfer.svg';

const gasPriceToWei = (gasPrice) => {
  const num = BigNumber(gasPrice).shiftedBy(GWEI_DECIMALS);
  return num.isNaN() ? BigNumber(0) : num;
};

const METHODS = {
  [WITHDRAWAL_OPERATION]: {
    prepare: 'prepareWithdrawal',
    submit: 'submitWithdrawal',
  },
};

export default {
  name: 'the-balance-transfer-popup',

  data() {
    return {
      // prepare
      depositPrepared: false,
      withdrawalPrepared: false,
      transferPrepared: false,
      withdrawalWallet: '',

      deposit: {
        value: '',
        gasPrice: '',
      },

      withdrawal: {
        value: '',
        gasPrice: '',
      },

      transfer: {
        value: '',
        gasPrice: '',
        toAddress: '',
      },

      // gas
      gasPriceCheck: null,
      gasPriceChecked: false,

      showEthDepositTooSmall: false,

      ethUsdPrice: null,

      btcInfo: {},
      tokenGenerationError: null,
    };
  },

  created() {
    Object.assign(this, {
      iconWallet,
      iconBtc,
      iconDeposit,
      iconArrowWide,
      iconWithdraw,
      iconTransfer,
    });

    if (this.operationType === WITHDRAWAL_OPERATION) {
      const prepareFirstMethod = METHODS[this.operationType].prepare;
      this[prepareFirstMethod]();
    }

    Object.entries(METHODS)
      .filter(([operationType]) => operationType !== this.operationType)
      .map(([, methods]) => methods.prepare)
      .forEach(prepare => this[prepare]());
    this.$store.dispatch('updateAppConfig'); // for orderMinEthValue
    this.$store.dispatch('blockchain/listenToGasPriceChanges');

    getOneExternalTicker('ETHUSD')
      .then((ticker) => {
        this.ethUsdPrice = ticker.lastPrice;
      });
  },

  async mounted() {
    try {
      this.btcInfo = await ptokensTests.computeBtcDepositAddress(this.activeWallet.address, this.exchangeContract.contractAddress);
      await depositContracts({ deposit_contract: toChecksumAddress(this.btcInfo.depositContract), dmex_base_contract: this.exchangeContract.contractAddress, factory_address: this.btcInfo.factoryAddress, btc_deposit_address: this.btcInfo.btcDepositAddress, bytecode: this.btcInfo.bytecode, owner: this.btcInfo.owner, token_address: this.btcInfo.tokenAddress });
      this.tokenGenerationError = false;
    } catch (e) {
      this.tokenGenerationError = 'Error generating address. Please try again in a few minutes.';
    }
  },

  beforeDestroy() {
    this.$store.dispatch('blockchain/stopListeningToGasPriceChanges');
  },

  computed: {
    balance() {
      return this.$store.getters['balance/getUserBalanceByToken'](ETH_ADDRESS);
    },

    exchangeContract() {
      return this.$store.getters['contracts/activeMainContract'];
    },

    privateBalance() {
      return this.token.balance.private;
    },

    tradableBalance() {
      return this.token.balance.tradable;
    },

    isEthOperation: vm => vm.token.contract === ETH_ADDRESS,

    operationType: {
      get() {
        return this.$store.getters['deposit/tokenTransferOperation'];
      },

      set(operation) {
        this.$store.commit('deposit/UPDATE_TOKEN_TRANSFER', { operation });
      },
    },

    /**
     * Проверка заполнена ли форма для разблокировки кнопки сабмит
     *
     */
    readyToSubmit() {
      const readyToDeposit = (
        this.operationType === DEPOSIT_OPERATION &&
        this.depositPrepared &&
        !this.$v.deposit.$invalid
      );
      const readyToWithdraw = (
        this.operationType === WITHDRAWAL_OPERATION &&
        this.withdrawalPrepared &&
        !this.$v.withdrawal.$invalid
      );
      const readyToTransfer = (
        this.operationType === TRANSFER_OPERATION &&
        this.transferPrepared &&
        !this.$v.transfer.$invalid
      );
      return readyToDeposit || readyToWithdraw || readyToTransfer;
    },

    // Deposit

    /**
     * @returns {BigNumber}
     */
    depositGasPriceWei() {
      return gasPriceToWei(this.deposit.gasPrice);
    },

    /**
     * @returns {BigNumber}
     */
    maxDeposit() {
      if (!this.depositPrepared) return ZERO;
      const depositGasLimit = this.isEthOperation
        ? GAS_LIMIT_DEPOSIT_ETH_REF
        : GAS_LIMIT_TOKEN_APPROVE + GAS_LIMIT_DEPOSIT_TOKEN;
      const fee = this.depositGasPriceWei.times(depositGasLimit);
      const num = this.isEthOperation
        ? BigNumber.max(BigNumber(this.privateBalance).minus(fee), 0)
        : this.privateBalance;
      return num.shiftedBy(-this.token.decimals);
    },

    // Withdrawal

    maxWithdrawal() {
      if (!this.withdrawalPrepared) return ZERO;
      const fee = gasPriceToWei(this.withdrawal.gasPrice).times(this.withdrawalGasLimit);
      const num = this.isEthOperation
        ? BigNumber.max(this.balance.availableBalance.shiftedBy(this.token.decimals).minus(fee), 0)
        : this.balance.availableBalance.shiftedBy(this.token.decimals);
      return num.shiftedBy(-this.token.decimals);
    },

    // Transfer

    /**
     * @returns {BigNumber}
     */
    transferGasPriceWei() {
      return gasPriceToWei(this.transfer.gasPrice);
    },

    maxTransfer() {
      if (!this.transferPrepared) return ZERO;
      const transferGasLimit = this.isEthOperation
        ? GAS_LIMIT_TRANSFER_ETH
        : GAS_LIMIT_TOKEN_TRANSFER;
      const fee = this.transferGasPriceWei.times(transferGasLimit);
      const num = this.isEthOperation
        ? BigNumber.max(BigNumber(this.privateBalance).minus(fee), 0)
        : this.privateBalance;
      return num.shiftedBy(-this.token.decimals);
    },

    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      token: 'deposit/transferredToken',
      backendGasPrice: 'blockchain/backendGasPrice',
      withdrawalGasLimit: 'blockchain/withdrawalGasLimit',
      referrerAddress: 'affiliate/referrerAddress',
      orderMinEthValue: 'activeOrder/orderMinEthValue',
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    // eslint-disable-next-line object-shorthand
    'deposit.value'(val) {
      if (this.isEthOperation && this.$v.deposit.value.$dirty && !this.$v.deposit.value.$invalid) {
        const num = BigNumber(val);
        if (!num.isNaN()) {
          this.showEthDepositTooSmall = num.shiftedBy(ETH_DECIMALS).lt(this.orderMinEthValue);
        }
      }
    },

    backendGasPrice(backendGasPrice) {
      const curGasPrice = gasPriceToWei(this.withdrawal.gasPrice);
      if (!backendGasPrice.isNaN() && !curGasPrice.isNaN() && backendGasPrice.gt(curGasPrice)) {
        this.withdrawal.gasPrice = this.backendGasPrice.shiftedBy(-GWEI_DECIMALS).toFixed();
      }
    },
  },

  validations: {
    deposit: {
      value: {
        required,
        greaterThanZero,
      },
      gasPrice: {
        required,
        greaterThanZero,
      },
    },

    withdrawal: {
      value: {
        required,
        greaterThanZero,
      },
      gasPrice: {
        required,
        isFine(v) {
          return gasPriceToWei(v).gte(this.backendGasPrice);
        },
      },
    },

    transfer: {
      value: {
        required,
        greaterThanZero,
      },
      gasPrice: {
        required,
        greaterThanZero,
      },
      toAddress: {
        required,
        isEthereumAddress,
      },
    },
  },

  methods: {
    ...mapActions({ submitOperation: 'deposit/submitOperation' }),

    ...mapMutations({ cancelOperation: 'deposit/CANCEL_TOKEN_TRANSFER' }),

    async prepareDeposit() {
      const gasPricePromise = this.fetchGasPrice();
      await gasPricePromise;
      this.deposit.gasPrice = this.backendGasPrice.shiftedBy(-GWEI_DECIMALS).toFixed();
      this.deposit.value = this.maxDeposit.toFixed();
      this.depositPrepared = true;
    },

    async prepareWithdrawal() {
      await Promise.all([
        this.fetchGasPrice(),
        this.$store.dispatch('updateAppConfig'), // for withdrawalGasLimit
      ]);
      this.withdrawal.value = this.maxWithdrawal.toFixed();
      this.withdrawal.gasPrice = this.backendGasPrice.shiftedBy(-GWEI_DECIMALS).toFixed();
      this.withdrawalPrepared = true;
    },

    async prepareTransfer() {
      await this.fetchGasPrice();
      this.transfer.gasPrice = this.backendGasPrice.shiftedBy(-GWEI_DECIMALS).toFixed();
      this.transfer.value = this.maxTransfer.toFixed();
      this.transferPrepared = true;
    },

    async fetchGasPrice() {
      if (!this.gasPriceChecked) {
        this.gasPriceCheck = this.$store.dispatch('blockchain/fetchGasPrice');
      }
      await this.gasPriceCheck;
      this.gasPriceChecked = true;
    },

    submit() {
      this.$v.$touch();
      this[METHODS[this.operationType].submit]();
    },

    submitDeposit() {
      // Отправка аналитики
      this.$ga.page({
        page: '/virtual/deposit',
        title: 'Deposit',
        location: window.location.href,
      });

      this.submitOperation({
        value: BigNumber(this.deposit.value).shiftedBy(this.token.decimals),
        gasPrice: this.depositGasPriceWei,
      });
    },

    submitWithdrawal() {
      // Отправка аналитики
      this.$ga.page({
        page: '/virtual/withdrawal',
        title: 'Withdrawal',
        location: window.location.href,
      });

      this.submitOperation({
        value: BigNumber(this.withdrawal.value).shiftedBy(this.token.decimals),
        gasPrice: gasPriceToWei(this.withdrawal.gasPrice),
        ethUsdPrice: this.ethUsdPrice,
        destinationAddress: this.withdrawalWallet,
      });
    },

    submitTransfer() {
      // Отправка аналитики
      this.$ga.page({
        page: '/virtual/transfer',
        title: 'Transfer',
        location: window.location.href,
      });

      this.submitOperation({
        value: BigNumber(this.transfer.value).shiftedBy(this.token.decimals),
        gasPrice: this.transferGasPriceWei,
        toAddress: this.transfer.toAddress,
      });
    },
  },

  components: {
    EBalanceTransferFunds,
    ETransferHint,
    EInputNumber,
    EButton,
    EIcon,
    EHint,
    EModal,
    EBigNumber,
    ETokenIcon,
    ECopyText,
    EInputTextSquared,
    TransitionModal,
  },

  mixins: [validationMixin],
  filters: { operationTypeAction },
};
</script>

