    .e-copy-text__qr-code-show canvas {
      width: 80px;
    }

    .e-copy-text__title {
      display: block;
      font-size: 10px;
      text-transform: uppercase;
      font-weight: 400;
      line-height: 12px;
      letter-spacing: 1px;
    }

    .e-copy-text__head {
      margin-top: 15px;
      margin-bottom: 0;
    }

    .e-copy-text__hash {
      font-size: 11px;
      font-weight: 400;
      letter-spacing: 1px;
    }

    .e-copy-text__btns {
      position: relative;
      top: 0;
      right: 0;

      @include r(1279) {
        right: -10px;
      }
    }

    .e-copy-text__btn {
      min-width: 0;
    }

    .e-copy-text__in {
      flex-wrap: wrap;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-right: 0;
      padding-left: 0 !important;
    }

    .e-button {
      width: 42px;
      height: 14px;
      background: #0097FE !important;
      color: #fff;
      border-radius: 2px;
      padding-left: 8px;
      padding-right: 8px;

      &:hover {
        transform: scale(1);
      }

      .e-icon {
        display: none;
      }

      .e-copy-text__btn-text {
        color: #fff !important;
        height: 14px;
        font-size: 9px;

        span {
          display: block;
          height: 14px;
          box-sizing: border-box;
        }
      }
    }

    @include r(1279) {
      font-size: 11px;
    }
  }

  &__bottom-text {
    width: 210px;
    margin: 25px auto 10px;
    font-size: 12px;
    color: #8D9098;
    text-align: center;
    line-height: 15px;
  }

  &__dark {
    #{$this}__head {
      background: $dark-background-lighter;
    }
    #{$this}__token-name {
      color: #fff;
    }

    #{$this}__balance-amount {
      color: #fff;

      .e-big-number {
        color: #fff;
      }
    }

    #{$this}__copy {
      .e-copy-text__title {
        color: #9A9EA7;
      }

      .e-copy-text__hash {
        color: #fff;
      }
    }

    #{$this}__body {
      background: $dark-background;
    }

    #{$this}__btn {
      #{$this}__btn-handle-1 {
        fill: $dark-background-lighter;
      }

      #{$this}__btn-handle-2 {
        fill: rgba(#fff, .8);
      }

      #{$this}__btn-text {
        color: $color-dark-grey;
      }

      &.is-active {
        #{$this}__btn-handle-1 {
          fill: $color-blue-dark-mode;
        }

        #{$this}__btn-handle-2 {
          fill: rgba(#fff, .8);
        }

        #{$this}__btn-text {
          color: $color-blue-dark-mode;
        }
      }
    }

    #{$this}__throw {
      color: $color-blue-dark-mode;
    }

    #{$this}__throw-icon .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    #{$this}__balance_tradable #{$this}__balance-icon .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-input-number.focused .e-input-number__box:before,
    .e-input-text-squared_focused .e-input-text-squared__box:before {
      border-color: $color-blue-dark-mode;
    }

    .e-input-number__box,
    .e-input-text-squared__box {
      background: $dark-background-lighter !important;
      border-color: transparent !important;
    }

    .e-input-number__input,
    .e-input-text-squared__input {
      color: #fff;
    }

    .e-input-number:hover:not(.e-input-number_non-editable):not(.focused) .e-input-number__unit {
      color: $color-blue-dark-mode;
    }

    .e-hint__label_wide {
      border-color: $color-blue-dark-mode;
      color: $color-blue-dark-mode;
    }

    .e-hint__info {
      background: $dark-background-lighter;
      color: $color-dark-grey;

      &::before {
        background-color: $dark-background-lighter;
      }
    }

    .e-hint.is-active .e-hint__label_wide, .e-hint:hover .e-hint__label_wide {
      background-color: $color-blue-dark-mode;
      color: #fff;
    }

    .range-slider-knob {
      background: $color-dark-grey;
    }

    .e-input-number_default .range-slider-knob .e-icon svg {
      fill: #fff;
    }

    .e-button_fill_green:disabled {
      background: $dark-background-lighter;
    }
  }

  .e-input-number__unit,
  .e-input-number_wide .e-input-number__unit_mod {
    top: 21px;
  }
}
</style>



