/**
 * @module store/blockhain/getters
 * @version 1.0.1
 */

import { Web3, Transactions, Contracts, ProviderAdapter, BlockObserver, ConfirmService, PrivateBalanceListener } from '@/api/ethereum';
import { BalanceMixed, SignaturesService } from '@/api/mixed';
import { PRIVATE_KEY_TYPE } from '@/util/constants';

export default {
  /**
   * @returns {BalanceMixed|null}
   */
  balanceApi: (state, getters, rootState, {
    'wallets/activeWallet': user,
    'contracts/activeMainContract': exchangeContract,
    'deposit/fundConfirmations': numberOfConfirmations,
  }) => (user && exchangeContract && numberOfConfirmations !== null
    ? BalanceMixed({ user, exchangeContract, numberOfConfirmations })
    : null
  ),

  /**
   * @returns {Transactions|null}
   */
  transactionsApi: (state, getters, rootState, {
    'wallets/activeWallet': user,
    'contracts/activeMainContract': exchangeContract,
    'deposit/fundConfirmations': numberOfConfirmations,
  }) => (user && exchangeContract && numberOfConfirmations !== null
    ? Transactions({ user, exchangeContract, numberOfConfirmations })
    : null
  ),

  /**
   * Getter:
   * API works for user without wallet.
   *
   * @returns {Contracts|null}
   * @since Version 1.0.1
   */
  contractsApi: (state, getters, rootState, {
    'wallets/activeWalletType': type,
    'contracts/activeMainContract': exchangeContract,
  }) => exchangeContract && Contracts({ type: type || PRIVATE_KEY_TYPE, exchangeContract }),

  /**
   * Getter:
   * API works for user without wallet.
   * @returns {Accounts}
   */
  accountsService: (state, getters, rootState, rootGetters) => ProviderAdapter({
    type: rootGetters['wallets/activeWalletType'],
    getAddressToPathMap: () => {
      const activeWallet = rootGetters['wallets/activeWallet'];
      return {
        [activeWallet.address.toLowerCase()]: activeWallet.derivationPath,
      };
    },
  }),

  /**
   * Getter:
   *
   * @returns {BlockObserver|null}
   */
  blockObserver: (state, getters, rootState, {
    'deposit/fundConfirmations': numberOfConfirmations,
  }) => (numberOfConfirmations !== null
    ? BlockObserver({ numberOfConfirmations })
    : null
  ),

  /**
   * Getter:
   *
   * @returns {SignaturesService|null}
   */
  signService: (state, getters, rootState, rootGetters) => {
    const user = rootGetters['wallets/activeWallet'];
    const exchangeContract = rootGetters['contracts/activeMainContract'];
    const futuresContract = rootGetters['contracts/activeFuturesContract'];
    const isMetamaskLedger = rootGetters['wallets/isMetamaskLedger'];
    return user && exchangeContract && SignaturesService({ user, exchangeContract, futuresContract, isMetamaskLedger });
  },

  /**
   * @returns {PrivateBalanceListener|null}
   */
  privateBalanceListener: (state, getters, rootState, {
    'wallets/activeWallet': user,
    'contracts/activeMainContract': exchangeContract,
    'deposit/fundConfirmations': numberOfConfirmations,
  }) => (user && exchangeContract && numberOfConfirmations !== null
    ? PrivateBalanceListener({ user, exchangeContract, numberOfConfirmations })
    : null
  ),

  confirmService: () => ConfirmService(),

  /**
   * Returns true if and only if
   * 1) the selected network was checked in browser Ethereum provider (Metamask)
   * 2) selected network appeared to be wrong
   *
   * @param state
   * @returns {boolean}
   */
  wrongNetworkSelected: state => state.networkChecked && !state.correctNetworkSelected,

  backendGasPrice: state => state.gasPrices.fast || null,

  gasPriceLoaded: state => state.gasPrices.fast !== null,

  withdrawalGasLimit: state => state.withdrawalGasLimit,

  tradeTakerGasLimit: state => state.tradeTakerGasLimit,

  web3Instance: (state, getters, rootState, {
    'wallets/activeWallet': user,
  }) => user && Web3({ type: user.type }),
};



