import * as types from '@/store/mutation-types';
import { BigNumber } from 'bignumber.js';
import { DEPOSIT_OPERATION, WITHDRAWAL_OPERATION, TRANSFER_OPERATION } from '@/util/constants';
import { ZERO } from '@/util/numbers';
import log from '@/util/logger';

export const TOKEN_PROPS = ['contract', 'name', 'shortName', 'decimals', 'price'];

const isPrivateBalanceReducer = o => o.type === DEPOSIT_OPERATION || o.type === TRANSFER_OPERATION;

// const isTradableBalanceIncreaser = o => o.type === DEPOSIT_OPERATION;

const isTradableBalanceReducer = o => o.type === WITHDRAWAL_OPERATION;

const sumSafeBy = param => operations => operations.reduce(
  (acc, operation) => {
    const paramValue = BigNumber(operation[param]);
    return paramValue.isNaN() ? acc : acc.plus(paramValue);
  },
  ZERO,
);

const sumSafeByValue = sumSafeBy('value');

const sumSafeByFee = sumSafeBy('fee');

export const privateFees = operations => sumSafeByFee(
  operations.filter(isPrivateBalanceReducer),
);

// средства которые изымаються из приватного кошелька
export const outgoingPrivateFunds = (token, operationsMap) => {
  const operations = operationsMap[token] || [];
  return sumSafeByValue(operations.filter(isPrivateBalanceReducer));
};

export const tradableFees = operations => sumSafeByFee(
  operations.filter(isTradableBalanceReducer),
);

// средства, которые изымаються с кошелька биржы
export const outgoingTradableFunds = (token, operationsMap) => {
  const operations = operationsMap[token] || [];
  return sumSafeByValue(operations.filter(isTradableBalanceReducer));
};

