// subscirbe / unscubscribe

let privateBalanceChangeHandler;

export const unsubscribePrivateBalance = ({ rootState, rootGetters }) => {
  if (rootGetters['wallets/isLogin'] && rootState.contracts.activeExchangeContractLoaded) {
    const listener = rootGetters['blockchain/privateBalanceListener'];
    listener.stopListeningToPrivateBalance(privateBalanceChangeHandler);
  }
};

export const subscribePrivateBalance = (context) => {
  const { rootGetters, commit } = context;
  if (rootGetters['wallets/isLogin']) {
    const tokens = rootGetters['tokens/tokenContracts'];
    const listener = rootGetters['blockchain/privateBalanceListener'];
    privateBalanceChangeHandler = ({ privateBalance }) => {
      commit(types.UPDATE_PRIVATE_BALANCE, privateBalance);
    };
    listener.listenToAllChanges(tokens, privateBalanceChangeHandler);
  }
};

const onBalanceLoadFail = ({ commit }) => {
  commit('ui/ADD_NOTIFICATION', {
    status: 'warning',
    headline: 'Balance Load Failed',
    message: 'Balance load failed due to connection issues. Please reload the page.',
  }, { root: true });
};

export const onPrivateBalanceLoadFail = ({ rootGetters, commit }, err, wallet) => {
  log.warn(`Private balance load error: ${err}`);
  if (wallet === rootGetters['wallets/activeWallet']) {
    commit(types.SET_PRIVATE_BALANCE_ASYNC.FAILURE);
    onBalanceLoadFail({ commit });
  }
};

export const onTradableBalanceLoadFail = ({ rootGetters, commit }, err, wallet) => {
  log.warn(`Tradable balance load error: ${err}`);
  if (wallet === rootGetters['wallets/activeWallet']) {
    commit(types.SET_TRADABLE_BALANCE_ASYNC.FAILURE);
    onBalanceLoadFail({ commit });
  }
};

