/**
 * @module store/trades/mutations
 * @version 1.0.6
 */

import * as types from '@/store/mutation-types';
import { ASYNC_STATES } from '@/util/constants';

import { addPriceDelta, addTrade } from './helpers';

const mutations = {
  /**
   * Начало асинхронной загрузки страницы тредов для текущего токена
   *
   * @since Version 1.0.5
   */
  [types.GET_LATEST_TRADES_ASYNC.PENDING](state) {
    state.latestTradesLoading = true;
    state.latestTrades = [];
  },

  /**
   * Обработка ошибки при загрузке страницы тредов для текущего токена
   *
   * @since Version 1.0.5
   */
  [types.GET_LATEST_TRADES_ASYNC.FAILURE](state) {
    state.latestTradesLoading = false;
    state.tradesBuffer = [];
  },

  /**
   * Обработка успешной загрузки страницы тредов для текущего токена
   * Мутирует trades, добавляя каждому елементу свойства: increase, priceDelta.
   *
   * @param trades новые trade-ы. Мутация мутирует trades, добавляя каждому елементу свойства: increase, priceDelta.
   * @since Version 1.0.5
   */
  [types.GET_LATEST_TRADES_ASYNC.SUCCESS](state, trades) {
    state.latestTrades = addPriceDelta(trades);
    state.latestTradesLoading = false;
    state.tradesBuffer.forEach((trade) => {
      if (!state.showOnlyInternalTrades || trade.isExternal === false) {
        addTrade(state.latestTrades, trade);
      }
    });
    state.tradesBuffer = [];
  },

  /**
   * Mutation:
   * Mutates param's newTrade properties: increase, priceDelta.
   *
   * @param newTrade trade data to be upserted in store. Is mutated in case of insert. New properties added: increase, priceDelta.
   */
  /* eslint-disable no-param-reassign */
  [types.UPSERT_LATEST_TRADE]({ showOnlyInternalTrades, latestTrades }, newTrade) {
    if (!showOnlyInternalTrades || newTrade.isExternal === false) {
      addTrade(latestTrades, newTrade);
    }
  },

  [types.TRADES_ADD_LISTENER]: (state) => {
    state.latestTradesListenersCount += 1;
  },

  [types.TRADES_REMOVE_LISTENER]: (state) => {
    state.latestTradesListenersCount -= 1;
  },

  [types.SET_VOLUMES_ASYNC.PENDING](state) {
    state.volumesInitState = ASYNC_STATES.PENDING;
  },

  [types.SET_VOLUMES_ASYNC.SUCCESS](state, volumes) {
    state.volumeTotal = volumes.intervalAll;
    state.volumesInitState = ASYNC_STATES.SUCCESS;
  },

  [types.SET_VOLUMES_ASYNC.FAILURE](state) {
    state.volumesInitState = ASYNC_STATES.ERROR;
  },

  [types.TRADE_VOLUMES_ADD_LISTENER]: (state) => {
    state.tradeVolumesListenersCount += 1;
  },

  [types.TRADE_VOLUMES_REMOVE_LISTENER]: (state) => {
    state.tradeVolumesListenersCount -= 1;
  },

  [types.SHOW_TRADE_DETAILS](state, trade) {
    state.selectedTrade = trade;
  },

  [types.HIDE_TRADE_DETAILS](state) {
    state.selectedTrade = null;
  },

  /**
   * Mutation: Устанавливает какие трейды отображаем (все или только внутренние)
   */
  [types.SET_TRADES_MODE](state, showOnlyInternalTrades) {
    state.showOnlyInternalTrades = showOnlyInternalTrades;
  },

  /**
   * Mutation: Добавляет трейд в буфер
   *
   * @since Version 1.0.6
   */
  [types.PUSH_TO_TRADES_BUFFER](state, trade) {
    if (!state.showOnlyInternalTrades || trade.isExternal === false) {
      state.tradesBuffer.push(trade);
    }
  },

};

export default mutations;



