import { INTERVAL_BALANCE_CHECK } from '@/config';
import { TRANSACTION_CONFIRM_TIMEOUT } from './constants';

export default class TimeoutConfirmService {
  constructor(web3) {
    this.web3 = web3;
  }

  confirmTransaction(hash, confirmations) {
    return new Promise(async (resolve) => {
      let checkInterval = 0;
      const cancelTimeout = setTimeout(() => {
        clearInterval(checkInterval);
        resolve(false);
      }, TRANSACTION_CONFIRM_TIMEOUT);
      let receipt;
      checkInterval = setInterval(async () => {
        if (receipt == null) {
          receipt = await this.web3.eth.getTransactionReceipt(hash);
          if (receipt === null) return;
        }

        let currentBlock;
        let currentReceipt;
        try {
          ([currentBlock, currentReceipt] = await Promise.all([
            this.web3.eth.getBlockNumber(),
            this.web3.eth.getTransactionReceipt(hash),
          ]));
        } catch (ignored) {
          // сделаем повторную попытку получить номер текущего блока и транзакцию на следующем интервале setInterval
          // если и потом не удастся, то Promise всё равно за-resolve-иться по timeout-у
          return;
        }
        const confirmBlocksMined = currentBlock - receipt.blockNumber >= confirmations;
        if (confirmBlocksMined) {
          clearTimeout(cancelTimeout);
          clearInterval(checkInterval);
          resolve(currentReceipt !== null && currentReceipt.status);
        }
      }, INTERVAL_BALANCE_CHECK);
    });
  }
}



