import backend from '../endpoint';

export const futuresContractSubscribe = futuresContractHash => backend.wsRequest('/futures/subscribe/futures-contract', futuresContractHash);

export const futuresAssetSubscribe = futuresAssetHash => backend.wsRequest('/futures/subscribe/futures-asset', futuresAssetHash);

export const smartContractSubscribe = smartContractAddress => backend.wsRequest('/futures/subscribe/smart-contract', smartContractAddress);



