<template>
  <div class="the-popup-wallet" :class="[ `the-popup-wallet__${theme}` ]">

   <!-- <div
     v-if="balanceEmpty"
     class="the-popup-wallet-empty__container"
   >
      <div class="the-popup-wallet-empty__title">Balances</div>
      <div class="the-popup-wallet-empty__copyes">
        <div class="the-popup-wallet__copy">
          <e-copy-text
            :title="`${walletLabel} Address`"
            :hash="activeWallet.address"
            :qrCode="true"
            btnName="COPY address"
            :smallHashSize="true"
            :walletType="true"
          />
        </div>
      </div>
      <div class="the-popup-wallet-empty__instructions">
        <div class="the-popup-wallet-empty__instructions-message">
          <div class="the-popup-wallet-empty__instructions-heading">Your wallet is empty!</div>
          <div class="the-popup-wallet-empty__instructions-text">Send some ETH or Tokens to your wallet address.</div>
        </div>
        <div class="he-popup-wallet-empty__instructions-button">
          <e-button
            fill="dark-blue"
            size="s"
            padding="secondary"
            fontSize="13"
            fontWeight="500"
          >
            Check Instructions
          </e-button>
        </div>
      </div>
      <div class="the-popup-wallet__balance">
        <div
          class="the-popup-wallet__balance-inner"
          :class="{ 'is-loading': balanceLoading }"
        >
          <the-wallet-balance/>
        </div>
        <e-preloader v-if="balanceLoading"/>
      </div>
    </div>

    <div
      v-else
      class="the-popup-wallet__container"
    >
      <div class="the-popup-wallet__copyes">
        <div class="the-popup-wallet__copy" data-test="walletAddress">
          <e-copy-text
            :title="`${walletLabel} Address`"
            :hash="activeWallet.address"
            :icon="iconLock"
            :qrCode="true"
            btnName="COPY address"
            :smallHashSize="true"
            :walletType="true"
          />
        </div>

        <div
          v-if="activeWallet.type === PRIVATE_KEY_TYPE"
          class="the-popup-wallet__copy"
        >
          <e-copy-text
            title="Private Key"
            :hash="displayedPrivateKey"
            is-masked
            :icon="iconKey"
            btnName="COPY key"
          />
        </div>
      </div>

      <div class="the-popup-wallet__balance">
        <div
          class="the-popup-wallet__balance-inner"
          :class="{ 'is-loading': balanceLoading }"
        >
          <the-wallet-balance/>
        </div>

        <e-preloader v-if="balanceLoading"/>
      </div>
    </div> -->

    <div class="the-popup-wallet__container">
      <div class="the-popup-wallet__copyes">
        <div class="the-popup-wallet__copy" data-test="walletAddress">
          <e-copy-text
            :title="`${walletLabel} Address`"
            :hash="activeWallet.address"
            :icon="iconLock"
            :qrCode="true"
            btnName="COPY address"
            :smallHashSize="true"
            :walletType="true"
          />
        </div>

        <!--<div-->
          <!--v-if="activeWallet.type === PRIVATE_KEY_TYPE"-->
          <!--class="the-popup-wallet__copy"-->
        <!--&gt;-->
          <!--<e-copy-text-->
            <!--title="Private Key"-->
            <!--:hash="displayedPrivateKey"-->
            <!--is-masked-->
            <!--:icon="iconKey"-->
            <!--btnName="COPY key"-->
          <!--/>-->
        <!--</div>-->
      </div>

      <div class="the-popup-wallet__balance">
        <div
          class="the-popup-wallet__balance-inner"
          :class="{ 'is-loading': balanceLoading }"
        >
          <the-wallet-balance/>
        </div>

        <!-- Прелоудер который отображается на во время загрузки данных -->
        <e-preloader v-if="balanceLoading"/>
      </div>
    </div>
  </div>
</template>

<script>
import { toDisplayedPrivateKey } from '@/util/crypto';
import { LEDGER_WALLET_TYPE, METAMASK_WALLET_TYPE, PRIVATE_KEY_TYPE } from '@/util/constants';
import { mapGetters } from 'vuex';

import TheBalanceEmpty from '@/containers/blocks/TheBalanceEmpty';
import TheWalletBalance from '@/containers/blocks/TheWalletBalance';
import ECopyText from '@/components/ECopyText';
import EIcon from '@/components/EIcon';
import EPreloader from '@/components/EPreloader';
import EButton from '@/components/EButton';

import iconLock from '@/icons/e-icon__wallet-address.svg';
import iconKey from '@/icons/e-icon__key-secret.svg';

export default {
  name: 'the-popup-wallet',

  computed: {
    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      balanceEmpty: 'balance/userBalanceAbsent',
      balanceLoading: 'balance/isBalanceRefreshing',
      theme: 'ui/getTheme',
    }),

    displayedPrivateKey() {
      return toDisplayedPrivateKey(this.activeWallet.privateKey);
    },

    walletLabel() {
      return {
        [PRIVATE_KEY_TYPE]: 'Wallet',
        [METAMASK_WALLET_TYPE]: 'MetaMask',
        [LEDGER_WALLET_TYPE]: 'Ledger',
      }[this.activeWallet.type];
    },
  },

  created() {
    Object.assign(this, {
      iconLock,
      iconKey,
    });
  },

  components: {
    ECopyText,
    EIcon,
    EPreloader,
    TheBalanceEmpty,
    TheWalletBalance,
    EButton,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-popup-wallet {

  .the-popup-wallet-empty {
    &__container {
      padding-right: 168px;
    }

    &__title {
      color: #0d105b;
      font-weight: 600;
      font-size: 36px;
      margin-bottom: 40px;
    }

    &__instructions {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: #2259ff;
      box-shadow: 0 7px 10px 0 rgba(0,0,0,.03);
      border-radius: 5px;
      padding: 16px 24px 16px 28px;
      box-sizing: border-box;
      margin: 40px 0 44px;
    }

    &__instructions-message {
      font-size: 18px;
      color: #fff;
      line-height: 22px;
      letter-spacing: 0;
    }

    &__instructions-heading {
      font-weight: 500;
      margin-bottom: 5px;
    }
  }

  &__container {
    padding: 0 0 13px 89px;

    @include r(1200) {
      padding: 0 0 13px 39px;
    }
  }

  &__copyes {
    margin-bottom: 44px;

    @include r(1200) {
      margin-bottom: 40px;
    }
  }

  &__copy {
    &:not(:last-child) {
      margin-bottom: 55px;

      @include r(1200) {
        margin-bottom: 50px;
      }
    }
  }

  &__balance-inner {
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }
  }

  &__dark {
    .e-copy-text__title {
      color: #fff;
    }

    .e-copy-text__qr .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-copy-text__qr-text {
      color: #0123E7;
    }

    .e-copy-text__qr-code-wrapper {
      &:hover {
        .e-copy-text__qr-icon .e-icon svg {
          fill: $color-blue-dark-mode;
        }

        .e-copy-text__qr-text {
          color: #0123E7;
        }
      }
    }

    .e-copy-text__hash {
      color: #8D9098;
    }

    .e-copy-text__type {
      color: #232323;
    }

    .e-copy-text__icon {
      background: #232323;
      box-shadow: 0 12px 24px rgba(0, 0, 0, 0.14);
    }

    .e-button_fill_white,
    .e-copy-text__btn_copy .e-button:hover {
      background: #232323;
      color: $color-blue-dark-mode;

      &::before {
        box-shadow: 0 5px 9px rgba(0, 0, 0, 0.14);
      }
    }

    .e-button_fill_white:hover {
      box-shadow: none;
    }

    .the-wallet-balance__heading {
      color: $color-dark-grey;
    }

    .e-token-search__search-input {
      color: #8D9098;
    }

    .the-wallet-balance__search-placeholder {
      color: #8D9098;
      background: #1A1A1A;

      .e-icon svg {
        fill: #8D9098;
      }
    }

    .the-wallet-balance__hint {
      color: #8D9098;
      background: #2e2e2e;
      border-color: #414141;
    }
  }
}
</style>



