/**
 * @module store/ui/actions
 *
 * @version 1.0.2
 */

import * as types from '@/store/mutation-types';
import { LAST_LOGIN_LOCAL_STORAGE_KEY } from '@/store/constants';

/**
 * Экшин, который считывает из локалстореджа дату последнего логина и обновляет ее.
 *
 * @since Version 1.0.0
 */
const getAndUpdateLastLogin = (context) => {
  const newTime = new Date();
  const lastLoginTime = window.localStorage.getItem(LAST_LOGIN_LOCAL_STORAGE_KEY);

  if (lastLoginTime) {
    context.commit(types.SET_LAST_LOGIN_TIME, new Date(parseInt(lastLoginTime, 10)));
  }

  window.localStorage.setItem(LAST_LOGIN_LOCAL_STORAGE_KEY, `${newTime.getTime()}`);
};


/**
 * Action: Change visibility
 *
 * @since Version 1.0.2
 */
const changeVisibility = (context) => {
  const tabVisibility = !document.hidden;
  context.commit(types.CHANGE_TAB_VISIBILITY, tabVisibility);
};


export default {
  changeVisibility,
  getAndUpdateLastLogin,
};



