    &.open {
      opacity: 1;
      pointer-events: auto;
      transform: translate(-100%, 0);
    }

    @include r(1200) {
      padding-left: 25px;
      padding-right: 25px;
    }
  }

  &__code {
    width: 100%;
    flex: 0 0 100%;
    position: relative;
    z-index: 2;
    padding: 25px 45px;
    background: #ECECEF;
