  }

  &__form-label {
    position: relative;
    display: block;
    height: 100px;
    margin-bottom: 0;

    &.error {
      #{$this}__form-input {
        border: 1px solid #DC4727;
      }

      .e-icon svg path,
      .e-icon svg rect {
        fill: #DC4727;
        opacity: 1;
      }
    }

    &.success {
      .e-icon svg path,
      .e-icon svg rect {
