import Vue from 'vue';
import store from '@/store';
// import TheWalletPagesPopup from '@/containers/blocks/TheWalletPagesPopup';
// import TheTokenCatalogue from '@/containers/blocks/TheTokenCatalogue';
// import TheSmartContracts from '@/containers/blocks/TheSmartContracts';
// import TheHistory from '@/containers/blocks/TheHistory';
import TheWalletPagesWallet from '@/containers/blocks/TheWalletPagesWallet';
import ThePopupStub from '@/containers/blocks/ThePopupStub';
// import TheMyWallet from '@/containers/blocks/TheMyWallet';

import {
  // POPUP_WALLET,
  // POPUP_TOKEN_CATALOGUE,
  // POPUP_HELP,
  POPUP_AFFILIATE,
  POPUP_LIST_TOKEN,
  POPUP_SMART_CONTRACTS,
  // POPUP_HISTORY,
  POPUP_BALANCE,
  // POPUP_MY_WALLET,
  // POPUP_IMPORT,
  POPUP_STUB,
} from '@/store/constants';

import openLink from '@/util/openLink';

const TheAffiliate = () => import(/* webpackChunkName: "the-affiliate" */ '@/containers/blocks/TheAffiliate');
// const TheHelp = () => import(/* webpackChunkName: "the-help" */ '@/containers/blocks/TheHelp');
// const TheListToken = () => import(/* webpackChunkName: "the-list-token" */ '@/containers/blocks/TheListToken');

export default (parentName) => {
  const beforeEnterDefault = ({ popupPage, analytics }) => (to, from, next) => {
    store.commit('ui/SET_DISPLAYED_POPUP', popupPage);
    Vue.$ga.page({
      page: to.path.substr(to.path.lastIndexOf('/') + 1),
      location: to.fullPath,
      ...analytics,
    });
    next();
  };

  const beforeEnterExternal = ({ link, analytics }) => (to, from, next) => {
    openLink(link);
    Vue.$ga.page({
      page: to.path.substr(to.path.lastIndexOf('/') + 1),
      location: to.fullPath,
      ...analytics,
    });
    next(false);
  };

  const beforeEnterWalletOnly = ({ popupPage, analytics }) => async (to, from, next) => {
    store.dispatch('wallets/loadPrivateWallets');
    if (store.getters['wallets/isLogin']) {
      store.commit('ui/SET_DISPLAYED_POPUP', popupPage);
      Vue.$ga.page({
        page: `/${to.path.substr(to.path.lastIndexOf('/') + 1)}`,
        location: to.fullPath,
        ...analytics,
      });
      next();
    } else {
      next({ name: parentName });
    }
  };

  return [
    // {
    //   path: 'create-wallet',
    //   component: TheWalletPagesPopup,
    //   name: `${parentName}/${POPUP_WALLET}`,
    //   beforeEnter: beforeEnterDefault({
    //     popupPage: POPUP_WALLET,
    //     analytics: { title: 'Wallet Creation Process Page' },
    //   }),
    // },

    // {
    //   path: 'tokens',
    //   component: TheTokenCatalogue,
    //   name: `${parentName}/${POPUP_TOKEN_CATALOGUE}`,
    //   beforeEnter: beforeEnterDefault({
    //     popupPage: POPUP_TOKEN_CATALOGUE,
    //     analytics: { title: 'Token Catalogue Page' },
    //   }),
    // },

    // {
    //   path: 'help',
    //   name: `${parentName}/${POPUP_HELP}`,
    //   beforeEnter: beforeEnterExternal({
    //     link: 'https://help.ethermium.com/',
    //     analytics: { title: 'Help Page' },
    //   }),
    // },

    {
      path: 'affiliate',
      component: TheAffiliate,
      name: `${parentName}/${POPUP_AFFILIATE}`,
      beforeEnter: beforeEnterWalletOnly({
        popupPage: POPUP_AFFILIATE,
        analytics: { title: 'Affiliate Page' },
      }),
    },

    {
      path: 'list-token',
      name: `${parentName}/${POPUP_LIST_TOKEN}`,
      beforeEnter: beforeEnterExternal({
        link: 'https://goo.gl/forms/skrvBcKXemhBy8513',
        analytics: { title: 'List Your Token Form' },
      }),
    },

    // {
    //   path: 'contracts',
    //   component: TheSmartContracts,
    //   name: `${parentName}/${POPUP_SMART_CONTRACTS}`,
    //   beforeEnter: beforeEnterDefault({
    //     popupPage: POPUP_SMART_CONTRACTS,
    //     analytics: { title: 'Smart Contracts Page' },
    //   }),
    // },

    // {
    //   path: 'history',
    //   component: TheHistory,
    //   name: `${parentName}/${POPUP_HISTORY}`,
    //   beforeEnter: beforeEnterWalletOnly({
    //     popupPage: POPUP_SMART_CONTRACTS,
    //     analytics: { title: 'Transaction History Page' },
    //   }),
    // },

    {
      path: 'balances',
      component: TheWalletPagesWallet,
      name: `${parentName}/${POPUP_BALANCE}`,
      beforeEnter: beforeEnterWalletOnly({
        popupPage: POPUP_SMART_CONTRACTS,
        analytics: { title: 'Balances Table Page' },
      }),
    },

    {
      path: 'menu',
      component: ThePopupStub,
      name: `${parentName}/${POPUP_STUB}`,
      beforeEnter: beforeEnterDefault({
        popupPage: POPUP_STUB,
        analytics: { title: 'Menu' },
      }),
    },

    // {
    //   path: 'wallet/:walletToken?',
    //   component: TheMyWallet,
    //   name: `${parentName}/${POPUP_MY_WALLET}`,
    //   props: route => ({ tokenAddress: route.params.walletToken }),
    //   beforeEnter: beforeEnterWalletOnly({
    //     popupPage: POPUP_MY_WALLET,
    //     analytics: {
    //       page: '/wallet',
    //       title: 'My Wallet Page',
    //     },
    //   }),
    // },

    // {
    //   path: 'signup',
    //   component: TheWalletPagesImport,
    //   name: `${parentName}/${POPUP_IMPORT}`,
    //   beforeEnter: beforeEnterDefault({
    //     popupPage: POPUP_IMPORT,
    //     analytics: 'Create or Import Wallet Page',
    //   }),
    // },
  ];
};



