<template>
  <e-text-input-length-validated
    v-bind="{ vuelidateProperty, value }"
    :maxlength="42"
    label="Wallet Address"
    type="text"
    :icon="iconWalletAddress"
    width="xl"
    icon-size="semi"
    autocomplete="off"
    autocorrect="off"
    autocapitalize="off"
    spellcheck="false"
    @input="v => $emit('input', v)"
  />
</template>

<script>
import iconWalletAddress from '@/icons/e-icon_wallet-adress.svg';

import ETextInputLengthValidated from '@/components/ETextInputLengthValidated';
import EIcon from '@/components/EIcon';

export default {
  name: 'e-input-wallet-address',
  components: { EIcon, ETextInputLengthValidated },
  props: {
    value: String,
    vuelidateProperty: {
      type: Object,
      required: true,
    },
  },
  data() {
    return { iconWalletAddress };
  },
};
</script>




