<template>
  <div class="the-wallet-ledger-signup-in-popup" :class="[ `the-wallet-ledger-signup-in-popup__${theme}` ]">
    <div class="the-login-flow-popup__body-header">
      <div class="the-login-flow-popup__body-header-icon">
        <e-icon :src="iconLedger" class="the-login-flow-popup__body-header-icon-el ledger" />
      </div>

      <div class="the-login-flow-popup__body-header-title">Connect Ledger</div>
      <div class="the-login-flow-popup__body-header-description">
        Connect and unlock your Ledger Wallet and open the Ethereum app.
        Make sure Browser Support and Contract Data are switched ON. <br>
        If the wallet list is not loading, press Back and select Ledger again. Make sure your Ledger is Unlocked and the Ethereum App is opened.
      </div>
    </div>

    <div class="the-wallet-ledger-signup-in-popup__container">
      <form
        class="the-wallet-ledger-signup-in-popup__form"
        @submit.prevent
      >
        <div class="the-wallet-ledger-signup-in-popup__inputbox no-label">
          <e-select
            v-model="pathType"
            :options="pathTypeOptions"
          />
        </div>
        <div v-if="pathType === 'custom'" class="the-wallet-ledger-signup-in-popup__inputbox">
          <e-text-input
            v-model="customDerivationPath"
            label="Input a path m/44'/60'/"
          />

          <e-button
            :disabled="!isValidCustomDerivationPath"
            fill="blue-blue"
            type="squared"
            size="ss"
            padding="xss"
            class="the-wallet-ledger-signup-in-popup__button-path-load"
            @click="updateLedgerAccounts"
          >
            Load accounts
          </e-button>
        </div>
        <div class="the-wallet-ledger-signup-in-popup__inputbox no-label">
          <e-select
            v-model="selectedLedgerAccountIndex"
            label="Select wallet"
            isLedger
            @ledgerPageNumber="handleLedgerPageNumber"
            :options="walletOptions"
          />
          <!--:options="Array.from(Array(40).keys()).map((_, i) => ({ value: i.toString(), label: `Wallet ${i}` }))"-->
        </div>
      </form>
    </div>

    <div class="the-login-flow-popup__footer-sbmt">
      <div class="the-login-flow-popup__footer-sbmt-item">
        <button
          type="button"
          class="e-receipt__btn e-receipt__btn_active the-login-flow-popup__cancel-button"
          @click="cancelOperation"
        >
          Close
        </button>
      </div>
      <div class="the-login-flow-popup__footer-sbmt-item">
        <button
          type="button"
          class="e-receipt__btn e-receipt__btn_active the-login-flow-popup__action-button"
          :disabled="isImportDisabled"
          @click="importWallet"
        >
          Import wallet
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { mapGetters, mapMutations } from 'vuex';
import { checkLedgerSupport, getLedgerAccounts } from '@/api/ethereum';
import getDerivationPathFromLast3Levels from '@/util/crypto/getDerivationPathFromLast3Levels';

import ETextInput from '@/components/ETextInput';
import EButton from '@/components/EButton';
import ESelect from '@/components/ESelect';
import EIcon from '@/components/EIcon';

import iconLedger from '@/icons/e-icon__ledger-logo.svg';

export default {
  name: 'the-wallet-ledger-signup-in-popup',

  data() {
    return {
      pathType: 'live',
      customDerivationPath: '',
      ledgerAccounts: [],
      selectedLedgerAccountIndex: -1,
      ledgerPageNumber: 0,
    };
  },

  watch: {
    pathType() {
      if (this.pathType === 'live') {
        this.updateLedgerAccounts();
      } else if (this.pathType === 'custom') {
        this.customDerivationPath = '';
        this.ledgerAccounts = [];
        this.selectedLedgerAccountIndex = -1;
      }
    },
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),

    isValidCustomDerivationPath() {
      return !!this.customDerivationPath;
    },

    isImportDisabled() {
      return this.$v.$invalid || this.selectedLedgerAccountIndex === -1;
    },

    walletOptions() {
      return this.ledgerAccounts.map((account, i) => ({
        label: account.address,
        value: i,
      }));
    },
  },

  validations: {
  },

  created() {
    Object.assign(this, {
      pathTypeOptions: [
        {
          label: 'Ledger live',
          value: 'live',
        },
        {
          label: 'Custom',
          value: 'custom',
        },
      ],
      iconLedger,
    });
    this.lastUpdateAccountsRequestId = -1;
  },

  mounted() {
    this.updateLedgerAccounts();
  },

  methods: {
    ...mapMutations({ cancelOperation: 'ui/HIDE_LOGIN_FLOW' }),

    handleLedgerPageNumber(val) {
      this.ledgerPageNumber = val;
      this.updateLedgerAccounts();
    },

    async updateLedgerAccounts() {
      const derivationPath = this.pathType === 'live'
        ? "m/44'/60'/0'/0/0"
        : getDerivationPathFromLast3Levels(this.customDerivationPath);
      this.ledgerAccounts = [];
      this.selectedLedgerAccountIndex = -1;
      this.lastUpdateAccountsRequestId += 1;
      const expectedRequestId = this.lastUpdateAccountsRequestId;
      if (await checkLedgerSupport()) {
        if (this.lastUpdateAccountsRequestId !== expectedRequestId) return;
        this.ledgerAccounts = await getLedgerAccounts(derivationPath, this.ledgerPageNumber * 3);
        // if (this.lastUpdateAccountsRequestId !== expectedRequestId) return;
        // if (this.ledgerAccounts.length === 0) return;
        // this.selectedLedgerAccountIndex = 0;
      }
    },

    async importWallet() {
      const wallet = this.ledgerAccounts[this.selectedLedgerAccountIndex];
      this.$store.commit('wallets/SET_LEDGER_WALLETS', [wallet]);
      this.$store.dispatch('wallets/setActiveWallet', wallet);
      this.$ga.event({
        eventCategory: 'login',
        eventAction: 'login_successful',
        eventLabel: 'ledger',
      });

      this.cancelOperation();
    },

    goToTrading() {
      this.$router.push({ name: 'futures' });
    },
  },

  components: {
    ETextInput,
    EButton,
    ESelect,
    EIcon,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-ledger-signup-in-popup {

  &__form {
    margin-bottom: 85px;
  }

  .e-select__option-icon-wrap {
    display: none;
  }

  .e-select,
  .e-select__box {
    min-height: 50px;
  }

  .e-select__selected {
    height: 50px;
  }

  .e-select__box,
  .e-text-input__box {
    border-width: 1px !important;
  }

  .e-select__options {
    margin-right: 0;
    max-height: 154px;
  }

  .e-select.expanded .e-select__selected {
    padding-bottom: 0;
  }

  .e-select__option-separator {
    height: 1px;
    margin-left: 0;
  }

  .e-select__label {
    top: 12px;
  }

  .e-select .e-icon svg,
  .e-text-input .e-icon svg {
    fill: $color-green-dark
  }

  .e-select.expanded .e-select__selected {
    height: 50px;
  }

  .e-select.selected .e-select__label {
    display: none;
  }

  &__container {
    margin-top: 30px;
    margin-bottom: 150px;
    max-width: 625px;

    @include r(1200) {
      max-width: 439px;
    }
  }

  &__inputbox {
    width: 100%;
    height: 52px;
    position: relative;
    margin-bottom: 30px;

    @include r(1200) {
      margin-bottom: 20px;
      margin-left: 0;
    }

    &.no-label {
      .e-select__content {
        display: flex;
        align-items: center;
      }

      .e-select__selected-label {
        top: 0;
      }
    }

    .e-pagination {
      background: #fff;
    }
  }

  &__button-path-load {
    position: absolute;
    right: 10px;
    top: calc(50% + 3px);
    transform: translate(0, -50%) !important;

    &:hover {
      transform: translate(0, -50%) !important;
    }

    &:active {
      transform: translate(0, calc(-50% + 1px)) !important;
    }

    &[disabled] {
      background: #aaa !important;
      box-shadow: none !important;
    }
  }

  .e-select.expanded {
    z-index: 2;
  }

  .e-pagination {
    background: #F8F9FC;
  }

  &__dark {
    .e-select__box,
    .e-select.expanded .e-select__selected,
    .e-select__options-item:hover,
    .e-text-input__box,
    .e-text-input__label,
    .e-text-input__input {
      background: $dark-background;
      color: #D9D9D9;
    }

    .e-select__options-item:hover,
    .e-select__options-item:hover .e-select__option {
      background: #151a25;
      color: $color-green-dark;
    }

    .e-select__option.selected {
      background: $dark-background-lighter;
      color: $color-green-dark;

      &:hover {
        background: $dark-background-lighter;
      }
    }

    .e-select__option-separator {
      background: #282C38;
    }

    .e-select__options-item:first-child {
      border-top: 1px solid #282C38;
    }

    .e-select__label {
      color: #D9D9D9;
    }

    .e-select__box,
    .e-text-input__box {
      border-color: #282C38 !important;
    }

    .e-pagination {
      background: $dark-background-lighter;
    }
  }
}
</style>




