/**
 * Following should be updated on Resume:
 * 1) last trade
 * 2) latest trade page
 * 3) plot prices
 * 4) token numbers
 * 5) private balance
 * 6) tradable balance
 * 7) pending deposit/withdraw/transfer
 * 8) my orders
 * 9) order book
 * 10) volumes
 *
 * Following should be stopped on Idle:
 * 1) WebSocket connection to server
 * 2) Private balance loop
 *
 * @module IdleEmmiter
 */

import EventEmitter from 'eventemitter3';
import { TIMEOUT_IDLE } from '@/config';

const EVENT_RESUME = 'resume';
const EVENT_IDLE = 'idle';

const emitter = new EventEmitter();
let idleTimer = null;
let isResumeEmittedOnShow = false;

document.addEventListener('visibilitychange', () => {
  if (document.hidden && !idleTimer) {
    idleTimer = setTimeout(() => {
      emitter.emit('idle');
      isResumeEmittedOnShow = true;
    }, TIMEOUT_IDLE);
  } else if (!document.hidden) {
    clearTimeout(idleTimer);
    idleTimer = null;
    if (isResumeEmittedOnShow) {
      emitter.emit('resume');
      isResumeEmittedOnShow = false;
    }
  }
});

export const onIdle = (callback) => {
  emitter.on(EVENT_IDLE, callback);
};

export const removeIdleListener = (listener) => {
  if (listener != null) {
    emitter.removeListener(EVENT_IDLE, listener);
  }
};

export const onResume = (listener) => {
  emitter.on(EVENT_RESUME, listener);
};

export const removeResumeListener = (listener) => {
  if (listener != null) {
    emitter.removeListener(EVENT_RESUME, listener);
  }
};

export const isNotVisible = () => document.hidden;

export const isSleep = () => idleTimer !== null;



