<template>
  <div
    class="e-icon"
    @click="$emit('click', $event)"
    v-html="src"
  >
  </div>
</template>

<script>
export default {
  name: 'e-icon',
  props: ['src'],
};
</script>

<style lang="scss">
.e-icon {
  position: relative;
  width: 100%;
  height: 100%;

  & svg {
    position: absolute;
    top: 0;
    left: 0;
  }
}
</style>



