<template>
  <div class="e-tocken-icon">
    <e-svg-from-store
      v-if="!isLoaded"
      icon="preloader"
      class="e-tocken-icon__preloader"
    />
    <template v-if="imageFormat === 'svg'">
      <img
        class="e-tocken-icon__img"
        :class="{ 'e-tocken-icon__img_visile': isLoaded }"
        :src="url[0]"
        :alt="name"
        @load="isLoaded = true"
      >
    </template>
    <template v-else>
      <img
        class="e-tocken-icon__img"
        :class="{ 'e-tocken-icon__img_visile': isLoaded }"
        :src="url[0]"
        :srcset="'' + url[0] + ' 2x'"
        :alt="name"
        @load="isLoaded = true"
      >
    </template>
  </div>
</template>

<script>
/**
 * Компонент рендер логотипа токена
 *
 * @version 1.0.2
 */
import { mapGetters } from 'vuex';
import { DEFAULT_TOKEN_ICON } from '@/util/constants';

// Components
import ESvgFromStore from '@/components/ESvgFromStore';

export default {
  name: 'e-token-icon',

  props: {
    contract: {
      type: String,
    },

    name: {
      type: String,
      default: '',
    },

    type: {
      type: String,
      default: 'default',
    },
  },

  data() {
    return {
      isLoaded: false,
    };
  },

  computed: {
    tokenImage() {
      return this.imgOfToken(this.contract, this.type);
    },

    imageFormat() {
      return this.tokenImage ? this.tokenImage.type : 'svg';
    },

    url() {
      return this.tokenImage ? this.tokenImage.urls : [DEFAULT_TOKEN_ICON];
    },

    ...mapGetters({
      imgOfToken: 'tokens/imgOfToken',
    }),
  },

  components: {
    ESvgFromStore,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-tocken-icon {
  position: relative;
  width: 100%;
  height: 100%;

  &__img {
    width: 100%;
    height: 100%;
    opacity: 0;
    object-fit: contain;
  }

  &__img_visile {
    opacity: 1;
  }
}
</style>



