<template>
  <e-notification
    status="error"
    :headline="`${capitalizeFirstLetter(operation)} ${token.shortName} error`"
    :duration="false"
    v-bind="$attrs"
    v-on="$listeners"
  >
    <template v-if="message">{{ message }}</template>
    <template v-else>
      Failed to {{ operationTypeAction(operation).toLowerCase() }}
      <e-big-number
        :value="value"
        :decimals="token.decimals"
        :max-decimal-places="token.contract === ETH_ADDRESS ? ETH_DISPLAYED_DECIMALS : null"
      />
      {{ token.shortName }}
    </template>
  </e-notification>
</template>

<script>
import { BigNumber } from 'bignumber.js';

import EBigNumber from '@/components/EBigNumber';
import { capitalizeFirstLetter, operationTypeAction } from '@/util/filters';
import ENotification from '@/components/ENotification';

export default {
  name: 'e-notification-balance-error',
  inheritAttrs: false,

  props: {
    operation: {
      type: String,
      required: true,
    },
    value: {
      type: [Number, String, BigNumber],
      required: true,
    },
    token: {
      type: Object,
      required: true,
      validator: token => token && token.contract && token.decimals != null && token.shortName,
    },
    message: {
      type: String,
      default: '',
    },
  },

  methods: {
    capitalizeFirstLetter,
    operationTypeAction,
  },

  components: { ENotification, EBigNumber },
};
</script>



