import validateLast3LevelsOfDerivationPath from '@/util/crypto/validateLast3LevelsOfDerivationPath';

export default (last3Levels) => {
  if (!validateLast3LevelsOfDerivationPath(last3Levels)) {
    throw new Error(`Invalid last 3 levels of derivation path: ${last3Levels}`);
  }
  const [part1, part2, part3] = last3Levels.split(/\//);
  const lvl3 = part1 === undefined ? '0\'' : `${parseInt(part1, 10)}'`;
  const lvl4 = part2 === undefined ? '0' : part2;
  return part3 === undefined
    ? `m/44'/60'/${lvl3}/${lvl4}`
    : `m/44'/60'/${lvl3}/${lvl4}/${part3}`;
};



