import { BigNumber } from 'bignumber.js';
import ERC20Contract from 'erc20-contract-js';
import log from '@/util/logger';
import { NO_ZERO_TRANSACTION, NOT_ENOUGH_BALANCE } from '@/util/errors';
import { isEthereumAddress } from '@/util/validators';
import { APP_ETH_NETWORK_ID } from '@/config';
import {
  GAS_LIMIT_DEPOSIT_ETH_REF,
  GAS_LIMIT_DEPOSIT_TOKEN,
  GAS_LIMIT_TOKEN_APPROVE,
  GAS_LIMIT_TOKEN_TRANSFER,
  GAS_LIMIT_TRANSFER_ETH,
} from './constants';

const chainId = APP_ETH_NETWORK_ID;

/**
 * @property user {{ address, type }}
 * @property web3 {Web3}
 * @property numberOfConfirmations {number}
 * @property nonceGenerator {NonceGenerator}
 * @property exchangeContract
 */
export default class Transactions {
  constructor(params) {
    Object.assign(this, params);
  }

  /**
   * Deposit ETH to exchange Contract.
   * Returns 2 promises.
   * depositPending resolves to deposit transaction hash. Rejected if some error occurred during transaction hash retrieval.
   * depositConfirmed resolves if deposit is confirmed or rejected if deposit is rejected
   *
   * @param value in wei to deposit
   * @param gasPrice
   * @param gas
   * @param referrerAddress (optional)
   * @returns {{depositPending: Promise<string>}}
   */
  depositEth({ value, referrerAddress, gasPrice, gas = GAS_LIMIT_DEPOSIT_ETH_REF }) {
    const from = this.user.address;
    let nonce;

    const deposit = (async () => {
      nonce = await this.nonceGenerator.nextTransactionNonce();
      return {
        transaction: this.exchangeContract.deposit(this.filterReferrer(referrerAddress))
          .send({ nonce, from, value, gasPrice, gas, chainId })
          .once('error', () => {
            this.nonceGenerator.resetLocalNonce();
          }),
      };
    })();

    const depositPending = new Promise(async (resolve, reject) => {
      try {
        const { transaction } = await deposit;
        transaction
          .once('transactionHash', (hash) => {
            log.debug(`depositEth from ${this.user.address} with nonce ${nonce} got hash ${hash}`);
            resolve(hash);
          })
          .once('error', reject);
      } catch (e) {
        reject(e);
      }
    });

    // Suppress UnhandledPromiseRejectionWarning
    depositPending.catch(() => {});

    return { depositPending };
  }

  /**
   * Deposit token
   * Returns 2 promises.
   * depositPending returns depositToken transaction hash. Rejected if some error occurred during tranasction hash retrieval.
   * depositConfirmed resolves if deposit is confirmed or rejected if deposit is rejected
   *
   * @param tokenAddress
   * @param value
   * @param gasPrice
   * @param referrerAddress (optional)
   * @returns {{approvePending: Promise<string|null>, depositPending: Promise<string>}}
   */
  depositToken({ tokenAddress, value, gasPrice }) {
    let nonce;
    const approvePending = this.setTokenAllowance({ tokenAddress, value, gasPrice });

    const deposit = (async () => {
      await approvePending;
      nonce = await this.nonceGenerator.nextTransactionNonce();
      log.debug(`depositToken of token ${tokenAddress} from ${this.user.address} with nonce ${nonce} sent`);
      return {
        transaction: this.exchangeContract
          .depositToken(tokenAddress, value)
          .send({
            from: this.user.address,
            gas: GAS_LIMIT_DEPOSIT_TOKEN,
            gasPrice,
            nonce,
            chainId,
          })
          .once('error', () => {
            this.nonceGenerator.resetLocalNonce();
          }),
      };
    })();

    const depositPending = new Promise(async (resolve, reject) => {
      try {
        const { transaction } = await deposit;
        transaction
          .once('transactionHash', (hash) => {
            log.debug(`depositToken of token ${tokenAddress} from ${this.user.address} with nonce ${nonce} got hash ${hash}`);
            resolve(hash);
          })
          .once('error', reject);
      } catch (e) {
        reject(e);
      }
    });

    // Suppress UnhandledPromiseRejectionWarning
    depositPending.catch(() => {});

    return { approvePending, depositPending };
  }

  transferEth({ value, toAddress: to, gasPrice }) {
    let nonce;
    const transfer = (async () => {
      const from = this.user.address;
      const gas = GAS_LIMIT_TRANSFER_ETH;
      nonce = await this.nonceGenerator.nextTransactionNonce();
      return {
        transaction: this.web3.eth
          .sendTransaction({ nonce, from, to, value, gasPrice, gas, chainId })
          .once('error', () => {
            this.nonceGenerator.resetLocalNonce();
          }),
      };
    })();
    const transferPending = new Promise(async (resolve, reject) => {
      try {
        const { transaction } = await transfer;
        transaction
          .once('transactionHash', resolve)
          .once('error', reject);
      } catch (e) {
        reject(e);
      }
    });
    const transferMined = new Promise(async (resolve, reject) => {
      try {
        const { transaction } = await transfer;
        transaction
          .once('receipt', resolve)
          .once('error', reject);
      } catch (e) {
        reject(e);
      }
    });
    const transferConfirmed = transferMined.then(({ transactionHash }) =>
      this.confirmService.confirmTransaction(transactionHash, this.numberOfConfirmations),
    );

    // Suppress UnhandledPromiseRejectionWarning
    transferPending.catch(() => {});
    transferMined.catch(() => {});
    transferConfirmed.catch(() => {});

    return { transferPending, transferMined, transferConfirmed };
  }

  transferToken({ tokenAddress, value, toAddress, gasPrice, gas = GAS_LIMIT_TOKEN_TRANSFER }) {
    const transfer = (async () => {
      const from = this.user.address;
      const nonce = await this.nonceGenerator.nextTransactionNonce();
      return {
        transaction: new ERC20Contract(this.web3, tokenAddress)
          .transfer(toAddress, BigNumber(value).toFixed())
          .send({ nonce, from, gas, gasPrice, chainId })
          .once('error', () => {
            this.nonceGenerator.resetLocalNonce();
          }),
      };
    })();
    const transferPending = new Promise(async (resolve, reject) => {
      try {
        const { transaction } = await transfer;
        transaction
          .once('transactionHash', resolve)
          .once('error', reject);
      } catch (e) {
        reject(e);
      }
    });
    const transferMined = new Promise(async (resolve, reject) => {
      try {
        const { transaction } = await transfer;
        transaction
          .once('receipt', resolve)
          .once('error', reject);
      } catch (e) {
        reject(e);
      }
    });
    const transferConfirmed = transferMined.then(({ transactionHash }) =>
      this.confirmService.confirmTransaction(transactionHash, this.numberOfConfirmations),
    );

    // Suppress UnhandledPromiseRejectionWarning
    transferPending.catch(() => {});
    transferMined.catch(() => {});
    transferConfirmed.catch(() => {});

    return { transferPending, transferMined, transferConfirmed };
  }

  /**
   * Checks if the referrer is valid and returns it.
   * If not valid returns undefined.
   *
   * @private
   * @param referrer
   * @returns {string|undefined}
   */
  filterReferrer(referrer) {
    return (this.user.address !== referrer && isEthereumAddress(referrer)) ? referrer : '0x0000000000000000000000000000000000000000';
  }

  async setTokenAllowance({ tokenAddress, value, gasPrice }) {
    const tokenContract = new ERC20Contract(this.web3, tokenAddress);
    const { resetAllowance, approve } = await this.getApproveSteps({ tokenContract, value });
    let resetAllowanceHash;
    if (resetAllowance) {
      resetAllowanceHash = await this.resetTokenAllowance({ tokenContract, gasPrice });
    }

    let approveAllowanceHash;
    if (approve) {
      approveAllowanceHash = await this.approveTokenDeposit({ tokenContract, gasPrice, value });
    }
    return resetAllowanceHash || approveAllowanceHash || null;
  }

  /**
   * Check which steps should be performed during this deposit token.
   *
   * @private
   * @param tokenContract
   * @param value
   * @returns {Promise<{resetAllowance: boolean, approve: boolean}>}
   */
  async getApproveSteps({ tokenContract, value }) {
    const balance = BigNumber(await tokenContract.balanceOf(this.user.address).call());
    if (balance.isZero()) {
      throw NO_ZERO_TRANSACTION;
    } else if (balance.lt(value)) {
      throw NOT_ENOUGH_BALANCE;
    }
    const oldAllowance = BigNumber(await tokenContract.allowance(this.user.address, this.exchangeContract.getAddress()).call());

    if (oldAllowance.isZero()) {
      return {
        resetAllowance: false,
        approve: true,
      };
    } else if (oldAllowance.gte(value)) {
      return {
        resetAllowance: false,
        approve: false,
      };
    }
    return {
      resetAllowance: true,
      approve: true,
    };
  }

  /**
   * Sends a ERC20 approve for specified params and gets transaction hash.
   *
   * @private
   * @param value
   * @param tokenContract
   * @param gasPrice
   * @returns {Promise<string>} Promise with ERC20 approve hash
   */
  approveTokenDeposit({ value, tokenContract, gasPrice }) {
    return new Promise(async (resolve, reject) => {
      let nonce;
      try {
        nonce = await this.nonceGenerator.nextTransactionNonce();
      } catch (e) {
        reject(e);
        return;
      }

      log.debug(`erc20Approve of token ${tokenContract.getAddress()} from ${this.user.address} with nonce ${nonce} sent`);
      tokenContract
        .approve(this.exchangeContract.getAddress(), value)
        .send({
          from: this.user.address,
          gas: GAS_LIMIT_TOKEN_APPROVE,
          gasPrice,
          nonce,
          chainId,
        })
        .once('transactionHash', (hash) => {
          log.debug(`erc20Approve of token ${tokenContract.getAddress()} from ${this.user.address} with nonce ${nonce} got hash ${hash}`);
          resolve(hash);
        })
        .once('error', (e) => {
          this.nonceGenerator.resetLocalNonce();
          reject(e);
        });
    });
  }

  /**
   * @private
   * @param tokenContract
   * @param gasPrice
   */
  resetTokenAllowance({ tokenContract, gasPrice }) {
    return this.approveTokenDeposit({ value: '0', tokenContract, gasPrice });
  }
}



