import backend from '../endpoint';

export const faucet = async ({ contractAddress, userAddress }) => backend.http.post(
  '/api/futures/faucet', {
    contract_address: contractAddress,
    user_address: userAddress,
  },
);

export const utm = async ({ utmSource, utmMedium, utmCampaign }) => backend.http.post(
  '/api/futures/utm', {
    utm_source: utmSource,
    utm_medium: utmMedium,
    utm_campaign: utmCampaign,
  },
);



