import { getStorageValue } from '@/util/storage';

const state = {
  localWallets: [],
  providedWallets: [],
  isMetamaskLedger: getStorageValue('IS_METAMASK_LEDGER') || false,
  ledgerWallets: [],
  activeWallet: null,
  newWalletAddress: null,
  totalWallets: 0,
  totalWalletsLoaded: false,
};

export default state;



