import { BigNumber } from 'bignumber.js';

/**
 * Округление до заданной цифры. Все цифры до заданной будут нулями.
 * Округление проводиться вверх, т.е. 5.1 => 6.
 *
 * @param number {BigNumber|number|string}
 * @param possiblyIncrementedDigitIndex индекс цифры которая будет увеличена в случае округления. Нумерация начинается с 0. То есть, если хотим округлить до целого числа, то нужно передать 0.
 */
export default (number, possiblyIncrementedDigitIndex) => BigNumber(number)
  .shiftedBy(-possiblyIncrementedDigitIndex)
  .dp(0, BigNumber.ROUND_UP)
  .shiftedBy(possiblyIncrementedDigitIndex);



