/**
 * @module store/contracts/mutations
 *
 * @version 1.1.0
 */


import * as types from '@/store/mutation-types';
import { ACTIVE_MAIN_CONTRACT_KEY, ACTIVE_FUTURES_CONTRACT_KEY, ACTIVE_DMEX_ORACLE_CONTRACT_KEY } from '@/store/constants';

const mutations = {
  /**
   * Mutation: Начало асинхронной загрузки списка доступных контрактов
   *
   * @since Version 1.0.1
   */
  [types.GET_EXCHANGE_CONTRACTS_ASYNC.PENDING]() {},
  [types.GET_FUTURES_CONTRACTS_ASYNC.PENDING]() {},
  [types.GET_DMEX_ORACLE_CONTRACTS_ASYNC.PENDING]() {},

  /**
   * Mutation: Обработка успешной загрузки списка доступных контрактов
   *
   * @since Version 1.0.1
   */
  [types.GET_EXCHANGE_CONTRACTS_ASYNC.SUCCESS](state, contracts) {
    state.exchangeContracts = contracts;
  },

  [types.GET_FUTURES_CONTRACTS_ASYNC.SUCCESS](state, contracts) {
    state.futuresContracts = contracts;
  },

  [types.GET_DMEX_ORACLE_CONTRACTS_ASYNC.SUCCESS](state, contracts) {
    state.dmexOracleContracts = contracts;
  },

  /**
   * Mutation: Обработка ошибки при загрузке списка доступных контрактов
   *
   * @since Version 1.0.1
   */
  [types.GET_EXCHANGE_CONTRACTS_ASYNC.FAILURE](state) {
    state.exchangeContractsFailure = true;
  },

  [types.GET_FUTURES_CONTRACTS_ASYNC.FAILURE](state) {
    state.futuresContractsFailure = true;
  },

  [types.GET_DMEX_ORACLE_CONTRACTS_ASYNC.FAILURE](state) {
    state.dmexOracleContractsFailure = true;
  },

  /**
   * Mutation: Начало асинхронной загрузки списка доступных контрактов
   *
   * @since Version 1.0.1
   */
  [types.GET_CONTRACTS_ASYNC.PENDING](state) {
    state.contractsLoading = true;
    state.contractsLoaded = false;
  },

  /**
   * Mutation: Обработка успешной загрузки списка доступных контрактов
   *
   * @since Version 1.0.1
   */
  [types.GET_CONTRACTS_ASYNC.SUCCESS](state, { affiliate }) {
    state.contractsLoading = false;
    state.contractsLoaded = true;
    state.affiliateContract = affiliate;
  },

  /**
   * Mutation: Обработка ошибки при загрузке списка доступных контрактов
   *
   * @since Version 1.0.1
   */
  [types.GET_CONTRACTS_ASYNC.FAILURE](state) {
    state.contractsLoading = false;
    state.contractsLoaded = false;
  },

  [types.GET_ACTIVE_CONTRACT.PENDING](state) {
    state.activeExchangeContractLoading = true;
  },

  [types.GET_ACTIVE_FUTURES_CONTRACT.PENDING](state) {
    state.activeFuturesContractLoading = true;
  },

  [types.GET_ACTIVE_DMEX_ORACLE_CONTRACT.PENDING](state) {
    state.activeDmexOracleContractLoading = true;
  },

  [types.GET_ACTIVE_CONTRACT.SUCCESS](state, contract) {
    state.activeExchangeContractLoading = false;
    state.activeExchangeContractLoaded = true;
    state.activeMainContract = contract;
  },

  [types.GET_ACTIVE_FUTURES_CONTRACT.SUCCESS](state, contract) {
    state.activeFuturesContractLoading = false;
    state.activeFuturesContractLoaded = true;
    state.activeFuturesContract = contract;
  },

  [types.GET_ACTIVE_DMEX_ORACLE_CONTRACT.SUCCESS](state, contract) {
    state.activeDmexOracleContractLoading = false;
    state.activeDmexOracleContractLoaded = true;
    state.activeDmexOracleContract = contract;
  },

  [types.GET_ACTIVE_CONTRACT.FAILURE](state) {
    state.activeExchangeContractLoading = false;
    state.activeExchangeContractLoaded = false;
  },

  [types.GET_ACTIVE_FUTURES_CONTRACT.FAILURE](state) {
    state.activeFuturesContractLoading = false;
    state.activeFuturesContractLoaded = false;
  },

  [types.GET_ACTIVE_DMEX_ORACLE_CONTRACT.FAILURE](state) {
    state.activeDmexOracleContractLoading = false;
    state.activeDmexOracleContractLoaded = false;
  },

  /**
   * Mutation: Устанавливает текущий главный контракт
   */
  [types.SET_ACTIVE_MAIN_CONTRACT](state, contract) {
    state.activeMainContract = contract;
  },

  [types.SET_ACTIVE_FUTURES_CONTRACT](state, contract) {
    state.activeFuturesContract = contract;
  },

  [types.SET_ACTIVE_DMEX_ORACLE_CONTRACT](state, contract) {
    state.activeDmexOracleContract = contract;
  },

  /**
   * Mutation: Удалит из локалстореджа информацию об активном главном контракте
   */
  [types.FORGE_ACTIVE_MAIN_CONTRACT]() {
    window.localStorage.removeItem(ACTIVE_MAIN_CONTRACT_KEY);
  },
  /**
   * Mutation: Удалит из локалстореджа информацию об активном главном контракте
   */
  [types.FORGE_ACTIVE_FUTURES_CONTRACT]() {
    window.localStorage.removeItem(ACTIVE_FUTURES_CONTRACT_KEY);
  },
  /**
   * Mutation: Удалит из локалстореджа информацию об активном главном контракте
   */
  [types.FORGE_ACTIVE_DMEX_ORACLE_CONTRACT]() {
    window.localStorage.removeItem(ACTIVE_DMEX_ORACLE_CONTRACT_KEY);
  },

  /**
   * Mutation: Начало асинхронной подписки на контракт
   *
   * @since Version 1.1.0
   */
  [types.SUBSCRIBE_TO_CONTRACT_ASYNC.PENDING](state) {
    state.contractSubscribing = true;
  },

  /**
   * Mutation: Обработка ошибки при асинхронной подписке на контракт
   *
   * @since Version 1.1.0
   */
  [types.SUBSCRIBE_TO_CONTRACT_ASYNC.FAILURE](state) {
    state.contractSubscribing = false;
  },

  /**
   * Mutation: Обработка успешной асинхронной подписки на контракт
   *
   * @since Version 1.1.0
   */
  [types.SUBSCRIBE_TO_CONTRACT_ASYNC.SUCCESS](state) {
    state.contractSubscribing = false;
  },
};

export default mutations;



