export default class Contracts {
  constructor({ exchangeContract }) {
    this.exchangeContract = exchangeContract;
  }

  affiliateContractAddress() {
    if (typeof this.exchangeContract.affiliateContract !== 'function') {
      return Promise.reject(new Error('Current exchange contract does not have affiliate contract function'));
    }

    return this.exchangeContract.affiliateContract().call();
  }
}



