<template>
  <div class="e-input-secret-key">
    <e-text-input-validated
      v-model="actualValue"
      :vuelidate-property="vuelidateProperty"
      type="password"
      label="Private Key"
      :icon="iconSecretLock"
      icon-size="semi"
      width="xl"
      is-validation-icon-displayed
      recolor-text
      @input="v => $emit('input', v)"
    />
  </div>
</template>

<script>
import ETextInputValidated from '@/components/ETextInputValidated';
import EIcon from '@/components/EIcon';

import iconSecretLock from '@/icons/e-icon_secret-lock.svg';

export default {
  name: 'e-input-secret-key',

  props: {
    value: {
      type: String,
      default: '',
    },

    vuelidateProperty: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      actualValue: this.value,
      iconSecretLock,
    };
  },

  watch: {
    actualValue(val) {
      this.$emit('input', val);
    },
  },

  components: {
    EIcon,
    ETextInputValidated,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-input-secret-key {
  .e-text-input {
    &__left-outer-icon-wrap {
      position: absolute;
      left: -15px + -44px;
      z-index: 1;
      width: 44px;
      min-width: 44px;
      max-width: 44px;
    }
  }
}
</style>




