<template>
  <div
    class="e-input-text-squared"
    :class="{
      'e-input-text-squared_focused': focused,
      'e-input-text-squared_wide': wide,
    }"
  >
    <div
      v-if="label != null"
      class="e-input-text-squared__label"
    >
      {{ label }}
    </div>
    <div class="e-input-text-squared__box">
      <input
        v-bind="{ value, ...$attrs }"
        class="e-input-text-squared__input"
        v-on="inputListeners"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-input-text-squared',

  props: {
    value: {
      type: String,
      default: '',
    },

    label: String,

    wide: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      focused: false,
    };
  },

  computed: {
    inputListeners() {
      const { input, focus, blur, ...rest } = this.$listeners;
      /* eslint-disable vue/no-side-effects-in-computed-properties */
      return {
        ...rest,
        input: (event) => {
          this.$emit('input', event.target.value);
        },
        focus: () => {
          this.focused = true;
          if (focus) focus();
        },
        blur: () => {
          this.focused = false;
          if (blur) blur();
        },
      };
      /* eslint-enable vue/no-side-effects-in-computed-properties */
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-input-text-squared {
  $this: &;

  position: relative;

  &__box {
    position: relative;
    background: #FFFFFF;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.08);
    box-sizing: border-box;
    border-radius: 2px;
    z-index: 1;
    height: 50px;
    transition: $transition-time;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      border-radius: 2px;
      overflow: hidden;
      border: 1px solid transparent;
      transition: $transition-time;
      z-index: -1;
    }

    &:hover {
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.28);
    }

    @include r(1200) {
      height: 40px;
    }

    @include h(750) {
      height: 40px;
    }

    @include rwh(2400, 1000) {
      height: 60px;
    }
  }

  &__label {
    color: #9A9EA7;
    font-size: 12px;
    margin-bottom: 1px;

    @include r(1200) {
      font-size: 10px;
    }

    @include h(750) {
      font-size: 10px;
    }

    @include rwh(2400, 1000) {
      font-size: 14px;
      margin-bottom: 3px;
    }
  }

  &__input {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    color: #0D105B;
    background: transparent;
    padding: 0 10px;
    font-size: 15px;
    letter-spacing: 0.87px;
    border-radius: 2px;

    @include r(1200) {
      font-size: 13px;
      padding: 1px 10px 0;
    }

    @include h(750) {
      font-size: 13px;
      padding: 1px 10px 0;
    }

    @include rwh(2400, 1000) {
      padding: 0 20px;
      font-size: 18px;
      letter-spacing: 1.2px;
    }
  }

  &_focused {
    #{$this}__box {
      &:before {
        border: 1px solid $color-blue;
      }
    }
  }

  &_wide {
    #{$this}__input {
      font-size: 9.9px;
      padding: 0 10px;
    }

    #{$this}__label {
      text-transform: uppercase;
      font-size: 10px;
      letter-spacing: 1px;
    }

    #{$this}__box {
      height: 40px;
    }
  }
}
</style>



