import log from '@/util/logger';
import * as types from '@/store/mutation-types';

export default {
  [types.UPDATE_REFERRER](state, referrerAddress) {
    state.referrerAddress = referrerAddress;
  },

  [types.UPDATE_AFFILIATES_CLICKS](state, clicksCount) {
    state.clicks = clicksCount;
  },


  // Загрузка данных об афилированных коммисиях
  [types.GET_AFFILIATE_TRADES_ASYNC.SUCCESS](state, trades) {
    // state[types.GET_AFFILIATE_TRADES_ASYNC.loadingKey] = false;
    // Vue.set(state, [types.GET_AFFILIATE_TRADES_ASYNC.stateKey], info);
    state.trades = trades;
  },

  [types.GET_AFFILIATE_TRADES_ASYNC.PENDING]() {
    // Vue.set(state, types.GET_AFFILIATE_TRADES_ASYNC.loadingKey, true);
  },

  [types.GET_AFFILIATE_TRADES_ASYNC.FAILURE](state, error) {
    log.error(`Backend affiliate trades unavailable: ${error}`, {
      module: 'trades',
      layer: 'backend',
    });
  },
  // ----------------------------------------------
};



