/**
 * @module store/orders/getters
 * @version 1.0.4
 */
// import { ORDERS_IN_ORDERBOOK } from '@/config';


const allOrders = state => state.all;

/**
 * Геттер: Возвращает текущую страницу моих оредров
 *
 * @since Version 1.0.1
 */
const myOrdersPage = state => state.myOrdersPage;


const cancelledOrder = state => state.cancelledOrder;

/**
 * Геттер: Возвращает текущий режим отображения моих ордеров
 *
 * @since Version 1.0.2
 */
const myOrdersMode = state => state.myOrdersMode;


/**
 * Геттер: Возвращает текущий общее количество моих ордеров в текущем режиме
 *
 * @since Version 1.0.3
 */
const totalNumberOfOrders = state => state.totalNumberOfOrders;


/**
 * Геттер: Возвращает количество оредров который нужно отображать на одной странице
 *
 * @since Version 1.0.3
 */
const ordersPerPage = state => state.ordersPerPage;


/**
 * Getter: Возвращает загружаются сейчас данные для страницы ордеров или нет
 *
 * @since Version 1.0.4
 */

const myOrdersPageLoading = state => state.myOrdersPageLoading;

export default {
  allOrders,
  cancelledOrder,
  myOrdersMode,
  myOrdersPage,
  myOrdersPageLoading,
  ordersPerPage,
  totalNumberOfOrders,
};



