/**
 * @module store/futures-positions/actions
 * @version 1.0.3
 */

import * as types from '@/store/mutation-types';
import log from '@/util/logger';
import { getOpenPositions, getClosedPositions, positionTransformer } from '@/api/backend/futures/positions-v2';

const fetchPositions = async ({ commit, rootGetters }) => {
  commit(types.FETCH_FUTURES_POSITIONS_ASYNC.PENDING);

  const userAddress = rootGetters['wallets/activeWalletAddress'];
  // const userAddress = '0x0A24BdE9D7618f9B745036FE0A7b57c7451ED724';

  if (!userAddress) {
    commit(types.FETCH_FUTURES_POSITIONS_ASYNC.FAILURE);
    return false;
  }

  try {
    const { data: positions } = await getOpenPositions({ userAddress });
    commit(types.FETCH_FUTURES_POSITIONS_ASYNC.SUCCESS, positions.map(positionTransformer));
    return true;
  } catch (error) {
    commit(types.FETCH_FUTURES_POSITIONS_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`backend futures positions unavailable: ${error}`, {
        module: 'futures-contracts',
        layer: 'backend',
      });
    }
    return false;
  }
};

const fetchClosedPositions = async ({ commit, rootGetters }) => {
  commit(types.FETCH_FUTURES_CLOSED_POSITIONS_ASYNC.PENDING);

  const userAddress = rootGetters['wallets/activeWalletAddress'];
  // const userAddress = '0x0A24BdE9D7618f9B745036FE0A7b57c7451ED724';

  if (!userAddress) {
    commit(types.FETCH_FUTURES_CLOSED_POSITIONS_ASYNC.FAILURE);
    return false;
  }

  try {
    const { data: positions } = await getClosedPositions({ userAddress });
    commit(types.FETCH_FUTURES_CLOSED_POSITIONS_ASYNC.SUCCESS, positions.map(positionTransformer));
    return true;
  } catch (error) {
    commit(types.FETCH_FUTURES_CLOSED_POSITIONS_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`backend futures positions unavailable: ${error}`, {
        module: 'futures-contracts',
        layer: 'backend',
      });
    }
    return false;
  }
};

export default {
  fetchPositions,
  fetchClosedPositions,
};



