<template>
  <transition-modal>
    <e-modal
      wide-design
      @close="onClose"
    >
      <template slot="inner">
        <div class="the-login-flow-popup" :class="[ `the-login-flow-popup__${theme}` ]">
          <div class="the-login-flow-popup__head">
            <button
              type="button"
              v-if="loginFlowModal && loginFlowModal !== 5"
              @click="setLoginFlow(0)"
              class="the-login-flow-popup__head-back"
            >
              <e-icon :src="$options.icons.iconArrow" class="the-login-flow-popup__head-back-icon" />
              Back
            </button>

            <span v-if="loginFlowModal === 5">Create New Wallet</span>
            <span v-else>Connect a Wallet</span>
          </div>

          <div class="the-login-flow-popup__content">
            <div class="the-login-flow-popup__main" v-if="loginFlowModal === 0">
              <div class="the-login-flow-popup__body">
                <div class="the-login-flow-popup__body-header">
                  <div class="the-login-flow-popup__body-header-title">Select Method</div>
                  <div class="the-login-flow-popup__body-header-description">Connect a wallet using one of the methods below</div>
                </div>

                <div class="the-login-flow-popup__login-list">
                  <div class="the-login-flow-popup__login-list-item" @click="setLoginFlow(1)">
                    <span class="the-login-flow-popup__login-list-text">
                      <e-icon :src="$options.icons.iconPrivateKey" class="the-login-flow-popup__login-list-icon" />
                      Private Key
                    </span>
                  </div>
                  <div class="the-login-flow-popup__login-list-item" @click="loginWithMetamask">
                    <span class="the-login-flow-popup__login-list-text">
                      <e-icon :src="$options.icons.iconMetamask" class="the-login-flow-popup__login-list-icon" />
                      Metamask
                    </span>
                  </div>
                  <div class="the-login-flow-popup__login-list-item" @click="setLoginFlow(3)">
                    <span class="the-login-flow-popup__login-list-text">
                      <e-icon :src="$options.icons.iconLedger" class="the-login-flow-popup__login-list-icon ledger" />
                      Ledger
                    </span>
                  </div>
                  <div class="the-login-flow-popup__login-list-item" @click="setLoginFlow(4)">
                    <span class="the-login-flow-popup__login-list-text">
                      <e-icon :src="$options.icons.iconMail" class="the-login-flow-popup__login-list-icon email" />
                      Email
                    </span>
                  </div>
                </div>

                <div class="the-login-flow-popup__main">
                  <div class="the-login-flow-popup__generate-label">Dont’t have a Wallet?</div>
                  <e-button fill="green-transparent" type="squared" size="ss"  @click="setLoginFlow(5)">Create A New Wallet</e-button>
                </div>
              </div>

              <div class="the-login-flow-popup__footer">
                <button class="e-receipt__btn e-receipt__btn_cancel the-login-flow-popup__cancel-button" @click="cancelOperation">
                  Close
                </button>
              </div>
            </div>

            <div class="the-login-flow-popup__main-tab" v-if="loginFlowModal === 1">
              <div class="the-login-flow-popup__body tab">
                <div class="the-login-flow-popup__body-header">
                  <div class="the-login-flow-popup__body-header-title">Connect with Private Key</div>
                  <div class="the-login-flow-popup__body-header-description">Connect a wallet using your Private Key</div>
                </div>
                <the-wallet-signup-in-popup />
              </div>
            </div>

            <div class="the-login-flow-popup__main-tab" v-if="loginFlowModal === 2">
              <div class="the-login-flow-popup__body tab">
                <div class="the-login-flow-popup__body-header">
                  <div class="the-login-flow-popup__body-header-title">Connect Metamask</div>
                  <div class="the-login-flow-popup__body-header-description">Check your Metamask Notification or <br> sign in the opened Metamask Popup</div>
                </div>

                <e-icon :src="$options.icons.iconMetamask" style="width: 125px; margin: 50px auto;" />

                <div class="the-login-flow-popup__footer-sbmt">
                  <div class="the-login-flow-popup__footer-sbmt-item">
                    <button
                      type="button"
                      class="e-receipt__btn e-receipt__btn_active the-login-flow-popup__cancel-button"
                      @click="cancelOperation"
                    >
                      Close
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div class="the-login-flow-popup__main-tab" v-if="loginFlowModal === 3">
              <div class="the-login-flow-popup__body tab">
                <the-wallet-ledger-signup-in-popup />
              </div>
            </div>

            <div class="the-login-flow-popup__main-tab generate" v-if="loginFlowModal === 5">
              <div class="the-login-flow-popup__body tab generate">
                <the-my-wallet-in-popup :closeGeneratePulse='closeGeneratePulse' />
              </div>
            </div>

            <div class="the-login-flow-popup__main-tab generate" v-if="loginFlowModal === 4">
              <div class="the-login-flow-popup__body tab">
                <the-login-in-popup />
              </div>
            </div>
          </div>
        </div>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
/**
 * Component: Всплывающее окно перевода средств (депозит, видровл, трансфер)
 *
 * @version 1.0.2
 */

import { mapActions, mapGetters, mapMutations } from 'vuex';

import iconArrow from '@/icons/e-icon_arrow_right.svg';
import iconMetamask from '@/icons/e-icon_metamask-normal.svg';
import iconLedger from '@/icons/e-icon__ledger-logo.svg';
import iconPrivateKey from '@/icons/e-icon__private-key.svg';
import iconMail from '@/icons/e-icon__mail.svg';
import iconLoading from '@/icons/e-icon__loading-gen.svg';

import { putGAClientID } from '@/api/backend/ga';

import EModal from '@/components/EModal';
import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';
import TransitionModal from '@/containers/transitions/TransitionModal';
import TheWalletSignupInPopup from '@/containers/blocks/TheWalletSignupInPopup';
import TheMyWalletInPopup from '@/containers/blocks/TheMyWalletInPopup';
import TheWalletLedgerSignupInPopup from '@/containers/blocks/TheWalletLedgerSignupInPopup';
import TheLoginInPopup from '@/containers/blocks/TheLoginInPopup';

export default {
  name: 'the-login-flow-popup',

  data() {
    return {
      activeTab: null,
      closeGeneratePulse: false,
    };
  },

  computed: {
    ...mapGetters({
      loginFlowModal: 'ui/loginFlowModal',
      theme: 'ui/getTheme',
    }),
  },

  icons: {
    iconArrow,
    iconPrivateKey,
    iconMetamask,
    iconLedger,
    iconMail,
    iconLoading,
  },

  methods: {
    ...mapMutations({ cancelOperation: 'ui/HIDE_LOGIN_FLOW', setLoginFlow: 'ui/SET_LOGIN_FLOW' }),

    ...mapActions({
      setActiveWallet: 'wallets/setActiveWallet',
    }),

    onClose() {
      if (this.loginFlowModal === 5) {
        this.closeGeneratePulse = true;
      } else {
        this.closeGeneratePulse = false;
        this.cancelOperation();
      }
    },

    /**
     * Логин с помощью метамаска
     * Если есть метамасковский кошелек, то он делается активным, если нет выводится инструкция, как подключить метамаск
     */
    async loginWithMetamask() {
      if (this.metamaskLoading) return;
      this.metamaskLoading = true;
      this.setLoginFlow(2);
      await this.$store.dispatch('wallets/loadMetamaskWallets');
      const wallet = this.$store.state.wallets.providedWallets[0];
      const cid = window.ga && window.ga.getAll() && window.ga.getAll()[0] ? window.ga.getAll()[0].get('clientId') : undefined;

      putGAClientID({ walletAddress: wallet.address, clientID: cid, ga: this.$ga, ref: this.referrerAddress, login_type: 'metamask' });
      this.$ga.event({
        eventCategory: 'login',
        eventAction: 'login_successful',
        eventLabel: 'metamask',
      });

      if (wallet) {
        // Делаем активным найденный кошелек Метамаск
        this.setActiveWallet(wallet);
        this.cancelOperation();
      } else {
        this.setLoginFlow(2);
        // Открытие инструкции по подключению Метамаска
        // this.signUpOptionIndex = 0;
        // this.currentVisibleBlock = 1;
      }
      this.metamaskLoading = false;
    },
  },

  components: {
    EModal,
    EIcon,
    EButton,
    TransitionModal,
    TheWalletSignupInPopup,
    TheMyWalletInPopup,
    TheWalletLedgerSignupInPopup,
    TheLoginInPopup,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-login-flow-popup {
  $this: &;
  background-color: #f4f5f9;

  &__main {
    position: relative;
  }

  &__main-tab {
    position: relative;
    min-height: 410px;
    padding-bottom: 70px;

    &.generate {
      display: flex;
      align-items: center;

      #{$this}__body {
        padding-top: 0;
      }
    }
  }

  &__head {
    position: relative;
    background-color: #fff;
    box-shadow: 0 0 3px rgba(black, .08);
    height: 50px;
    border-radius: 3px 3px 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    font-weight: 600;
    font-size: 16px;
    color: #000;
    text-align: center;
  }

  &__head-back {
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translate(0, -50%);
    cursor: pointer;
    padding-left: 26px;
    font-weight: 600;
    font-size: 11px;
    color: $color-grey;
    text-transform: uppercase;
    letter-spacing: 0.73px;
    height: 18px;
    display: flex;
    align-items: center;
  }

  &__head-back-icon {
    width: 18px;
    height: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    background: $color-grey;
    transition: all .15s ease-in-out;
    position: absolute;
    left: 0;
    border-radius: 4px;

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) rotate(-180deg);
      width: 14px;
      height: 14px;
      fill: #fff;
    }
  }

  &__body {
    padding: 54px 10px;
    width: 100%;
    max-width: 420px;
    margin: 0 auto;
    box-sizing: border-box;

    &.tab {
      padding-bottom: 0;
    }

    &.generate {
      max-width: 480px;
    }
  }

  &__body-header {
    color: $color-dark-blue;
    text-align: center;

    &.text-left {
      text-align: left;
    }

    &.qr {
      display: flex;
      justify-content: space-between;
      align-items: center;

      #{$this}__body-header-title {
        letter-spacing: -.015em;
      }
    }

    a {
      color: $color-green-dark;
      font-weight: 500;
      text-decoration: none;
    }
  }

  &__qr {
    margin-left: 10px;
  }

  &__body-header-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 8px;

    @include r(640) {
      font-size: 15px;
      line-height: 18px;
    }
  }

  &__body-header-description {
    font-size: 13px;
    line-height: 19px;
    color: $color-dark-blue;

    @include r(640) {
      font-size: 13px;
      line-height: 15px;
    }
  }

  &__login-list {
    max-width: 380px;
    margin: 20px auto 26px;
  }

  &__login-list-item {
    width: 100%;
    height: 50px;
    margin-bottom: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    box-sizing: border-box;
    border: 1px solid $color-border-light;
    border-radius: 3px;
    cursor: pointer;
    transition: border-color .15s ease-in-out;

    &:hover {
      border-color: $color-green-dark;
    }
  }

  &__login-list-text {
    display: flex;
    align-items: center;
    flex: 0 0 115px;
    font-weight: 500;
    font-size: 15px;
    color: $color-blue;
    letter-spacing: 0;
  }

  &__login-list-icon {
    display: flex;
    align-items: center;
    width: 16px;
    height: 16px;
    margin-right: 20px;

    svg {
      position: relative;
      width: 100%;
    }

    &.ledger {
      svg g > g {
        fill: $color-blue
      }
    }

    &.email {
      svg {
        rect {
          fill: $color-green-light;
        }
        path {
          stroke: #f4f5f9;
        }
      }
    }
  }

  &__generate {
    .e-button_padding_default {
      font-size: 11px;
      height: 32px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__generate-label {
    font-size: 13px;
    line-height: 19px;
    color: $color-dark-blue;
    margin-bottom: 8px;
  }

  &__footer {
    overflow: hidden;
    border-radius: 0 0 3px 3px;
  }

  &__cancel-button,
  &__action-button {
    font-size: 12px;
    letter-spacing: 1.09px;
    text-align: center;
    line-height: 14px;
    align-items: center;
    background-color: $color-grey-light;
    color: $color-blue;
    display: flex;
    flex-direction: row;
    font-weight: 600;
    padding: 17px;
    text-transform: uppercase;
    transition: background-color ease $transition-time, color ease $transition-time;
    text-decoration: none;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
    height: 50px;
  }

  &__action-button {
    color: #232323;
    background: $color-green-dark;

    &:disabled {
      color: #51545D;
      background: $color-grey-second;
    }
  }

  &__cancel-button {
    &:disabled {
      color: $color-grey;
    }
  }

  &__footer-sbmt {
    display: flex;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
  }

  &__footer-sbmt-item {
    flex-grow: 1;
    flex-basis: 0;
  }

  &__body-header-icon {
    margin: 0 auto 12px;
    background: #fff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__body-header-icon-el {
    position: relative;
    width: 21px;
    height: 21px;

    &.ledger {
      svg {
        top: 1px;

        g > g {
          fill: #000
        }
      }
    }
  }

  &__qr {
    width: 100px;
    height: 100px;
    background: #fff;
  }

  &__dark {
    background: $dark-background;

    #{$this}__body-header-icon {
      background: #1C222D;
    }

    #{$this}__head {
      background: $dark-background-lighter;
      color: $color-green-dark;
    }

    #{$this}__head-back {
      color: #fff;
    }
    #{$this}__head-back-icon {
      background: #343E4E;
    }

    #{$this}__body {
      color: #fff;
    }

    #{$this}__body-header {
      color: #FFFFFF;
    }

    #{$this}__body-header-description {
      color: #D9D9D9;
    }

    #{$this}__body-header-icon-el {
      &.ledger {
        svg g > g {
          fill: #fff
        }
      }
    }

    #{$this}__login-list-item {
      background-color: transparent;
      border: 1px solid #282C38;

      &:hover {
        border-color: $color-green-dark;
      }
    }

    #{$this}__login-list-text {
      color: #fff;
    }

    #{$this}__login-list-icon {
      &.ledger svg g > g {
        fill: #fff;
      }

      &.email svg {
        rect {
          fill: $color-green-dark;
        }

        path {
          stroke: $dark-background;
        }
      }
    }

    #{$this}__generate-label {
      color: #D9D9D9;
    }

    #{$this}__cancel-button {
      background: $dark-background-lighter;
      color: #D9D9D9;

      &:hover {
        color: #fff;
      }

      &:disabled {
        color: #696b6f;
      }
    }

    #{$this}__action-button {
      background: $color-green-dark;
      color: #232323;

      &:disabled {
        color: #51545D;
        background: #2F333C;
      }
    }
  }
}
</style>



