import { MY_ORDERS_MODE_ACTIVE } from '@/store/constants';

const state = {
  /**
   * Активная страница ордеров
   */
  myOrdersPage: [],

  /**
   * Флаг устанавливается в значение true когда мои активные ордера загружаются, и в false когда они загружены
   */
  myOrdersPageLoading: false,


  /**
   * Текущий режим отображения ордеров (активные или история)
   */
  myOrdersMode: MY_ORDERS_MODE_ACTIVE,

  /**
   * Номер текущей отображаемой страницы
   */
  activePageNumber: 0,

  /**
   * Количество элементов на странице
   */
  ordersPerPage: 20,

  /**
   * Общее количество ордеров в текущем режиме отображения
   */
  totalNumberOfOrders: 0,

  cancelledOrder: null,
};


export default state;



