// Adapted version from: https://codepen.io/lollocll/pen/qPmLMr
/* eslint-disable */

function TextScramble(el, text) {
  this.el = el;
  this.text = text || this.el.innerText;
  this.chars = '+^=$~#________';
  if (text) {
    this.text = this.el.innerText = text;
  } else {
    this.text = this.el.innerText || '';
  }
}

TextScramble.prototype = {
  setText(newText) {
    const oldText = this.el.innerText;
    const length = Math.max(oldText.length, newText.length);
    const promise = new Promise(resolve => (this.resolve = resolve));
    this.queue = [];
    for (let i = 0; i < length; i++) {
      const from = oldText[i] || '';
      const to = newText[i] || '';
      const start = Math.floor(Math.random() * 40);
      const end = start + Math.floor(Math.random() * 40);
      this.queue.push({ from, to, start, end });
    }
    cancelAnimationFrame(this.frameRequest);
    this.frame = 0;
    this.update();
    return promise;
  },

  update() {
    let output = '';
    let complete = 0;
    for (let i = 0, n = this.queue.length; i < n; i++) {
      let { from, to, start, end, char } = this.queue[i];
      if (this.frame < start) {
        output += from;
      } else if (this.frame < end) {
        if (!char || Math.random() < 0.28) {
          char = this.randomChar();
          this.queue[i].char = char;
        }
        output += `<span class="text-scramble__temp-char">${char}</span>`;
      } else {
        complete++;
        output += to;
      }
    }
    this.el.innerHTML = output;
    if (complete === this.queue.length) {
      this.resolve();
    } else {
      this.frameRequest = requestAnimationFrame(this.update.bind(this));
      this.frame++;
    }
  },

  randomChar() {
    return this.chars[Math.floor(Math.random() * this.chars.length)];
  },
};

export default TextScramble;



