<template>
  <div
    class="e-pagination"
    :class="[`e-pagination_${type}`, `e-pagination__${theme}`]"
  >
    <button
      type="button"
      class="e-pagination__nav-btn e-pagination__nav-btn_prev"
      :disabled="value === 0"
      @click="onPageClick(Math.max(0, value - 1))"
    >
      <template v-if="!isSelectPagination">
        <e-icon :src="iconArrowLeft"/>
        Previous
      </template>

      <e-icon v-else :src="iconArrowSelect" class="e-pagination__nav-btn_select"/>
    </button>

    <!-- BEGIN: Номера страниц -->
    <button
      v-if="skipPages && pageCount > 0"
      :key="0"
      :class="{ 'e-pagination__page-btn_active': value === 0 }"
      class="e-pagination__page-btn"
      @click="onPageClick(0)"
    >1</button>

    <span
      v-if="skipStart"
      class="e-pagination__skip"
    >...</span>

    <button
      v-for="i of displayedPages"
      :key="i"
      :class="{ 'e-pagination__page-btn_active': i === value }"
      class="e-pagination__page-btn"
      @click="onPageClick(i)"
    >{{ i + 1 }}</button>

    <span
      v-if="skipEnd"
      class="e-pagination__skip"
    >...</span>

    <button
      v-if="skipPages && pageCount > 2 && !isLedger"
      :key="pageCount - 1"
      :class="{ 'e-pagination__page-btn_active': pageCount - 1 === value }"
      class="e-pagination__page-btn"
      @click="onPageClick(pageCount - 1)"
    >{{ pageCount }}</button>
     <!-- END: Номера страниц -->

    <button
      type="button"
      class="e-pagination__nav-btn e-pagination__nav-btn_next"
      :disabled="value === pageCount - 1"
      @click="onPageClick(Math.min(value + 1, pageCount))"
    >
      <template v-if="!isSelectPagination">
        Next
        <e-icon :src="iconArrowRight"/>
      </template>

      <e-icon v-else :src="iconArrowSelect" class="e-pagination__nav-btn_select"/>
    </button>

    <label v-if="isSelectPagination" class="e-pagination__to-page">
      <span class="e-pagination__to-page-text">Go to Page</span>
      <input type="number" v-model="toPage" class="e-pagination__to-page-input" @focus="toPage = undefined" @blur="toPage = value + 1">
    </label>
  </div>
</template>

<script>
/**
 * Компонент кнопок навигации по страницам (пагинации)
 *
 * @version 1.0.2
 */

import { mapGetters } from 'vuex';

import EIcon from '@/components/EIcon/EIcon';
import iconArrowLeft from '@/icons/e-icon_arrow_left.svg';
import iconArrowRight from '@/icons/e-icon_arrow_right.svg';

import iconArrowSelect from '@/icons/e-icon__arrow-demo-popup.svg';

export default {
  name: 'e-pagination',

  data() {
    return {
      toPage: 1,
    };
  },

  props: {
    /**
    * Active page
    *
    * @since Version 1.0.1
    */
    value: {
      type: Number,
      default: 0,
    },

    isLedger: Boolean,

    isSelectPagination: Boolean,

    /**
    * Total namber of pages
    *
    * @since Version 1.0.1
    */
    pageCount: {
      type: Number,
      required: true,
    },

    /**
    * Количество страниц справа и слева от текущий для отборажения (для одной из сторон)
    *
    * @since Version 1.0.2
    */
    neighbors: {
      type: Number,
      default: 1,
    },


    /**
    * The size of the buttons
    * `default, small`
    *
    * @since Version 1.0.1
    */
    type: {
      type: String,
      default: 'default',
      validator: type => ['default', 'small', 'select'].includes(type),
    },
  },

  computed: {
    skipPages() {
      return this.pageCount > 2 + this.neighbors;
    },

    skipStart() {
      return this.skipPages && this.value > this.neighbors;
    },

    skipEnd() {
      return this.skipPages && this.value < this.pageCount - 2 - this.neighbors;
    },

    displayedPages() {
      let pages = [];

      const inc = this.isLedger ? 1 : 0;

      if (!this.skipPages) {
        pages = Array.from(Array(this.pageCount).keys());
      } else {
        for (let i = Math.max(this.value - this.neighbors, 1); i <= Math.min(this.value + this.neighbors, this.pageCount - 2) + inc; i += 1) {
          pages.push(i);
        }
      }

      return pages;
    },

    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    toPage(value) {
      if (!value) { this.toPage = undefined; return; }
      if (value > this.pageCount) { this.toPage = this.pageCount; return; }
      if (value < 1) { this.toPage = 1; return; }

      if (value) {
        this.$emit('input', value - 1);
      }
    },
  },

  created() {
    Object.assign(this, {
      iconArrowLeft,
      iconArrowRight,
      iconArrowSelect,
    });
  },

  methods: {
    onPageClick(value) {
      this.$emit('input', value);
      this.toPage = value + 1;
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-pagination {
  $this: &;

  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-border-dark;
  font-weight: 700;
  line-height: 0;
  padding-left: 10px;
  padding-right: 13px;

  &__nav-btn {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    height: 100%;
    border: 1px solid #f4f5f9;
    background-color: #fff;
    transition: $transition-time;

    .e-icon {
      height: 8px;
      width: 8px;

      svg {
        fill: $color-blue;
      }
    }

    &:hover:not(:disabled) {
      background-color: #f4f5f9;
      /*border-color: $color-green-dark;*/

      svg path {
        fill: $color-green-dark;
      }
    }

    svg path {
      transition: fill .15s ease-in-out;
    }

    &:disabled {
      background-color: transparent;
      border-color: transparent;
      cursor: default;
      color: #FFFFFF;

      .e-icon svg {
        fill: #ffffff;
      }
    }
  }

  &__page-btn {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-color: #ffffff;
    border: 1px solid #f4f5f9;
    transition: all .15s ease-in-out;

    &:hover:not(&_active) {
      background-color: #fff;
      border-color: $color-green-dark;
    }
  }

  &__page-btn_active {
    background-color: $color-green-dark;
    border-color: $color-green-dark;
    color: $color-white;
    cursor: default;
  }

  &__skip {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    letter-spacing: -0.4px;
  }

  &__nav-btn_prev {
   #{$this}__nav-btn_select {
     transform: rotate(180deg);
   }
  }

  &__nav-btn_select {
    width: 9px !important;
    height: 9px !important;
    margin: 0 !important;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  /* type-specific styles */

  &_default {
    height: 40px;
    font-size: 10px;
    letter-spacing: 0.71px;

    #{$this}__nav-btn {
      width: 149px;
      border-radius: 30px;
    }

    #{$this}__nav-btn_prev {
      margin-right: 5px;
      .e-icon {
        margin-right: 9px;
      }
    }

    #{$this}__nav-btn_next {
      margin-left: 5px;
      .e-icon {
        margin-left: 9px;
      }
    }

    #{$this}__page-btn {
      margin: 0 5px;
      width: 40px;
      border-radius: 30px;
    }

    #{$this}__page-btn_active {
    }

    #{$this}__skip {
      margin: 0 5px;
    }
  }

  &_small {
    height: 20px;
    font-size: 9px;
    letter-spacing: 0.64px;

    #{$this}__nav-btn {
      width: 92px;
      border-radius: 10px;
    }

    #{$this}__nav-btn_prev {
      margin-right: 3px;

      .e-icon {
        margin-right: 6px;
      }
    }

    #{$this}__nav-btn_next {
      margin-left: 2px;

      .e-icon {
        margin-left: 6px;
      }
    }

    #{$this}__page-btn {
      margin: 0 2px;
      width: 20px;
      border-radius: 10px;
    }

    #{$this}__page-btn_active {
    }

    #{$this}__skip {
      margin: 0 2px;
    }
  }

  &_select {
    height: 35px;
    font-size: 12px;

    #{$this}__nav-btn {
      width: 32px;
      border-radius: 3px;

      &:disabled {
        svg path {
          fill: #ccc;
        }
      }
    }

    #{$this}__nav-btn_prev {
      margin-right: 3px;
      .e-icon {
        margin-right: 9px;
      }
    }

    #{$this}__nav-btn_next {
      margin-left: 3px;
      .e-icon {
        margin-left: 9px;
      }
    }

    #{$this}__page-btn {
      margin: 0 2px;
      width: 34px;
      border-radius: 3px;
    }

    #{$this}__page-btn_active {
    }

    #{$this}__skip {
      margin: 0 2px;
    }
  }

  &__to-page {
    cursor: pointer;
    width: 100px;
    height: 35px;
    padding: 0 0 0 8px;
    border-radius: 3px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    font-weight: 500;
    justify-content: space-between;
    border: 1px solid #f4f5f9;
    background: #fff;
    letter-spacing: -.4px;
    font-size: 12px;
    margin-left: auto;
    position: relative;
    left: 5px;
  }

  &__to-page-text {
    position: relative;
  }

  &__to-page-input {
    background-color: transparent;
    height: 100%;
    width: 19px;
    margin-right: 3px;
    text-align: center;
  }

  &__to-page-input::-webkit-outer-spin-button,
  &__to-page-input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &__to-page-input {
    -moz-appearance: textfield;
  }

  &__dark {
    color: #8D9098;

    #{$this}__to-page {
      border: 1px solid $color-border-dark;
      background: #141924;
    }

    #{$this}__to-page-input {
      color: #fff;
    }

    #{$this}__page-btn {
      background: #141924;
      color: #8D9098;
      border-color: transparent;

      &:hover {
        background: #141924;
        box-shadow: none;
      }
    }

    #{$this}__page-btn_active {
      background: #141924;
      border-color: $color-green-dark;
      color: #fff;

      &:hover {
        background: #121722;
      }
    }

    #{$this}__nav-btn {
      background: #282C38;
      color: #8D9098;
      border-color: transparent;

      .e-icon svg {
        fill: #8D9098;
      }

      &:hover {
        background: #282C38;
        box-shadow: none;
      }
    }

    #{$this}__nav-btn:disabled {
      background: transparent;
      color: rgba(255,255,255,.75);

      .e-icon svg {
        fill: rgba(255,255,255,.75);
      }

      .e-icon svg path {
        fill: rgba(255,255,255,.2);
      }
    }
  }
}
</style>



