import axios from 'axios';
import io from 'socket.io-client';
import ioReq from 'socket.io-request';
import { BACKEND_URL, BACKEND_PROD_URL, TIMEOUT_BACKEND_REQUEST } from '@/config';

const httpProd = axios.create({
  baseURL: BACKEND_PROD_URL,
});

const http = axios.create({
  baseURL: BACKEND_URL,
});

let socket = null;

const getSocket = () => {
  if (!socket) {
    socket = io(BACKEND_URL, {
      timeout: TIMEOUT_BACKEND_REQUEST,
      transports: process.env.NODE_ENV === 'production' ? ['polling', 'websocket'] : ['websocket'],
    });
  }
  return socket;
};

let ioReqSocket = null;

const getIoReqSocket = () => {
  if (!ioReqSocket) {
    ioReqSocket = ioReq(getSocket(), {
      timeout: TIMEOUT_BACKEND_REQUEST,
    });
  }
  return ioReqSocket;
};

export default {
  get socket() {
    return getSocket();
  },

  wsRequest: (...args) => getIoReqSocket().request(...args),

  http,

  httpProd,
};



