import { SELL_TOKEN_ORDER, BUY_TOKEN_ORDER, ETH_DECIMALS, DEFAULT_DECIMALS } from '@/util/constants';
import { BigNumber } from 'bignumber.js';
import toDisplayedPrice from '@/util/format/toDisplayedPrice';
import getNameForNamelessToken from '@/util/getNameForNamelessToken';

/**
 * Хелпер, который мапит ордер из полученных с бекенда данных
 *
 * @param {Order} order - Ордер для мепнга
 * @param {{ contract, decimals }} activeToken - Количество знаков после запятой токена данного ордера
 */
// eslint-disable-next-line import/prefer-default-export
export const mapOrder = (order, { activeToken }) => {
  const isBuyOrder = order.is_buy;
  const tokenAddress = order.token_address;
  /* eslint-disable no-nested-ternary */
  const decimals = (isBuyOrder && order.buy_decimals != null)
    ? order.buy_decimals
    : (!isBuyOrder && order.sell_decimals != null)
      ? order.sell_decimals
      : (activeToken != null && activeToken.contract === tokenAddress)
        ? activeToken.decimals
        : DEFAULT_DECIMALS;
  const shortName = (isBuyOrder && order.buy_symbol != null)
    ? order.buy_symbol
    : (!isBuyOrder && order.sell_symbol != null)
      ? order.sell_symbol
      : (activeToken != null && activeToken.contract === tokenAddress)
        ? activeToken.decimals
        : getNameForNamelessToken(tokenAddress);
  /* eslint-enable no-nested-ternary */
  const unitPrice = toDisplayedPrice(order.unit_price, decimals);

  const amountPending = isBuyOrder
    ? BigNumber(order.amount_filled.pending)
    : BigNumber(order.amount_filled.pending).shiftedBy(decimals).div(unitPrice.shiftedBy(ETH_DECIMALS));
  const amountApproved = isBuyOrder
    ? BigNumber(order.amount_filled.approved)
    : BigNumber(order.amount_filled.approved).shiftedBy(decimals).div(unitPrice.shiftedBy(ETH_DECIMALS));
  const amountFilledTotal = amountPending.plus(amountApproved);
  const amount = isBuyOrder
    ? BigNumber(order.amount_buy)
    : BigNumber(order.amount_sell);

  // Является ли одрер отмененным
  const isCanceled = order.canceled_at !== null;

  // Является ли ордер полностью заполненым
  const isFulfilled = order.fulfilled_at === null;

  return {
    amount,
    amountFilled: {
      pending: amountPending,
      approved: amountApproved,
      total: amountFilledTotal,
    },
    contractAddress: order.contract_address,
    createdAt: new Date(order.created_at),
    canceledAt: order.canceled_at ? new Date(order.canceled_at) : null,
    fulfilledAt: order.fulfilled_at ? new Date(order.fulfilled_at) : null,
    id: order.id,
    type: isBuyOrder ? BUY_TOKEN_ORDER : SELL_TOKEN_ORDER,
    nonce: order.nonce,
    orderHash: order.order_hash,
    r: order.r,
    s: order.s,
    token: {
      contract: tokenAddress,
      shortName,
      decimals,
    },
    unitPrice,
    stopPrice: order.stop_price ? toDisplayedPrice(order.stop_price, decimals) : null,
    isStopped: order.is_stopped,
    userAddress: order.user_address,
    v: order.v,
    isActive: isFulfilled && !isCanceled,
    isTradable: amountFilledTotal.lt(amount) && isFulfilled && !isCanceled,
    isCanceled,
  };
};



