import { ORDERS_PAGE_SIZE } from '@/config';
import backend from './endpoint';

// Константы для различных типов экшинов происходящих по ордеру
export const ACTION_CREATE_ORDER = 'create_order';
export const ACTION_CANCEL_ORDER = 'cancel_order';
export const ACTION_INTERNAL_TRADE = 'internal_trade';
export const ACTION_ORDER_MATCH = 'order_match';
export const ACTION_TRADE_CONFIRMATION = 'trade_confirmation';

/**
 * Read my order history.
 *
 * @param userAddress
 * @param contractAddress
 * @param pageNumber
 * @returns {Promise<{total: *, data}>}
 */
export const getOrderHistoryPage = async ({ userAddress, contractAddress, pageNumber }) => {
  const { data, meta } = await backend.wsRequest('/orders/get', {
    filters: {
      // token_address: tokenAddress,
      user_address: userAddress,
      contract_address: contractAddress,
      is_history: true,
    },
    limit: ORDERS_PAGE_SIZE,
    offset: pageNumber * ORDERS_PAGE_SIZE,
  });

  return {
    data,
    total: meta.count_items,
  };
};

export const getActiveOrdersPage = async ({ userAddress, contractAddress, pageNumber }) => {
  const { data, meta } = await backend.wsRequest('/orders/get', {
    filters: {
      // token_address: tokenAddress,
      user_address: userAddress,
      contract_address: contractAddress,
      is_canceled: false,
      is_fulfilled: false,
    },
    limit: ORDERS_PAGE_SIZE,
    offset: pageNumber * ORDERS_PAGE_SIZE,
  });

  return {
    data,
    total: meta.count_items,
  };
};

/**
 * Создание ордера
 *
 * @param {{contractAddress: string, stopPrice: string|undefined}} params
 * @since Version 1.0.1
 */
export const createOrder = async ({ contractAddress, userAddress, buyAddress, buyAmount, sellAddress, sellAmount, signature, stopPrice }) => {
  const { nonce, v, r, s } = signature;
  await backend.wsRequest('/orders/create', {
    contract_address: contractAddress,
    token_buy_address: buyAddress,
    amount_buy: buyAmount,
    token_sell_address: sellAddress,
    amount_sell: sellAmount,
    user_address: userAddress,
    order_hash: signature.orderHash,
    nonce,
    v,
    r,
    s,
    stop_price: stopPrice,
  });
};

/**
 * Отмена ордера.
 *
 * @param contractAddress {string}
 * @param orderHash {string}
 * @param userAddress {string}
 * @param nonce {number}
 * @param v {number}
 * @param r {string}
 * @param s {string}
 * @param cancelHash {string} hash который подтверждает, что пользователь намерен удалить order
 * @returns {Promise<*>}
 */
export const cancelOrder = ({ contractAddress, orderHash, userAddress, nonce, v, r, s, cancelHash }) => (
  backend.wsRequest('/orders/cancel', {
    contract_address: contractAddress,
    order_hash: orderHash,
    user_address: userAddress,
    nonce,
    v,
    r,
    s,
    cancel_hash: cancelHash,
  })
);

export const removeOrderListener = () => {
  backend.socket.removeAllListeners('my_orders');
};

export const onOrderChange = (callback) => {
  removeOrderListener();
  backend.socket.on('my_orders', callback);
};



