<template>
  <div
    :class="{ 'the-balance-transfer-popup__balance_tradable': tradable }"
    class="the-balance-transfer-popup__balance"
  >
    <div class="the-balance-transfer-popup__balance-body">
      <div class="the-balance-transfer-popup__balance-icon">
        <e-icon :src="iconWallet"/>
      </div>
      <div class="the-balance-transfer-popup__balance-amount">
        <e-big-number
          :value="balance"
          :decimals="token.decimals"
          :min-decimal-places="token.contract === ETH_ADDRESS ? ETH_DISPLAYED_DECIMALS : 3"
          :max-decimal-places="token.contract === ETH_ADDRESS ? ETH_DISPLAYED_DECIMALS : 8"
        />
        <span class="the-balance-transfer-popup__balance-shortname">{{ token.shortName }}</span>
      </div>
    </div>
    <div class="the-balance-transfer-popup__balance-title">
      {{ tradable ? 'Tradable' : 'Private' }} funds
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';

import iconWallet from '@/icons/e-icon_wallet.svg';

import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';

export default {
  name: 'EBalanceTransferFunds',

  props: {
    tradable: {
      type: Boolean,
      default: false,
    },
    balance: {
      type: BigNumber,
      required: true,
    },
    token: {
      type: Object,
      required: true,
    },
  },

  created() {
    this.iconWallet = iconWallet;
  },

  components: {
    EIcon,
    EBigNumber,
  },
};
</script>



