import delay from 'delay';
import { TimeoutError } from './errors';

/**
 * Executes a series of retries to get a request result.
 *
 * Example:
 * 1) Send request
 * 2) Request is not returned in maxRetryDelay ms
 * 3) Send request again, but don't forget about the previous one. Second request is sent as a backup plan if first doesn't return
 * 4) First request returned. Resolving the result.
 * 5) Second request returned. Ignore it's result, because we already have a valid answer.
 *
 * Doesn't throw an error when attempt last too long.
 * Function can be used for actions that fail on timeout by themselves.
 *
 * @type {any}
 * @param {number} tryCount - max number of tries before getting result
 * @param {number} timeout - global timeout for series of retries (in ms)
 * @param {number} minRetryDelay - minimal delay after first request was sent before sending a retry request. It helps to counter errors like '429 TOO MANY REQUESTS'
 * @param {number} maxRetryDelay - minimal delay before we suspect that first request failed and trying a new one
 * @param {function} action - function that can be executed few times
 */
const withRetry = ({
  tryCount = 20,
  timeout = 10000 - 33,
  minRetryDelay = 500 - 33,
  maxRetryDelay = 3400 - 33,
} = {}, action) => new Promise(async (resolve, reject) => {
  let resultSettled = false;
  let curTryCount = 0;
  let errorFromAction;
  let nextAttemptTimer;
  let fireAttemptAndTimer;
  let prevAttemptDate;

  const globalRejectTimer = setTimeout(() => {
    resultSettled = true;
    reject(errorFromAction || new TimeoutError());
  }, timeout);

  const onAttemptFulfilled = (result) => {
    if (!resultSettled) {
      resultSettled = true;
      clearTimeout(globalRejectTimer);
      clearTimeout(nextAttemptTimer);
      resolve(result);
    }
  };

  const onAttemptReject = async (err) => {
    if (!resultSettled) {
      errorFromAction = err;
      clearTimeout(nextAttemptTimer);
      const retryDelay = minRetryDelay - (Date.now() - prevAttemptDate);
      if (retryDelay > 0) {
        await delay(retryDelay);
      }
      fireAttemptAndTimer();
    }
  };

  const executeAttempt = () => {
    if (!resultSettled) {
      curTryCount += 1;
      prevAttemptDate = Date.now();
      Promise.resolve(action()).then(onAttemptFulfilled, onAttemptReject);
    }
  };

  fireAttemptAndTimer = () => {
    if (curTryCount !== tryCount) {
      executeAttempt();
      if (curTryCount !== tryCount) {
        nextAttemptTimer = setTimeout(executeAttempt, maxRetryDelay);
      }
    }
  };

  fireAttemptAndTimer();
});

export const withRetryForTask = withRetry.bind(null, {
  timeout: 20000 - 33,
});

export const withRetryForAsync = withRetry.bind(null, {
  timeout: 20000 - 33,
  maxRetryDelay: 10000 - 33,
});



