/* eslint-disable */
import { MAIN_NETWORK_ID, TEST_NETWORK_ID } from '@/util/constants';
import { getStorageValue } from '@/util/storage';

// Page title
export const PAGE_TITLE = 'DMEX - Decentralized (SAFE) Margin Trading';

/**
 * Указывает в какой сети Ethereum работает приложение
 * main - главная сеть
 * test - тестовая сеть Rinkeby
 * @type {string}
 */
export const APP_ENV = getStorageValue('APP_ENV') || process.env.PROD_ENV;
export const NETWORK_TYPE = process.env.NETWORK_TYPE;

export const BACKEND_PROD_URL = 'https://api.dmex.app';
export const BACKEND_STAGE_URL = 'https://api.demo.dmex.app';
export const BACKEND_STAGE2_URL = 'https://api.stag.dmex.app';

// Backend URL
export const BACKEND_URL = NETWORK_TYPE === 'ropsten' ? BACKEND_STAGE2_URL  : APP_ENV === 'main'
  ? BACKEND_PROD_URL
  : BACKEND_STAGE_URL;

/**
 * URL Ethereum Node, с которым взаимодействует приложение во время работы по Private Key.
 *
 * @type {string}
 */
export const ETHEREUM_NODE_HTTP = NETWORK_TYPE === 'ropsten' ? 'https://ropsten.infura.io/v3/c042c5fda8a440f8949ca9bf68838545' : APP_ENV === 'main'
  ? 'https://mainnet.infura.io/v3/c042c5fda8a440f8949ca9bf68838545'
  : 'https://rinkeby.infura.io/v3/c042c5fda8a440f8949ca9bf68838545';

// /**
//  * URL Ethereum Node, с которым взаимодействует приложение во время работы по Private Key.
//  *
//  * @type {string}
//  */
// export const ETHEREUM_NODE_WS = APP_ENV === 'main'
//   ? 'wss://mainnet.infura.io/ws'
//   : 'wss://rinkeby.infura.io/ws';

// URL блокчейн сети
export const ETHERSCAN_URL = NETWORK_TYPE === 'ropsten' ? 'https://ropsten.etherscan.io' : APP_ENV === 'main'
  ? 'https://etherscan.io'
  : 'https://rinkeby.etherscan.io';

export const APP_ETH_NETWORK_ID = APP_ENV === 'main'
  ? MAIN_NETWORK_ID
  : TEST_NETWORK_ID;

/**
 * Версия приложения
 */
export const APP_VERSION = process.env.APP_VERSION;

/**
 * Дата сборки приложения (фронтенда)
 */
export const APP_BUILD_DATE = process.env.APP_BUILD_DATE;

/**
 * Максимальное количество запросов которые агрегируются в одном batch запросе к Ethereum Node (e.g. Infura).
 * @type {number}
 */
export const BATCH_CHUNK_SIZE = 150;

// настройки связанные с комиссиями, затратами на gas и прочими финансовыми вопросами
export * from './currency';

// время
export * from './time';

// paging

/**
 * Возвращает количество оредров который нужно отображать на одной странице
 */
export const ORDERS_PAGE_SIZE = 20;
export const HISTORY_PAGE_SIZE = 50;
export const HISTORY_RECENT_SIZE = 5;
export const TOKEN_CATALOGUE_PAGE_SIZE = 20;
export const PAGE_SIZE_TRADES = 40;

/**
 * Число новостей для отображения
 * @type {number}
 */
export const NEWS_PAGE_SIZE = 20;

/**
 * Количество ордеров (в данном случае это ордера сгруппированные по цене), которые будут показываться в ордербуке по каждому типу (покупка, продажа)
 *
 * @type {number}
 */
export const ORDERS_IN_ORDERBOOK = 30;



