<template>
  <transition-modal>
    <e-modal
      class="the-password-popup"
      :secondaryDesign="true"
      :autoHeight="true"
      :btnClose="false"
      :level="1"
      :darkBackground="true"
      @close="closeModal"
    >
      <template slot="inner">
        <form class="the-password-popup__container" :class="[ theme ]" @submit.prevent="confirm">
          <h2 class="the-password-popup__headline">
            Enter your password
          </h2>

          <input type="password" class="the-password-popup__password-input" placeholder="Password" v-model="password" />

          <div class="the-password-popup__footer">
            <div
              class="the-password-popup__btns"
            >
              <button type="button" class="the-password-popup__btn the-password-popup__btn_cancel the-password-popup__pin" @click.prevent="closeModal">
                cancel
              </button>
              <button
                class="the-password-popup__btn the-password-popup__btn_confirm the-password-popup__pin"
                data-test="confirmOrder"
              >
                Confirm
              </button>
            </div>
          </div>
        </form>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex';

import TransitionModal from '@/containers/transitions/TransitionModal';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';
import EIcon from '@/components/EIcon';

export default {
  name: 'the-password-popup',

  data() {
    return {
      password: '',
    };
  },

  created() {
  },

  methods: {
    ...mapMutations({
      close: 'ui/HIDE_PASSWORD_PRIVATE_KEY',
    }),

    closeModal() {
      this.password = '';
      this.close();
    },

    confirm() {
      // console.log(this.password);
    },
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  components: {
    TransitionModal,
    EModal,
    EModalTrades,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-password-popup {
  $this: &;

  &__header {
    color: $color-dark-blue;
    box-shadow: 0 0 3px 0 rgba(0,0,0,0.2);
    font-size: 16px;
    font-weight: 800;
    line-height: 1.375;
    padding: 25px 10px 21px;
  }

  &__headline {
    font-weight: 500;
    font-size: 26px;
    color: $color-dark-blue;
    text-align: center;
    position: relative;
    margin-bottom: 40px;

    &:after {
      content: '';
      position: absolute;
      display: block;
      bottom: -20px;
      left: calc(50% - 15px);
      width: 30px;
      height: 2px;
      background: #00DB9C;
    }
  }

  &__password-input {
    width: 260px;
    height: 56px;
    line-height: 56;
    font-size: 15px;
    font-weight: 500;
    color: $color-dark-blue;
    background: #fff;
    border: 1px solid #eeefef;
    margin-bottom: 80px;
    padding: 0 20px;
    box-sizing: border-box;
  }

  &__container {
    padding: 24px 0 0;

    &.dark {

      #{$this}__headline {
        color: #FFFFFF;
      }

      #{$this}__password-input {
        color: #5C6062;
        background: #121722;
        border-color: #282C38;
      }

      .the-password-popup__btn.the-password-popup__btn_cancel.the-password-popup__pin {
        background: $dark-background-lighter;
        color: #9a9ea7;

        &:hover {
          color: #fff;
        }
      }

      .the-password-popup__btn.the-password-popup__btn_confirm.the-password-popup__pin {
        color: $dark-background-lighter;
      }
    }
  }

  &__image {
    height: 106px;
    position: relative;
  }

  &__textual {
    color: $color-dark-blue;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.312em;
    margin: auto;
    max-width: 296px;
  }

  &__btns {
    display: flex;
  }

  &__pin {
    align-items: center;
    background-color: $color-grey-light;
    color: $color-blue;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    font-weight: 800;
    padding: 17px;
    text-transform: uppercase;
    transition: background-color ease $transition-time, color ease $transition-time;
    text-decoration: none;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
  }

  &__btn_confirm {
    background-color: $color-green-bright;
    color: #ffffff;

    &:hover {
      background-color: darken($color-green-bright, 5%);
      color: #ffffff;
    }
  }
}
</style>



