import { NEWS_AND_ORDERS_TAB_POSITIONS } from '@/store/constants';
import { getStorageValue } from '@/util/storage';

const state = {
  demoStep: getStorageValue('demoStep') || null,

  // Размеры окна приложения для отслеживания ресайза
  windowSize: {
    width: 0,
    height: 0,
  },

  /**
   * Видимость вкладки с приложение
   */
  tabVisibility: true,

  // Задает показывается или нет в данный момент попап
  isPopup: false,

  // Задает какой именно из попапов показывается в данный момент
  displayedPopup: null,

  // Сейты связанные с попапок управления кошелком
  wallet: {
    activeStep: -1,
  },

  positionsTab: NEWS_AND_ORDERS_TAB_POSITIONS,

  newsAndMyOrdersTab: NEWS_AND_ORDERS_TAB_POSITIONS,

  // Время последнего логина
  lastLoginTime: null,

  // Показ дисконект попапа
  isNetworkStatusPopupClosed: false,

  // Показывает модельное окно с просьбой подтвердить транзакцию в Metamask
  metamaskSignRequired: false,

  // Показывает модельное окно с loginFlow
  loginFlowModal: null,

  // Показывает модельное окно с просьбой подтвердить транзакцию в Ledger Nano S
  ledgerApproveModal: false,

  // Показывает модельное окно с запросом пароля для расшифровки encrypted  privateKey
  passwordPrivateKeyModal: false,

  // true если было закрыто модельное окно, которое оповещает о неправильной выбранной сети Metamask
  metamaskNetworkPopupCancelled: false,

  // Показывает модельное окно с уведомлением о том что недостаточно баланса для оплаты fee deposit/withdraw/transfer
  balanceEthLowPopup: null,

  // Показывает модальное окно с уведомлением о том что баланса на контракте
  errorOrderBalance: false,

  // Показывает модальное окно с уведомлением о том что не хватает баланса для создания ордера (для случая когда баланс в ордерах)
  errorOrderBalanceReserved: false,

  // масив отображаемых модальных окон и их z-index-ы
  displayedModalLevels: [],

  // масив нотификаций
  notifications: [],

  assetType: 'crypto',

  // Теманая/ светлая тема
  theme: 'dark',
};

export default state;



