import { BigNumber } from 'bignumber.js';
import * as types from '@/store/mutation-types';
import { CREATE_ORDER_FAILURE, CREATE_ORDER_PENDING, CREATE_ORDER_SUCCESS } from '@/store/constants';

const mutations = {
  [types.FILL_CREATE_ORDER](state, { buyOrSell, amount, limitPrice }) {
    if (buyOrSell) {
      state.buyOrSell = buyOrSell;
    }

    if (amount) {
      state.orderParams = { ...state.orderParams, amount };
    }

    if (limitPrice) {
      state.orderParams = { ...state.orderParams, limitPrice };
    }
  },

  [types.SET_CREATE_ORDER_TYPE](state, orderType) {
    state.orderType = orderType;
  },

  [types.SET_CREATE_ORDER_BUY_OR_SELL](state, buyOrSell) {
    state.buyOrSell = buyOrSell;
  },

  [types.SET_CREATE_ORDER_NUMBERS](state, orderParams = { amount: '' }) {
    state.orderParams = orderParams;
  },

  [types.ORDER_ASK_CONFIRMATION](state, orderDetails) {
    state.orderConfirmation = orderDetails;
  },

  [types.ORDER_REJECT_CONFIRMATION](state) {
    state.orderConfirmation = null;
  },

  [types.CREATE_ORDER_ASYNC.PENDING](state) {
    state.orderConfirmation = null;
    state.status = CREATE_ORDER_PENDING;
  },

  [types.CREATE_ORDER_ASYNC.SUCCESS](state) {
    state.status = CREATE_ORDER_SUCCESS;
  },

  [types.CREATE_ORDER_ASYNC.FAILURE](state) {
    state.status = CREATE_ORDER_FAILURE;
  },

  [types.SHOW_MIN_ORDER_ERROR](state) {
    state.minOrderError = true;
  },

  [types.HIDE_MIN_ORDER_ERROR](state) {
    state.minOrderError = false;
  },

  [types.UPDATE_ETHUSD_PRICE](state, ethUsdPrice) {
    state.ethUsdPrice = ethUsdPrice;
  },

  [types.UPDATE_ORDER_MIN_ETH_VALUE](state, orderMinEthValue) {
    state.orderMinEthValue = BigNumber(orderMinEthValue);
  },
};

export default mutations;



