<template>
  <transition-modal>
    <e-modal
      class="e-popup-info"
      :level="level"
      :secondaryDesign="true"
      :autoHeight="true"
      :btnClose="false"
      :darkBackground="true"
      @close="$emit('close')"
    >
      <template slot="inner">
        <e-modal-trades
          :headerTitle="header"
          btnText="Ok"
          @close="$emit('close')"
        >
          <template slot="body">
            <div class="e-popup-info__container">
              <div class="e-popup-info__main">
                <div class="e-popup-info__message">
                  <slot name="message"/>
                </div>
                <div class="e-popup-info__details">
                  <slot name="details"/>
                  <slot name="info"/>
                </div>
              </div>
            </div>
          </template>
        </e-modal-trades>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';
import TransitionModal from '@/containers/transitions/TransitionModal';

export default {
  name: 'EPopupInfo',
  props: {
    level: {
      type: Number,
      default: 1,
    },
    header: {
      type: String,
      required: true,
    },
  },
  components: { TransitionModal, EModal, EModalTrades },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-popup-info {
  .e-modal-trades__header {
    font-weight: bold;
  }

  &__container {
    padding-top: 19px;
    padding-bottom: 12px;
  }

  &__message {
    color: $color-ultramarine;
    font-size: 19px;
    line-height: 1.1;
    font-weight: 400;
    text-align: center;
    margin-bottom: 16px;
  }

  &__details {
    color: $color-ultramarine;
    font-size: 16px;
    font-weight: 600;
  }
}

</style>



