/**
 * @property $loadPromise
 */
export default {
  data() {
    return {
      $_load_loaded: false,
      $_load_loading: true,
    };
  },

  computed: {
    $loaded() {
      return this.$data.$_load_loaded;
    },

    $loading() {
      return this.$data.$_load_loading;
    },
  },

  async created() {
    const opts = this.$options.loading || {};
    if (process.env.NODE_ENV !== 'production' && Object.keys(opts).length === 0) {
      throw new Error('Please specify component option "loading" if you are using loadingMixin');
    }
    const { actions = [] } = opts;
    await Promise.all(actions.map(a => this.$store.dispatch(a)));
    this.$data.$_load_loaded = true;
    this.$data.$_load_loading = false;
    this.$emit('load:success');
  },
};



