<template>
  <transition-modal>
    <e-modal
      class="the-metamask-sign-popup"
      secondary-design
      auto-height
      :btn-close="false"
      :level="1"
      dark-background
      @close="close"
    >
      <template slot="inner">
        <e-modal-trades
          headerTitle="Sign transaction in MetaMask"
          @close="close"
        >
          <template slot="body">
            <div class="the-metamask-sign-popup__container">
              <div class="the-metamask-sign-popup__image">
                <e-icon :src="depositIcon" />
              </div>
              <div class="the-metamask-sign-popup__textual">
                <p>
                  Check your Metamask Notification or sign in the opened Metamask Popup.
                </p>
              </div>
            </div>
          </template>
        </e-modal-trades>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
import { mapMutations } from 'vuex';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';
import EIcon from '@/components/EIcon';
import TransitionModal from '@/containers/transitions/TransitionModal';

import depositIcon from '@/icons/e-icon_deposit-popup-icon.svg';

export default {
  name: 'the-metamask-sign-popup',

  data() {
    return {
      depositIcon,
    };
  },

  methods: {
    ...mapMutations({
      close: 'ui/HIDE_METAMASK_SIGN',
    }),
  },

  components: {
    EModal,
    EModalTrades,
    EIcon,
    TransitionModal,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-metamask-sign-popup {
  &__header {
    color: $color-dark-blue;
    box-shadow: 0 0 3px 0 rgba(0,0,0,0.2);
    font-size: 16px;
    font-weight: 800;
    line-height: 1.375;
    padding: 25px 10px 21px;
  }

  &__container {
    padding: 24px 0 7px;
  }

  &__image {
    height: 180px;
    position: relative;
    margin-left: -30px;
    width: calc(100% + 30px);
  }

  &__textual {
    color: $color-dark-blue;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.312em;
    margin: auto;
    max-width: 296px;
  }
}
</style>



