import backend from './endpoint';

export const depositContracts = async ({ deposit_contract, dmex_base_contract, factory_address, btc_deposit_address, bytecode, owner, token_address }) =>
  backend.http.post('/api/futures/deposit-contracts', { deposit_contract, dmex_base_contract, factory_address, btc_deposit_address, bytecode, owner, token_address });

export const getBackendBalance = async ({ walletAddress, contract_address, token_address }) => backend.http.get(`/api/users/${walletAddress}/balances?contract_address=${contract_address}&token_address=${token_address}`);



