import NotificationBalanceSuccess from '@/components/ENotificationBalanceSuccess';
import NotificationBalanceError from '@/components/ENotificationBalanceError';

export const notificationComponents = {
  NotificationBalanceSuccess,
  NotificationBalanceError,
};

export const notificationTypes = Object.keys(notificationComponents)
  .map(key => ({ [key]: key }))
  .reduce((acc, e) => Object.assign(acc, e), {});



