/* eslint-disable no-restricted-syntax */
import { BigNumber } from 'bignumber.js';
import { ETH_ADDRESS, ETH_BASE_ADDRESS } from '@/util/constants';
import { getBackendBalance } from '@/api/backend/balance';
import deserializeBalance from './deserializeBalance';
import getWorker from './getWorker';


/**
 * @property {string} userAddress
 * @property {{ balanceOf, getAddress, getVersion }} exchangeContract
 */
export default class BalanceBlockchain {
  /**
   * @param {string} userAddress
   * @param {{ balanceOf, getAddress, getVersion }} exchangeContract
   */
  constructor({ userAddress, exchangeContract }) {
    this.userAddress = userAddress;
    this.exchangeContract = exchangeContract;
  }

  async getBalanceFromBackend(tokenAddress) {
    const bal = await getBackendBalance({ walletAddress: this.userAddress, contract_address: this.exchangeContract.getAddress(), token_address: tokenAddress });

    try {
      const decodedBalance = bal.data.data[0];

      const response = {
        reserved: BigNumber(decodedBalance.reserved),
        balance: BigNumber(decodedBalance.balance),
        pending: BigNumber(decodedBalance.pending),
      };

      return response;
    } catch (error) {
      const response = {
        reserved: BigNumber(0),
        balance: BigNumber(0),
        pending: BigNumber(0),
      };

      return response;
    }
  }

  /**
   * Returns private ETH balance.
   *
   * @returns {Promise<*>}
   */
  async getPrivateEth() {
    return BigNumber(await getWorker().getPrivateEth({
      userAddress: this.userAddress,
    }));
  }

  /**
   * Returns specific token private balance.
   *
   * @param tokenAddress
   * @returns {Promise<*>}
   */
  async getPrivateToken({ tokenAddress }) {
    return BigNumber(await getWorker().getPrivateToken({
      userAddress: this.userAddress,
      tokenAddress,
    }));
  }

  /**
   * Returns both specific token and ETH private balance.
   *
   * @param tokenAddress
   * @returns {Promise<{eth: any, tokens: {}}>}
   */
  async getPrivateEthAndToken({ tokenAddress }) {
    const [ethBalance, tokenBalance] = await Promise.all([
      this.getPrivateEth(),
      this.getPrivateToken({ tokenAddress }),
    ]);
    return {
      eth: ethBalance,
      tokens: {
        [tokenAddress]: tokenBalance,
      },
    };
  }

  /**
   * Get private (i.e. wallet) balance for each token and eth.
   * Do not throw exception
   *
   * @param tokens
   * @returns {Promise<{privateBalance, privateFailed: Array}>}
   */
  async getPrivateEthAndTokens({ tokens }) {
    return deserializeBalance(await getWorker().getPrivateEthAndTokens({
      userAddress: this.userAddress,
      tokens,
    }));
  }

  /**
   * Get contract balance of ETH
   *
   * @returns {Promise<BigNumber>}
   */
  async getTradableEth() {
    const balance = await this.getBalanceFromBackend(ETH_BASE_ADDRESS);
    return balance.balance;
  }

  /**
   * Get contract balance of specified tokenAddress
   *
   * @returns {Promise<BigNumber>}
   */
  async getTradableToken({ tokenAddress }) {
    const balance = await this.getBalanceFromBackend(tokenAddress);
    return balance.balance;
    // return BigNumber(await getWorker().getTradableToken({
    //   tokenAddress,
    //   userAddress: this.userAddress,
    //   exchangeContractAddress: this.exchangeContract.getAddress(),
    //   exchangeContractVersion: this.exchangeContract.getVersion(),
    // }));
  }

  async getAvailableBalance({ tokenAddress }) {
    const balance = await this.getBalanceFromBackend(tokenAddress);
    return balance.balance.minus(balance.reserved)/* .plus(balance.pending) */;
    // return BigNumber(await getWorker().getAvailableBalance({
    //   tokenAddress,
    //   userAddress: this.userAddress,
    //   exchangeContractAddress: this.exchangeContract.getAddress(),
    //   exchangeContractVersion: this.exchangeContract.getVersion(),
    // }));
  }

  /**
   * Get contract balance of token and eth
   *
   * @param tokenAddress address of token to be checked
   * @returns {Promise<{tradableBalance, tradableFailed: number}>}
   */
  async getTradableEthAndToken({ tokenAddress }) {
    const [ethBalance, tokenBalance] = await Promise.all([
      this.getTradableEth(),
      this.getTradableToken({ tokenAddress }),
    ]);
    return {
      balance: {
        [ETH_ADDRESS]: ethBalance,
        [tokenAddress]: tokenBalance,
      },
    };
  }

  /**
   * Get contract balance of each token
   * Block Number is mandatory.
   * Does not throw exceptions
   *
   * @param tokens
   * @returns {Promise<{tradableBalance, tradableFailed: number}>}
   */
  async getTradableEthAndTokens({ tokens }) {
    return deserializeBalance(await getWorker().getTradableEthAndTokens({
      tokens,
      userAddress: this.userAddress,
      exchangeContractAddress: this.exchangeContract.getAddress(),
      exchangeContractVersion: this.exchangeContract.getVersion(),
    }));
  }

  /**
   * Get both private and contract balance of ETH and traded token
   *
   * @param tokenAddress address of token to be checked
   * @returns {Promise<{tradableBalance, tradableFailed: number}>}
   */
  async getBalanceAfterOperation({ tokenAddress }) {
    const promises = [
      this.getPrivateEth(),
      this.getTradableEth(),
      this.getAvailableBalance({ tokenAddress }),
    ];
    let privateBalance;
    let tradableBalance;
    let availableBalance;

    if (tokenAddress === ETH_ADDRESS) {
      const [privateEth, tradableEth, availableEth] = await Promise.all(promises);
      privateBalance = { eth: privateEth };
      tradableBalance = { balance: { [ETH_ADDRESS]: tradableEth } };
      availableBalance = { balance: { availableBalance: availableEth } };
    } else {
      promises.push(
        this.getPrivateToken({ tokenAddress }),
        this.getTradableToken({ tokenAddress }),
      );
      const [privateEth, tradableEth, privateToken, tradableToken] = await Promise.all(promises);
      privateBalance = { eth: privateEth, tokens: { [tokenAddress]: privateToken } };
      tradableBalance = {
        balance: {
          [ETH_ADDRESS]: tradableEth,
          [tokenAddress]: tradableToken,
        },
      };
    }
    return {
      privateBalance,
      tradableBalance,
      availableBalance,
    };
  }
}



