/**
 * @module store/tokens/actions
 * @version 1.0.3
 */

import * as types from '@/store/mutation-types';
import log from '@/util/logger';
import { getStorageValue } from '@/util/storage';
import {
  getTokens,
  tokenSubscribe as sendTokenSubscription,
  onTokenNumbers,
  onReconnect,
  removeTokenNumbersListener,
  removeReconnectListener,
} from '@/api/backend';
import { TokenContracts } from '@/api/ethereum';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import { wrapCachedAction, resetCachedAction } from '@/store/utils';
import { ACTIVE_TOKEN_DAI, ACTIVE_TOKEN_BTC } from '@/util/constants';
import { mapToken, mapTokenNumbers } from './helpers';

let tokensReconnectListener;

/**
 * Экшин загрузки токенов
 *
 * @since Version 1.0.1
 */
const loadTokens = wrapCachedAction('loadTokens', async ({ commit, rootGetters, dispatch }) => {
  await dispatch('contracts/subscribeToContract', undefined, { root: true }); // ensures active contract loaded
  try {
    const res = await getTokens(rootGetters['contracts/activeMainContractAddress']);
    const tokens = res.map(mapToken);
    commit(types.SET_TOKENS, tokens);
    commit(types.LOAD_SELECTED_TOKENS);
  } catch (error) {
    if (rootGetters.isConnected) {
      log.error(`backend tokens unavailable: ${error}`, {
        module: 'tokens',
        layer: 'backend',
      });
    }
  }
});

/**
 * Action: Экшин, который устанавливает текущи токен по его адресу
 * Возвращает Promise, который заканчивается в момент, когда будет проставлен токен, но вся логика,
 * которая реаигрует на изменине токена ещё выполняется
 *
 * @since Version 1.0.1
 * @todo Нужно реализовать проверку существуют ли такой токен. Проблема в том, что во время установки активног токена, такого токена может еще не существовать в сторе
 * @todo Переключаться на другой токен с гарантией того что не было пропущено сообщений из бекенда
 */
const setActiveToken = async ({ commit }, tokenName) => {
  // if (BASE_TOKEN_DAI === getStorageValue('BASE_TOKEN')) {
  //   commit(types.SET_ACTIVE_TOKEN.SUCCESS, ACTIVE_TOKEN_DAI);
  // }

  if (tokenName === 'BTC' || getStorageValue('BASE_TOKEN_BTC')) {
    commit(types.SET_ACTIVE_TOKEN.SUCCESS, ACTIVE_TOKEN_BTC);
  } else {
    commit(types.SET_ACTIVE_TOKEN.SUCCESS, ACTIVE_TOKEN_DAI);
  }


  // console.log(getters.activeTokenContract, nextTokenContract);

  // if (getters.activeTokenContract !== nextTokenContract) {
  //   await dispatch('loadTokens');
  //   const token = getters.tokenByContractSimple(nextTokenContract);
  //   commit(types.SET_ACTIVE_TOKEN.SUCCESS, token);
  //   // setStorageValue(ACTIVE_TOKEN_CONTRACT_LOCAL_STORAGE_KEY, nextTokenContract);
  //
  //   // notify others. await не нужен, т.к. нужно возвращать как только проставился токен
  //   resetCachedAction('subscribeToToken');
  //   dispatch('subscribeToToken'); // Подписка на обновление текущего токена
  //   emitAfterTokenChange();
  //   dispatch('orders/loadMyOrdersPage', undefined, { root: true });
  //   // dispatch('trades/loadLatestTrades', undefined, { root: true });
  // }
};

/**
 * Preconditions: activeToken !== null
 */
const subscribeToToken = ({ getters, rootGetters }) => {
  const tokenAddress = getters.activeTokenContract;
  if (!tokenAddress) return;

  sendTokenSubscription(tokenAddress).catch((error) => {
    if (rootGetters.isConnected) {
      log.error(`Error subscribing to active token: ${error}`, {
        module: 'tokens',
        layer: 'backend',
      });
    }
  });
  // Оптимизация: action resolve-иться как только запрос на подписку отправлен, не дожидаясь ответа.
};

/**
 * Изменение статуса токена (starred/non-starred).
 * Synchronous.

 * @todo optimise token access
 * @param {string} contract контракт токена
 */
const toggleTokenFavorite = ({ commit }, contract) => {
  commit(types.CHANGE_TOKEN_SELECTED, contract);
};

/**
 * Подписка на обновления числовых данных по токенам
 * Preconditions: activeToken !== null
 * Synchronous.
 *
 * @since Version 1.0.1
 * @todo ensure contract loaded
 */
const listenToTokensChanges = ({ state, getters, rootGetters, commit, dispatch }) => {
  commit(types.TOKEN_NUMBERS_ADD_LISTENER);
  if (state.tokenNumbersListenerCount === 1) {
    const handler = (data) => {
      const token = rootGetters['tokens/tokenByContractSimple'](data.contract_address);
      if (!token) return;
      const nextTokenState = mapTokenNumbers({ ...data, decimals: token.decimals });
      if (nextTokenState.contract === rootGetters['contracts/activeMainContractAddress']) {
        const prevTokenState = getters.tokenMap.get(nextTokenState.contract);
        commit(types.SET_TOKEN_2, { prevTokenState, nextTokenState });
      }
    };
    removeReconnectListener(tokensReconnectListener);
    removeResumeListener(tokensReconnectListener);
    tokensReconnectListener = () => {
      if (state.tokenNumbersListenerCount) {
        resetCachedAction('loadTokens');
        dispatch('loadTokens');
      }
    };
    onTokenNumbers(handler);
    onReconnect(tokensReconnectListener);
    onResume(tokensReconnectListener);
  }
};

const stopListeningToTokensChanges = ({ state, commit }) => {
  commit(types.TOKEN_NUMBERS_REMOVE_LISTENER);
  if (state.tokenNumbersListenerCount === 0) {
    removeTokenNumbersListener();
    removeReconnectListener(tokensReconnectListener);
    removeResumeListener(tokensReconnectListener);
  }
};

const setNewTokenAsActive = async ({ commit, dispatch }, address) => {
  commit(types.SET_ACTIVE_TOKEN.PENDING);
  try {
    const tokenData = await TokenContracts().getTokenData(address);
    commit(types.SET_ACTIVE_TOKEN.SUCCESS, tokenData);
  } catch (e) {
    log.debug(e);
    commit(types.SET_ACTIVE_TOKEN.FAILURE);
  }
  resetCachedAction('subscribeToToken');
  dispatch('subscribeToToken'); // Подписка на обновление текущего токена
  dispatch('orders/loadMyOrdersPage', undefined, { root: true });
  // dispatch('trades/loadLatestTrades', undefined, { root: true });
};

export default {
  listenToTokensChanges,
  loadTokens,
  setActiveToken,
  stopListeningToTokensChanges,
  subscribeToToken,
  toggleTokenFavorite,
  setNewTokenAsActive,
};



