import { BigNumber } from 'bignumber.js';
import { PENDING_STATUS, APPROVED_STATUS, DECLINED_STATUS, DEPOSIT_OPERATION, WITHDRAWAL_OPERATION, GWEI_DECIMALS } from '@/util/constants';
import { roundUp } from '@/util/numbers';
import { dateToString } from '@/util/dates';

/**
 * Функция хелпер, которая преобразует контракт из формата отдаваемого бекендом, в тот, с которым работает фронтенд
 * @returns {id, contractAddress, version, createdAt, type}
 */
export const mapContract = contract => ({
  id: contract.id,
  contractAddress: contract.contract_address,
  version: contract.version,
  createdAt: new Date(contract.created_at),
  type: contract.type,
});

export const fillUiContractVersion = (contracts) => {
  const results = [];

  let uiVersion = 0;
  let prevVersion = null;
  for (let i = contracts.length - 1; i >= 0; i -= 1) {
    uiVersion = prevVersion === contracts[i].version ? (uiVersion + 1) : 0;
    prevVersion = contracts[i].version;

    results.push({
      ...contracts[i],
      uiVersion: `${contracts[i].version}.${uiVersion}`,
    });
  }

  return results.reverse();
};

/**
 * Функция хелпер, которая преобразует volumes из формата отдаваемого бекендом, в тот, с которым работает фронтенд
 * @param {{interval_all: string}} v
 * @returns {{intervalAll: BigNumber}}
 */
export const mapVolumes = v => ({ intervalAll: BigNumber(v.interval_all) });

const getOperationType = op => (op.is_deposit ? DEPOSIT_OPERATION : WITHDRAWAL_OPERATION);

export const mapOperationStatus = (isConfirmed) => {
  switch (isConfirmed) {
    case true: return APPROVED_STATUS;
    case false: return DECLINED_STATUS;
    default: return PENDING_STATUS;
  }
};

/**
 * Transform operation from the backend format to frontend format.
 * @param op operation in backend format
 * @returns {{value: BigNumber, contractAddress: *, userAddress: (*|string|string), tokenAddress: (string|string|*), transactionHash: *, type: *, status, createdAt: number}}
 */
export const mapOperation = op => ({
  value: BigNumber(op.amount),
  contractAddress: op.contract_address,
  userAddress: op.user_address,
  tokenAddress: op.token_address,
  blockNumber: op.block_number,
  transactionHash: op.transaction_hash,
  type: getOperationType(op),
  status: mapOperationStatus(op.is_confirmed),
  createdAt: Date.parse(op.created_at),
});

/**
 * Меп новости из формата возвращаемого бекендом
 */
export const mapNews = news => ({
  id: news.id,
  publishedAt: dateToString(new Date(news.published_at)),
  source: news.source,
  title: news.title,
  url: news.url,
  votes: news.votes,
  tokens: news.tokens.map(item => item.contract_address),
});

/**
 * Transform gasPrice from the backend format to frontend format.
 *
 * @param {Object} prices
 * @param {string} gasPriceType
 * @returns {{ standard: *, fast: * }}
 */
export const mapGasPrice = (prices, { gasPriceType }) => {
  const operationPrice = prices[gasPriceType];
  return {
    fast: operationPrice != null
      ? roundUp(operationPrice, GWEI_DECIMALS)
      : roundUp(prices.standard, GWEI_DECIMALS),
  };
};



