import scoped from './scoped';

/* eslint-disable-next-line */
const getKey = ({ type, exchangeContract, futuresContract }) => exchangeContract ? `${type}@${exchangeContract.id}` : `${type}@${futuresContract.id}`;

export default scoped(getKey);



