<template>
  <div
    class="e-balance-transfer-operations"
    :class="`e-balance-transfer-operations_${position}`"
  >
    <div
      v-for="{ type, status = 'pending', key, value, transactionHash } of operations"
      :key="transactionHash || key"
      class="e-balance-transfer-operations__operation"
    >
      <div
        class="e-balance-transfer-operations__amount"
        :class="`e-balance-transfer-operations__amount_${type}`"
      >
        {{ type !== TRANSFER_OPERATION ? '+' : '-' }}
        <e-big-number
          :value="value"
          :decimals="token.decimals"
          :min-decimal-places="DEFAULT_MIN_DECIMALS"
          :max-decimal-places="DEFAULT_MAX_DECIMALS"
        />
      </div>
      <div class="e-balance-transfer-operations__info">
        <!-- <div class="e-balance-transfer-operations__preloader">
          <e-preloader :small-preloader="true"/>
        </div> -->
        <!-- <span
          class="e-balance-transfer-operations__stage"
          :class="`e-balance-transfer-operations__stage_${status.toLowerCase()}`"
        /> -->
        <a
          v-if="transactionHash"
          :href="`${ETHERSCAN_URL}/tx/${transactionHash}`"
          class="e-balance-transfer-operations__link"
          target="_blank"
          rel="nofollow"
          @click.native.stop
        >
          <e-icon :src="depositLinkIcon" />
          <span class="e-balance-transfer-operations__link-txt">Info</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>

import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';

import depositLinkIcon from '@/icons/e-icon_deposit-link.svg';

export default {
  name: 'e-balance-transfer-operations',

  props: {
    position: {
      type: String,
      default: 'left',
      validator: v => ['left', 'right'].includes(v),
    },
    token: {
      type: Object,
      required: true,
    },

    operations: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      depositLinkIcon,
    };
  },

  components: {
    EBigNumber,
    EIcon,
  },
};
</script>

<style lang="scss">
.e-balance-transfer-operations {
  $this: &;

  font-size: 10px;

  &__operation {
    display: flex;

    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }

  &__amount {
    font-weight: 600;
    letter-spacing: .67px;
    display: inline-block;
    text-transform: uppercase;
    animation: e-balance-transfer-operations__pulse 2s infinite;
  }

  &__amount_deposit {
    color: #1fce43;
  }

  &__amount_withdrawal {
    color: #1fce43;
  }

  &__amount_transfer {
    color: #DA4830;
  }

  &__info {
    align-items: center;
    display: flex;
    white-space: nowrap;
    position: relative;
  }

  &_left {
    #{$this}__info {
      justify-content: flex-end;
      padding-right: 20px;
    }

    /*#{$this}__stage {
      order: 2;
      padding-left: 0;
    }

    #{$this}__stage {
      order: 1;
    }*/

    #{$this}__preloader {
      left: auto;
      right: 0
    }
  }

  &__preloader {
    position: absolute;
    top: 2px;
    left: 0;
    width: 14px;
    height: 14px;
  }

  /*&__stage {
    color: #0D105B;
    font-size: 12px;
    padding-top: 1px;
    padding-left: 20px;
    text-transform: capitalize;
  }*/

  &__link {
    align-items: center;
    display: flex;
    flex-direction: row;
    margin: 0 10px;
    color: #0123e7;
    letter-spacing: 0.83px;
    text-transform: uppercase;
    text-decoration: none;

    .e-icon {
      display: block;
      position: relative;
      top: -2px;
      margin-right: 3px;
      width: 12px;
      height: 12px;

      svg {
        fill: #0123e7;
      }
    }
  }

  &__link-text {
    display: block;
    margin-top: 2px;
  }
}

@keyframes e-balance-transfer-operations__pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
</style>



