import { helpers } from 'vuelidate/lib/validators';

const VALID_WORD_COUNT_LIST = [12, 15, 18, 21, 24];

export default helpers.withParams(
  { type: 'mnemonicPhrase' },
  (phrase) => {
    const isEmpty = !helpers.req(phrase);
    if (isEmpty) return true;
    const actualWordCount = phrase.trim().split(/\s+/g).length;
    return VALID_WORD_COUNT_LIST.includes(actualWordCount);
  },
);



