import { CONNECTION_STATUS } from '@/store/constants';

export default {
  /**
   * Переменная, которая отвечает за статус коннекта с бекендом.
   * True if is Connected to backend.
   * В случае дисконнекта должна меняться на false
   */
  isConnected: state => state.backendConnectionStatus === CONNECTION_STATUS.OK,

  serverTimestamp: state => state.serverTimestamp,
};



