import BigNumber from 'bignumber.js';
import { BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';
import backend from '../endpoint';

export const tradeTransformer = t => ({
  id: t.id,
  contractAddress: t.contract_address,
  futuresContractHash: t.futures_contract_hash,
  futuresAssetHash: t.futures_asset_hash,
  futuresAssetName: t.futures_asset_name,
  makerAddress: t.maker_address,
  takerAddress: t.taker_address,
  side: t.side,
  price: BigNumber(t.price),
  decimals: t.futures_asset.decimals,
  amountFilled: BigNumber(t.amount_filled),
  makerPctFee: BigNumber(t.maker_pct_fee),
  takerPctFee: BigNumber(t.taker_pct_fee),
  makerAmountFee: BigNumber(t.maker_amount_fee),
  takerAmountFee: BigNumber(t.taker_amount_fee),
  takerGasLimitFee: BigNumber(t.taker_gas_limit_fee),
  takerGasFee: BigNumber(t.taker_gas_fee),
  txGasPrice: BigNumber(t.tx_gas_price),
  txBlockNumber: t.tx_block_number,
  txHash: t.tx_hash,
  isConfirmed: t.is_confirmed,
  makerOrderHash: t.maker_order_hash,
  takerOrderHash: t.taker_order_hash,
  createdAt: new Date(t.created_at),
  exchange: t.exchange,

  type: t.side ? BUY_TOKEN_ORDER : SELL_TOKEN_ORDER,
});

export const getTrades = async ({ limit = 50, offset = 0, ...otherParams }) => {
  const reqParams = {
    limit,
    offset,
  };

  if (otherParams.futuresContractHash) {
    reqParams.futures_contract_hash = otherParams.futuresContractHash;
  }

  if (otherParams.futuresAssetHash) {
    reqParams.futures_asset_hash = otherParams.futuresAssetHash;
  }

  if (otherParams.futuresAssetName) {
    reqParams.futures_asset_name = otherParams.futuresAssetName;
  }

  if (otherParams.userAddress) {
    reqParams.user_address = otherParams.userAddress;
  }

  if (otherParams.isNotUnconfirmed !== undefined) {
    reqParams.is_not_unconfirmed = otherParams.isNotUnconfirmed;
  }

  const resp = await backend.http.get('/api/futures/trades', { params: reqParams });

  return resp.data;
};

export const getOneTrade = async (tradeId) => {
  const resp = await backend.http.get(`/api/futures/trades/${tradeId}`);

  return resp.data;
};


export const onTradeChange = (callback) => {
  backend.socket.on('futures_trades', callback);
};

export const removeTradeChangeHandler = (callback) => {
  backend.socket.removeListener('futures_trades', callback);
};



