<template>
  <transition-modal>
    <e-modal
      wide-design
      @close="onClose"
    >
      <template slot="inner">
        <div class="the-edit-margin-popup" :class="[ `the-edit-margin-popup__${theme}` ]">
          <div class="the-edit-margin-popup__head">
            Add Margin
          </div>

          <div class="the-edit-margin-popup__content">
            <form class="the-edit-margin-popup__body" @submit="submit">
              <div class="the-edit-margin-popup__current-margin-list">
                <div class="the-edit-margin-popup__current-margin-item">
                  <div class="the-edit-margin-popup__current-margin-item-label">Your Current Position:</div>
                  <div class="the-edit-margin-popup__current-margin-item-value">
                    <e-big-number :value="position.size" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="4"/> {{position.symbol}}
                    <span class="green">({{Number(position.leverage).toFixed()}}x)</span></div>
                </div>
                <div class="the-edit-margin-popup__current-margin-item">
                  <div class="the-edit-margin-popup__current-margin-item-label">Current Assigned Margin:</div>
                  <div class="the-edit-margin-popup__current-margin-item-value">
                    <e-big-number :value="position.collateral " :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="4"/>
                    {{position.baseToken.symbol}}
                  </div>
                </div>
                <div class="the-edit-margin-popup__current-margin-item">
                  <div class="the-edit-margin-popup__current-margin-item-label"><span class="green">Available Margin:</span></div>
                  <div class="the-edit-margin-popup__current-margin-item-value">
                    <span class="green">
                      <e-big-number
                        :value="balance.availableBalance"
                        :decimal-places="decimalBalance"
                      />
                      <span class="the-edit-margin-popup__balance-token">{{isETH ? 'ETH' : 'BTC' }}</span>
                    </span>
                  </div>
                </div>
              </div>

              <label class="the-edit-margin-popup__form-label" :class="{ 'error': $v.margin.$invalid && margin, 'success': !$v.margin.$invalid }">
                <div class="the-edit-margin-popup__form-label-text">Add margin</div>
                <input type="number" class="the-edit-margin-popup__form-input" v-model="margin" :vuelidate-property="$v.margin" autocomplete="off" />

                <span class="the-edit-margin-popup__form-input-symbol">{{position.baseToken.symbol}}</span>

                <div class="the-edit-margin-popup__form-validation" v-if="marginError">{{marginError}}</div>
              </label>

              <div class="the-edit-margin-popup__footer-sbmt">
                <div class="the-edit-margin-popup__footer-sbmt-item">
                  <button
                    type="button"
                    class="e-receipt__btn e-receipt__btn_active the-edit-margin-popup__cancel-button"
                    @click="onClose"
                  >
                    Close
                  </button>
                </div>
                <div class="the-edit-margin-popup__footer-sbmt-item">
                  <button
                    :disabled="!margin"
                    type="submit"
                    class="e-receipt__btn e-receipt__btn_active the-edit-margin-popup__action-button"
                    @click="submit"
                  >
                    <span v-if="!isLoading">Add Margin</span>
                    <e-icon v-else :src="iconDottedAnimation" class="the-edit-margin-popup__action-button-loading" />
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
/**
 * Component: Всплывающее окно перевода средств (депозит, видровл, трансфер)
 *
 * @version 1.0.2
 */

import { mapGetters } from 'vuex';
import { required } from 'vuelidate/lib/validators';
import { validationMixin } from 'vuelidate';
import { FUTURES_AMOUNT_DECIMALS, ETH_BASE_ADDRESS } from '@/util/constants';
import { BigNumber } from 'bignumber.js';
import get from 'lodash/get';

import { updateMargin } from '@/api/backend/positions';

import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EBigNumber from '@/components/EBigNumber';
import TransitionModal from '@/containers/transitions/TransitionModal';

import iconDottedAnimation from '@/icons/e-icon_dotted-animation.svg';

export default {
  name: 'the-edit-margin-popup',

  data() {
    return {
      margin: '1',
      marginError: '',
      isLoading: false,
      iconDottedAnimation,
    };
  },

  validations: {
    margin: { required },
  },

  computed: {
    ...mapGetters({
      baseToken: 'balance/baseToken',
      position: 'futuresPositions/marginPosition',
      theme: 'ui/getTheme',
    }),

    balance() {
      return this.$store.getters['balance/getUserBalanceByToken'](this.baseToken);
    },

    isETH() {
      return this.baseToken === ETH_BASE_ADDRESS;
    },

    decimalBalance() {
      return this.isETH ? 4 : 2;
    },
  },

  // mounted() {
  //   this.margin = this.position.collateral.shiftedBy(-FUTURES_AMOUNT_DECIMALS).toFormat(4);
  // },

  methods: {
    onClose() {
      this.$store.commit('futuresPositions/SET_MARGIN_POSITION', null);
    },

    async submit(e) {
      e.preventDefault();

      if (this.margin && !this.isLoading) {
        this.marginError = '';

        this.isLoading = true;
        try {
          const signService = this.$store.getters['blockchain/signService'];
          const marginPrice = BigNumber(this.margin).shiftedBy(FUTURES_AMOUNT_DECIMALS).toString();
          const signature = await signService.createUpdateMarginHash({ user: this.position.userAddress, futuresContractHash: this.position.futuresContractHash, side: this.position.side, newMargin: (+marginPrice + +this.position.collateral).toString() });

          await updateMargin({
            futuresContractHash: this.position.futuresContractHash,
            userAddress: this.position.userAddress,
            side: this.position.side,
            newMargin: +marginPrice + +this.position.collateral,
            ...signature,
          });

          this.$store.commit('ui/ADD_NOTIFICATION', {
            status: 'success',
            headline: 'Margin updated',
            message: ['Update request has been registered'].join(' '),
          }, { root: true });

          this.onClose();
        } catch (err) {
          this.marginError = get(err, 'response.data.errors[0].message', 'Error');
        }

        this.isLoading = false;
      } else {
        this.marginError = 'Required';
      }
    },
  },

  components: {
    EIcon,
    EModal,
    EBigNumber,
    TransitionModal,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-edit-margin-popup {
  $this: &;
  background-color: #f4f5f9;

  &__main {
    position: relative;
  }

  &__main-tab {
    position: relative;
    min-height: 410px;
    padding-bottom: 70px;
  }

  &__head {
    position: relative;
    background-color: #fff;
    box-shadow: 0 0 3px rgba(black, .08);
    height: 50px;
    border-radius: 3px 3px 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    font-weight: 600;
    font-size: 16px;
    color: #000;
    text-align: center;
  }

  &__body {
    padding: 54px 10px;
    width: 100%;
    max-width: 420px;
    margin: 0 auto;
    box-sizing: border-box;
    min-height: 300px;
  }

  &__footer {
    overflow: hidden;
    border-radius: 0 0 3px 3px;
  }

  &__cancel-button,
  &__action-button {
    font-size: 12px;
    letter-spacing: 1.09px;
    text-align: center;
    line-height: 14px;
    align-items: center;
    background-color: $color-grey-light;
    color: $color-blue;
    display: flex;
    flex-direction: row;
    font-weight: 600;
    padding: 17px;
    text-transform: uppercase;
    transition: background-color ease $transition-time, color ease $transition-time;
    text-decoration: none;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
    height: 50px;
  }

  &__action-button {
    color: #232323;
    background: $color-green-dark;

    &:disabled {
      color: #51545D;
      background: $color-grey-second;
    }
  }

  &__action-button-loading {
    width: 50px;
    opacity: .4;

    svg > g > g {
      fill: #fff;
    }
  }

  &__cancel-button {
    &:disabled {
      color: $color-grey;
    }
  }

  &__footer-sbmt {
    display: flex;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
  }

  &__footer-sbmt-item {
    flex-grow: 1;
    flex-basis: 0;
  }

  &__current-margin-list {
    width: 100%;
  }
  &__current-margin-item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 5px;
  }
  &__current-margin-item-label {
    font-size: 13px;
    line-height: 19px;
    color: $color-dark-blue;
  }

  &__current-margin-item-value {
    font-weight: 500;
    font-size: 13px;
    text-align: right;
    line-height: 19px;
    color: #000;

    .e-big-number {
      color: #000;
    }
  }

  .green {
    color: $color-green-dark;

    .e-big-number {
      color: $color-green-dark;
    }
  }

  &__form-label {
    position: relative;
    display: block;
    height: 100px;
    margin-top: 30px;
    margin-bottom: 30px;

    &.error {
      #{$this}__form-input {
        border: 1px solid #DC4727;
      }

      .e-icon svg path,
      .e-icon svg rect {
        fill: #DC4727;
        opacity: 1;
      }
    }

    &.success {
      .e-icon svg path,
      .e-icon svg rect {
        fill: #00DB9C;
        opacity: 1;
      }
    }
  }

  &__form-label-text {
    font-weight: 500;
    font-size: 12px;
    color: $color-light-grey;
    line-height: 19px;
    text-align: left;
    margin-bottom: 5px;
  }

  &__form-input-symbol {
    position: absolute;
    font-weight: 500;
    right: 23px;
    width: 23px;
    top: 38px;
  }

  &__form-input {
    background: #FFFFFF;
    border: 1px solid $color-border-light;
    width: 100%;
    text-align: right;
    height: 50px;
    border-radius: 3px;
    padding: 0 50px 0 18px;
    box-sizing: border-box;
    font-size: 16px;
    color: #112271;
    letter-spacing: 0;
    line-height: 24px;
    -webkit-appearance: none;
    transition: all .15s ease-in-out;

    &:focus {
      border-color: rgba(17,34,113, .2) !important;
    }
  }

  &__form-input::-webkit-outer-spin-button,
  &__form-input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &__form-input {
    -moz-appearance: textfield;
  }

  &__form-validation {
    display: flex;
    align-items: center;
    color: #DC4727;
    font-size: 12px;
    font-weight: 500;
    margin-top: 4px;
    margin-bottom: 8px;

    &.verificationCode {
      justify-content: center;
      margin-top: 0;
    }

    &.form {
      margin-top: 14px;
      margin-bottom: 3px;
    }

    .e-icon {
      width: 14px;
      height: 14px;
      margin-right: 5px;
    }
  }

  &__dark {
    background: $dark-background;

    #{$this}__head {
      background: $dark-background-lighter;
      color: $color-green-dark;
    }

    #{$this}__body {
      color: #fff;
    }

    #{$this}__cancel-button {
      background: $dark-background-lighter;
      color: #D9D9D9;

      &:hover {
        color: #fff;
      }

      &:disabled {
        color: #696b6f;
      }
    }

    #{$this}__action-button {
      background: $color-green-dark;
      color: #232323;

      &:disabled {
        color: #51545D;
        background: #2F333C;
      }
    }

    #{$this}__current-margin-item-label {
      color: #D9D9D9;
    }
    #{$this}__current-margin-item-value {
      color: #fff;

      .e-big-number {
        color: #fff;
      }
    }

    .green {
      color: $color-green-dark;

      .e-big-number {
        color: $color-green-dark;
      }
    }

    #{$this}__form-input {
      background-color: transparent;
      color: #fff;
      border-color: $color-border-dark;

      &:focus {
        border-color: #323746 !important;
      }

      /*&:-webkit-autofill {
        -webkit-text-fill-color: #D9D9D9 !important;
        -webkit-box-shadow: 0 0 0 1000px $dark-background inset;
      }*/
    }

    #{$this}__form-label-text {
      color: #8D9098;
    }
  }
}
</style>



