import cachedActionTypes from './cachedActions';

const cachedActions = {};
cachedActionTypes.forEach((t) => {
  cachedActions[t] = null;
});

export const wrapCachedAction = (key, action) => (
  (context, payload) => {
    if (cachedActions[key] === null) {
      cachedActions[key] = Promise.resolve(action(context, payload));
    }
    return cachedActions[key];
  }
);

export const resetCachedAction = (type) => {
  cachedActions[type] = null;
};


