<template>
  <transition
    name="transition-page"
  >
    <slot></slot>
  </transition>
</template>

<script>
export default {
  name: 'transition-page',
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.transition-page-enter-active, .transition-page-leave-active {
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  transition: 0.5s;
  transition-property: transform, opacity;
  will-change: transform, opacity;
}

.transition-page-enter, .transition-page-leave-to {
  opacity: 0;
  transform: translateY(100px);
}
</style>



