<template>
  <div class="e-switch" :class="[ `e-switch__${theme}` ]">
    <label :class="['e-switch__label', size && `e-switch__label-size-${size}`, color && `e-switch__label-color-${color}`]">
      <input
        class="e-switch__checkbox"
        type="checkbox"
        v-model="actualValue"
      >
      <span
        class="e-switch__switcher"
        tabindex="0"
      />
      <span class="e-switch__text">
        <slot></slot>
      </span>
    </label>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'e-switch',

  props: {
    value: {
      type: Boolean,
      default: false,
    },

    size: {
      type: String,
      default: null,
    },

    color: {
      type: String,
      default: null,
    },
  },

  data: () => ({
    actualValue: this.value,
  }),

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    actualValue(val) {
      this.$emit('input', val);
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-switch {
  $this: &;

  &__label {
    position: relative;
    padding-left: 44px;
    cursor: pointer;
  }

  &__label-size-small {
    padding-left: 34px;

    #{$this}__switcher {
      width: 27px;
      height: 15px;
      border-width: 1px;

      &::before {
        top: 1px;
        left: 1px;
        width: 11px;
        height: 11px;
      }
    }

    #{$this}__checkbox:checked ~ #{$this}__switcher {
      &::before {
        left: calc(100% - 12px);
      }
    }

    #{$this}__text {
      font-size: 11px;
      text-transform: uppercase;
    }
  }

  &__label-color-blue {
    #{$this}__switcher {
     border-color: rgba(131, 132, 171, .3);

      &::before {
        background: #8384AB;
      }
    }

    #{$this}__text {
      color: #8384AB;
      opacity: 1;
    }

    #{$this}__checkbox:checked ~ #{$this}__switcher {
      border-color: rgba(9, 22, 138, .3);

      &::before {
        background: #09168A;
      }
    }

    #{$this}__checkbox:checked ~ #{$this}__text {
      color: #09168A;
    }

    &:hover {
      #{$this}__switcher,
      #{$this}__checkbox:checked ~ #{$this}__switcher {
        border-color: rgba(9, 22, 138, .3);

        &::before {
          background: #0123E7;
        }
      }

      #{$this}__text,
      #{$this}__checkbox:checked ~ #{$this}__text {
        color: #0123E7;
        opacity: 1;
      }
    }
  }

  &__checkbox {
    position: absolute;
    visibility: hidden;
    top: 0;
    left: 0;

    &:checked {
      & ~ .e-switch__switcher {
         border-color: #17DA9D;

         &::before {
           background-color: #17DA9D;
           left: calc(100% - 18px)
         }
      }

      & ~ .e-switch__text {
        opacity: 1;
      }
    }
  }

  &__switcher {
    outline: none;
    position: absolute;
    box-sizing: border-box;
    width: 35px;
    height: 20px;
    top: 50%;
    left: 0;
    background-color: #FFFFFF;
    border: 2px solid #DA4830;
    box-shadow: 0 7px 10px 0 rgba(0,0,0,0.03);
    border-radius: 17.5px;
    transition: all 0.15s ease-in-out;
    transform: translateY(-50%);

    &::before {
      content: '';
      position: absolute;
      top: -2px;
      left: -2px;
      width: 20px;
      height: 20px;
      background-color: #DA4830;
      border-radius: 50%;
      transition: background-color .15s ease-in-out, left .2s linear, right .2s linear;
    }
  }

  &__text {
    color: #0d105b;
    font-size: 14px;
    font-weight: 400;
    opacity: 0.4;
    position: relative;
    top: -2px;
    transition: color 0.15s ease-in-out;

    a {
      color: #0123e7;
      text-decoration: none;
    }
  }

  &__dark {
    #{$this}__text {
      color: $color-dark-grey;
    }

    #{$this}__switcher {
      border-color: #343536;
      background-color: transparent;

      &::before {
        background: #8D9098;
      }
    }

    #{$this}__label-color-blue:hover #{$this}__switcher, #{$this}__label-color-blue:hover #{$this}__checkbox:checked ~ #{$this}__switcher {
      border-color: #343536;
      background-color: transparent;

      &::before {
        background: #9A9EA7;
      }
    }

    .e-switch__label-color-blue:hover .e-switch__text, .e-switch__label-color-blue:hover .e-switch__checkbox:checked ~ .e-switch__text {
      color: #9A9EA7;
    }

    .e-switch__label-color-blue .e-switch__checkbox:checked ~ .e-switch__switcher {
      border-color: $color-blue-dark-mode !important;

      &::before {
        background: $color-blue-dark-mode !important;
      }
    }

    .e-switch__label-color-blue .e-switch__checkbox:checked ~ .e-switch__text {
      color: $color-blue-dark-mode !important;
    }
  }
}
</style>



