<template>
  <span class="the-svg-store">
    <span
      v-for="(src, key) of icons"
      :key="key"
      ref="icons"
      v-html="src"
    />
  </span>
</template>

<script>
/* eslint-disable no-restricted-syntax */

import arrowDown from '@/icons/e-icon_arrow_down.svg';
import arrowUp from '@/icons/e-icon_arrow_up.svg';
import coin1 from '@/icons/e-icon_coin-one.svg';
import coin2 from '@/icons/e-icon_coin-two.svg';
import coin3 from '@/icons/e-icon_coin-three.svg';
import earned from '@/icons/e-icon_earned.svg';
// import gradients from '@/icons/e-icon_gradients.svg';
import loss from '@/icons/e-icon_loss.svg';
import profit from '@/icons/e-icon_profit.svg';
import searchWide from '@/icons/e-icon_search_wide.svg';
import star from '@/icons/e-icon_star.svg';
import starEmpty from '@/icons/e-icon_star_empty.svg';
import websiteLink from '@/icons/e-icon_link-site.svg';
import world from '@/icons/e-icon_world.svg';
import preloader from '@/icons/e-icon_preloader.svg';

const icons = Object.freeze({
  arrowDown,
  arrowUp,
  coin1,
  coin2,
  coin3,
  earned,
  // gradients,
  loss,
  preloader,
  profit,
  searchWide,
  star,
  starEmpty,
  websiteLink,
  world,
});

export default {
  name: 'the-svg-store',

  data() {
    return { icons };
  },

  async mounted() {
    await this.$nextTick();
    let i = 0;
    for (const key of Object.keys(icons)) {
      this.$refs.icons[i].firstChild.id = `e-icon__${key}`;
      i += 1;
    }
  },
};
</script>

<style lang="scss">
.the-svg-store {
  display: none;
}
</style>



