import backend from './endpoint';

// eslint-disable-next-line import/prefer-default-export
export const updateMargin = async ({ futuresContractHash, userAddress, side, newMargin, s, r, v }) =>
  backend.http.post('/api/futures/positions-v2/update-margin', {
    futures_contract_hash: futuresContractHash,
    user_address: userAddress,
    side,
    new_margin: newMargin.toString(),
    s,
    r,
    v,
  });



