import * as types from '@/store/mutation-types';

export default {
  [types.CHECK_ETH_NETWORK.PENDING](state) {
    state.correctNetworkSelected = false;
    state.networkChecked = false;
  },

  [types.CHECK_ETH_NETWORK.SUCCESS](state, correctNetworkSelected) {
    state.correctNetworkSelected = correctNetworkSelected;
    state.networkChecked = true;
  },

  [types.CHECK_ETH_NETWORK.FAILURE](state) {
    state.correctNetworkSelected = false;
    state.networkChecked = true;
  },

  [types.GAS_PRICE_UPDATE](state, value) {
    state.gasPrices = value;
  },

  [types.SET_BACKEND_GAS_LIMITS](state, { withdrawal, tradeTaker }) {
    state.withdrawalGasLimit = withdrawal;
    state.tradeTakerGasLimit = tradeTaker;
  },


  [types.GAS_PRICE_ADD_LISTENER]: (state) => {
    state.gasPriceListenersCount += 1;
  },

  [types.GAS_PRICE_REMOVE_LISTENER]: (state) => {
    state.gasPriceListenersCount -= 1;
  },

};



