/**
 * Opens Balance Transfer Popup with provided options:
 * 1) operation: deposit/withdraw/transfer - can be changed later
 * 2) tokenContract - can't be changed later
 */
const startTokenTransfer = ({ commit }, { operation, tokenContract }) => {
  commit(types.START_TOKEN_TRANSFER, { operation, tokenContract });
};

const startDeposit = ({ dispatch }, tokenContract) => {
  dispatch('startTokenTransfer', {
    operation: DEPOSIT_OPERATION,
    tokenContract,
  });
};

const startWithdrawal = ({ dispatch }, tokenContract) => {
  dispatch('startTokenTransfer', {
    operation: WITHDRAWAL_OPERATION,
    tokenContract,
  });
};

const startTransfer = ({ dispatch }, tokenContract) => {
  dispatch('startTokenTransfer', {
    operation: TRANSFER_OPERATION,
    tokenContract,
  });
};

/**
 * Action: Loads all deposit/withdraw operations that are in pending state (both not mined & mined but not confirmed).
 * deposit/withdraw that are confirmed or rejected are not loaded.
 * transfer is not loaded at the moment.
 */
const loadPendingBalanceOperations = async ({ rootGetters, commit, dispatch }) => {
  await Promise.all([
    dispatch('contracts/subscribeToContract', undefined, { root: true }), // ensures active contract loaded
    dispatch('wallets/subscribeToAccount', undefined, { root: true }),
    dispatch('updateAppConfig', undefined, { root: true }), // for confirmation numbers
  ]);
  const userAddress = rootGetters['wallets/activeWalletAddress'];
  const contractAddress = rootGetters['contracts/activeMainContractAddress'];

  if (!contractAddress) {
    log.error('contractAddress must not be null', {
      module: 'deposit',
      layer: 'store',
    });
    return;
  }

  if (!rootGetters['wallets/isLogin'] || rootGetters['wallets/isActiveWalletNew']) {
    return;
  }

  let operations;
  try {
    ({ data: operations } = await getPendingBalanceOperations({ userAddress, contractAddress }));
  } catch (error) {
    if (rootGetters.isConnected) {
      log.error(`Error loading deposit/withdraw: ${error}`, {
        module: 'deposit',
        layer: 'backend',
      });
    }
  }
  if (operations) {
    operations
      .reverse()
      .filter(o => o.status === PENDING_STATUS)
      .forEach(o => commit(types.ADD_PENDING_OPERATION, o));
    // commit(types.UPDATE_OPERATIONS, {
    //   userAddress,
    //   operations: history.filter(o => o.status !== PENDING_STATUS),
    // });
  }
};

/**
 * Action: Synchronous
 * Listen to balance operations (funds), i.e. incoming balance operation (funds) events are not ignored after this action.
 * Listener reacts on reconnects and idle events and handle them properly.
 * After getting notification updates balance for affected coins/tokens and displays notifications.
 *
 * @todo unsubscribe - add stopListen action if needed
 * @todo ensure wallets and contract loaded
 */
const listenToBalanceOperations = wrapCachedAction('listenToBalanceOperations', ({ commit, getters, rootGetters, dispatch }) => {
  dispatch('loadPendingBalanceOperations');
  removeAfterWalletListener('operations');
  afterWalletChange('operations', () => {
    dispatch('loadPendingBalanceOperations');
  });
  onFunds(async (operation) => {
    if (
      operation.contractAddress === rootGetters['contracts/activeMainContractAddress'] &&
      operation.userAddress === rootGetters['wallets/activeWalletAddress']
    ) {
      const prevOperationState = getters.pendingOperations.find(o => o.transactionHash === operation.transactionHash);
      const isStatusChanged = prevOperationState == null || (prevOperationState.status !== operation.status);

      if (isStatusChanged) {
        const { tokenAddress, blockNumber } = operation;
        // Balance update
        if ([APPROVED_STATUS, DECLINED_STATUS].includes(operation.status)) {
          const balanceApi = rootGetters['blockchain/balanceApi'];
          try {
            const balance = await balanceApi.getBalanceAfterOperation({ blockNumber, tokenAddress });
            commit('balance/UPDATE_BALANCE', balance, { root: true });
          } catch (error) {
            log.warn(`Error updating balance after ${operation.type}: ${error}`, {
              module: 'deposit',
              layer: 'blockchain',
            });
          }
        }

        // Notify about operation
        if (operation.status === APPROVED_STATUS) {
          commit('ui/ADD_NOTIFICATION', {
            type: notificationTypes.NotificationBalanceSuccess,
            operation: operation.type,
            value: operation.value,
            token: rootGetters['tokens/tokenByContract'](tokenAddress),
          }, { root: true });
        } else if (operation.status === DECLINED_STATUS) {
          commit('ui/ADD_NOTIFICATION', {
            type: notificationTypes.NotificationBalanceError,
            operation: operation.type,
            value: operation.value,
            token: rootGetters['tokens/tokenByContract'](tokenAddress),
          }, { root: true });
        }
      }

      commit(types.UPSERT_OPERATION, operation);
    }
  });
  const reconnectHandler = () => {
    dispatch('loadPendingBalanceOperations');
  };
  onReconnect(reconnectHandler);
  onResume(reconnectHandler);
});

const stopListeningToBalanceOperations = () => {
  // TODO
};

/**
 * Action helper: Submit some transaction with Metamask approve.
 *
 * @param func function to be executed with metamask approval. Function must return Promise
 */
const submitWithMetamaskApprove = async ({ commit }, func) => {
  commit('ui/SHOW_METAMASK_SIGN', undefined, { root: true });
  let result;
  try {
    result = await func();
  } finally {
    commit('ui/HIDE_METAMASK_SIGN', undefined, { root: true });
  }
  commit(types.SUBMIT_TOKEN_TRANSFER);
  return result;
};

/**
 * Action helper: Submit some transaction with Ledger Nano S approve.
 *
 * @param func function to be executed with metamask approval. Function must return Promise
 */
const submitWithLedgerApprove = async ({ commit }, func) => {
  commit('ui/SHOW_LEDGER_SIGN', undefined, { root: true });
  let result;
  try {
    result = await func();
  } finally {
    commit('ui/HIDE_LEDGER_SIGN', undefined, { root: true });
  }
  commit(types.SUBMIT_TOKEN_TRANSFER);
  return result;
};

/**
 * Action helper: Submits deposit ETH operation from private key wallet with specified params.
 * Shows ETH too low popup if private wallet ETH amount is not enough to pay gas fee.
 * Handling of deposit ETH events doesn't happen in this action.
 *
 * @param value - amount of ETH to be deposit
 * @param gasPrice - selected gas price
 */
const submitDepositEthFromPrivateKey = async ({ commit, rootGetters }, { value, gasPrice }) => {
  const api = rootGetters['blockchain/transactionsApi'];
  const userAddress = rootGetters['wallets/activeWalletAddress'];
  const referrerAddress = rootGetters['affiliate/referrerAddress'];
  // balance check
  const fee = BigNumber(GAS_LIMIT_DEPOSIT_ETH_REF).times(gasPrice);
  const ethToBeConsumed = fee.plus(value);
  if (rootGetters['balance/privateEthAvailable'].lt(ethToBeConsumed)) {
    commit(
      'ui/SHOW_BALANCE_ETH_LOW_POPUP',
      {
        header: 'Deposit Error',
        title: 'Insufficient funds',
        message: 'Your balance is not sufficient to cover the deposit amount and the transaction fee. Please reduce the deposit amount and try again.',
      },
      { root: true },
    );
    return;
  }

  const key = generateId();
  commit(types.SUBMIT_TOKEN_TRANSFER);
  commit(types.ADD_HASHLESS_OPERATION, { userAddress, key, type: DEPOSIT_OPERATION, tokenAddress: ETH_ADDRESS, value, fee });
  const { depositPending } = api.depositEth({ value, referrerAddress, gasPrice });
  let transactionHash;
  try {
    transactionHash = await depositPending;
  } catch (e) {
    commit(types.REMOVE_HASHLESS_OPERATION, { userAddress, key });
    throw e;
  }
  commit(types.SET_OPERATION_HASH, { userAddress, key, transactionHash });
  // try {
  //   const { blockNumber } = await depositConfirmed;
  //   commit(types.UPSERT_OPERATION, { userAddress, transactionHash, blockNumber, status: PENDING_STATUS });
  // } catch (e) {
  //   commit(types.UPSERT_OPERATION, { userAddress, transactionHash, status: DECLINED_STATUS });
  // }
};


/**
 * Action helper: Submits deposit token operation from private key wallet with specified params.
 * Shows ETH too low popup if private wallet ETH amount is not enough to pay gas fee.
 * Handling of deposit token events doesn't happen in this action.
 *
 * @param tokenAddress - address of token
 * @param value - amount of token to be deposit
 * @param gasPrice - selected gas price
 */
const submitDepositTokenFromPrivateKey = async ({ commit, rootGetters }, { tokenAddress, value, gasPrice }) => {
  const api = rootGetters['blockchain/transactionsApi'];
  const userAddress = rootGetters['wallets/activeWalletAddress'];
  const referrerAddress = rootGetters['affiliate/referrerAddress'];

  // balance check
  const fee = BigNumber(GAS_LIMIT_DEPOSIT_TOKEN + GAS_LIMIT_TOKEN_APPROVE).times(gasPrice);
  const ethPrivateBalance = rootGetters['balance/privateEthAvailable'];
  const tokenPrivateBalance = rootGetters['balance/getPrivateBalanceFor'](tokenAddress);
  const valueBN = BigNumber(value);
  // Проверка наличия средств (токена) для депозита
  if (tokenPrivateBalance.lt(valueBN)) {
    commit(
      'ui/SHOW_BALANCE_ETH_LOW_POPUP',
      {
        header: 'Deposit Error',
        title: 'Insufficient funds',
        message: 'Your balance is not sufficient to cover the deposit amount and the transaction fee. Please reduce the deposit amount and try again.',
      },
      { root: true },
    );
    return;
  }

  // Проверка наличия средств для выплаты Gas Fee
  if (ethPrivateBalance.lt(fee)) {
    commit(
      'ui/SHOW_BALANCE_ETH_LOW_POPUP',
      {
        header: 'Deposit Error',
        title: 'Insufficient funds',
        message: 'Your balance is not sufficient to cover the deposit amount and the transaction fee. Please reduce the deposit amount and try again.',
      },
      { root: true },
    );
    return;
  }

  const key = generateId();
  commit(types.SUBMIT_TOKEN_TRANSFER);
  commit(types.ADD_HASHLESS_OPERATION, { userAddress, key, type: DEPOSIT_OPERATION, tokenAddress, value, fee });
  const { approvePending, depositPending } = api.depositToken({ tokenAddress, value, referrerAddress, gasPrice });

  let approveHash;
  try {
    approveHash = await approvePending;
  } catch (e) {
    commit(types.REMOVE_HASHLESS_OPERATION, { userAddress, key });
    throw e;
  }
  if (approveHash) {
    commit(types.UPDATE_HASHLESS_OPERATION, { userAddress, key, approveHash });
  }

  let transactionHash;
  try {
    transactionHash = await depositPending;
  } catch (e) {
    commit(types.REMOVE_HASHLESS_OPERATION, { userAddress, key });
    throw e;
  }
  commit(types.SET_OPERATION_HASH, { userAddress, key, transactionHash });
  // try {
  //   const { blockNumber } = await depositConfirmed;
  //   commit(types.UPSERT_OPERATION, { userAddress, transactionHash, blockNumber, status: PENDING_STATUS });
  // } catch (e) {
  //   commit(types.UPSERT_OPERATION, { userAddress, transactionHash, status: DECLINED_STATUS });
  // }
};

