/**
 * Action helper: Submits deposit operation with specified params.
 * Handling of withdrawal events doesn't happen in this action.
 *
 * @param {{ value, gasPrice, gas }} args deposit params: (value - amount of ETH/token to be deposit, gasPrice - selected gas price, gas - esmitated gas limit).
 */
const submitDeposit = (context, args) => {
  const { rootGetters } = context;
  const isEth = args.tokenAddress === ETH_ADDRESS;
  const privateKeyWallet = rootGetters['wallets/activeWalletType'] === PRIVATE_KEY_TYPE;
  const metamaskWallet = rootGetters['wallets/activeWalletType'] === METAMASK_WALLET_TYPE;
  const ledgerWallet = rootGetters['wallets/activeWalletType'] === LEDGER_WALLET_TYPE;
  let deposit;
  if (privateKeyWallet && isEth) {
    deposit = submitDepositEthFromPrivateKey;
  } else if (privateKeyWallet && !isEth) {
    deposit = submitDepositTokenFromPrivateKey;
  } else if (metamaskWallet && isEth) {
    deposit = submitDepositEthFromMetamask;
  } else if (metamaskWallet && !isEth) {
    deposit = submitDepositTokenFromMetamask;
  } else if (ledgerWallet && isEth) {
    deposit = submitDepositEthFromLedger;
  } else if (ledgerWallet && !isEth) {
    deposit = submitDepositTokenFromLedger;
  }

  return deposit(context, args);
};

/**
 * Action helper: Submits withdrawal operation with specified params.
 * Handling of withdrawal events doesn't happen in this action.
 *
 * @param tokenAddress contract address of token to be transferred
 * @param value amount of token to be transferred (in base units, ignoring decimals)
 * @param gasPrice selected gas price
 */
const submitWithdrawal = async ({ commit, rootGetters }, { tokenAddress, value, gasPrice, ethUsdPrice, destinationAddress }) => {
  const { address: userAddress, type: userType } = rootGetters['wallets/activeWallet'];
  const contractAddress = rootGetters['contracts/activeMainContractAddress'];
  const withdrawalGasLimit = rootGetters['blockchain/withdrawalGasLimit'];
  const baseToken = rootGetters['balance/baseToken'];
  const signService = rootGetters['blockchain/signService'];
  const operationType = WITHDRAWAL_OPERATION;

  let withdrawFee = baseToken === ETH_BASE_ADDRESS
    ? (gasPrice).times(withdrawalGasLimit).toFixed()
    : (gasPrice).times(withdrawalGasLimit).times(ethUsdPrice).toFixed();

  if (destinationAddress) {
    withdrawFee = BigNumber('0.001').shiftedBy(18).toFixed();
  }

  if (NETWORK_TYPE === 'ropsten') {
    withdrawFee = '1';
  }

  // balance check
  const gasLimit = rootGetters['blockchain/withdrawalGasLimit'];
  const ethContractBalance = rootGetters['balance/contractEthNotInOrders'];
  const fee = BigNumber(gasPrice).times(gasLimit);
  const valueBN = BigNumber(value);

  if (tokenAddress === ETH_ADDRESS) {
    // Случай когда делаеться withdraw эфира - Проверка наличия средств (ETH) для депозита
    const ethToBeConsumed = fee.plus(valueBN);
    if (ethContractBalance.lt(ethToBeConsumed)) {
      commit(
        'ui/SHOW_BALANCE_ETH_LOW_POPUP',
        {
          header: 'Withdrawal Error',
          title: 'Insufficient funds',
          message: 'Your balance is not sufficient to cover the withdrawal amount and the transaction fee. Please reduce the withdrawal amount and try again.',
        },
        { root: true },
      );
      return;
    }
  } else {
    // Случай когда делается withdraw токена

    // Проверка наличия средств (токена) для withdraw
    const tokenContractBalance = rootGetters['balance/getContractBalanceFor'](tokenAddress);
    if (tokenContractBalance.lt(valueBN)) {
      commit(
        'ui/SHOW_BALANCE_ETH_LOW_POPUP',
        {
          header: 'Withdrawal Error',
          title: 'Insufficient funds',
          message: 'Your balance is not sufficient to cover the withdrawal amount and the transaction fee. Please reduce the withdrawal amount and try again.',
        },
        { root: true },
      );
      return;
    }

    // // Проверка наличия средств не в ордерах (токена) для withdraw
    // const tokenContractBalanceNotInOrders = rootGetters['balance/getContractBalanceNotInOrdersFor'](tokenAddress);
    // if (tokenContractBalanceNotInOrders.lt(valueBN)) {
    //   commit(
    //     'ui/SHOW_BALANCE_ETH_LOW_POPUP',
    //     {
    //       header: 'Withdrawal Error',
    //       title: 'Insufficient funds',
    //       message: 'You cannot withdraw this amount because you have active orders for this token. Please cancel some of your orders and try again.',
    //     },
    //     { root: true },
    //   );
    //   return;
    // }
    //
    // // Проверка наличия средств для выплаты Gas Fee
    // if (ethContractBalance.lt(fee)) {
    //   commit(
    //     'ui/SHOW_BALANCE_ETH_LOW_POPUP',
    //     {
    //       header: 'Withdrawal Error',
    //       title: 'Insufficient funds',
    //       message: 'Your balance is not sufficient to cover the withdrawal amount and the transaction fee. Please reduce the withdrawal amount and try again.',
    //     },
    //     { root: true },
    //   );
    //   return;
    // }
  }

  // signature
  let signature;
  const key = generateId();
  if (userType === PRIVATE_KEY_TYPE) {
    commit(types.SUBMIT_TOKEN_TRANSFER);
    commit(types.ADD_HASHLESS_OPERATION, { userAddress, key, tokenAddress, value, type: operationType, fee });
    try {
      signature = !destinationAddress
        ? await signService.signWithdrawal({ tokenAddress, value, withdrawFee })
        : await signService.signPTokenRedeem({ tokenAddress, value, withdrawFee, destinationAddress });
    } catch (e) {
      commit(types.REMOVE_HASHLESS_OPERATION, { userAddress, key });
      throw e;
    }
  } else if (userType === METAMASK_WALLET_TYPE) {
    signature = await submitWithMetamaskApprove({ commit, rootGetters }, () => {
      if (destinationAddress) {
        return signService.signPTokenRedeem({ tokenAddress, value, withdrawFee, destinationAddress });
      }
      return signService.signWithdrawal({ tokenAddress, value, withdrawFee });
    });
    commit(types.ADD_HASHLESS_OPERATION, { userAddress, key, tokenAddress, value, type: operationType, fee });
  } else if (userType === LEDGER_WALLET_TYPE) {
    signature = await submitWithLedgerApprove({ commit, rootGetters }, () => {
      if (destinationAddress) {
        return signService.signPTokenRedeem({ tokenAddress, value, withdrawFee, destinationAddress });
      }
      return signService.signWithdrawal({ tokenAddress, value, withdrawFee });
    });
    commit(types.ADD_HASHLESS_OPERATION, { userAddress, key, tokenAddress, value, type: operationType, fee });
  }

  // withdraw request
  let transactionHash;
  const withdrawReq = destinationAddress ? withdrawWithAddress : withdraw;

  try {
    transactionHash = await withdrawReq({
      contractAddress,
      userAddress,
      tokenAddress,
      amount: value,
      gasPrice,
      baseToken,
      withdrawalGasLimit,
      ethUsdPrice,
      destinationAddress,
      withdrawFee,
      ...signature,
    });
    commit(types.SET_OPERATION_HASH, { userAddress, key, transactionHash });
  } catch (e) {
    commit(types.REMOVE_HASHLESS_OPERATION, { userAddress, key });
    if (e.message === 'User Token balance is reserved in some orders, cancel orders and try again') {
      commit(
        'ui/SHOW_BALANCE_ETH_LOW_POPUP',
        {
          header: 'Withdrawal Error',
          title: 'Insufficient funds',
          message: 'You cannot withdraw this amount because you have active orders for this token. Please cancel some of your orders and try again.',
        },
        { root: true },
      );
    } else if (e.message === 'User Token balance is not enough') {
      commit(
        'ui/SHOW_BALANCE_ETH_LOW_POPUP',
        {
          header: 'Withdrawal Error',
          title: 'Insufficient funds',
          message: 'Your balance is not sufficient to cover the withdrawal amount and the transaction fee. Please reduce the withdrawal amount and try again.',
        },
        { root: true },
      );
    } else {
      log.warn(`Error: Withdraw. Name: ${e.name}. Type: ${e.type}. Message: ${e.message}`, {
        module: 'withdraw',
        layer: 'backend',
      });
      throw e;
    }
  }
};

const getTransferTransaction = ({ api, tokenAddress, value, toAddress, gasPrice }) => (
  tokenAddress === ETH_ADDRESS
    ? api.transferEth({ value, toAddress, gasPrice })
    : api.transferToken({ tokenAddress, value, toAddress, gasPrice })
);

/**
 * Action helper: Submits transfer operation with specified params.
 * Handling of transfer events happens in this action.
 *
 * @param tokenAddress contract address of token to be transferred
 * @param value amount of token to be transferred (in base units, ignoring decimals)
 * @param toAddress target wallet for token transfer
 * @param gasPrice selected gas price
 */
const submitTransfer = async ({ rootGetters, commit }, { tokenAddress, value, toAddress, gasPrice }) => {
  const { address: userAddress, type: walletType } = rootGetters['wallets/activeWallet'];
  const operationType = TRANSFER_OPERATION;
  const api = rootGetters['blockchain/transactionsApi'];
  const balanceApi = rootGetters['blockchain/balanceApi'];

  // balance check
  const gas = tokenAddress === ETH_ADDRESS ? GAS_LIMIT_TRANSFER_ETH : GAS_LIMIT_TOKEN_TRANSFER;
  const fee = BigNumber(gasPrice).times(gas);
  const privateEth = rootGetters['balance/privateEthAvailable'];
  const privateToken = rootGetters['balance/getPrivateBalanceFor'](tokenAddress);
  const valueBN = BigNumber(value);

  if (tokenAddress === ETH_ADDRESS) {
    // Случай когда выполняется трансфер эфира
    const ethToBeConsumed = fee.plus(value);
    if (rootGetters['balance/privateEthAvailable'].lte(ethToBeConsumed)) {
      commit(
        'ui/SHOW_BALANCE_ETH_LOW_POPUP',
        {
          header: 'Transfer Error',
          title: 'Insufficient funds',
          message: 'Your balance is not sufficient to cover the transfer amount and the transaction fee. Please reduce the transfer amount and try again.',
        },
        { root: true },
      );
      return;
    }
  } else {
    // Случай когда выполняется трансфер токена

    // Проверка наличия средств (токена) для депозита
    if (privateToken.lt(valueBN)) {
      commit(
        'ui/SHOW_BALANCE_ETH_LOW_POPUP',
        {
          header: 'Transfer Error',
          title: 'Insufficient funds',
          message: 'Your balance is not sufficient to cover the transfer amount and the transaction fee. Please reduce the transfer amount and try again.',
        },
        { root: true },
      );
      return;
    }

    // Проверка наличия средств для выплаты Gas Fee
    if (privateEth.lt(fee)) {
      commit(
        'ui/SHOW_BALANCE_ETH_LOW_POPUP',
        {
          header: 'Transfer Error',
          title: 'Insufficient funds',
          message: 'Your balance is not sufficient to cover the transfer amount and the transaction fee. Please reduce the transfer amount and try again.',
        },
        { root: true },
      );
      return;
    }
  }

  // transfer
  const { transferPending, transferMined, transferConfirmed } = getTransferTransaction({ api, tokenAddress, value, toAddress, gasPrice });
  let transactionHash;
  if (walletType === PRIVATE_KEY_TYPE) {
    const key = generateId();
    commit(types.SUBMIT_TOKEN_TRANSFER);
    commit(types.ADD_HASHLESS_OPERATION, { userAddress, key, tokenAddress, value, type: operationType, fee });
    try {
      transactionHash = await transferPending;
    } catch (e) {
      commit(types.REMOVE_HASHLESS_OPERATION, { userAddress, key });
      throw e;
    }
    commit(types.SET_OPERATION_HASH, { userAddress, key, transactionHash });
  } else {
    transactionHash = await submitWithMetamaskApprove({ commit, rootGetters }, () => transferPending);
    commit(types.ADD_PENDING_OPERATION, { userAddress, transactionHash, tokenAddress, value, type: operationType, fee });
  }

  // balance update
  try {
    const { blockNumber } = await transferMined;
    commit(types.UPSERT_OPERATION, { userAddress, transactionHash, blockNumber, status: PENDING_STATUS });
    await transferConfirmed;
    try {
      const balance = await (tokenAddress === ETH_ADDRESS
        ? balanceApi.getPrivateEth({ blockNumber }).then(eth => ({ eth }))
        : balanceApi.getPrivateEthAndToken({ tokenAddress, blockNumber })
      );
      commit('balance/UPDATE_PRIVATE_BALANCE', balance, { root: true });
    } catch (ignored) {
      log.error(`Balance check error after transfer: ${ignored}`);
    }
    commit('ui/ADD_NOTIFICATION', {
      type: notificationTypes.NotificationBalanceSuccess,
      operation: operationType,
      value,
      token: rootGetters['tokens/tokenByContract'](tokenAddress),
    }, { root: true });
  } catch (e) {
    commit('ui/ADD_NOTIFICATION', {
      type: notificationTypes.NotificationBalanceError,
      operation: operationType,
      value,
      token: rootGetters['tokens/tokenByContract'](tokenAddress),
    }, { root: true });
  } finally {
    commit(types.REMOVE_PENDING_OPERATION, { userAddress, transactionHash });
  }
};

/**
 * Mapping of operation types to handler methods.
 */
const SUBMIT_OPERATION_METHOD = {
  [DEPOSIT_OPERATION]: submitDeposit,
  [WITHDRAWAL_OPERATION]: submitWithdrawal,
  [TRANSFER_OPERATION]: submitTransfer,
};

/**
 * Action: Submits any blockchain operation: deposit/withdrawal/transfer and provides a fallback error notification.
 *
 * @since 1.0.0
 */
const submitOperation = async (context, payload) => {
  const { state, commit, rootGetters } = context;
  // сохраняем текущее состояние приложения в переменные
  const { tokenContract, operation: operationType } = state.tokenTransfer;
  const { address: userAddress, type: userType } = rootGetters['wallets/activeWallet'];
  const token = rootGetters['tokens/tokenByContract'](tokenContract);
  const args = mapPayloadToOperationParams(tokenContract, payload);
  const submit = SUBMIT_OPERATION_METHOD[operationType];
  try {
    await submit(context, args);
  } catch (e) {
    const manualCancel = (
      (userType === METAMASK_WALLET_TYPE && ERRORS_METAMASK_DENY.some(m => e.message.includes(m))) ||
      (userType === LEDGER_WALLET_TYPE && e.message === ERROR_LEDGER_DENY)
    );
    if (!manualCancel) {
      commit('ui/ADD_NOTIFICATION', {
        type: notificationTypes.NotificationBalanceError,
        operation: operationType,
        message: e.message,
        token,
      }, { root: true });
      log.error(`${operationType} ${tokenContract} by ${userAddress}(${userType}). Message: ${e.message}`);
    }
  }
};

/**
 * Deposit some ETH to contract balance.
 *
 * @param value amount of token or ETH to deposit
 */
const submitDepositEthWithoutPopup = async (context, { value }) => {
  const { commit, rootGetters } = context;
  // getter нужно вызывать здесь, на случай если аккаунт будет переключён
  const { address: userAddress, type: userType } = rootGetters['wallets/activeWallet'];
  const gas = GAS_LIMIT_DEPOSIT_ETH_REF;
  const gasPrice = rootGetters['blockchain/backendGasPrice'];
  const args = mapPayloadToOperationParams(ETH_ADDRESS, { value, gas, gasPrice });

  try {
    await submitDepositEthFromPrivateKey(context, args);
  } catch (e) {
    const manualCancel = (
      (userType === METAMASK_WALLET_TYPE && ERRORS_METAMASK_DENY.some(m => e.message.includes(m))) ||
      (userType === LEDGER_WALLET_TYPE && e.message === ERROR_LEDGER_DENY)
    );
    if (!manualCancel) {
      commit('ui/ADD_NOTIFICATION', {
        type: notificationTypes.NotificationBalanceError,
        operation: DEPOSIT_OPERATION,
        value,
        token: ETH_TOKEN,
      }, { root: true });
      log.error(`Deposit ETH by ${userAddress}(${userType}). Message: ${e.message}`);
    }
  }
};

/**
 * Deposit all token to contract balance.
 *
 * @param tokenAddress address of token to deposit
 */
const submitDepositAllEthWithoutPopup = async ({ rootGetters, dispatch }) => {
  const gasPrice = rootGetters['blockchain/backendGasPrice'];
  const balance = rootGetters['balance/privateEthAvailable'];
  const gas = GAS_LIMIT_DEPOSIT_ETH_REF;
  try {
    const value = balance.minus(gasPrice.times(gas));
    if (value.gt(0)) {
      await dispatch('submitDepositEthWithoutPopup', { value });
    }
  } catch (e) {
    // ok for now
  }
};

/**
 * Deposit some token to contract balance.
 *
 * @param value amount of token or ETH to deposit
 * @param tokenAddress address of token to deposit
 */
const submitDepositTokenWithoutPopup = async (context, { value, tokenAddress }) => {
  const { commit, rootGetters } = context;
  // сохраняем текущее состояние приложения в переменные
  const { address: userAddress, type: userType } = rootGetters['wallets/activeWallet'];
  const token = rootGetters['tokens/tokenByContract'](tokenAddress);
  const gas = Math.max(GAS_LIMIT_TOKEN_APPROVE, GAS_LIMIT_DEPOSIT_TOKEN);
  const gasPrice = rootGetters['blockchain/backendGasPrice'];
  const args = mapPayloadToOperationParams(tokenAddress, { value, gas, gasPrice });
  // TODO validations - const gasFee = getters.ethDepositGasFee;

  try {
    await submitDepositTokenFromPrivateKey(context, args);
  } catch (e) {
    const manualCancel = (
      (userType === METAMASK_WALLET_TYPE && ERRORS_METAMASK_DENY.some(m => e.message.includes(m))) ||
      (userType === LEDGER_WALLET_TYPE && e.message === ERROR_LEDGER_DENY)
    );
    if (!manualCancel) {
      commit('ui/ADD_NOTIFICATION', {
        type: notificationTypes.NotificationBalanceError,
        operation: DEPOSIT_OPERATION,
        value,
        token,
      }, { root: true });
      log.error(`Deposit ${tokenAddress} by ${userAddress}(${userType}). Message: ${e.message}`);
    }
  }
};

/**
 * Deposit all token to contract balance.
 *
 * @param tokenAddress address of token to deposit
 */
const submitDepositAllTokenWithoutPopup = async ({ rootGetters, dispatch }, { tokenAddress }) => {
  const value = rootGetters['balance/getPrivateBalanceFor'](tokenAddress);
  if (!value.isZero()) {
    try {
      await dispatch('submitDepositTokenWithoutPopup', { tokenAddress, value });
    } catch (e) {
      // ok for now
    }
  }
};

export default {
  startTokenTransfer,
  startDeposit,
  startWithdrawal,
  startTransfer,
  loadPendingBalanceOperations,
  listenToBalanceOperations,
  stopListeningToBalanceOperations,
  submitOperation,
  submitDepositEthWithoutPopup,
  submitDepositAllEthWithoutPopup,
  submitDepositTokenWithoutPopup,
  submitDepositAllTokenWithoutPopup,
};



