<template>
  <div class="e-modal-trades" :class="[ `e-modal-trades__${theme}` ]">
    <div class="e-modal-trades__header">
      <span v-if="!customHeader">{{ headerTitle }}</span>
      <slot
        v-else
        name="header"
      />
    </div>
    <div class="e-modal-trades__body">
      <slot name="body"/>
    </div>
    <div class="e-modal-trades__footer">
      <button
        v-if="btnCancel"
        class="e-modal-trades__btn"
        @click="close"
      >
        {{btnText}}
      </button>
      <slot
        v-else
        name="footer"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'e-modal-trades',

  props: {
    customHeader: {
      type: Boolean,
      default: false,
    },

    headerTitle: {
      type: String,
      default: '',
    },

    btnCancel: {
      type: Boolean,
      default: true,
    },

    btnText: {
      type: String,
      default: 'Cancel',
    },
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  methods: {
    close() {
      this.$emit('close');
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-modal-trades {
  $this: &;

  background-color: $color-light-blue;

  &__header {
    background-color: $color-white;
    box-shadow: 0 0 3px 0 rgba(0,0,0,0.2);
    color: $color-dark-blue;
    font-size: 16px;
    font-weight: 800;
    line-height: 1.375;
    padding: 25px 10px 21px;
  }

  &__body {
    padding: 30px;
  }

  &__btn {
    background-color: $color-grey-light;
    color: $color-blue;
    font-size: 12px;
    font-weight: 700;
    display: block;
    height: 50px;
    letter-spacing: 1.09px;
    text-transform: uppercase;
    width: 100%;
  }

  &__dark {
    background: #121212;

    #{$this}__header {
      background: #232323;
      color: #fff;
    }

    #{$this}__btn {
      background: #2E2E2E;
      color: #8D9098;
    }

    .the-maintenance-popup__title-msg {
      color: #fff;
    }

    .the-maintenance-popup__textual,
    .the-metamask-sign-popup__textual,
    .the-ledger-popup-sign__textual,
    .the-popup-metamask-network__main {
      color: #8D9098;
    }

    .the-ledger-popup-sign__image svg g path {
      fill: #fff;
    }

    .the-min-order-popup__container {
      color: #8D9098;
    }
  }
}
</style>



