<template>
  <div class="the-my-wallet-in-popup" :class="[ `the-my-wallet-in-popup__${theme}` ]">
    <div v-if="isGenerateShow">
      <div class="the-login-flow-popup__body-header">
        <div class="the-login-flow-popup__body-header-icon">
          <e-icon :src="iconLoading" class="the-login-flow-popup__body-header-icon-el" />
        </div>
        <div class="the-login-flow-popup__body-header-title">Generating Wallet</div>
        <div class="the-login-flow-popup__body-header-description">Please wait a moment while your <br/> wallet is generated</div>
      </div>
    </div>

    <div v-else>
      <template v-if="activeWallet.type === PRIVATE_KEY_TYPE">

        <div class="the-login-flow-popup__body-header text-left qr">
          <div class="the-login-flow-popup__body-header-left">
            <div class="the-login-flow-popup__body-header-title">
              Your new Ethereum wallet was generated. <br />
              Save your private key in a safe place.
            </div>
            <div class="the-login-flow-popup__body-header-description">
              The wallet was generated in your browser. <br />
              You are the only one having access to the private key
            </div>
          </div>

          <div class="the-login-flow-popup__qr">
            <qriously :value="activeWallet.address" :size="100" />
          </div>
        </div>

        <div class="the-my-wallet-in-popup__address-wrapper">
          <div class="the-my-wallet-in-popup__copy the-my-wallet-in-popup__copy-address">
            <e-copy-text
              title="Wallet address"
              :hash="activeWallet.address"
              compact
              small-hash-size
              btn-name="Copy"
            />
          </div>

          <div class="the-my-wallet-in-popup__copy-key-wrapper">
            <div class="the-my-wallet-in-popup__copy the-my-wallet-in-popup__copy-key">
              <e-copy-text
                title="Private Key"
                :hash="displayedPrivateKey"
                is-masked
                compact
                btn-name="Copy"
              />
            </div>
          </div>
        </div>

        <div class="the-my-wallet-in-popup__consent" :class="{'pulseShadow': (closeGeneratePulse || closeGeneratePulseIn) && !checked }" @click="checked = true">
          <div class="e-tooltip__content" v-if="(closeGeneratePulse || closeGeneratePulseIn) && !checked">
            You must check this box
          </div>

          <e-icon :src="iconConsent" class="the-my-wallet-in-popup__consent-icon" :class="{ 'checked': checked }" />

          I understand that DMEX has no access to my private key and in case of loss I will not be able to restore access to my wallet and the funds on the smart contracts.
        </div>
      </template>
    </div>

    <div class="the-login-flow-popup__footer-sbmt">
      <div class="the-login-flow-popup__footer-sbmt-item">
        <button
          type="button"
          class="e-receipt__btn e-receipt__btn_active the-login-flow-popup__cancel-button"
          @click="cancelOperationMethod"
        >
          Close
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import EBalanceTransferOperations from '@/components/EBalanceTransferOperations';
import ETooltip from '@/components/ETooltip';

/**
 * Component: Компонент моего кошелька
 *
 * @version 1.0.1
 */
import { mapGetters, mapMutations } from 'vuex';
import Qriously from 'vue-qriously/src/components/v-qriously';
// import store from '@/store';
import { toDisplayedPrivateKey } from '@/util/crypto';

import TheMyWalletTransactions from '@/containers/blocks/TheMyWalletTransactions';
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';
import EHeading from '@/components/EHeading';
import EButton from '@/components/EButton';
import ECopyText from '@/components/ECopyText';
import ETokenIcon from '@/components/ETokenIcon';

import iconImportant from '@/icons/e-icon__important.svg';
import iconLoading from '@/icons/e-icon__loading-gen.svg';
import iconConsent from '@/icons/e-icon__consent.svg';

/**
 * Assuming that user has active wallet selected
 */
export default {
  name: 'the-my-wallet-in-popup',

  data() {
    return {
      destroyed: false,
      isGenerateShow: true,

      // Icons
      iconImportant,
      iconLoading,
      iconConsent,
      checked: false,
      closeGeneratePulseIn: false,
    };
  },

  props: {
    closeGeneratePulse: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    displayedPrivateKey() {
      return toDisplayedPrivateKey(this.activeWallet.privateKey);
    },

    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      theme: 'ui/getTheme',
      referrerAddress: 'affiliate/referrerAddress',
    }),
  },

  watch: {
  },

  created() {
    this.$store.dispatch('balance/listenToPrivateBalanceChanges');
    this.$store.dispatch('balance/listenToContractBalanceChanges');
  },

  mounted() {
    if (this.destroyed) return;
    setTimeout(async () => {
      if (this.destroyed) return;
      this.$store.dispatch('wallets/createWallet', { ga: this.$ga, referrerAddress: this.referrerAddress });
      this.isGenerateShow = false;
    }, 2000);
  },

  beforeDestroy() {
    this.$store.dispatch('balance/stopListeningToPrivateBalanceChanges');
    this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
  },

  destroyed() {
    this.destroyed = true;
  },

  methods: {
    ...mapMutations({ cancelOperation: 'ui/HIDE_LOGIN_FLOW' }),

    cancelOperationMethod() {
      if (this.checked) {
        this.cancelOperation();
      } else {
        this.closeGeneratePulseIn = true;
      }
    },
  },

  components: {
    Qriously,
    EBalanceTransferOperations,
    TheMyWalletTransactions,
    EBigNumber,
    EIcon,
    EHeading,
    EButton,
    ECopyText,
    ETokenIcon,
    ETooltip,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-my-wallet-in-popup {
  $this: &;
  padding-top: 30px;

  &__consent {
    position: relative;
    background-color: #C66D6D;
    width: 100%;
    padding: 14px 18px 14px 50px;
    box-sizing: border-box;
    border-radius: 3px;
    font-weight: 500;
    font-size: 13px;
    color: #FFFFFF;
    line-height: 17px;
    cursor: pointer;
    text-align: left;
    margin-top: 20px;
    transition: background-color .15s ease-in-out;

    &:hover {
      background-color: #c65b5e;
    }

    .e-tooltip__content {
      display: none;
      width: 230px;
      top: -36px;
      left: 5px;
      background: #0497FE;

      &:before {
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 6px solid #0497FE;
        left: 19px;
        bottom: -11px;
        top: auto;
      }
    }

    &.pulseShadow {
      animation: pulseShadow 2s infinite;

      .e-tooltip__content {
        display: block;
        opacity: 1;
        transform: translate(0, 5px);
      }
    }
  }

  &__consent-icon {
    width: 20px;
    height: 20px;
    position: absolute;
    left: 20px;
    top: 12px;
    background-color: #fff;
    border-radius: 3px;
    transition: background-color .15s ease-in-out;

    &.checked {
      background-color: transparent;
    }
  }

  &__copy {
    max-width: 520px;
    flex: 0 0 auto;

    .e-copy-text__btn {
      @include r(1200) {
        min-width: 160px;
      }
    }

    .e-copy-text__head {
      margin-bottom: 0;
    }

    .e-copy-text__title {
      font-size: 13px;
      font-weight: 500;
      color: #000;
      line-height: 13px;
      margin-right: 5px;
    }

    .e-copy-text__qr,
    .e-copy-text__toggle-text {
      font-size: 8px;
      letter-spacing: .05em;
      line-height: 12px;
      font-weight: 500;
    }

    .e-copy-text__hash {
      font-size: 20px;
      font-weight: 500;
      color: #232323;
    }

    .e-copy-text__in {
      padding-right: 0;
      padding-left: 0;

      @include r(1200) {
        padding-left: 0;
      }
    }

    .e-copy-text__btns {
      position: absolute;
      top: calc(50% + 2px);
      right: 6px;

      .e-copy-text__btn {
        min-width: 40px;
      }
    }
  }

  &__copy-address {
    margin-top: 0;

    .e-copy-text__hash {
      font-size: 15px;
      font-weight: 500;

      @include r(640) {
        font-size: 13px;
      }
    }
  }

  &__copy-key-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__copy-key {
    display: flex;
    align-items: center;
    margin-top: 23px;
    max-width: 100%;
    width: 100%;

    .e-copy-text__hash {
      font-size: 11px;
      letter-spacing: -.015em;

      @include r(640) {
        font-size: 10px;
      }
    }
  }

  &__address-wrapper {
    text-align: left;
    background: #fff;
    border-radius: 3px;
    margin-top: 20px;
    padding: 20px;
    box-sizing: border-box;

    .e-copy-text__btn {
      .e-button {
        width: 40px;
        height: 18px;
        background: $color-green-dark;
        font-weight: 600;
        font-size: 8px;
        color: #FFFFFF;
        letter-spacing: 0.53px;
        padding: 0;
        border-radius: 4px;
      }

      .e-copy-text__btn-text {
        height: 19px;
      }
    }

    .e-copy-text__btn_copy .e-button:hover {
      background: $color-green-dark;
    }

    .e-copy-text__btn-text.e-copy-text__btn-text-copied span span {
      display: none;
    }

    .e-copy-text__head {
      font-weight: 500;
      font-size: 12px;
      color: #8D9098;
      line-height: 19px;
    }

    .e-copy-text__wrapper-btns {
      width: 100%;
      height: 40px;
      margin-top: 5px;
      border-radius: 3px;
      background: $color-light-blue;
      padding: 5px 8px;
      box-sizing: border-box;
    }
  }

  &__dark {
    #{$this}__copy {
      .e-copy-text__title {
        color: #8D9098;
      }
    }

    .e-copy-text__qr .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-copy-text__qr-text,
    .e-copy-text__toggle-text,
    .e-copy-text__toggle:hover .e-copy-text__toggle-text {
      color: $color-green-dark;
    }

    #{$this}__address-wrapper {
      background: $dark-background-lighter;

      .e-copy-text__wrapper-btns {
        background: #fff;
      }
    }
  }

  @keyframes pulseShadow {
    0% {
      box-shadow: 0 0 0 0 rgba(198,109,109, 0.5);
    }
    80% {
      box-shadow: 0 0 0 20px rgba(198,109,109, 0);
    }
    100% {
      box-shadow: 0 0 0 0 rgba(198,109,109, 0);
    }
  }
}
</style>



