/**
 * @module store/contracts/actions
 *
 * @version 1.1.0
 */


import * as types from '@/store/mutation-types';
import { getContracts, contractSubscribe as contractSubscribeRequest } from '@/api/backend';
import { ACTIVE_MAIN_CONTRACT_KEY, ACTIVE_FUTURES_CONTRACT_KEY, ACTIVE_DMEX_ORACLE_CONTRACT_KEY } from '@/store/constants';
import log from '@/util/logger';
import { getStorageValue, setStorageValue } from '@/util/storage';
import { wrapCachedAction } from '@/store/utils';

const loadExchangeContracts = async ({ commit, rootGetters }) => {
  commit(types.GET_EXCHANGE_CONTRACTS_ASYNC.PENDING);
  let contracts;
  try {
    contracts = await getContracts('exchange');
  } catch (error) {
    commit(types.GET_EXCHANGE_CONTRACTS_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`Error loading exchange contracts: ${error}`, {
        module: 'contracts',
        layer: 'backend',
      });
    }
  }
  if (contracts) {
    commit(types.GET_EXCHANGE_CONTRACTS_ASYNC.SUCCESS, contracts);
  }
};

const loadFuturesContracts = async ({ commit, rootGetters }) => {
  commit(types.GET_FUTURES_CONTRACTS_ASYNC.PENDING);
  let contracts;
  try {
    contracts = await getContracts('futures');
  } catch (error) {
    commit(types.GET_FUTURES_CONTRACTS_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`Error loading exchange contracts: ${error}`, {
        module: 'contracts',
        layer: 'backend',
      });
    }
  }
  if (contracts) {
    commit(types.GET_FUTURES_CONTRACTS_ASYNC.SUCCESS, contracts);
  }
};

const loadDmexOracleContracts = async ({ commit, rootGetters }) => {
  commit(types.GET_DMEX_ORACLE_CONTRACTS_ASYNC.PENDING);
  let contracts;
  try {
    contracts = await getContracts('dmex_oracle');
  } catch (error) {
    commit(types.GET_DMEX_ORACLE_CONTRACTS_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`Error loading exchange contracts: ${error}`, {
        module: 'contracts',
        layer: 'backend',
      });
    }
  }
  if (contracts) {
    commit(types.GET_DMEX_ORACLE_CONTRACTS_ASYNC.SUCCESS, contracts);
  }
};

/**
 * Загружает всех контрактов с бекенда
 *
 * @see See [Docs](https://docs.demo.dmex.app/#api-Contracts-WsContractsGetAll)
 * @since Version 1.0.1
 */
const loadContracts = wrapCachedAction('loadContracts', async ({ state, commit, dispatch, rootGetters }) => {
  commit(types.GET_CONTRACTS_ASYNC.PENDING);
  await dispatch('loadExchangeContracts');
  await dispatch('loadFuturesContracts');
  await dispatch('loadDmexOracleContracts');
  if (state.exchangeContractsFailure) {
    commit(types.GET_CONTRACTS_ASYNC.FAILURE);
  } else {
    const blockchainApi = rootGetters['blockchain/contractsApi'];
    let affiliate;
    try {
      affiliate = await blockchainApi.affiliateContractAddress();
      commit(types.GET_CONTRACTS_ASYNC.SUCCESS, { affiliate });
    } catch (error) {
      commit(types.GET_CONTRACTS_ASYNC.SUCCESS, { affiliate: null });
    }
  }
});

/**
 * Action: Загружает активный главный контракт из локалсторенда или если там его нет с бекенда
 *
 * @since Version 1.0.2
 */
const loadActiveExchangeContract = wrapCachedAction('loadActiveExchangeContract', async ({ state, commit, dispatch, getters }) => {
  commit(types.GET_ACTIVE_CONTRACT.PENDING);
  commit(types.GET_ACTIVE_FUTURES_CONTRACT.PENDING);
  commit(types.GET_ACTIVE_DMEX_ORACLE_CONTRACT.PENDING);
  await dispatch('loadExchangeContracts');
  await dispatch('loadFuturesContracts');
  await dispatch('loadDmexOracleContracts');
  if (state.exchangeContractsFailure) {
    commit(types.GET_ACTIVE_CONTRACT.FAILURE);
    commit(types.GET_ACTIVE_FUTURES_CONTRACT.FAILURE);
    commit(types.GET_ACTIVE_DMEX_ORACLE_CONTRACT.FAILURE);
    return;
  }

  const storedContractId = getStorageValue(ACTIVE_MAIN_CONTRACT_KEY);
  const storedFuturesContractId = getStorageValue(ACTIVE_FUTURES_CONTRACT_KEY);
  const storedDmexOracleContractId = getStorageValue(ACTIVE_DMEX_ORACLE_CONTRACT_KEY);
  const contractByStorage = getters.getExchangeContractById(storedContractId);
  const contractFuturesByStorage = getters.getFuturesContractById(storedFuturesContractId);
  const contractDmexOracleByStorage = getters.getDmexOracleContractById(storedDmexOracleContractId);

  if (contractByStorage) {
    // Если в локал сторедже есть сохраненный текущий главный контракт
    commit(types.GET_ACTIVE_CONTRACT.SUCCESS, contractByStorage);
  } else {
    commit(types.GET_ACTIVE_CONTRACT.SUCCESS, state.exchangeContracts[0]); // latest is first
    setStorageValue(ACTIVE_MAIN_CONTRACT_KEY, state.activeMainContract.id);
  }

  if (contractFuturesByStorage) {
    // Если в локал сторедже есть сохраненный текущий главный контракт
    commit(types.GET_ACTIVE_FUTURES_CONTRACT.SUCCESS, contractFuturesByStorage);
  } else {
    commit(types.GET_ACTIVE_FUTURES_CONTRACT.SUCCESS, state.futuresContracts[0]); // latest is first
    setStorageValue(ACTIVE_FUTURES_CONTRACT_KEY, state.activeFuturesContract.id);
  }

  if (contractDmexOracleByStorage) {
    // Если в локал сторедже есть сохраненный текущий главный контракт
    commit(types.GET_ACTIVE_DMEX_ORACLE_CONTRACT.SUCCESS, contractDmexOracleByStorage);
  } else {
    commit(types.GET_ACTIVE_DMEX_ORACLE_CONTRACT.SUCCESS, state.dmexOracleContracts[0]); // latest is first
    setStorageValue(ACTIVE_DMEX_ORACLE_CONTRACT_KEY, state.activeDmexOracleContract.id);
  }
});

/**
 * Action: Изменение активного главного контракта. Сохраняем главный контракт в localstorage
 *
 * @since Version 1.0.2
 */
const changeActiveMainContract = async ({ commit, state }, contract) => {
  if (state.activeMainContract === null || contract.id !== state.activeMainContract.id) {
    commit(types.SET_ACTIVE_MAIN_CONTRACT, contract);
    setStorageValue(ACTIVE_MAIN_CONTRACT_KEY, contract.id);
  }
};
const changeActiveFuturesContract = async ({ commit, state }, contract) => {
  if (state.activeFuturesContract === null || contract.id !== state.activeFuturesContract.id) {
    commit(types.SET_ACTIVE_FUTURES_CONTRACT, contract);
    setStorageValue(ACTIVE_FUTURES_CONTRACT_KEY, contract.id);
  }
};
const changeActiveDmexOracleContract = async ({ commit, state }, contract) => {
  if (state.activeDmexOracleContract === null || contract.id !== state.activeDmexOracleContract.id) {
    commit(types.SET_ACTIVE_DMEX_ORACLE_CONTRACT, contract);
    setStorageValue(ACTIVE_DMEX_ORACLE_CONTRACT_KEY, contract.id);
  }
};

/**
 * Action: Подписка на конкретный контракт
 * (только после нее можно получать ивенты обновления)
 *
 * Оптимизация:
 * Promise разрешается как только запрос на подписку отправлен, не дожидаясь того чтобы тот вернулся обратно.
 *
 * @since Version 1.1.0
 */
const subscribeToContract = wrapCachedAction('subscribeToContract', async ({ state, commit, dispatch }) => {
  commit(types.SUBSCRIBE_TO_CONTRACT_ASYNC.PENDING);
  await dispatch('loadActiveExchangeContract');
  // Оптимизация: Promise resolve-иться как только запрос на подписку отправлен, не дожидаясь ответа.
  (async () => {
    if (!state.activeMainContract) {
      commit(types.SUBSCRIBE_TO_CONTRACT_ASYNC.FAILURE);
    } else {
      await contractSubscribeRequest(state.activeMainContract.contractAddress);
      commit(types.SUBSCRIBE_TO_CONTRACT_ASYNC.SUCCESS);
    }
  })();
});

export default {
  loadExchangeContracts,
  loadFuturesContracts,
  loadDmexOracleContracts,
  loadContracts,
  loadActiveExchangeContract,
  changeActiveMainContract,
  changeActiveFuturesContract,
  changeActiveDmexOracleContract,
  subscribeToContract,
};



