/**
 * @module store/order-book/getters
 *
 * @version 1.0.0
 */
import { BigNumber } from 'bignumber.js';
import { ORDERS_IN_ORDERBOOK } from '@/config';
import { ETH_DECIMALS } from '@/util/constants';
import { compressOrderBookElements } from './helpers';

/**
 * Getter: Возвращает загружаются сейчас данные ордербука или нет
 *
 * @since Version 1.0.4
 */
const orderBookLoading = state => state.orderBookLoading;


/**
 * Getter: Возвращает все предложения продажи
 *
 * @since Version 1.0.4
 */
const getAsks = (state, getters, rootState, rootGetters) => {
  const res = [];

  const activeToken = rootGetters['tokens/activeToken'];

  if (activeToken) {
    let asks = compressOrderBookElements(state.asks);
    if (asks.length > ORDERS_IN_ORDERBOOK) {
      asks = asks.slice(0, ORDERS_IN_ORDERBOOK);
    }
    const decimals = activeToken.decimals;
    let sum = BigNumber(0);

    const average = asks.reduce((acc, current) => acc.plus(current[0]), BigNumber(0)).dividedBy(asks.length);

    for (let i = 0; i < asks.length; i += 1) {
      const amount = asks[i][0];
      const unitPrice = asks[i][1];
      const volume = amount.shiftedBy(ETH_DECIMALS - decimals).times(unitPrice);
      const proportion = amount.dividedBy(average).toNumber();

      sum = sum.plus(volume);
      res.unshift({
        amount,
        proportion,
        unitPrice,
        ethAmount: sum,
      });
    }
  }

  return res;
};


/**
 * Getter: Возвращает все предложения покупки
 *
 * @since Version 1.0.4
 */
const getBids = (state, getters, rootState, rootGetters) => {
  const res = [];
  const activeToken = rootGetters['tokens/activeToken'];

  if (activeToken) {
    let bids = compressOrderBookElements(state.bids);
    if (bids.length > ORDERS_IN_ORDERBOOK) {
      bids = bids.slice(0, ORDERS_IN_ORDERBOOK);
    }
    const decimals = activeToken.decimals;
    let sum = BigNumber(0);

    const average = bids.reduce((acc, current) => acc.plus(current[0]), BigNumber(0)).dividedBy(bids.length);

    for (let i = 0; i < bids.length; i += 1) {
      const amount = bids[i][0];
      const unitPrice = bids[i][1];
      const volume = amount.times(unitPrice).shiftedBy(ETH_DECIMALS - decimals);
      const proportion = amount.dividedBy(average).toNumber();

      sum = sum.plus(volume);
      res.push({
        amount,
        proportion,
        unitPrice,
        ethAmount: sum,
      });
    }
  }

  return res;
};


export default {
  getAsks,
  getBids,
  orderBookLoading,
};



