import { INTERVAL_BALANCE_CHECK } from '@/config';
import log from '@/util/logger';

/**
 * ETH update callback
 *
 * @callback PrivateBalanceListener~ethChangeHandler
 * @param {BigNumber} actual blockchain ETH balance
 */

/**
 *
 * Token balance update callback
 *
 * @callback PrivateBalanceListener~tokenChangeHandler
 * @param {BigNumber} actual blockchain token balance
 */

/**
 * Private balance callback
 * privateBalance is object with 2 props: eth - BigNumber, tokens - object with tokens (key is token contract - string, value is token balance - BigNumber)
 * privateErrors contains list of tokens that were failed to fetch
 *
 * @callback PrivateBalanceListener~balanceChangeHandler
 * @param {{ privateBalance: { eth: BigNumber, tokens: {} }, privateErrors: string[] }} args
 */

/**
 * Listens to Private Balance.
 *
 *
 * @property {number|undefined} lastTimeout
 * @property {BalanceBlockchain} balanceApi
 */
export default class PrivateBalanceListener {
  constructor({ balanceApi, blockObserver }) {
    this.balanceApi = balanceApi;
    this.blockObserver = blockObserver;
    this.active = false;
  }

  /**
   * Subscribe on ETH update events
   *
   * @param {PrivateBalanceListener~ethChangeHandler} handler - function that receives private balance as argument
   */
  listenToEthChanges(handler) {
    this.listenToChanges(handler, async ({ blockNumber }) => {
      const balance = await this.balanceApi.getPrivateEth({ blockNumber });
      handler(balance);
    });
  }

  /**
   * Subscribe on concrete token events
   *
   * @param {string} tokenAddress - token to be listened to in blockchain
   * @param {PrivateBalanceListener~tokenChangeHandler} handler - function that receives private balance as argument
   */
  listenToTokenChanges(tokenAddress, handler) {
    this.listenToChanges(handler, async ({ blockNumber }) => {
      const balance = await this.balanceApi.getPrivateToken({ tokenAddress, blockNumber });
      handler(balance);
    });
  }

  /**
   * Subscribe on ETH and multiple token balance changes
   *
   * @param {string[]}tokens - list of tokens that will be listened to in blockchain
   * @param {PrivateBalanceListener~balanceChangeHandler} handler - function that receives private balance as argument
   */
  listenToAllChanges(tokens, handler) {
    this.listenToChanges(handler, async ({ blockNumber }) => {
      const balance = await this.balanceApi.getPrivateEthAndTokens({ tokens, blockNumber });
      handler(balance);
    });
  }

  /**
   * Stop listener that was started earlier.
   * handler is the function that was passed as listener callback.
   */
  stopListeningToPrivateBalance() {
    this.active = false;
    clearTimeout(this.lastTimeout);
  }

  /**
   * Only for internal use (i.e. not a part of public interface).
   * Common subscription code is extracted here (DRY).
   *
   * @private
   * @param listenerId - id of listener
   * @param tickCallback - callback, that is called on timeout
   */
  listenToChanges(listenerId, tickCallback) {
    this.active = true;
    const loop = async () => {
      try {
        const blockNumber = await this.blockObserver.getApprovedBlockNumber();
        if (!this.active) return; // stop any activity if is not active
        await tickCallback({ blockNumber });
      } catch (error) {
        log.warn(`update private balance error ${error}`);
      }
      if (!this.active) return; // stop any activity if is not active
      this.lastTimeout = setTimeout(loop, INTERVAL_BALANCE_CHECK);
    };
    this.lastTimeout = setTimeout(loop, INTERVAL_BALANCE_CHECK);
  }
}



