window.backend = backend; // test global var

// Connection controls

export const reconnect = () => {
  if (backend.socket.disconnected) {
    backend.socket.open();
  }
};

export const close = () => {
  backend.socket.close();
};

/**
 * Задает функцию колбек, которая будет вызываться при соединении или востановлении соединения с сервером
 *
 * @param {Function} callback - Колбек, который будет вызываться при конекте
 * @returns {void}
 */
export const onConnect = (callback) => {
  backend.socket.on('connect', callback);
};

export const onConnectError = (callback) => {
  backend.socket.on('connect_error', callback);
};

/**
 * Задает функцию колбек, которая будет вызываться при разрыве соединении с сервером
 *
 * @param {Function} callback - Колбек, который будет вызываться при разрыве соединения с сервером
 * @returns {void}
 */
export const onDisconnect = (callback) => {
  backend.socket.on('disconnect', callback);
};

/**
 * Задает функцию колбек, которая будет вызываться при востановлении соединения с сервером
 *
 * @param {Function} callback - Колбек, который будет вызываться при востановлении соединения с сервером
 * @returns {void}
 */
export const onReconnect = (callback) => {
  backend.socket.on('reconnect', callback);
};

export const removeReconnectListener = (listener) => {
  if (listener != null) {
    backend.socket.removeListener('reconnect', listener);
  }
};

// Subscriptions

/**
 * Subscribe on events related to selected exchange contract.
 *
 * @see See [Docs](https://docs.demo.dmex.app/#api-Contracts-WsContractsSubscribe)
 * @param {string} contractAddress - Ethereum address of exchange contract
 * @returns {Promise<string>} - success message
 */
export const contractSubscribe = contractAddress => backend.wsRequest('/contracts/subscribe', contractAddress);

/**
 * Subscribe on events related to selected tokenAddress.
 *
 * @see See [Docs](https://docs.demo.dmex.app/#api-Tokens-WsTokensSubscribe)
 * @param {string} tokenAddress - Ethereum address of token
 * @returns {Promise<string>} - success message
 */
export const tokenSubscribe = tokenAddress => backend.wsRequest('/tokens/subscribe', tokenAddress);

/**
 * Subscribe on events related to selected user account.
 *
 * @see See [Docs](https://docs.demo.dmex.app/#api-Accounts-WsAccountsSubscribe)
 * @param {string} userAddress - Ethereum address of user wallet
 * @returns {Promise<string>} - success message
 */
export const accountSubscribe = userAddress => backend.wsRequest('/accounts/subscribe', userAddress);

// General

/**
 * App configs. Contain some fee-related info, limits and timestamp
 *
 * @todo refactor to return camelCased values
 * @returns {Promise<{trade_confirmations: number, fund_confirmations: number, trade_taker_gas_limit_fee: number, withdraw_gas_limit_fee: number, order_min_eth: number, server_timestamp: number, gasPriceType: string}>}
 */
export const getConfigs = async () => {
  const config = await backend.wsRequest('/platform/configs');
  return {
    gasPriceType: config.ow_gas_price_type,
    ...config,
  };
};

/**
 * Server timestamp. Can be used to create signature nonce.
 *
 * @returns {Promise<number>}
 */
export const getTimestamp = async () => {
  const response = await backend.wsRequest('/platform/configs');
  return response.server_timestamp;
};

/**
 * Total number of wallets
 *
 * @see See [Docs](https://docs.demo.dmex.app/#api-Platform-WsPlatformCountWallets)
 * @returns {Promise<number>}
 */
export const getTotalWallets = () => backend.wsRequest('/platform/count-wallets');

