export const uploadTokenLogo = async (logo, onProgress) => {
  const data = new window.FormData();
  data.set('logo_file', logo);
  const response = await backend.http.post('/api/tokens/upload-logo', data, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    onUploadProgress: (e) => {
      onProgress(Math.floor((e.loaded * 100) / e.total));
    },
  });
  return response.data.data;
};

