// History

/**
 * Функция меппинга треда для отборажения инфомрации об афилированных коммисиях
 */
const mapHistoryItem = (data, { activeToken }) => {
  const amountTokenAddress = data.amount_token_address;
  const getCurrencyByAddress = (address) => {
    const options = {
      [ETH_TOKEN.contract]: ETH_TOKEN,
      [amountTokenAddress]: {
        contract: amountTokenAddress,
        shortName: data.token_symbol || 'N/A',
        decimals: data.token_decimals || null,
      },
    };
    if (activeToken) {
      options[activeToken.contract] = activeToken;
    }
    return options[address] || null;
  };
  const amountToken = getCurrencyByAddress(amountTokenAddress);
  const rawUnitPrice = data.unit_price;
  const isAbleToHaveUnitPrice = data.unit_price != null;
  let amount = BigNumber(data.amount);
  let unitPrice = null;
  let netAmount = BigNumber(data.net_amount);
  const gasFee = data.gas_fee ? BigNumber(data.gas_fee) : null;
  const fee = data.fee ? BigNumber(data.fee) : null;
  if (data.section === SECTION_FUTURES_TRADES) {
    unitPrice = BigNumber(rawUnitPrice).shiftedBy(-8);
    amount = amount.shiftedBy(10);
    netAmount = netAmount.shiftedBy(10);
    // gasFee = gasFee !== null ? gasFee.shiftedBy(10) : null;
    // fee = fee !== null ? fee.shiftedBy(10) : null;
  } else if (isAbleToHaveUnitPrice && amountToken.decimals != null) {
    unitPrice = toDisplayedPrice(rawUnitPrice, amountToken.decimals);
  }

  return {
    actionId: data.action_id,
    createdAt: new Date(data.created_at),
    section: data.section,
    action: data.action,
    tokenSymbol: data.token_symbol,
    tokenName: data.token_name,
    futuresAssetName: data.futures_asset_name,
    futuresAssetDecimals: data.futures_asset_decimals,
    amount,
    amountToken,
    incomeToken: getCurrencyByAddress(data.net_amount_token_address), // net and fee token address
    fee,
    gasFeeToken: getCurrencyByAddress(data.gas_fee_token_address),
    gasFee,
    netAmount,
    isAbleToHaveUnitPrice,
    unitPrice,
    transactionHash: data.transaction_hash || undefined,
    isConfirmed: data.is_confirmed,
  };
};

const mapHistoryRecent = (data, tokenDecimals) => ({
  actionId: data.action_id,
  createdAt: new Date(data.created_at),
  action: data.action,
  tokenAmount: BigNumber(data.amount),
  tokenAddress: data.amount_token_address,
  unitPrice: data.unit_price == null ? undefined : toDisplayedPrice(data.unit_price, tokenDecimals),
  isConfirmed: data.is_confirmed,
});

/**
 * Постраничное получение истории
 *
 * @param {Object} filters - Фильтры для получения истории
 * @param {{ activeToken: Object }} helpers - текущий активный токен. может использоваться для дополнения недостающей информации
 */
export const getHistoryPage = async ({ userAddress, contractAddress, section, pageNumber }, helpers) => {
  const size = HISTORY_PAGE_SIZE;
  const { data, meta } = await backend.wsRequest('/history/get', {
    filters: {
      user_address: userAddress,
      contract_address: contractAddress,
      section,
    },
    limit: size,
    offset: size * pageNumber,
  });
  return {
    pageData: data.map(e => mapHistoryItem(e, helpers)),
    pageCount: Math.ceil(meta.count_items / size),
  };
};

/**
 * Постраничное получение истории
 *
 * @param {Object} filters - Фильтры для получения истории
 * @param {{ activeToken: Object }} helpers - текущий активный токен. может использоваться для дополнения недостающей информации
 */
export const getAffiliateHistoryPage = async ({ userAddress, pageNumber, activeToken }) => {
  const size = HISTORY_PAGE_SIZE;

  const { data: { data } } = await backend.http.get('/api/futures/affiliates', {
    params: {
      user_address: userAddress,
      limit: size,
      offset: size * pageNumber,
    },
  });

  return {
    pageData: data.map(e => mapHistoryItem(e, activeToken || {})),
  };
};


