import { bufferToHex, fromRpcSig, toBuffer, sha3, stripHexPrefix, addHexPrefix } from 'ethereumjs-util';
import { TIMEOUT_LEDGER_USER_RESPONSE } from '@/config';
import { getLedgerApp } from '@/api/ethereum/ledger';
import { PRIVATE_KEY_TYPE, METAMASK_WALLET_TYPE, LEDGER_WALLET_TYPE } from '@/util/constants';

const appendEthPrefix = (data) => {
  const prefix = toBuffer('\x19Ethereum Signed Message:\n');
  const buffer = prefix.constructor.concat([prefix, toBuffer('32'), toBuffer(data)]);
  return bufferToHex(sha3(buffer));
};

const signWithoutPrefix = async ({ web3, wallet, data }) => {
  const signature = await web3.eth.sign(data, wallet.address);
  const { v, r, s } = fromRpcSig(signature);
  return {
    v,
    r: bufferToHex(r),
    s: bufferToHex(s),
  };
};

const signWithPrefix = ({ data, ...rest }) => signWithoutPrefix({ data: appendEthPrefix(data), ...rest });

const signWithLedger = async ({ wallet, data }) => {
  const ledgerApp = await getLedgerApp({ exchangeTimeout: TIMEOUT_LEDGER_USER_RESPONSE });
  if (wallet.derivationPath === undefined) throw new Error('Derivation Path is undefined');
  const path = wallet.derivationPath.slice(2);
  const message = stripHexPrefix(data);
  const { v, r, s } = await ledgerApp.signPersonalMessage(path, message);
  return {
    v,
    r: addHexPrefix(r),
    s: addHexPrefix(s),
  };
};

const signFunctions = {
  [PRIVATE_KEY_TYPE]: signWithoutPrefix,
  [METAMASK_WALLET_TYPE]: signWithPrefix,
  [LEDGER_WALLET_TYPE]: signWithLedger,
};

export default ({ web3, wallet, data }) => signFunctions[wallet.type]({ web3, wallet, data });



