import { ASYNC_STATES } from '@/util/constants';

const state = {
  activeToken: null,

  setActiveTokenState: ASYNC_STATES.INITIAL,

  // Информация по всем токенам
  exchangeTokens: [],

  // externalTokens: [],

  // My tokens (starred tokens)
  selectedTokens: [],

  assets: [],

  /**
   * Flag: true when all tokens are successfully loaded
   */
  tokensLoaded: false,

  /**
   * Флаг, который показывает загружются ли в текущий момент числовые данные по токенам
   */
  tokensNumbersLoading: false,

  tokenNumbersListenerCount: 0,
};

export default state;



